const Backbone = require('backbone');
const { BASE_URL } = require('progeny/config');
const { buildUrl } = require('@infosight/elmer/dist/utils/url');
const _ = require('underscore');
var Marionette = require('backbone.marionette');
var URI = require('urijs');
var InnerHitView = require('./InnerHitView');
var tpl = require('raw-loader!./HitView.html');

var VERSION_FAMILY_COLLAPSE = [
    // If all 2.n.x families are present, collapse to a single 2.x entry
    ['2.0.x', '2.1.x', '2.2.x', '2.3.x'], '2.x',
];

module.exports = Marionette.CompositeView.extend({

    template: _.template(tpl, { variable: 'hit' }),

    templateHelpers: function () {
        var model = this.model;
        return {
            _,
            versionFamilies: function () {
                var families = model.get('_source')['Version Family'];
                if (!families) {
                    return [];
                }
                if (!Array.isArray(families)) {
                    families = [families];
                }
                for (var i = 0; i < VERSION_FAMILY_COLLAPSE.length; i++) {
                    var childVersions = VERSION_FAMILY_COLLAPSE[i];
                    var parentVersion = VERSION_FAMILY_COLLAPSE[++i];
                    if (_.every(childVersions, function (childVersion) {
                        return _.contains(families, childVersion);
                    })) {
                        families = _.difference(families, childVersions);
                        families.push(parentVersion);
                    }
                }
                families.sort();
                return families;
            }(),

            mediaUrl(...path) {
                return buildUrl(BASE_URL, 'media', ...path);
            },
        };
    },

    tagName: 'li',
    className: 'search-hit',

    childView: InnerHitView,

    childViewContainer: 'ul.search-inner-hits',

    initialize: function () {
        this.collection = new Backbone.Collection(
            this.model.get('inner_hits') ? this.model.get('inner_hits').topics.hits.hits : []);
    },

});
