import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import classnames from 'classnames/bind';

import Async from '@infosight/elmer/dist/components/Async';

import { addressSelector, assetSelector } from '../reducer';
// import { listSelector } from '../../arrays/collection/reducer';

import { fetchAddresses, fetchAssets } from '../actionCreators';

import AddressCard from './AddressCard';
// import SelectWithFeedback from './SelectWithFeedback';

import style from './style.scss';

const cx = classnames.bind(style);


/* eslint-disable jsx-a11y/label-has-for */
/* eslint-disable react/prefer-stateless-function */
class AssetRegistry extends Component {
    constructor(props) {
        super(props);
        autobind(this);

        this.state = {
            searchText: '',
        };
    }

    /* eslint-disable react/prop-types */
    componentDidMount() {
        this.load();
    }

    onChangeAssetSearch(e) {
        this.setState({ searchText: e.target.value });
    }

    onClearAssetSearch() {
        this.setState({ searchText: '' });
    }

    load() {
        if (!this.props.address.loadingAddressCollection && !this.props.address.loadedAddressCollection) {
            this.props.fetchAddresses();
        }
        if (!this.props.address.loadingAssetCollection && !this.props.address.loadedAssetCollection) {
            this.props.fetchAssets();
        }
    }

    searchFilter(asset) {
        const { searchText } = this.state;
        if (!searchText) {
            return true;
        }

        const searchFields = [
            'System_Name__c',
            'name',
            'serialNumber',
            'ContactEmail__c',
            'Install_Street1__c',
            'Install_Street2__c',
            'Install_City__c',
            'Install_State_Province__c',
            'Install_Zip_Code__c',
            'Install_Country__c',
            'SLA__c',
            'Support_Level__c',
            'assetArenaProductCode__c',
        ];

        return searchFields.some(
            field => (asset[field] ? asset[field].indexOf(searchText) !== -1 : false)
        );
    }

    render() {
        return (
            <Async loading={
                this.props.address.loadingAddressCollection ||
                this.props.address.loadingAssetCollection
            }
            >
                <div className={cx('assetRegistry')}>
                    <div className={classnames('row expanded')}>
                        <div className="column small-12 medium-10 large-8">
                            <div className={cx('search-container')}>
                                <div className={cx('search-icon-container')}>
                                    <label htmlFor="asset-search" className={cx('search-icon', 'search-icon-inline')}>
                                        Search <i className="fa fa-search" />
                                    </label>
                                </div>
                                <div className={cx('search-input-container')}>
                                    <input
                                        id="asset-search"
                                        className={cx('search')}
                                        type="text"
                                        autoFocus
                                        onChange={this.onChangeAssetSearch}
                                        value={this.state.searchText}
                                    />
                                    <button className={cx('cancel-search')} onClick={this.onClearAssetSearch}>{'\u00D7'}</button>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className={classnames('row expanded', cx('assetRegistry'))}>
                        {
                            (this.props.address.assetCollection && this.props.address.addressCollection &&
                                this.props.address.assetCollection
                                    .filter(x => x.Order_Type__c !== 'VM Image')
                                    .filter(this.searchFilter)
                                    .map(asset => (<AddressCard key={asset.id} asset={asset} />))
                            )
                        }
                    </div>
                </div>
            </Async>
        );
    }
}

AssetRegistry.propTypes = {
    fetchAddresses: PropTypes.func,
    fetchAssets: PropTypes.func,
    address: PropTypes.object,
};

const mapStateToProps = state => ({
    ...addressSelector(state),
    ...assetSelector(state),
});

const mapDispatchToProps = dispatch => bindActionCreators({
    fetchAddresses,
    fetchAssets,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(AssetRegistry);
