import _ from 'underscore';
import moment from 'moment-timezone';
import React from 'react';
import { withFormik } from 'formik';
import classnames from 'classnames/bind';

import style from './style.scss';

const cx = classnames.bind(style);

/* eslint-disable react/no-multi-comp */
/* eslint-disable jsx-a11y/label-has-for */
/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */

const formikEnhancer = withFormik({
    handleSubmit: () => {
    },
});

const AddressValidationForm = (props) => {
    const {
        id,
        values,
        assetData,
        touched,
        dirty,
        errors,
        handleChange,
        handleBlur,
        handleSubmit,
        handleReset,
        setFieldValue,
        setFieldTouched,
        isSubmitting,
        onConfirm,
    } = props;

    const installAddress = assetData.Install_Street1__c ||
    assetData.Install_City__c ||
    assetData.Install_State_Province__c ||
    assetData.Install_Zip_Code__c;

    const is4HourSla = _.contains(['4HR', '4ON'], assetData.Support_Level__c);
    const lastValidatedBy = assetData.assetPartShipLastValidatedByInfo__c;
    const lastValidatedDate = assetData.assetPartShipLastValidatedDatetime__c;
    const deliveryPreference = assetData.assetSameDayPartDeliveryPreference__c;
    const addressBookId = assetData.assetPartShipAddressBook__c;

    let validationIcon;
    let validationText;
    /* eslint-disable no-mixed-operators */
    if (lastValidatedDate && (!is4HourSla || is4HourSla && deliveryPreference) && addressBookId) {
        const lastModifiedInMonths = moment(Date.now()).diff(lastValidatedDate, 'months');

        /* eslint-disable no-nested-ternary */
        validationIcon = (lastModifiedInMonths > 6) ? <i className="fa fa-times" />
            : (lastModifiedInMonths > 5) ? <i className="fa fa-info-circle" />
                : <i className="fa fa-check-circle" />;

        validationText = <span>Last confirmed<br />{`${lastValidatedBy ? ` by ${lastValidatedBy}` : ''} on ${moment(lastValidatedDate).format('ll')}.`}</span>;
    } else {
        validationIcon = <i className="fa fa-times-circle" />;
        validationText = 'Please confirm your address';
    }

    return (
        <form onSubmit={handleSubmit} id={id}>
            <div className="row">
                <div className="column medium-16">
                    <label className={cx('confirmation')}>{validationIcon} {validationText}</label>
                </div>
                <div className="column medium-8">
                    <button
                        className={cx('button', 'expanded')}
                        onClick={onConfirm}
                        disabled={assetData.assetPartShipAddressBook__c === null ||
                            assetData.assetSameDayPartDeliveryPreference__c === null ||
                            (assetData.assetInstallAddressBook__c === null && !installAddress)
                        }
                    >
                        {lastValidatedBy ? 'Confirm Again' : 'Confirm'}
                    </button>
                </div>
            </div>
        </form>
    );
};

const FormikAddressValidationForm = formikEnhancer(AddressValidationForm);

export default FormikAddressValidationForm;
