const $ = require('jquery');
const { BASE_URL } = require('progeny/config');
var baseUrl = BASE_URL + '/es';

module.exports = {
    searchTemplate: function (options) {
        return $.ajax({
            type: 'POST',
            url: baseUrl + '/' + options.index + '/_search/template',
            contentType: 'application/json',
            data: JSON.stringify({
                id: options.template,
                params: options.params,
            }),
        });
    },
};
