const _ = require('underscore');
var Backbone = require('backbone'),
    tpl = require('raw-loader!./SpaceUsageView.html'),
    DataTables = require('datatables.net'),
    SpaceUsageChart = require('./SpaceUsageChart');

var template = _.template(tpl);

module.exports = Backbone.View.extend(
    /** @lends views/components/dashboard/SpaceUsageView.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/dashboard/SpaceUsageView
         * @constructor
         * @augments Backbone.View
         */


        className: 'space-usage-view',

        render: function () {
            this.$el.html(template());

            // space usage section
            this.spaceUsedChart = new SpaceUsageChart({
                model: this.model,
            });
            this.$el.find('.space-usage-summary').append(
                this.spaceUsedChart.render().el);

            this.$el.find('TABLE').dataTable({
                // bFilter: false
                sDom: '<"top">rt<"bottom"><"clear">',
            });

            this.$el.find('.space-usage-table-container').tabs();

            return this;
        },

    });
