const _ = require('underscore');
var Marionette = require('backbone.marionette');
var PaginationModel = require('./PaginationModel');
var BreadcrumbsView = require('./BreadcrumbsView');
var PaginationView = require('./PaginationView');
var HitsView = require('./HitsView');
var EmptyHitsView = require('./EmptyHitsView');
var tpl = require('raw-loader!./ResultsView.html');

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    regions: {
        breadcrumbs: '[data-region="breadcrumbs"]',
        hits: '[data-region="hits"]',
        pagination: '[data-region="pagination"]',
    },

    initialize: function (options) {
        this.query = options.query;
        this.facets = options.facets;
        this.hits = options.hits;
        this.hits.getFirstPage();
        this.pagination = new PaginationModel({}, { collection: this.hits });
    },

    onRender: function () {
        this.showChildView('breadcrumbs', new BreadcrumbsView({
            query: this.query,
            facets: this.facets,
            pagination: this.pagination,
        }));

        if (this.hits.isEmpty()) {
            this.showChildView('hits', new EmptyHitsView());
        } else {
            this.showChildView('hits', new HitsView({
                collection: this.hits,
            }));

            if (this.hits.state.totalPages > 1) {
                this.showChildView('pagination', new PaginationView({
                    model: this.pagination,
                }));
                this.$('[data-region="pagination"]').removeClass('hidden');
            }
        }
    },

});
