const $ = require('jquery');
const _ = require('underscore');
var Backbone = require('backbone');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');
const { CHART_COLORS } = require('progeny/util/chartColors');

module.exports = Backbone.View.extend(
    /** @lends views/components/dashboard/charts/CpuAreaChart.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/dashboard/charts/CpuAreaChart
         * @constructor
         * @augments Backbone.View
         */


        className: 'cpu-area-chart',

        renderChart: function (container, series, maxY) {
            var self = this;
            this.chart = new Highcharts.Chart({
                chart: {
                    type: 'area',
                    renderTo: container,
                    height: 150,
                    events: {
                        redraw: function () {
                            var containerWidth = $(this.container).width();

                            // if the no data label group is present, ensure it's centered
                            if (self.svgNoDataLabelGroup) {
                                self.svgNoDataLabelGroup.translate(
                                    (containerWidth / 2) - (self.svgNoDataLabelGroup.element.getBoundingClientRect().width / 2) + 20,
                                    65,
                                );
                            }
                        },
                        load: function () {
                            var chart = this,
                                renderer = chart.renderer,
                                hasData = false;

                            for (var i = 0; i < chart.series.length && !hasData; i++) {
                                if (chart.series[i].data && chart.series[i].data.length > 0) {
                                    hasData = true;
                                }
                            }

                            if (!hasData) {
                                self.svgNoDataLabelGroup = renderer.g().attr({ id: 'svgNoDataLabel' }).add();
                                self.svgNoDataLabelText = renderer.text('No Utilization Data Available').attr({
                                    fill: '#666',
                                }).add(self.svgNoDataLabelGroup);
                            }

                            if (!self.width) {
                                _.delay(function () {
                                    chart.reflow.call(chart);
                                }, 50);
                            }
                        },
                    },
                },
                credits: {
                    enabled: false,
                },
                title: {
                    text: null,
                },
                legend: {
                    enabled: false,
                },
                xAxis: {
                    type: 'datetime',
                    dateTimeLabelFormats: {
                        day: '%e. %b',
                        year: '%b',
                        month: '%b',
                    },
                    title: {
                        text: 'Week Ending',
                        enabled: false,
                    },
                    tickInterval: 24 * 3600 * 1000 * 90,
                },
                yAxis: {
                    min: 0,
                    max: maxY,
                    title: {
                        text: null,
                    },
                    labels: {
                        format: '{value}%',
                    },
                },
                tooltip: {
                    enabled: false,
                },
                plotOptions: {
                    area: {
                        animation: false,
                        enableMouseTracking: false,
                        marker: {
                            enabled: false,
                        },
                        stacking: 'normal',
                    },
                },
                series: series,
            });
        },

        render: function () {
            this.$el.html(this.el);

            var data = this.model.get('utilization'),
                greenSeries = { name: 'Sufficient', id: 'cpuGreenSeries', color: CHART_COLORS.legacyGreen, data: [], pointInterval: 7 * 24 * 3600 * 1000, yAxis: 0 },
                redSeries = { name: 'Insufficient', id: 'cpuRedSeries', color: CHART_COLORS.legacyRed, data: [], pointInterval: 7 * 24 * 3600 * 1000, yAxis: 0 },
                yellowSeries = { name: 'Marginal', id: 'cpuYellowSeries', color: CHART_COLORS.legacyYellow, data: [], pointInterval: 7 * 24 * 3600 * 1000, yAxis: 0 },
                maxY = 100;

            for (var i = 0; i < data.length; i++) {
                var dataPoint = data[i];

                maxY = Math.max(maxY, dataPoint.cpu);

                redSeries.data.push([dataPoint.ts, dataPoint.cpuRed]);
                yellowSeries.data.push([dataPoint.ts, dataPoint.cpuYellow]);
                greenSeries.data.push([dataPoint.ts, dataPoint.cpuGreen]);
            }

            this.renderChart(this.el, [redSeries, yellowSeries, greenSeries], maxY);

            return this;
        },

    });
