var Backbone = require('backbone'),
    UpgradeSummaryItemView = require('./UpgradeSummaryItemView');

module.exports = Backbone.View.extend(
    /** @lends views/components/dashboard/UpgradeSummaryCollectionView.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/dashboard/UpgradeSummaryCollectionView
         * @constructor
         * @augments Backbone.View
         */


        render: function () {
            this.$el.html(this.el);

            var container = document.createDocumentFragment();

            this.collection.each(function (model) {
                var row = new UpgradeSummaryItemView({ model: model });
                container.appendChild(row.render().el);
            });

            this.$el.append(container);

            return this;
        },

    });
