// SupportListCases.js

import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import { buildUrl } from '@infosight/elmer/dist/utils/url';
import { Spinner } from '@infosight/elmer/dist/components/Spinner';
import { Link } from 'react-router-dom';
import { caseListSelector, fetchCasesFailedSelector } from '../../reducer/support-cases';
import { fetchCases, fetchSfdcContact } from '../../library/support-api';
import {
    makeDomId,
    filterParamDispatch,
    filterStatusDispatch,
    getCurrentUserContactId,
} from '../../library/support-helper';
import { awaitLegacyApp } from '../../../../containers/NimbleLegacyContainer';
// import supportHelper from '../library/support-helper';

import { listSelector } from '../../../nimble/arrays/collection/reducer';
import { getArrays } from '../../../nimble/arrays/collection/actionCreators';
import { redirectToLogin } from '../../../../util/url';

import SupportTab from '../SupportTab';
import SearchNav from '../SearchNav';

import GridView from '../../../../components/GridView';
import DateTime from '../../../../components/DateTime';

import { AUTHORITIES, AuthorizationEvaluator } from '../../../user';

import { SupportLoadingError } from '../SupportLoading';
// import PriorityLabel from '../PriorityLabel';

import style from './SupportListCases.scss';
import { contactSelector } from '../../reducer/core';

const cx = classnames.bind(style);

const columns = [
    { key: 'id', visible: false },
    {
        key: 'priority',
        header: 'Priority',
        subquery: 'summary',
        summaryOptions: [{ name: 'P1', value: 'p1' },
            { name: 'P2', value: 'p2' },
            { name: 'P3', value: 'p3' },
            { name: 'P4', value: 'p4' }],
    },
    {
        key: 'assetName',
        header: 'Serial #',
        render(cd) {
            if (cd === 'Unknown Asset') {
                return (<span>Unknown Asset</span>);
            }
            const href = buildUrl('/infrastructure/storage/nimble/arrays/', cd);
            return (<Link to={href}>{cd}</Link>);
        },
    },
    {
        key: 'caseNumber',
        header: 'Case #',
        render(cd) { return (<Link to={`${buildUrl('/resources/nimble/support/case/', cd)}`}>{cd}</Link>); },
    },
    {
        key: 'status',
        header: 'Status',
        subquery: 'summary',
    },
    {
        key: 'ownerName',
        header: 'Assigned',
    },
    {
        key: 'createdDateOnly',
        header: 'Opened After',
        subquery: 'date-range',
        datePickerFormat: 'n/j/Y',
        render(cd) { return (<DateTime date={cd} format="M/D/YYYY" />); },
    },
    { key: 'subject', header: 'Subject' },
    {
        key: 'lastUpdatedDate',
        header: 'Last Updated',
        subquery: 'date-range',
        datePickerFormat: 'n/j/Y',
        render(cd) { return (<DateTime date={cd} format="M/D/YYYY" />); },
    },
];

const tableOptions = {
    rowKey: 'id',
    search: null,
    sorting: [
        { colname: 'lastUpdatedTS', order: 'desc' },
    ],
};

function addCaseInfo(cases, arrays) {
    const temp = {};
    for (let i = 0; i < cases.length; i += 1) {
        temp[cases[i].status] = true;

        // add serial number (assetName) to cases
        if (arrays) {
            const foundAsset = arrays.find(a => a.arrayId === cases[i].assetId);
            if (foundAsset) {
                cases[i].assetName = foundAsset.arraySn;
            } else {
                cases[i].assetName = 'Unknown Asset';
            }
        }
    }

    const statusList = Object.keys(temp);
    const summaryOptions = statusList.sort().map((s) => {
        const keyValue = { name: s, value: s.toLowerCase() };
        return keyValue;
    });

    columns.map((col) => {
        if (col.key === 'status') {
            col.summaryOptions = summaryOptions;
            return col;
        }
        return col;
    });
}

class SupportListCases extends React.Component {
    constructor(props) {
        super(props);
        autobind(this);

        this.state = {
            isSupportCaseAccessOnly: AuthorizationEvaluator.evaluate({
                access: {
                    authorities: [AUTHORITIES.SUPPORT_CASE],
                },
            }),
            tableSearch: '',
        };

        this.checkboxId = makeDomId();
        this.fromDateId = makeDomId();
        this.toDateId = makeDomId();
    }

    componentDidMount() {
        const { contact } = this.props;
        this.props.fetchCases();
        this.props.getArrays();

        if (!contact || (!contact.loaded && !contact.loading)) {
            this.props.fetchSfdcContact();
        }
    }

    getFilteredCases(caselog) {
        const { filtering } = this.props;

        const startDate = new Date(filtering.startDate);
        startDate.setHours(0); // starts at midnight
        startDate.setMinutes(0);

        const endDate = new Date(filtering.endDate);
        endDate.setHours(23); // ends just before midnight
        endDate.setMinutes(59);
        endDate.setSeconds(59);

        const filteredCases = caselog
            .filter((c) => {
                if (c === false) return false;
                if (typeof c !== 'object') return false;
                if (!c) return false;
                return true;
            })
            .filter((c) => {
                if (!filtering.caseStatus) return true;
                return (c.bucket === filtering.caseStatus);
            })
            .filter((c) => {
                if (!filtering.hideAutoCases) return true;
                return (c.Auto_Open__c !== filtering.hideAutoCases);
            })
            .filter((c) => {
                if (!filtering.justMine) return true;
                return (c.contactId === getCurrentUserContactId());
            })
            .filter((c) => {
                if (!filtering.startDate) return true;
                const caseOpened = new Date(c.createdDate);
                return (caseOpened >= startDate);
            })
            .filter((c) => {
                if (!filtering.endDate) return true;
                const caseOpened = new Date(c.createdDate);
                const caseClosed = new Date(c.closedDate);
                const maxDate = Math.max(caseOpened, caseClosed);
                return (maxDate <= endDate);
            });
        return filteredCases;
    }

    setStatusFilter(e) {
        let { status } = e.target.dataset;
        if (status === this.props.filtering.caseStatus) status = null; // toggle
        this.props.filterStatusDispatch(status);
    }

    eventJustMineChange(e) {
        const justMineIsChecked = !!e.target.checked;
        this.props.filterParamDispatch('justMine', justMineIsChecked);
    }

    eventFromDateChange(dateString) {
        this.props.filterParamDispatch('startDate', dateString);
    }

    eventToDateChange(dateString) {
        this.props.filterParamDispatch('endDate', dateString);
    }

    eventSearching(searchString) {
        this.setState({ tableSearch: searchString });
    }

    render() {
        const { cases, fetchCasesFailed, arrays, contact } = this.props;

        if (this.state.isSupportCaseAccessOnly) {
            redirectToLogin();
            return <div />;
        }

        if (!cases.loaded || !arrays || !contact.loadedContact) { return (<Spinner />); }
        if (fetchCasesFailed) { return (<SupportLoadingError />); }

        const { filtering } = this.props;

        const caseList = cases.value;

        let caseSum = { Total: 0, Open: 0, Closed: 0, Resolved: 0 };
        if (caseList) {
            caseSum = caseList.reduce((r, n) => {
                let pval = r[n.bucket] >>> 0;  // eslint-disable-line
                pval += 1;
                r[n.bucket] = pval; // eslint-disable-line
                return r;
            }, caseSum);

            caseSum.Total = caseList.length;
        }

        const filteredCases = this.getFilteredCases(caseList);
        addCaseInfo(caseList, arrays);
        const isInternal = AuthorizationEvaluator.evaluate({ access: { authorities: AUTHORITIES.REALM_INTERNAL } });

        return (
            <SupportTab>

                <div className={cx('support-list-cases')}>

                    <SearchNav currentFilters={filtering} isInternal={isInternal} onSearch={this.eventSearching} />

                    <div className={cx('grid-inset')}>
                        <GridView
                            data={filteredCases}
                            options={tableOptions}
                            tableQuery={this.state.tableSearch}
                            subqueries={{}}
                            columns={columns}
                            noDataMsg="No Data Available"
                        />
                    </div>

                </div>

            </SupportTab>
        );
    }
}


SupportListCases.propTypes = {
    arrays: PropTypes.array,
    cases: PropTypes.object,
    filtering: PropTypes.object,
    fetchCases: PropTypes.func,
    fetchCasesFailed: PropTypes.bool,
    filterStatusDispatch: PropTypes.func,
    filterParamDispatch: PropTypes.func,
    fetchSfdcContact: PropTypes.func,
    getArrays: PropTypes.func.isRequired,
    contact: PropTypes.object,
};

function mapStateToProps(state) {
    return {
        cases: caseListSelector(state),
        fetchCasesFailed: fetchCasesFailedSelector(state),
        filtering: state.supportTab.filtering,
        contact: contactSelector(state),
        ...listSelector(state),
    };
}

const mapDispatchToProps = {
    getArrays,
    fetchCases,
    filterStatusDispatch,
    filterParamDispatch,
    fetchSfdcContact,
};

export default awaitLegacyApp(connect(mapStateToProps, mapDispatchToProps)(SupportListCases));
