import React, { Component } from 'react';
import autobind from 'react-autobind';
import { withFormik } from 'formik';
import classnames from 'classnames/bind';

// import Yup from 'yup';
import Select from 'react-select';
import 'react-select/dist/react-select.css';

import style from './style.scss';

const cx = classnames.bind(style);

/* eslint-disable react/no-multi-comp */
/* eslint-disable jsx-a11y/label-has-for */
/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */

const formikEnhancer = withFormik({
    // FIXME: this is incompatible with validateForm() on mount
    // enableReinitialize: true,
    handleSubmit: (values, { setSubmitting }) => {
    },
    validate: (values, props) => {
        const errors = {};

        if (!values.addressId) {
            errors.addressId = 'is required';
        }

        if (props.onValidationResult && !props.hasDoneInitialValidation) {
            props.onValidationResult(errors);
        }

        return errors;
    },

});

class AddressForm extends Component {
    componentDidMount() {
        this.props.validateForm();
    }

    render() {
        const {
            id,
            title,
            values,
            addressOptions,
            addressUpdating,
            touched,
            dirty,
            errors,
            handleChange,
            handleBlur,
            handleSubmit,
            handleReset,
            setFieldValue,
            setFieldTouched,
            isSubmitting,
            onFieldChanged,
            onEdit,
        } = this.props;

        const disabled = (Object.keys(errors).length > 0) ||
            addressUpdating ||
            (values.addressId && values.addressId.value && values.addressId.value.newAddress);

        return (
            <form onSubmit={handleSubmit}>
                <div className="row">
                    <div className="column medium-18">
                        <AddressSelect
                            title={title}
                            id={id}
                            value={values.addressId}
                            options={addressOptions}
                            onChange={
                                (name, selection) => {
                                    setFieldValue(name, selection);
                                    onFieldChanged(name, selection.value, id);
                                }
                            }
                            onBlur={setFieldTouched}
                            error={errors.addressId}
                            touched={touched.addressId}
                        />
                    </div>
                    <div className="column medium-6">
                        <button
                            disabled={disabled}
                            className={cx('button', 'expanded', 'edit', (disabled ? 'disabled' : ''))}
                            onClick={onEdit}
                        >
                            <i className="fa fa-edit fa-lg" />
                            <span> Edit</span>
                        </button>
                    </div>
                </div>
            </form>
        );
    }
}

class AddressSelect extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    handleChange(value) {
        this.props.onChange('addressId', value);
    }

    handleBlur() {
        this.props.onBlur('addressId', true);
    }

    render() {
        const { error } = this.props;

        return (
            <div className={error ? cx('error') : ''}>
                <label htmlFor={this.props.id}>
                    {this.props.title} {error}
                </label>
                <Select
                    id={this.props.id}
                    placeholder="Select an address..."
                    options={this.props.options}
                    multi={false}
                    clearable={false}
                    onChange={this.handleChange}
                    onBlur={this.handleBlur}
                    value={this.props.value}
                />
            </div>
        );
    }
}

const FormikAddressForm = formikEnhancer(AddressForm);

export default FormikAddressForm;
