const $ = require('jquery');
const App = require('app/Application');
var _ = require('underscore');
var Marionette = require('backbone.marionette');
var Backbone = require('backbone');
var AsyncView = require('app/common/AsyncView');
var ArrayForecastCollection = require('./ArrayForecastCollection');
var AssetCapacityTooltipView = require('./AssetCapacityTooltipView');
var tpl = require('raw-loader!./ArrayCapacityDashboardView.html');

var View = Marionette.ItemView.extend({

    template: _.template(tpl),

    animateOnLoad: false,

    templateHelpers: function () {
        return {
            _,
            arrayForecasts: this.arrayForecasts.toJSON(),
            arrayBuckets: this.arrayBuckets.toJSON(),
        };
    },

    className: 'dashboard-array-capacity-view',

    initialize: function () {
        this.arrayForecasts = new ArrayForecastCollection();
        this.arrayBuckets = new Backbone.Collection();
    },

    onRender: function () {
        var popoverDefaults = {
            html: true,
            placement: 'left',
            trigger: 'focus',
            // Container needs to be the entire view so that the z-index stacking context is shared across all panels
            container: '.ops-dashboard-view',
        };

        this.$('.dashboard-capacity-bucket.critical a[data-count!=0]').popover(_({
            content: this.renderPopover.bind(this, AssetCapacityTooltipView, this.arrayBuckets.findWhere({ label: '<=30' }), 'danger'),
        }).defaults(popoverDefaults));

        this.$('.dashboard-capacity-bucket.warning a[data-count!=0]').popover(_({
            content: this.renderPopover.bind(this, AssetCapacityTooltipView, this.arrayBuckets.findWhere({ label: '<=90' }), 'warning'),
        }).defaults(popoverDefaults));

        // disabled for now
        /*
        this.$('.dashboard-capacity-bucket.good a[data-count!=0]').popover(_({
            content: this.renderPopover.bind(this, AssetCapacityTooltipView, this.arrayBuckets.findWhere({label: '>90'}))
        }).defaults(popoverDefaults));
        */

        this.$('.dashboard-capacity-bucket a[data-count=0]')
            .attr('tabIndex', '-1')
            .attr('disabled', true);

    },

    renderPopover: function (View, bucket, style) {
        var bucketArrays = new Backbone.Collection(bucket.toJSON().arrays);

        return new View({
            collection: bucketArrays,
            style: style,
        }).render().el;
    },

});

module.exports = AsyncView.extend({
    ViewConstructor: View,

    useSpinner: false,

    load: function () {
        this.deferred = $.Deferred();

        var self = this;

        $.when(
            this.view.arrayForecasts.fetch(),
            App.request('global', 'get:assets:async', 'jQuery.Deferred'),
        )
            .fail(_.bind(this.deferred.reject, this.deferred))
            .done(function (forecasts, assets) {

                var bucketLessThan30 = [];
                var bucketLessThan90 = [];
                var bucketGreaterThan90 = [];

                // for (var i=0, len=this.arrayForecasts.length; i<len; i++)
                _.each(self.view.arrayForecasts.toJSON(), function (item, i) {
                    var daysUntilFull = item.daysUntilFull;

                    var asset = assets.findWhere({ serialNumber: item.arraySn });
                    if (asset) {
                        asset.set('daysUntilFull', daysUntilFull);

                        if (daysUntilFull == null) {
                            bucketGreaterThan90.push(asset);
                        } else if (daysUntilFull <= 30) {
                            bucketLessThan30.push(asset);
                        } else if (daysUntilFull <= 90) {
                            bucketLessThan90.push(asset);
                        } else if (daysUntilFull > 90) {
                            bucketGreaterThan90.push(asset);
                        }
                    }
                });

                var dataBuckets = [
                    { label: '<=30', arrays: bucketLessThan30 },
                    { label: '<=90', arrays: bucketLessThan90 },
                    { label: '>90', arrays: bucketGreaterThan90 },
                ];

                self.view.arrayBuckets.set(dataBuckets);

                // AssetCapacityTooltipView.collection = self.view.arrayForecasts;

                self.deferred.resolve(self.view);
            });

        return this.deferred;
    },

});
