const _ = require('underscore');
var Marionette = require('backbone.marionette');
var stickit = require('backbone.stickit');

module.exports = Marionette.ItemView.extend({
    tagName: 'li',
    template: _.template('<%=email%>&nbsp;<span name="deleteButton" class="glyphicon glyphicon-trash deleteEmailButton"  title="Delete this email" ></span>'),
    onRender: function () {
        this.stickit();
    },
    ui: {
        deleteButton: 'span[name="deleteButton"]',
    },
    events: {
        'click @ui.deleteButton': 'deleteEmail',
    },
    deleteEmail: function () {
        //remove model from collection without triggering DELETE request to server
        this.model.collection.remove(this.model);
    },
});
