const _ = require('underscore');
var Backbone = require('backbone'),
    ArrayModel = require('app/models/ArrayModel'),
    CapacitySummaryModel = require('./CapacitySummaryModel'),
    CapacityForecastModel = require('app/models/capacity/CapacityForecastModel'),
    UpgradeSummaryModel = require('./UpgradeSummaryModel');

module.exports = ArrayModel.extend({
    defaults: _.defaults({
        utilization: [],
        capacity: new CapacityForecastModel(),
        upgradeSummary: new UpgradeSummaryModel(),
    }, ArrayModel.prototype.defaults),

    parse: function (response, options) {

        var utilizationData = [];
        for (var i = 0; i < response.utilization.length; i++) {
            var data = response.utilization[i],
                cacheColors = this.getColorCodedBreakdown(data.cache),
                cpuColors = this.getColorCodedBreakdown(data.cpu);

            utilizationData.push({
                ts: data.ts,
                cache: data.cache,
                cpu: data.cpu,
                cacheGreen: cacheColors.green,
                cacheYellow: cacheColors.yellow,
                cacheRed: cacheColors.red,
                cpuGreen: cpuColors.green,
                cpuYellow: cpuColors.yellow,
                cpuRed: cpuColors.red,
            });
        }

        // take the raw response from the server and wrap some of the
        // properties in their backbone model counterparts.
        var obj = _.defaults({
            utilization: utilizationData,
            capacity: new CapacitySummaryModel(response.capacity),
            upgradeSummary: new UpgradeSummaryModel(response.upgradeSummary),
        }, response);

        return obj;
    },

    /**
     * Helper method for returning the color coded breakdown for a given usage.
     * 0-79% is green, 80-99% is yellow, 100%+ is red.
     * @param  {Number} usage The current usage.
     * @return {Object}       Returns a parameterized object containg the [ green, yellow, red ] values.
     */
    getColorCodedBreakdown: function (usage) {
        var green, yellow, red;

        if (usage >= 100) {
            red = usage - 100;
            yellow = 20;
            green = 80;
        } else if (usage >= 80) {
            red = 0;
            yellow = usage - 80;
            green = 79;
        } else {
            red = 0;
            yellow = 0;
            green = usage;
        }

        return {
            green: green,
            yellow: yellow,
            red: red,
        };
    },
});
