const _ = require('underscore');
var Marionette = require('backbone.marionette');
var Api = require('app/modules/api/Api');
var FormBehavior = require('app/common/FormBehavior');
var PermissionModel = require('./PermissionModel');
var tpl = require('raw-loader!./AddPermissionForm.html');
var FlashNotification = require('@infosight/elmer/dist/components/FlashNotification').default;

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    behaviors: {
        FormBehavior: {
            behaviorClass: FormBehavior,
        },
    },

    ui: {
        submitButton: 'button[name="submit"]',
    },

    events: {
        'click @ui.submitButton': 'submit',
    },

    initialize: function (options) {
        this.permissionGroup = options.permissionGroup;
        this.model = new PermissionModel({
            grantee: this.permissionGroup.get('grantee'),
        }, {
            collection: this.permissionGroup.collection.collection, // Need raw permission collection
        });
        this.listenTo(this.model, 'validated', this.onValidated);
    },

    bindings: {
        '[name="privilege"]': {
            observe: 'privilege',
            setOptions: { validate: true },
            selectOptions: {
                defaultOption: {
                    label: 'Choose one...',
                    value: null,
                    disabled: false,
                },
                collection: function () {
                    var currentPrivileges = _.pluck(this.permissionGroup.get('permissions'), 'privilege');
                    return _.pairs(PermissionModel.privilegeNameMap).map(function (pair) {
                        return {
                            label: pair[1],
                            value: pair[0],
                            disabled: _(currentPrivileges).contains(pair[0]),
                        };
                    });
                },
            },
        },
    },

    onValidated: function (isValid) {
        this.ui.submitButton.prop('disabled', !isValid);
    },

    submit: function () {
        var permission = this.model;
        permission.save()
            .done(function () {
                permission.collection.add(permission);
                FlashNotification.success(
                    `Successfully granted permission ${PermissionModel.privilegeNameMap[permission.get('privilege')]} to ${permission.get('grantee').userLogin}`,
                );
            })
            .fail(function (jqxhr) {
                FlashNotification.error(
                    `Error granting permission: ${jqxhr.responseJSON.message}`,
                );
            });
    },

});
