const _ = require('underscore');
var Marionette = require('backbone.marionette');
var MaintenanceWindowStatusModel = require('./MaintenanceWindowStatusModel');
var tpl = require('raw-loader!./MaintenanceWindowStatusView.html');

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    className: 'maintenance-window-status',

    initialize: function () {
        this.model = new MaintenanceWindowStatusModel();
        this.model.fetch().done(this.render.bind(this));
    },

});
