const _ = require('underscore');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');
var $ = require('jquery'),
    jQueryUI = require('jqueryui'),
    Backbone = require('backbone'),
    FormattingUtils = require('app/util/FormattingUtils');

module.exports = Backbone.View.extend(
    /** @lends views/components/dashboard/./SpaceSavingsWaterfallView.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/dashboard/./SpaceSavingsWaterfallView
         * @constructor
         * @augments Backbone.View
         */


        className: 'space-savings-waterfall-view',

        renderChart: function (container, series) {
            var self = this;
            this.chart = new Highcharts.Chart({
                chart: {
                    type: 'waterfall',
                    renderTo: container,
                    height: 300,
                    events: {
                        redraw: function () {
                            this.series[0].points.forEach(function (point) {
                                var barHeight = point.shapeArgs.height;

                                // If label is smaller than the bar, don't move it
                                if (point.dataLabel.height <= barHeight + 4) {
                                    return;
                                }

                                // Move label from it's position at the center to just above the bar
                                var yTranslation = Math.ceil(barHeight / 2) + 8;

                                var svgElem = point.dataLabel.element;
                                var currentTransform = svgElem.getAttribute('transform');

                                // set the label color to black so it's visible on the white background
                                point.dataLabel.css({ color: 'black', textShadow: 'none' });

                                // update the transform to move the label above the bar
                                if (currentTransform.indexOf('translate') != -1) {
                                    var regex = /translate\((\d+)(,| )(\d+)\)/g;
                                    var matches = regex.exec(currentTransform);
                                    if (matches && matches.length == 4) {
                                        var xPos = matches[1],
                                            yPos = matches[3],
                                            newTransform = 'translate(' + xPos + ',' + (parseInt(yPos, 10) - yTranslation) + ')';
                                        svgElem.setAttribute('transform', newTransform);
                                    }
                                }
                            });
                        },
                        load: function () {
                            var chart = this;

                            // reflow the chart to ensure it fills the container
                            _.delay(function () {
                                chart.reflow.call(chart);
                            }, 50);
                        },
                    },
                },
                title: {
                    text: 'Nimble Space Savings',
                },
                xAxis: {
                    type: 'category',
                    labels: {
                        // pass-through formatter overrides global formatter which expects dateTimeLabelFormat
                        formatter: function () {
                            return this.value;
                        },
                    },
                    crosshair: false,
                },
                yAxis: {
                    title: {
                        text: 'Data (TiB)',
                    },
                    labels: {
                        overflow: 'justify',
                    },
                },
                credits: {
                    enabled: false,
                },
                legend: {
                    enabled: false,
                },
                tooltip: {
                    formatter: function () {
                        var point = this.point,
                            series = this.series,
                            pctSaved = '';

                        if (point.name != 'Capacity Used<br>(Pre-Savings)') {
                            var traditionalSpace = self.model.get('traditionalSpace'),
                                pct = Highcharts.numberFormat((this.y / traditionalSpace * 100), 2);
                            pctSaved = ' (' + pct + '%)';
                        }

                        return '<span style="color:' + point.color + '">' + point.name + '</span>' +
                            ': <b>' + FormattingUtils.formatTB(point.y) + '</b>' + pctSaved + '<br/>';
                    },//,
                },
                plotOptions: {
                    series: {
                        dataLabels: {
                            // crop: false,
                            // overflow: 'none',
                            enabled: true,
                            formatter: function () {
                                return FormattingUtils.formatTB(this.y);
                            },
                            style: {
                                color: '#FFFFFF',
                                fontWeight: 'bold',
                                textShadow: 'none',
                            },
                        },
                        minPointLength: 3,
                    },
                },
                series: series,
            });
        },

        render: function () {
            this.$el.html(this.el);

            var series = {
                name: 'Space',
                data: [
                    {
                        name: 'Capacity Used<br>(Pre-Savings)',
                        y: this.model.get('traditionalSpace'),
                        color: 'gray',
                    },
                ],
                pointPadding: 0,
            };

            if (this.model.get('compression') > 0) {
                series.data.push({
                    name: 'Compression Savings',
                    y: -this.model.get('compression'),
                    color: '#2f7ed8',
                });
            }
            if (this.model.get('zeroCopyClones') > 0) {
                series.data.push({
                    name: 'Clones Savings',
                    y: -this.model.get('zeroCopyClones'),
                    color: '#2f7ed8',
                });
            }
            if (this.model.get('deduplication') > 0) {
                series.data.push({
                    name: 'Deduplication Savings',
                    y: -this.model.get('deduplication'),
                    color: '#2f7ed8',
                });
            }

            series.data.push({
                name: 'Capacity Used<br>(Post-Savings)',
                isSum: true,
                y: this.model.get('spaceUsed'),
                color: '#8bbc21',
            });

            this.renderChart(this.el, [series]);

            return this;
        },

    });
