/**
 * TODO
 * This view previously did "double-duty" by allowing internal users (with VirtualArray.Admin authority) to
 * manage *other users* license keys.  That functionality is currently broken in the Lasik context.
 *
 * Since license keys are tied to an individual user account (and not a Nimble Tenant), this view only
 * suppports managing ones own license keys (for internal and external users alike).  Managing others'
 * license keys could be done by user impersonation or some other mechanism TBD.
 */

const App = require('app/Application');
var $ = require('jquery'),
    _ = require('underscore'),
    jQueryUI = require('jqueryui'),
    moment = require('moment'),
    Backbone = require('backbone'),
    Marionette = require('backbone.marionette'),
    GridView = require('app/views/components/grid/GridView'),
    ConfirmationRequestView = require('app/views/components/confirmation/ConfirmationRequestView'),
    CollectionGridModel = require('app/models/grid/CollectionGridModel'),
    VirtualArrayModel = require('app/modules/virtualArrays/VirtualArrayModel'),
    VirtualArrayCollection = require('app/modules/virtualArrays/VirtualArrayCollection'),
    ExpirationDateModal = require('app/modules/virtualArrays/VirtualArraysExpirationDateModal'),
    errorTpl = require('raw-loader!app/templates/components/error.html'),
    tpl = require('raw-loader!app/modules/virtualArrays/LicenseKeysGridView.html');

const { AuthorizationEvaluator, AUTHORITIES } = require('../../../progeny/modules/user');

var FlashNotification = require('@infosight/elmer/dist/components/FlashNotification').default;

var errorTemplate = _.template(errorTpl);
var ErrorModel = Backbone.Model.extend({
    message: null,
    errorThrown: null,
});

var dateColumnOptions = {
    sortable: true,
    searchable: false,
    render: function (date) {
        return date ? moment(date).format('L LTS') : '';
    },
};

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    templateHelpers: function () {
        return {
            userCanViewAllAccounts: this.userCanViewAllAccounts,
            userCanExtendLicense: this.userCanExtendLicense,
            userCanDeleteLicense: this.userCanDeleteLicense,
            login: this.session.get('username'),
        };
    },

    events: {
        'click @ui.generateLicenseButton': 'generateLicense',
        'click @ui.extendExpirationButton': 'extendExpirationDate',
        'click @ui.expireLicenseButton': 'expireLicenseKey',
    },

    columns: [
        { data: 'owner' },
        { data: 'arraySn' },
        { data: 'licenseKey' },
        { data: 'macAddress' },
        { data: 'ipAddress' },
        { data: 'uuid' },
        _.defaults({ data: 'registrationDate' }, dateColumnOptions),
        _.defaults({ data: 'activationDate' }, dateColumnOptions),
        _.defaults({ data: 'expirationDate' }, dateColumnOptions),
        { data: 'status' },
    ],

    ui: {
        table: 'table',
        toolbarContent: '.toolbar-content',
        generateLicenseButton: 'button[name="generate-license"]',
        extendExpirationButton: 'button[name="extend-expiration-date"]',
        expireLicenseButton: 'button[name="expire-license-key"]',
    },

    regions: {
        gridRegion: '#license-keys-grid-container',
        messageRegion: '.message-region',
    },

    initialize: function () {
        this.listenTo(this.collection, 'sync', this.renderGrid);
        this.listenTo(this.collection, 'error', this.onErroneuousCollectionLoad);

        this.session = App.request('global', 'get:current:session');
        this.currentAccount = App.request('global', 'get:account:selection').getAccount();
        this.getAuthorization();

        this.collection.fetch();
    },

    getAuthorization: function () {
        var userIsReseller = AuthorizationEvaluator.evaluate({ access: { authorities: [[AUTHORITIES.ATTRIBUTE_NIMBLE_RESELLER]] } });
        var userIsSuperInternal = AuthorizationEvaluator.evaluate({
            access: {
                authorities: [
                    [AUTHORITIES.REALM_INTERNAL],
                    [AUTHORITIES.VIRTUAL_ARRAY_ADMIN],
                ],
            },
        });
        var isSiteAdmin = AuthorizationEvaluator.evaluate({ access: { authorities: [[AUTHORITIES.SITE_ADMIN]] } });
        var userIsSe = userIsSuperInternal && !isSiteAdmin;

        this.userCanViewAllAccounts = (userIsSuperInternal && !userIsSe);
        this.userCanExtendLicense = userIsSuperInternal;
        this.userCanDeleteLicense = (userIsSuperInternal || userIsReseller);
    },

    renderGrid: function () {
        this.columns[0].visible = this.userCanViewAllAccounts;
        var self = this;
        _.each(this.collection.models, function (vmArray) {
            if (vmArray.get('accountId') === self.currentAccount.get('id')) {
                vmArray.set('owner', self.currentAccount.get('name'));
            }
            else {
                vmArray.set('owner', self.session.get('username'));
            }
        });

        var gridModel = new CollectionGridModel({
            collection: this.collection,
            columns: this.columns,
            emptyLabel: 'No license keys found',
            showFilters: false,
            pageLength: 10,
        });

        this.grid = new GridView({
            id: 'license-keys-grid',
            tableTemplate: this.ui.table,
            toolbar: this.ui.toolbarContent.html(),
            model: gridModel,
            rowSelectionMode: GridView.RowSelectionMode.SINGLE,
            sort: [
                {
                    column: 'Registration Date',
                    order: 'desc',
                },
            ],
        });

        this.listenTo(this.grid, 'error', this.onErroneuousGridLoad);
        this.gridRegion.show(this.grid);
        // Unprocessed table is initially hidden. Show it now.
        this.ui.table.show();

        this.listenTo(this.grid, 'row-selection:changed', this.onRowSelectionChange);
    },

    /**
     * Captures grid load failure and writes error to console.log
     * @param  xhr        The request
     * @param  textStatus The request status
     * @param  errorThrown The request error
     */
    onErroneuousCollectionLoad: function (collection, response) {
        var errorModel = new ErrorModel({
            message: 'Error while fetching license keys: ' +
                response.statusText +
                '. Contact Nimble Storage support.',
        });
        this.gridRegion.show(
            new Marionette.ItemView({
                model: errorModel,
                template: errorTemplate,
            }),
        );
    },

    /**
     * Captures grid load failure and writes error to console.log
     * @param  xhr        The request
     * @param  textStatus The request status
     * @param  errorThrown The request error
     */
    onErroneuousGridLoad: function (xhr, textStatus, errorThrown) {
        var errorModel = new ErrorModel({
            message: 'Error while loading the grid: ' +
                errorThrown +
                '. Contact Nimble Storage support.',
        });
        this.gridRegion.show(
            new Marionette.ItemView({
                model: errorModel,
                template: errorTemplate,
            }),
        );
    },

    onRowSelectionChange: function () {
        this.bindUIElements();
        var row = this.grid.getSelectedRowData();
        this.ui.extendExpirationButton.prop('disabled', !row);
        this.ui.expireLicenseButton.prop('disabled', !row);
    },

    generateLicense: function () {
        var self = this,
            virtualArray = new VirtualArrayModel({}, { collection: this.collection });

        var hasExceededLimit = this.session.get('vmImageLicenseLimit') !== null && this.collection.length >= this.session.get('vmImageLicenseLimit');
        if (hasExceededLimit) {
            FlashNotification.error(
                'There was a problem while generating a new license: You have reached your license key limit.',
            );
            return;
        }

        virtualArray.save()
            .done(function () {
                virtualArray.set('owner', self.session.get('username'));
                self.collection.add(virtualArray);
                FlashNotification.success(
                    `Generated new License: ${virtualArray.get('licenseKey')}`,
                );
            })
            .fail(function (jqxhr) {
                FlashNotification.error(
                    `There was a problem while generating a new license. Please contact Nimble Support: ${jqxhr.responseJSON.message}`,
                );
            });
    },

    extendExpirationDate: function () {
        var self = this,
            row = this.grid.getSelectedRowData();
        var modal = new ExpirationDateModal({
            model: new Backbone.Model({
                licenseKey: row.licenseKey,
            }),
        });
        this.listenTo(modal, 'expiration-date:updated', function () {
            self.collection.fetch();
        });
        modal.showDialog();
    },

    expireLicenseKey: function () {
        var self = this,
            row = this.grid.getSelectedRowData();

        var confirmationView = new ConfirmationRequestView({
            title: 'Expire License Key',
            action: 'Yes',
            content: '<p>Are you sure you would like to expire license key ' + row.licenseKey + '?</p>',
        });

        this.listenTo(confirmationView, 'submit',
            // wrap callback in an anonymous function to pass in parameter licenseKey
            function () {
                self.expireLicenseKeyCallback(row.licenseKey);
            });
        App.modalRegion.show(confirmationView);
    },

    expireLicenseKeyCallback: function (licenseKey) {
        $.ajax({
            url: this.api().central().segment('vmarrays').segment(licenseKey),
            type: 'PUT',
            contentType: 'application/json',
            dataType: 'json',
            data: JSON.stringify({ expirationDate: Date.now() }),
        })
            .done(() => {
                FlashNotification.success(
                    `License key is now expired: ${licenseKey}`,
                );
                this.collection.fetch();
            })
            .fail(function (jqxhr) {
                FlashNotification.error(
                    `Could not expire license key ${licenseKey}: ${jqxhr.responseJSON.message}`,
                );
            });
    },

});
