const _ = require('underscore');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');
var $ = require('jquery'),
    jQueryUI = require('jqueryui'),
    Marionette = require('backbone.marionette'),
    FormattingUtils = require('app/util/FormattingUtils'),
    tinycolor = require('tinycolor2');
const { CHART_COLORS } = require('progeny/util/chartColors');

module.exports = Marionette.ItemView.extend(
    /** @lends views/components/capacity/CapacityForecastChart.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/capacity/CapacityForecastChart
         * @constructor
         * @augments Marionette.ItemView
         */


        tagName: 'div',

        chartOptions: {
            height: 175,
            tooltipsEnabled: true,
            enableMouseTracking: true,
            showYAxisLabel: true,
            showBoundsSeries: true,
            tickInterval: null,
            dateTimeLabelFormats: {
                day: '%e of %b',
                month: '%b',
            },
        },

        // height: 175,

        // tooltipsEnabled: true,
        // enableMouseTracking: true,
        // showYAxisLabel: true,
        // showBoundsSeries: true,
        // tickInterval: null,

        initialize: function (options) {
            this.listenTo(this.model, 'change:events', this.onChangeEvents);

            if (options.chartOptions) {
                this.chartOptions = _.defaults(options.chartOptions, this.chartOptions);
            }
            // this.height = options.height ? options.height : this.height;
            // this.tooltipsEnabled = options.tooltipsEnabled !== undefined ? options.tooltipsEnabled : this.tooltipsEnabled;
            // this.enableMouseTracking = options.enableMouseTracking !== undefined ? options.enableMouseTracking : this.enableMouseTracking;
            // this.showYAxisLabel = options.showYAxisLabel !== undefined ? options.showYAxisLabel : this.showYAxisLabel;
            // this.showBoundsSeries = options.showBoundsSeries !== undefined ? options.showBoundsSeries : this.showBoundsSeries;
            // this.tickInterval = options.tickInterval !== undefined ? options.tickInterval : this.tickInterval;
        },

        /**
         * Handler for when the events for this array have changed.
         * This will trigger the addition of the flags to the chart.
         */
        onChangeEvents: function () {

            var eventsCollection = this.model.get('events'),
                flagSeries = this.chart.get('shelfFlagSeries');

            eventsCollection.each(function (eventModel) {
                var rawCapacity = eventModel.get('change');
                flagSeries.addPoint({
                    x: eventModel.get('date'),
                    title: '+Shelf',
                    text: eventModel.get('current') + ' shelf added ' +
                        eventModel.get('change'),
                }, false /* don't redraw */);
            });

            this.chart.redraw();
        },

        renderChart: function (containerId, series) {
            var self = this,
                arrayModel = this.model.get('arrayModel');

            this.chart = new Highcharts.Chart({
                chart: {
                    renderTo: containerId,
                    height: this.chartOptions.height,
                    events: {
                        redraw: function () {
                            var containerWidth = $(this.container).width();

                            // if the no data label group is present, ensure it's centered
                            if (self.svgNoDataLabelGroup) {
                                self.svgNoDataLabelGroup.translate(
                                    (containerWidth / 2) - (self.svgNoDataLabelGroup.element.getBoundingClientRect().width / 2) + 20,
                                    65,
                                );
                            }
                        },
                        load: function () {
                            var chart = this,
                                renderer = chart.renderer,
                                hasData = false;

                            for (var i = 0; i < chart.series.length && !hasData; i++) {
                                if (chart.series[i].data && chart.series[i].data.length > 0) {
                                    hasData = true;
                                }
                            }

                            if (!hasData) {
                                self.svgNoDataLabelGroup = renderer.g().attr({ id: 'svgNoDataLabel' }).add();
                                self.svgNoDataLabelText = renderer.text('No Capacity Data Available').attr({
                                    fill: '#666',
                                }).add(self.svgNoDataLabelGroup);
                            }

                            if (!self.width) {
                                _.delay(function () {
                                    self.chart.reflow.call(self.chart);
                                }, 50);
                            }

                            for (var j = 0; j < chart.series.length; j++) {
                                var seriesId = chart.series[j].options.id;
                                switch (seriesId) {
                                case 'future':
                                    this.futureSeries = chart.series[j];
                                    break;
                                case 'range':
                                    this.rangeSeries = chart.series[j];
                                    break;
                                case 'throttle':
                                    this.throttleSeries = chart.series[j];
                                    break;
                                }
                            }
                        },
                    },
                },
                credits: {
                    enabled: false,
                },
                title: {
                    text: null,
                },
                xAxis: {
                    type: 'datetime',
                    dateTimeLabelFormats: this.chartOptions.dateTimeLabelFormats,
                    // dateTimeLabelFormats: {
                    //     day: '%e of %b'
                    // },
                    title: {
                        text: 'Week Ending',
                        enabled: false,
                    },
                    tickInterval: this.chartOptions.tickInterval,
                },
                yAxis: {
                    title: {
                        text: this.chartOptions.showYAxisLabel ? 'Capacity %' : null,
                    },
                    labels: {
                        format: '{value}%',
                    },
                    min: 0,
                },
                legend: {
                    enabled: false,
                },
                tooltip: {
                    enabled: this.chartOptions.tooltipsEnabled,
                    shared: false,
                    style: { width: '300px' },
                    valueSuffix: '%',
                    formatter: function () {
                        var currentVal = null,
                            futureVal = null,
                            rangeVals = [],
                            currentColor = null,
                            futureColor = null,
                            rangeColor = null,
                            capacityMB = null;

                        var series = this.point.series,
                            seriesId = series.options.id,
                            index = this.point.index,
                            chartThrottleVal = self.chart.throttleSeries.data[index].y,
                            chartThrottleColor = self.chart.throttleSeries.color;

                        switch (seriesId) {
                        case 'current':
                            currentVal = this.point.y;
                            currentColor = series.color;
                            if (this.point.capacityMB) {
                                capacityMB = this.point.capacityMB;
                            }
                            break;
                        case 'future':
                            futureVal = this.point.y;
                            futureColor = series.color;
                            if (this.point.capacityMB) {
                                capacityMB = this.point.capacityMB;
                            }

                            // get range values for this point
                            rangeVals.push(self.chart.rangeSeries.data[index].low, self.chart.rangeSeries.data[index].high);
                            rangeColor = self.chart.rangeSeries.color;
                            break;
                        case 'range':
                            rangeVals.push(this.point.low, this.point.high);
                            rangeColor = series.color;

                            //get future value for this point
                            futureVal = self.chart.futureSeries.data[index].y;
                            futureColor = self.chart.futureSeries.color;
                            if (self.chart.futureSeries.data[index].capacityMB) {
                                capacityMB = self.chart.futureSeries.data[index].capacityMB;
                            }
                            break;
                        case 'throttle':
                            return '<b><span style="color: ' + series.color + '">Performance Degradation Possible: </span></b>' +
                                '<span>' + this.point.y + '%</span>';
                        case 'shelfFlagSeries':
                            return '<b>' + Highcharts.dateFormat('%A, %b %e, %Y', this.point.x) + '</b><br/>' +
                                this.point.text; // This will add the text on the flags
                        }

                        var capacityTB = null;
                        if (capacityMB) {
                            capacityTB = FormattingUtils.formatSize(capacityMB, 2);
                        }

                        var s = '<b>Week Of:</b> ' + Highcharts.dateFormat('%e of %b, %Y', this.x) + '<br/>';
                        if (currentVal) {
                            s += '<b><span style="color: ' + currentColor + '">Capacity: </span></b>' +
                                '<span>' + currentVal + '% (' + capacityTB + ')</span><br/>';
                        }
                        if (futureVal) {
                            s += '<b><span style="color: ' + futureColor + '">Projected Capacity: </span></b>' +
                                '<span>' + futureVal + '% (' + capacityTB + ')</span><br/>';
                        }
                        if (rangeVals.length > 0) {
                            s += '<b><span style="color: ' + rangeColor + '">Projected Range: </span></b>' +
                                '<span>' + rangeVals[0] + '% - ' + rangeVals[1] + '%</span><br/>';
                        }
                        if (futureVal >= chartThrottleVal || currentVal >= chartThrottleVal || rangeVals[1] >= chartThrottleVal) {
                            s += '<b><span style="color: ' + chartThrottleColor + '">Performance Degradation Possible: </span></b>' +
                                '<span>' + chartThrottleVal + '%</span>';
                        }
                        return s;
                    },
                },
                plotOptions: {
                    series: {
                        enableMouseTracking: this.chartOptions.enableMouseTracking,
                        marker: {
                            enabled: false,
                        },
                    },
                },
                series: series,
            });
        },

        render: function () {
            this.$el.html(this.el);

            var data = this.model.get('data'),
                flagSeriesData = [],
                possiblyExceeds = this.model.get('possiblyExceedsThreshold'),
                probablyExceeds = this.model.get('probablyExceedsThreshold');

            if (data) {
                var pastSeries = { id: 'current', type: 'line', name: 'Current Capacity', zIndex: 1, color: CHART_COLORS.legacyLightBlue, data: [], connectNulls: false },
                    futureSeries = {
                        id: 'future',
                        type: 'line',
                        name: 'Projected Capacity',
                        zIndex: 2,
                        dashStyle: 'shortdash',
                        color: CHART_COLORS.legacyLightBlue,
                        data: [],
                        connectNulls: false,
                    },
                    boundsSeries = {
                        id: 'range',
                        type: 'arearange',
                        lineWidth: 0,
                        zIndex: 1,
                        name: 'Range',
                        color: CHART_COLORS.legacyLightBlue,
                        fillOpacity: 0.3,
                        data: [],
                        connectNulls: false,
                    },
                    throttleSeries = {
                        id: 'throttle',
                        type: 'line',
                        name: 'Performance Degradation Possible',
                        zIndex: 1,
                        dashStyle: 'ShortDot',
                        color: CHART_COLORS.legacyRed,
                        data: [],
                        connectNulls: false,
                    };

                for (var i = 0; i < data.length; i++) {
                    var dataPoint = data[i],
                        isPrediction = !dataPoint.pctUse,
                        capacityMB = null;

                    if (isPrediction) {
                        capacityMB = dataPoint.cap * (dataPoint.predUse * 0.01);

                        pastSeries.data.push([
                            dataPoint.ts, null,
                        ]);
                        futureSeries.data.push({
                            x: dataPoint.ts,
                            y: dataPoint.predUse,
                            capacityMB: capacityMB,
                        });
                        boundsSeries.data.push([
                            dataPoint.ts, dataPoint.low, dataPoint.high,
                        ]);
                    } else {
                        capacityMB = dataPoint.cap * (dataPoint.pctUse * 0.01);
                        pastSeries.data.push({
                            x: dataPoint.ts,
                            y: dataPoint.pctUse,
                            capacityMB: capacityMB,
                        });
                        futureSeries.data.push([dataPoint.ts, null]);
                        boundsSeries.data.push([dataPoint.ts, null, null]);
                    }

                    throttleSeries.data.push([
                        dataPoint.ts, dataPoint.thresh,
                    ]);
                }

                // add flags for when this array might exceed the performance threshold
                // if ( probablyExceeds ) {
                //     flagSeriesData.push({
                //         x:     probablyExceeds,
                //         title: 'Space',
                //         text:  'Probably exceeds the performance<br/>throttling threshold.'
                //     });
                // }
                // if ( possiblyExceeds && (possiblyExceeds != probablyExceeds) ) {
                //     flagSeriesData.push({
                //         x:     possiblyExceeds,
                //         title: 'Space',
                //         text:  'Possibly exceeds the performance<br/>throttling threshold.'
                //     });
                // }
                //
                var series = [pastSeries, futureSeries];
                if (this.chartOptions.showBoundsSeries) {
                    series.push(boundsSeries);
                }

                series.push(throttleSeries, {
                    id: 'spaceFlagSeries',
                    type: 'flags',
                    data: flagSeriesData,
                    onSeries: 'future',
                    shape: 'flag',
                    width: 50,
                    zIndex: 10,
                    fillColor: '#F20C0C',
                    color: '#F20C0C',
                    style: { // text style
                        color: 'white',
                    },
                    states: {
                        hover: {
                            fillColor: tinycolor('#F20C0C').desaturate(25).toString(),
                        },
                    },
                }, {
                    id: 'shelfFlagSeries',
                    type: 'flags',
                    data: [],
                    onSeries: 'current',
                    shape: 'flag',
                    width: 50,
                    zIndex: 10,
                    fillColor: '#5FAEE3',
                    color: '#5FAEE3',
                    style: { // text style
                        color: 'white',
                    },
                    states: {
                        hover: {
                            fillColor: tinycolor('#5FAEE3').desaturate(25).toString(),
                        },
                    },
                });
                // console.log(boundsSeries);
                this.renderChart(this.el, series);
            }
            return this;
        },

        onDestroy: function () {
            console.log('CapacityForecastChart:onDestroy()');
            if (this.chart) {
                this.chart.destroy();
                delete this.chart;
            }
        },

    });
