import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import { IconAdaptor } from '@infosight/elmer/dist/components/IconAdaptor';
import SortIcon from 'react-icons/lib/fa/sort';
import SortAscIcon from 'react-icons/lib/fa/sort-asc';
import SortDescIcon from 'react-icons/lib/fa/sort-desc';
import { flatten, compact } from 'underscore';
import objectHash from 'object-hash';
import warn, { assert } from '../../util/warn';
import Pagination from '../Pagination';
import style from './style.scss';

import DatePicker from '../DatePicker';

const cx = classnames.bind(style);

const LIST_SIZES = [
    { name: 'just 5', value: 5 },
    { name: '10', value: 10 },
    { name: '25', value: 25 },
    { name: '50', value: 50 },
    { name: '100!', value: 100 },
];

const MAX_LIST_SIZE = 100;

// just for now testing
// (used to make a sorted summary pulldown <select> of the unique column data)
function searchArrayFor(sourceArray, targetprop) {
    const rs = sourceArray.reduce((r, n) => {
        const pdata = n[targetprop];
        const pdk = (`${pdata}`).toLowerCase(); // "normalized"
        r[pdk] = r[pdk] || {
            name: pdata,
            value: pdk,
        };
        return r;
    }, {});
    return Object.values(rs).sort((a, b) => {
        // empty data goes first
        if (!a.name || !a.value) return -1;
        else if (!b.name || !b.value) return 1;

        // sortable data
        if (a.value < b.value) return -1;
        else if (a.value > b.value) return 1;
        return 0;
    });
}

class GridView extends Component {

    constructor(props) {
        super(props);
        autobind(this);

        // TODO alias v1 withOptions and withData to v2 options and data;
        // const userOptions = props || props.withOptions;
        const userOptions = props;
        const data = props.data || props.withData;

        // @note: withOptions deprecated
        const optionSet = props.options || props.withOptions;

        const defaultOptions = {
            pagination: {
                perPage: 10,
                offset: 0,
            },
        };

        const nestedOptions = keys => keys.reduce((nested, key) => {
            if (props.key === false) {
                return nested;
            }
            nested[key] = { ...defaultOptions[key], ...props[key] };
            return nested;
        }, {});

        const options = {
            ...defaultOptions,
            ...userOptions,
            ...optionSet,
            ...nestedOptions(['pagination'])
        };


        // todo: we need to refactor this to react pattern standards...
        let tableData = data || options.data;

        // copy data
        if (Array.isArray(tableData)) tableData = tableData.slice(0);
        else tableData = [];

        const colsToShow = options.columns || [];
        const hoverRows = options.hoverRows || false;

        const colReference = colsToShow.reduce((r, n) => { r[n.key] = n; return r; }, {});

        const perPage = options.pagination ? (options.pagination.perPage || 10): Number.MAX_SAFE_INTEGER;
        const offset = options.pagination ? (options.pagination.offset || 0) : 0;

        // normalize sorting options
        let sorting = options.sorting || [];
        sorting = sorting.map((si, i) => {
            switch (si.order) {
            case 'D': case 'd': case 'des': case 'desc': case -1: case '-': si.order = -1; break;
            case 'A': case 'a': case 'asc': case 1: case '+': default: si.order = 1;
            }
            return { colname: si.colname, order: si.order };
        });

        const recordSample = tableData[0] || {}; // we sample the first item for autodetect
        const knownKeys = Object.keys(recordSample);

        // var prettyMap = knownKeys
        // .reduce((r,n)=>{ r[n] = n; return r; }, {}); // set k = v to start
        // Object.assign(prettyMap, options.prettyMap || props.prettyMap || {});

        this.state = {
            colsToShow,
            colReference,
            knownKeys,
            hoverRows,
            tableData,
            sorting,
            perPage,
            offset,
        };
    }

    componentWillReceiveProps({ data }) {
        /*
         * The 'data' prop is the only one that is allowed to be modified
         * The remainder of the 'state' still reflects the initial props
         * This is acceptable for current usages of GridView, however this may need to be more intelligent going forward.
         */

        // If the number of rows have changed (i.e. data has been filtered or unfiltered), we must reset the view to the first page
        const resetToFirstPage = this.state.tableData.length !== data.length;

        this.setState(
            Object.assign({}, this.state, {
                tableData: data,
                offset: resetToFirstPage ? 0 : this.state.offset
            })
        );
    }

    setStateOffset(offset) {
        offset = parseInt(offset, 10) || 0;
        const tableDataLength = this.state.tableData.length;
        if (offset < 0) offset = 0;
        else if (offset >= tableDataLength) offset = tableDataLength - 1;
        this.setState({ offset });
    }

    setStatePerPage(npp) {
        npp = parseInt(npp, 10) || 0;
        if (npp > MAX_LIST_SIZE) npp = MAX_LIST_SIZE;
        this.setState({ perPage: npp });
    }

    getSortArrow(column) {
        if (column.columns) {
            return null;
        }

        const sortKeyOptions = this.state.sorting.find(sd => (sd.colname === column.key));
        if (!sortKeyOptions || !sortKeyOptions.order) { // TODO is order always >= 1?
            return (
                <div className={cx('sortIcon')}>
                    <IconAdaptor size="xsmall" type="svg"><SortIcon /></IconAdaptor>
                </div>
            );
        }

        // TODO remove this line if it won't be used
        // const columnSortOrder = this.state.sorting.indexOf(sortKeyOptions) + 1;
        if (sortKeyOptions.order > 0) {
            return (
                <div className={cx('sortIcon')}>
                    <IconAdaptor size="xsmall" type="svg"><SortAscIcon /></IconAdaptor>
                </div>
            );
        } else {
            return (
                <div className={cx('sortIcon')}>
                    <IconAdaptor size="xsmall" type="svg"><SortDescIcon /></IconAdaptor>
                </div>
            );
        }
    }

    getHeaderContent(column) {
        return column.header || column.key;
    }


    getColumnsByRow() {
        const columns = [];
        const rows = [];
        let rowCount = 1;

        // This only works for the first two rows. I don't feel like adding the complexity of recursitvly doing it
        // for n-levels of grouping because two rows is probably going to work for 99% of use cases and more than two
        // probably isn't going to be very friendly. Whoever needs the 1% can refactor as needed.
        const columnMapper = (rowIndex = 0, parentColumnIndex = 0) => (column, columnIndex) => {
            if (rowIndex > 1) {
                return warn('GridView: Column grouping is only supported for the first two levels.' +
                    ' Refactor GridView if you need to support this edge case')
            }

            if (column.visible === false) {
                return;
            }

            columns[parentColumnIndex || columnIndex] = columns[parentColumnIndex || columnIndex] || [];
            columns[parentColumnIndex || columnIndex][rowIndex] = columns[parentColumnIndex || columnIndex][rowIndex] || [];
            columns[parentColumnIndex || columnIndex][rowIndex].push(column);

            rows[rowIndex] = rows[rowIndex] || [];
            rows[rowIndex][parentColumnIndex || columnIndex] = rows[rowIndex][parentColumnIndex || columnIndex] || [];
            rows[rowIndex][parentColumnIndex || columnIndex].push(column);

            if (column.columns) {
                column.columns.forEach(columnMapper(rowIndex + 1, columnIndex));
            }

            if (rowCount < rowIndex + 1) {
                rowCount = rowIndex + 1;
            }

            // Patch with a key
            if (column.key === null || column.key === undefined) {
                column.key = objectHash(column);
            }
        };

        // TODO move columns to prop out of options
        (this.props.columns || this.props.options.columns).forEach(columnMapper());

        return {
            rows,
            columns: compact(flatten(columns.map(column => column[column.length - 1]))),
        };
    }

    /** @return {Array} of {colsets} */
    getRenderColumns() {
        return this.state.colsToShow
        .filter(cs => !(cs.visible === false))
        .map((cs) => {
            if (!cs.key) cs.key = (`${Math.random()}`).slice(2); // jit key
            return cs;
        });
    }

    /** @return {Array} of {datarows} */
    getRenderRows() {
        const desensitizeCase = value => (value === null || value === undefined ? '' : value).toString().toLowerCase();
        const subqueries = { ...this.props.subqueries, ...this.state.subqueries };

        return this.state.tableData
            .filter((rowData) => {
                let { tableQuery } = this.props;
                if (!tableQuery) {
                    return true;
                }

                tableQuery = desensitizeCase(tableQuery);
                for (const key in rowData) {
                    if (!rowData.hasOwnProperty(key)) {
                        continue;
                    }

                    const value = desensitizeCase(rowData[key]);
                    if (value.includes(tableQuery)) {
                        return true;
                    }
                }

                return false;
            })
            .filter((rowData) => {
                if (!subqueries) {
                    return true;
                }

                return Object.keys(subqueries)
                    .reduce((includeRow, key) => {
                        const columnValue = desensitizeCase(rowData[key]);
                        const filterKey = subqueries[key];
                        const filter = desensitizeCase(filterKey);
                        if (typeof filterKey === 'object') {
                            const comparator = filterKey.comparator;
                            const filterx = filterKey.value;
                            if (comparator === 'gte') {
                                return includeRow && (columnValue >= filterx);
                            }
                        }
                        return includeRow && columnValue.includes(filter);
                    }, true);
            })
            .sort((a, b) => {
                const sorting = this.state.sorting;
                if (!sorting) return 0;

                const sortpasses = sorting.length;

                for (let i = 0; i < sortpasses; i++) {
                    const sortgroup = sorting[i];
                    const gsk = sortgroup.colname;
                    const gso = sortgroup.order;

                    let atest = a[gsk] || '',
                        btest = b[gsk] || '';

                    if (atest < btest) return (-1 * gso);
                    else if (atest > btest) return (1 * gso);

                    // if we end here, the order is 0, so we dont return it
                    // we loop again and try to resolve the sort with the next sort group
                }
            });
    }


    // ---[ rendering methods ]----------------------------------------------------
    render() {
        const rowData = this.getRenderRows();
        const currentPageRows = rowData.slice(this.state.offset, this.state.offset + this.state.perPage);

        return (
            <div className={cx('nmbl-grid-view', this.props.className)}>

                {this.writeSettings()}
                <table className={this.state.hoverRows ? 'hover' : ''}>
                    <thead>
                        {this.writeHeader()}
                        {this.writeSubfilter()}
                    </thead>
                    <tbody>
                        {this.writeRows(currentPageRows)}
                    </tbody>
                    <tfoot>
                        {/* this.writeFooter() */}
                    </tfoot>
                </table>
                <div className={cx('nmbl-grid-view-footer') + ' container-fluid'}>
                    {currentPageRows.length===0 && <span>{this.props.noDataMsg}</span>}
                    {/* TODO Implement "Rows Selected" from UX when needed */}
                    {this.writePagination(rowData)}
                </div>
                <br/><br/>
            </div>
        );
    }

    writeSettings() {
        return (
            <form className="settings container-fluid">
                <div className="row">
                    {this.writeUserPerPageSelection()}
                </div>
            </form>
        );
    }

    writeHeader() {
        const {rows} = this.getColumnsByRow();

        const getRowSpan = (rowIndex, colIndex) => {
            // Column is part of a group
            if (rowIndex > 0) {
                return 1;
            }

            // Has child columns, meaning it's a group header
            if (rows[rowIndex+1] && rows[rowIndex+1][colIndex]) {
                return 1;
            }

            // Either it's a column that is not a group header OR there are no group headers
            return rows.length;
        };

        const renderCell = (column, rowIndex, columnIndex) => {
            if (Array.isArray(column)) {
                return column.map(c => renderCell(c, rowIndex, columnIndex));
            }

            if (column.visible === false) {
                return;
            }

            const colSpan = column.columns ? column.columns.length : 1;
            const rowSpan = getRowSpan(rowIndex, columnIndex);
            const sortNode = this.getSortArrow(column);

            // TODO sorting markup should only be added if sorting is enabled
            const isGroupHeader = !!column.columns;
            const thProps = {
                'data-sortable': isGroupHeader ? undefined : '',
                'data-group-header': isGroupHeader ? '' : undefined,
                onClick: isGroupHeader ? undefined : this.eventHeaderSort,
                colSpan: colSpan > 1 ? colSpan : undefined,
                rowSpan: rows.length > 1 && rowSpan > 1 ? rowSpan : undefined,
            };

            return (
                <th className={cx({ sorted: sortNode })} key={column.key} data-colname={column.key} {...thProps}>
                    {this.getHeaderContent(column)}
                    <span className="pull-right float-right">
                        {/* Allocate space to avoid jumping around and wrapping */}
                        {sortNode || (<i className="fa fa-sort-asc invisible" />)}
                    </span>
                </th>
            );
        };

        return rows.map((row, rowIndex) => (
            <tr key={rowIndex}>
                {compact(flatten(row.map((col, colIndex) => renderCell(col, rowIndex, colIndex))))}
            </tr>
        ));
    }

    // todo : expand subqueries options set to include (text, summary list, range...)
    // TODO implement filterValue or query prop to pass in the filter such as from a URL
    writeSubfilter() {
        if (!this.props.subqueries) {
            return null;
        }
        const { columns } = this.getColumnsByRow();
        return (
            <tr className="gv-subquery-filters">
                {columns.map(column => {
                    switch (column.subquery) {
                    case 'date-range':
                        return this.showSubfilterDateRange(column);
                    case 'summary':
                        return this.showSubfilterSelect(column);
                    case false:
                        return this.showNoSubfilter(column);
                    case true:
                    default:
                        return this.showSubfilterInput(column);
                    }
                })}
            </tr>
        );
    }

    showSubfilterDateRange(column) {
        return (
            <th key={column.key}>
                <DatePicker altFormat={column.datePickerFormat} subfilter={column.key} onChange={this.eventDateRangeSubfilter} />
            </th>);
    }

    showNoSubfilter(column) {
        return (<th key={column.key}>&nbsp;</th>);
    }

    showSubfilterInput(column) {
        return (
            <th key={column.key}>
                <input
                    className={cx('subquery')}
                    type="text"
                    data-subfilter={column.key}
                    onInput={this.eventSubfilter}
                />
            </th>);
    }

    showSubfilterSelect(column) {
        if (column.summaryOptions) {
            return (
                <th key={column.key}>
                    <select ref={column.key} data-subfilter={column.key} onChange={this.eventSubfilter}>
                        <option value="" />
                        { column.summaryOptions.map((lo) => {
                            return (<option key={lo.value} value={lo.value}>{lo.name}</option>);
                        })}
                    </select>
                </th>
            );
        }
        return (
            <th key={column.key}>
                <select ref={column.key} data-subfilter={column.key} onChange={this.eventSubfilter}>
                    <option value="" />
                    { searchArrayFor(this.state.tableData, column.key).map((lo) => {
                        const isEmpty = (!lo.name && lo.name !== 0);
                        return (<option key={lo.value} value={lo.value}>{isEmpty ? '(is blank)' : lo.name}</option>);
                    })}
                </select>
            </th>
        );
    }

    writeCell(column, rowData) {
        let cellData = rowData[column.key];
        if (typeof column.render === 'function') {
            cellData = column.render(cellData, rowData);
        } else {
            // demo data type unpacker...
            // if(typeof cellData === 'number')
            //     cellData = parseFloat(cellData).toFixed(2);
            // else if(cellData instanceof Date)
            //     cellData = moment(cellData);
        }

        return <td key={column.key} data-colname={column.key}>{cellData}</td>;
    }

    writeRows(rowData) {
        const { columns } = this.getColumnsByRow();
        return rowData.map((rowData, index) => {
            assert(
                rowData[this.props.rowKey] !== undefined,
                `GridView: shimming key for row with undefined key. index=${index} rowKey=${this.props.rowKey}`,
                rowData,
            );

            const rowKey = rowData[this.props.rowKey] || objectHash(rowData);

            // proof of concept demo for child rows:
            // if(idx === 3 && this.props.childRowComponent) {
            //     let child = createElement(this.props.childRowComponent, d);
            //     return (<tr key={'child'+d.key}><td colSpan={99}>{child}</td></tr>);
            // }

            const oddRow = (index + 1) % 2;
            const customAttributes = {
                'data-odd': oddRow ? '' : undefined,
                'data-even': !oddRow ? '' : undefined,
                'data-rowkey': rowKey,
            };

            return (
                <tr key={rowKey} onClick={this.eventRowTouch} {...customAttributes}>
                    {columns.map(column => this.writeCell(column, rowData))}
                </tr>
            );
        });
    }

    // will be the counterpoint to headers
    writeFooter() {
        // var eventSubfilter = this.eventSubfilter.bind(this);
        return (
            <tr>
                {this.getRenderColumns().map(c => (
                    <td key={c.key}>
                        unfinished
                    </td>),
                )}
            </tr>
        );
    }

    writeSummary() {
        return (<div>{this.state.tableData.length} rows in memory</div>);
    }


    writePagination(rowData) {
        if (this.props.pagination) {
            return (
                <Pagination
                    className="pull-right"
                    dataOffset={this.state.offset}
                    numberPerPage={this.state.perPage}
                    itemCount={rowData.length}
                    onPageSelect={this.gotPageSelect}
                />
            );
        }

        return null;
    }


    writeUserPerPageSelection() {
        if (this.props.pagination && this.props.pagination.enableUserPerPageSelection) {
            return (
                <div className="medium-6 columns">
                    <label>Number per page:
                        <select value={this.state.perPage} onChange={(e) => { this.setStatePerPage(e.currentTarget.value); }}>
                            {LIST_SIZES.map(lo => (<option key={lo.value} value={lo.value}>{lo.name}</option>))}
                        </select>
                    </label>
                </div>
            );
        }

        return null;
    }


    // ---[ pagination receivers ]----------------------------------------------------

    gotPageSelect({ dataOffset }) {
        this.setStateOffset(dataOffset);
    }

    // ---[ component events ]----------------------------------------------------------


    // @todo: a row was clicked, expose/hide child row
    // @bug: we are looking at the render position index, not the data index!!!
    eventRowTouch(event) {
        let rkey = event.currentTarget.dataset.rowkey,  // could be our vector for lookup instead...
            ridx = event.currentTarget.dataset.rowindex; // this is render.  we need data index
    }

    eventDateRangeSubfilter(date) {
        const sfk = date.subfilter;
        const sfq = date.date;
        // console.log("filtering [%s] with:", sfk, sfq);

         if (!sfq) {
            const cs = Object.assign({}, this.state);
            if (cs.subqueries) {
                delete cs.subqueries[sfk];
                this.setState(cs);
            }
            return;
        }

        const csf = Object.assign({}, this.state.subqueries);
        csf[sfk] = { comparator: "gte", value: sfq };
        this.setState({
            offset: 0, // back to first item
            subqueries: csf,
        });

    }


    // responds to subfilter events adds the search text(s) to the subfilter set
    eventSubfilter(event) {
        const eventSrc = event.currentTarget;
        const sfk = eventSrc.dataset.subfilter;
        const sfq = eventSrc.value;
        // console.log("filtering [%s] with:", sfk, sfq);

        if (!sfq) {
            const cs = Object.assign({}, this.state);
            delete cs.subqueries[sfk];
            this.setState(cs);
            return;
        }

        const csf = Object.assign({}, this.state.subqueries);
        csf[sfk] = sfq;
        this.setState({
            offset: 0, // back to first item
            subqueries: csf,
        });
    }

    // responds to header sort events and queues the sort order
    // todo: if shift-click, push ADDITIONAL sort rule,
    //       otherwise CLEAR the previous rules first
    eventHeaderSort(event) {
        const wantsAdd = !!event.shiftKey;
        const sk = event.currentTarget.dataset.colname;
        let sko = this.state.sorting.find(sd => (sd.colname === sk));

        let sorting = this.state.sorting.slice(0); // copy

        if (sko) {
            if (sko.order == 0) sko.order = 1;
            else if (sko.order == 1) sko.order = -1;
            else {
                sko.order == 0;
                sorting.splice(sorting.indexOf(sko), 1); // remove
                sko = null;
            }
        } else {
            sko = { colname: sk, order: 1 };
            wantsAdd ? sorting.push(sko) : sorting = [sko];
        }

        this.setState({ sorting });
    }

}

// TODO document any missing props
GridView.propTypes = {
    /**
     * Define filtering by column. Each key in this object corresponds to a `props.columns[].key`
     */
    subqueries: PropTypes.object,

    data: PropTypes.arrayOf(PropTypes.object).isRequired,

    // TODO add any missing props
    columns: PropTypes.arrayOf(PropTypes.shape({
        visible: PropTypes.bool,
        header: PropTypes.node,

        /**
         * Unique Key in data prop which provides the source data of this column.
         * If you don't care about a specific value, i.e. when using a custom render function, this value must at
         * least be unique
         */
        key: PropTypes.string.isRequired,

        /**
         * Define column level filtering style
         * @default true
         * @enum 'summary' User can select from a list of all values in column
         * @enum true - plain text input
         * @enum false - Disable subfilter
         */
        // TODO shouldn't this be false by default?
        // TODO add story
        subquery: PropTypes.oneOf(['summary', 'date-range', true, false]),

        /**
         * @function
         * @param columnValue
         * @param rowData
         * @return PropTypes.node
         */
        render: PropTypes.func,

        /**
         * Create a column group. All options of the root columns hash apply, except "columns".
         * If you need the edge case of more than one level of grouping, implement it.
         *
         * The parent column becomes the group header, which cannot be filtered or sorted.
         */
        columns: PropTypes.arrayOf(PropTypes.object),
    })).isRequired,

    pagination: PropTypes.oneOfType([
        /**
         * false - disable pagination
         * true - use default pagination
         */
        PropTypes.bool,

        /**
         * Specify pagination options
         */
        PropTypes.shape({
            perPage: PropTypes.number, // Default: 10
            offset: PropTypes.number, // Default: 0
            enableUserPerPageSelection: PropTypes.bool, // Default: false TODO remove this. Can be passed in perPage.
        }),
    ]),

    // TODO add typeahead story.
    // I removed embedded table-filtering since mockups show it outside of the table and we can easily take the filter
    // as a prop
    tableQuery: PropTypes.string,

    /**
     * Key in data hash that uniquely identifies the row. If this is not unique, React will not render the row.
     */
    rowKey: PropTypes.string.isRequired,
    noDataMsg: PropTypes.string,
};

GridView.defaultProps = {
    options: null,
    data: null,
    childRowComponent: null,
    pagination: true,
    tableQuery: '',
    rowKey: 'id',
};

export default GridView;
/* eslint-enable */
