import { actionsFor } from '@infosight/elmer/dist/utils/redux';

import Request from '../../../services/Request';
import { FETCH_ADDRESSES, FETCH_ASSETS, ADD_ADDRESS, EDIT_ADDRESS, SET_DELIVERY_PREFERENCE, VALIDATE_ADDRESS } from './constants';

const ASSET_SERVICE_URL = 'assetRegistry/addressManagement/assets';
const ADDRESS_SERVICE_URL = 'assetRegistry/addressManagement/addresses';

export function fetchAddresses() {
    return (dispatch) => {
        const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(FETCH_ADDRESSES, dispatch);

        dispatchStart();

        new Request().central().account().slash(ADDRESS_SERVICE_URL)
            .get()
            .then((response) => {
                dispatchSuccess(response.data);
            })
            .catch(dispatchError);
    };
}

export function fetchAssets() {
    return (dispatch) => {
        const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(FETCH_ASSETS, dispatch);

        dispatchStart();

        new Request().central().account().slash(ASSET_SERVICE_URL)
            .get()
            .then((response) => {
                dispatchSuccess(response.data);
            })
            .catch(dispatchError);
    };
}

export function addAddress(address) {
    return (dispatch) => {
        const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(ADD_ADDRESS, dispatch);

        dispatchStart();

        return new Request().central().account().slash(ADDRESS_SERVICE_URL)
            .post(address)
            .then((response) => {
                dispatchSuccess(response.data);
                return response;
            })
            .catch(dispatchError);
    };
}

export function editAddress(address, addressId) {
    return (dispatch) => {
        const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(EDIT_ADDRESS, dispatch);

        dispatchStart();

        return new Request().central().account().slash(ADDRESS_SERVICE_URL, addressId)
            .patch(address)
            .then((response) => {
                dispatchSuccess(response.data);
                return response;
            })
            .catch((e) => {
                dispatchError(e);
                throw e;
            });
    };
}

// edit install address
export function editAsset(asset, assetId) {
    return (dispatch) => {
        const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(SET_DELIVERY_PREFERENCE, dispatch);

        dispatchStart();

        return new Request().central().account().slash(ASSET_SERVICE_URL, assetId)
            .patch(asset)
            .then((response) => {
                dispatchSuccess(response.data);
            })
            .catch(dispatchError);
    };
}

export function setDeliveryAddress(asset, assetId) {
    return (dispatch) => {
        const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(SET_DELIVERY_PREFERENCE, dispatch);

        dispatchStart();

        return new Request().central().account().slash(ASSET_SERVICE_URL, assetId)
            .patch({
                assetPartShipAddressBook__c: asset.assetPartShipAddressBook__c,
            })
            .then((response) => {
                dispatchSuccess(response.data);
            })
            .catch(dispatchError);
    };
}

export function setDeliveryPreference(asset, addressId) {
    return (dispatch) => {
        const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(SET_DELIVERY_PREFERENCE, dispatch);

        dispatchStart();

        return new Request().central().account().slash(ASSET_SERVICE_URL, addressId)
            .patch({
                assetSameDayPartDeliveryPreference__c: asset.assetSameDayPartDeliveryPreference__c,
            })
            .then((response) => {
                dispatchSuccess(response.data);
            })
            .catch(dispatchError);
    };
}

export function validateAddress(assetId) {
    return (dispatch) => {
        const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(VALIDATE_ADDRESS, dispatch);

        dispatchStart();

        return new Request().central().account().slash(ASSET_SERVICE_URL, assetId)
            .patch({})
            .then((response) => {
                dispatchSuccess(response.data);
            })
            .catch(dispatchError);
    };
}
