const _ = require('underscore');
var Backbone = require('backbone');
var WhitelistModel = require('./WhitelistModel');
var Request = require('../../../progeny/services/Request').default;

module.exports = Backbone.Collection.extend({

    model: WhitelistModel,

    fetch: function() {
        return new Request().urlSegments('software', 'access')
            .get()
            .then((access) => {
                this.set(access.data);
            });
    },

});
