const $ = require('jquery');
const App = require('app/Application');
const _ = require('underscore');
var Marionette = require('backbone.marionette');
var ConfirmationRequestView = require('app/views/components/confirmation/ConfirmationRequestView');
var AddPermissionForm = require('./AddPermissionForm');
var PermissionModel = require('./PermissionModel');
var tpl = require('raw-loader!./OutboundPermissionsCellView.html');
var itemTpl = require('raw-loader!./OutboundPermissionItemView.html');
var FlashNotification = require('@infosight/elmer/dist/components/FlashNotification').default;

var ItemView = Marionette.ItemView.extend({

    tagName: 'li',

    template: _.template(itemTpl),

    templateHelpers: function () {
        var privilege = this.model.get('privilege');
        return {
            privilegeName: PermissionModel.privilegeNameMap[privilege],
            revocable: privilege != 'Feature.Core.Access',
        };
    },

    events: {
        'click [data-action="revoke"]': 'onClickRevoke',
    },

    initialize: function (options) {
        console.assert(options.permissionCollection);
        this.permissionCollection = options.permissionCollection;
    },

    onClickRevoke: function (event) {
        event.preventDefault();
        var view = new ConfirmationRequestView({
            title: 'Revoke Permission',
            action: 'Revoke',
            content: `<p>Are you sure you want to revoke <strong>${PermissionModel.privilegeNameMap[this.model.get('privilege')]}</strong> permission granted to user <strong>${this.model.get('grantee').userLogin}</strong> within organization <strong>${this.model.get('grantee').account.name}</strong>?`,
        });

        this.listenTo(view, 'submit', this.revokePermission);

        App.modalRegion.show(view);
    },

    revokePermission: function () {
        var permission = this.permissionCollection.get(this.model.id); // The unnormalized permission
        permission.destroy({ wait: true })
            .done(function () {
                FlashNotification.success(
                    `Successfully revoked permission ${PermissionModel.privilegeNameMap[permission.get('privilege')]} from ${permission.get('grantee').userLogin}`,
                );
            })
            .fail(function (jqxhr) {
                FlashNotification.error(
                    `Error revoking permission ${PermissionModel.privilegeNameMap[permission.get('privilege')]} from ${permission.get('grantee').userLogin}: ${jqxhr.responseJSON.message}`,
                );
            });
    },

});

module.exports = Marionette.CompositeView.extend({

    template: _.template(tpl),

    childView: ItemView,

    childViewOptions: function () {
        return {
            permissionCollection: this.permissionCollection,
        };
    },

    childViewContainer: 'ul',

    ui: {
        addLink: '[data-action="add"]',
    },

    events: {
        'click @ui.addLink': 'onClickAdd',
        'click [name="revoke-all"]': 'onClickRevokeAll',
    },

    initialize: function (options) {
        console.assert(options.permissionCollection);
        this.permissionGroup = options.permissionGroup;
        this.permissionCollection = options.permissionCollection;
        this.grantee = this.collection.first().get('grantee');
    },

    onRender: function () {
        this.ui.addLink.toggle(this.collection.length < _.size(PermissionModel.privilegeNameMap));
    },

    onClickAdd: function (event) {
        event.preventDefault();
        App.modalRegion.show(new AddPermissionForm({
            collection: this.permissionCollection,
            permissionGroup: this.permissionGroup,
        }));
    },

    onClickRevokeAll: function () {
        var grantee = this.collection.first().get('grantee');
        var view = new ConfirmationRequestView({
            title: 'Revoke All Permissions',
            action: 'Revoke',
            content: `<p>Are you sure you want to revoke all permissions granted to user <strong>${grantee.userLogin}</strong> within organization <strong>${grantee.account.name}</strong>?`,
        });

        this.listenTo(view, 'submit', this.revokeAll);

        App.modalRegion.show(view);
    },

    revokeAll: function () {
        var permissionCollection = this.permissionCollection;
        var grantee = this.grantee;
        var ids = this.collection.pluck('id');
        $.ajax({
            type: 'DELETE',
            url: this.permissionCollection.url().addQuery({ id: ids }),
        })
            .done(function () {
                permissionCollection.remove(ids);
                FlashNotification.success(
                    `Successfully revoked all permissions from ${grantee.userLogin}`,
                );
            })
            .fail(function (jqxhr) {
                FlashNotification.error(
                    `Error revoking all permissions from ${grantee.userLogin}: ${jqxhr.responseJSON.message}`,
                );
            });
    },

});
