import { Component } from 'react';
import autobind from 'react-autobind';
import { removeSubject, addSubject } from '@infosight/shell-api/lib/Search';
import { onUserContextChange } from '@infosight/shell-api/lib/UserProfile';
import { AuthorizationEvaluator, AUTHORITIES } from '../../user';
import { productInterface as nimble } from '../config';
import LocalObjectSearchSubject from '../../infrastructure/LocalObjectSearchSubject';
import { OBJECTS as NimbleObjects } from '../inventory';
import { NimbleUserTenantHelpers } from '../tenantSelection';
import crossTenantArraySearchSubject from '../search/crossTenantArraySearchSubject';

class SearchExtension extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    componentDidMount() {
        this.handleUpdate();
        this.unsubscribe = onUserContextChange(this.handleUpdate);
    }

    componentWillUnmount() {
        if (this.unsubscribe) {
            this.unsubscribe();
        }
    }

    handleUpdate() { // eslint-disable-line class-methods-use-this
        // Infrastructure
        if (AuthorizationEvaluator.evaluate({
            access: {
                custom() {
                    return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() && AuthorizationEvaluator.evaluate({
                        access: { authorities: AUTHORITIES.PRODUCT_FAMILY_NIMBLE },
                    });
                },
            },
        })) {
            this.localSearchProvider = this.localSearchProvider || new LocalObjectSearchSubject({ productInterface: nimble, OBJECTS: NimbleObjects });
            addSubject({ id: 'nimble', provider: this.localSearchProvider });
        } else {
            if (this.localSearchProvider) {
                this.localSearchProvider.destroy();
            }

            removeSubject({ id: 'nimble' });
        }

        // Tenant Selector
        if (NimbleUserTenantHelpers.canAccessMultipleTenants()) {
            addSubject({ id: 'nimble_tenant_selector', provider: crossTenantArraySearchSubject });
        } else {
            removeSubject({ id: 'nimble_tenant_selector' });
        }
    }

    render() {
        return null;
    }
}

export default SearchExtension;
