const _ = require('underscore');
var Backbone = require('backbone');
var esClient = require('app/modules/elasticsearch/ElasticsearchClient');

var Model = Backbone.Model.extend({
    parse: function (data) {
        data.version = _.first(data.Version);
        return data;
    },
});

module.exports = Backbone.Collection.extend({

    model: Model,

    fetch: function () {
        return esClient.searchTemplate({
            index: 'docs',
            template: 'docs',
            params: {
                size: 50,
                filters: [
                    {
                        term: {
                            'Nimble Software and Solutions': 'NimbleOS',
                        },
                    }, {
                        term: {
                            'Document Type': 'Release Notes',
                        },
                    },
                ],
            },
        }).done(this.load.bind(this));
    },

    load: function (response) {
        this.reset(_.pluck(response.hits.hits, '_source'), { parse: true });
    },

});
