var Backbone = require('backbone');
var ProductModel = require('./ProductModel');

module.exports = Backbone.Collection.extend({

    model: ProductModel,

    initialize: function (models, options) {
        console.assert(options.category);
        this.category = options.category;
        this.categoryName = options.category.name;

        this.ORDERING = options.prioritizedCategories.reduce(function (memo, category) {
            memo[category.name] = category.products;
            return memo;
        }, {});
    },

    comparator: function (model) {
        var ordering = this.ORDERING[model.collection.categoryName];
        if (!ordering) {
            return Number.POSITIVE_INFINITY;
        }
        var index = ordering.indexOf(model.get('value'));
        return index === -1 ? Number.POSITIVE_INFINITY : index;
    },

});
