var Marionette = require('backbone.marionette');
var topVMsBarChart = require('./TopVMsBarChart');
var tpl = require('raw-loader!../templates/TopVmsChartView.html');
var numeral = require('numeral');
var _ = require('underscore');

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    className: 'dashboard-topvms-view',

    ui: {
        totalValueDisplay: '.total-value',
        topVmsChart: '.chart',
    },

    onAttach: function () {
        if (this.collection.isEmpty()) {
            this.$el.html('No VMs found');
            return;
        }

        this.populateMaxValueDisplay();

        var topVMs = _.chain(this.collection.toJSON())
            .sortBy('iopsRanking')
            .first(this.getOption('vmCount'))
            .value();

        this.chart = topVMsBarChart({
            dataValues: _(topVMs).pluck('totalIops'),
            categories: _(topVMs).map(function (vm) {
                return {
                    name: vm.name,
                    id: vm.id,
                };
            }),
            element: this.ui.topVmsChart,
            yAxisLabelFormatter: function () {
                return this.isLast ? numeral(this.value).format('0.0a').toUpperCase() + ' ops' : null;
            },
            tooltipFormatter: function () {
                var value = numeral(this.point.y).format('0,0');
                return this.key.name + '</br><span style="color:' + this.point.color + '">\u25CF</span> I/O: <b>' + value + ' </b>';
            },
        });
    },

    populateMaxValueDisplay: function () {
        var maxValue = this.collection.first().get('totalIopsAllVms');
        var tpl = _.template('<%= number %><span class="number-text ops-label"><%= magnitude %> TOTAL I/O</span>');
        var displayValue = numeral(maxValue).format('a');
        this.ui.totalValueDisplay.html(tpl({
            number: displayValue.slice(0, -1),
            magnitude: displayValue.slice(-1),
        }));
    },
});
