var Backbone = require('backbone'),
    AssetShelfModel = require('app/models/asset/AssetShelfModel');

module.exports = Backbone.Collection.extend({

    model: AssetShelfModel,

    serialNumber: null,

    initialize: function (attributes, options) {
        if (!options || !options.serialNumber) {
            throw new Error('AssetShelfCollection requires a serial number.');
        }
        this.serialNumber = options.serialNumber;
    },

    url: function () {
        return this.api().array(this.serialNumber).segment('shelves');
    },

});
