const _ = require('underscore');
var Marionette = require('backbone.marionette');
var AsyncView = require('app/common/AsyncView');
var TopVMsByLatencyView = require('app/modules/vmops/dashboard/views/TopVMsByLatencyView');
var TopVMsByIOView = require('app/modules/vmops/dashboard/views/TopVMsByIOView');
var TopVMsCollection = require('app/modules/vmops/dashboard/collections/TopVMsCollection');
var tpl = require('raw-loader!./TopVmsDashboardView.html');

var View = Marionette.LayoutView.extend({

    template: _.template(tpl),

    regions: {
        iops: '[data-region="iops"]',
        latency: '[data-region="latency"]',
    },

    initialize: function () {
        this.collection = new TopVMsCollection();
    },

    onRender: function () {
        if (this.collection.length) {
            this.iops.show(new TopVMsByIOView({
                collection: this.collection,
                vmCount: 10,
            }));

            this.latency.show(new TopVMsByLatencyView({
                collection: this.collection,
                vmCount: 10,
            }));
        }
    },

});

module.exports = AsyncView.extend({
    ViewConstructor: View,
});
