import React, { Component } from 'react';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import produce from 'immer';
import { Card } from '@infosight/elmer/dist/components/Card';
import { FlashNotification } from '@infosight/elmer/dist/components/FlashNotification';
import { noop } from 'underscore';
import { Modal } from '@infosight/elmer/dist/components/Modal';
import Request from '../../../../services/Request';
import TelemetrySettingsGrid from './TelemetrySettingsGrid';
import style from './style.scss';

const cx = classnames.bind(style);

const Description = () => (
    <div>
        <p>
            Diagnostics for Nimble Analytics allows the HPE Nimble Storage array to send periodic health updates to HPE Nimble Storage Support.
            These updates contain statistics and diagnostic data that summarize the health and performance of the HPE Nimble Storage array
            : no application data will be sent.
        </p>
        <p>
            This page allows you to monitor and configure which diagnostic information is collected and how frequently it is sent back to
            HPE Nimble Storage Support.
        </p>
        <div className="callout secondary">
            <b>Notes:</b>
            <ul>
                <li>Daily diagnostics delivery must be enabled or disabled on the array itself</li>
                <li>The Nimble vCenter plugin must be manually registered with your VMware vCenter installation</li>
            </ul>
        </div>
    </div>
);

export default class TelemetrySettingsContainer extends Component {
    constructor(props) {
        super(props);
        autobind(this);

        this.state = {
            data: null,
            modal: null,
        };
    }

    componentDidMount() {
        this.fetchData();
    }

    fetchData() {
        return new Request()
            .pod()
            .account()
            .urlSegment('telemetry/settings')
            .get()
            .then(response => (
                // returning negative values for sorting
                // asc sort:  1 day ago, 1 week ago, 1 year ago
                // desc sort: 1 year ago, 1 week ago, 1 day ago
                response.data.map(obj => ({
                    ...obj,
                    lastHeartbeat: obj.heartbeat.latestDelivery ? -obj.heartbeat.latestDelivery : null,
                    lastDaily: obj.daily.latestDelivery ? -obj.daily.latestDelivery : null,
                    arraySearch: (obj.arrayHostname || obj.arraySn),
                    realtimeSearch: `${obj.realtime.enabled ? 'on' : 'off'} ${obj.realtime.latestDelivery || 0}`,
                    vmwareSearch: `${obj.vmware.enabled ? 'on' : 'off'} ${obj.vmware.status === 'OK' ? 0 : 1}`,
                }))
            ))
            .then((data) => {
                this.setState({ data });
            });
    }

    closeModal() {
        this.setState({ modal: null });
    }

    handleChange(change) {
        const arrayTelemetrySettings = this.state.data.find(it => it.arraySn === change.arraySn);

        if (change.setting === 'realtime' && !change.enabled && arrayTelemetrySettings.vmware.enabled) {
            this.setState({
                modal: (
                    <Modal
                        title="Error"
                        type="alert"
                        showCloseIcon={false}
                        hideSecondaryButton
                        onClickPrimaryButton={() => this.applyChange({ arrayTelemetrySettings, changes: [change] })}
                        isStoreDriven={false}
                        hide={this.closeModal}
                        isOpen
                    >
                        <p>VMware telemetry requires that streaming delivery be enabled.</p>
                        <p><b>You must disable VMware telemetry</b> prior to disabling streaming delivery</p>
                    </Modal>
                ),
            });
        } else if (change.setting === 'vmware' && change.enabled && !arrayTelemetrySettings.realtime.enabled) {
            this.setState({
                modal: (
                    <Modal
                        title="Notice"
                        type="info"
                        showCloseIcon={false}
                        onClickPrimaryButton={() => {
                            this.applyChange({
                                arrayTelemetrySettings,
                                changes: [
                                    change,
                                    {
                                        setting: 'realtime',
                                        enabled: true,
                                    },
                                ],
                            });
                        }}
                        onClickSecondaryButton={noop}
                        isStoreDriven={false}
                        hide={this.closeModal}
                        isOpen
                    >
                        <p>VMware telemetry requires that streaming delivery be enabled.</p>
                        <p>Streaming delivery <b>will be automatically enabled</b> as part of this operation</p>
                    </Modal>
                ),
            });
        } else {
            this.applyChange({
                arrayTelemetrySettings,
                changes: [change],
            });
        }
    }

    applyChange({ arrayTelemetrySettings, changes }) {
        this.setState(
            produce((state) => {
                const arraySettings = state.data.find(it => it.arraySn === arrayTelemetrySettings.arraySn);
                changes.forEach((change) => {
                    arraySettings[change.setting].enabled = change.enabled;
                    state.modal = null;
                });
            })
        );

        changes.forEach((change) => {
            const { setting, enabled } = change;
            const { arraySn, arrayHostname } = arrayTelemetrySettings;
            const arrayName = arrayHostname || arraySn;

            return new Request()
                .pod()
                .account()
                .urlSegments('telemetry/settings', arraySn)
                .patch({
                    setting,
                    enabled,
                })
                .then(() => {
                    const settingDisplay = {
                        vmware: 'VMware',
                        realtime: 'streaming',
                    }[setting];
                    FlashNotification.info(`${enabled ? 'Enabled' : 'Disabled'} ${settingDisplay} telemetry for ${arrayName}`);
                })
                .catch(() => {
                    FlashNotification.error(
                        `An internal error occurred while attempting to update telemetry for ${arrayName}. Please contact Nimble Support.`, {
                            timeout: 'none',
                        }
                    );

                    // Refetch data from server as source of truth
                    this.setState({
                        data: null,
                    });
                    this.fetchData();
                });
        });
    }

    render() {
        return (
            <Card className={cx('telemetry-settings')}>
                <Description />
                <TelemetrySettingsGrid loading={this.state.data == null} data={this.state.data} handleChange={this.handleChange} />
                {this.state.modal}
            </Card>
        );
    }
}
