const _ = require('underscore');
var Marionette = require('backbone.marionette');
var stickit = require('backbone.stickit');
var FormBehavior = require('app/common/FormBehavior');
var MaintenanceWindowModel = require('./MaintenanceWindowModel');
var MaintenanceWindowCollection = require('./MaintenanceWindowCollection');
var tpl = require('raw-loader!./MaintenanceWindowSidePanelView.html');
var itemTpl = require('raw-loader!./MaintenanceWindowSidePanelItemView.html');
const moment = require('moment');

var SidePanelItemView = Marionette.ItemView.extend({

    template: _.template(itemTpl),

    tagName: 'a',

    attributes: function () {
        return {
            href: '#',
            'data-model-id': this.model.id,
        };
    },

    templateHelpers: { moment },
    className: 'list-group-item',

    triggers: {
        'click': 'selected',
    },

    modelEvents: {
        sync: 'render',
    },

});

module.exports = Marionette.CompositeView.extend({

    template: _.template(tpl),

    childView: SidePanelItemView,

    childViewContainer: '.list-group',

    childEvents: {
        'selected': 'onItemSelected',
    },

    emptyView: Marionette.ItemView.extend({
        template: _.constant('<li class="list-group-item"> There are no maintenance windows scheduled</li>'),
    }),

    onItemSelected: function (view) {
        this.selectOne(view.model);
    },

    selectOne: function (model) {
        this.unselectAll();
        this.$('.list-group-item[data-model-id="' + model.id + '"]').addClass('active');
    },

    unselectAll: function () {
        this.$('.list-group-item.active').removeClass('active');
    },

});
