import React from 'react';
import { isFunction } from 'underscore';
import URI from 'urijs';
import { CancelToken } from 'axios';
import { ObjectIcon } from '@infosight/elmer/dist/infrastructure';
import Request from '../../../services/Request';
import { buildNimbleTenantUrl, NimbleUserTenantHelpers } from '../tenantSelection';
import { OBJECTS } from '../inventory';
import { productInterface } from '../config';

const NIMBLE_ARRAY = OBJECTS.ARRAY;

let cancellationExecutor;

function cancelSearch() {
    if (isFunction(cancellationExecutor)) {
        cancellationExecutor();
        cancellationExecutor = null;
    }
}

async function search({ query, submit }) {
    const tenant = NimbleUserTenantHelpers.getCurrentTenant();
    const detailView = productInterface.getDetailView(NIMBLE_ARRAY, { includeInvisible: true });
    const { data } = await new Request().central()
        .slash('search/arrays')
        .query({ term: query, boostAccountId: tenant && tenant.id })
        .get({
            cancelToken: new CancelToken((cancel) => {
                cancellationExecutor = cancel;
            }),
        });

    cancellationExecutor = null;

    if (!data || !data.length) {
        submit.noOp();
        return;
    }

    const opts = { [productInterface.id]: { excludeTenant: true } };

    // Determine if the result set contains arrays from other tenants
    let hasResultsFromOtherTenants = false;
    for (const index in data) { // eslint-disable-line no-restricted-syntax
        if (tenant && tenant.id !== data[index].accountId) {
            hasResultsFromOtherTenants = true;
            break;
        }
    }

    const hits = data.map(({ arraySn, arraySn: id, accountName, accountId, arrayHostname }) => {
        const { pathname, search: thisSearch } = productInterface.getLinkPropsForView(detailView, { id }, null, null, opts).to;
        return {
            id,
            name: !arrayHostname ? arraySn : `${arraySn} (${arrayHostname})`,
            topic: NIMBLE_ARRAY,
            url: new URI('')
                .pathname(tenant && tenant.id === accountId ? pathname : buildNimbleTenantUrl(accountId, pathname))
                .search(thisSearch)
                .valueOf(),

            // Don't include the tenant name for the current account or when user has a single tenant
            label: hasResultsFromOtherTenants ? accountName : undefined,
        };
    });

    submit.hits(hits);
}

export default {
    search,
    cancelSearch,
    id: 'nimble-cross-tenant-array-search',
    name: productInterface.attributes.name,
    topics: [
        {
            id: NIMBLE_ARRAY,
            singularName: productInterface.getSingularTitle(NIMBLE_ARRAY),
            pluralName: productInterface.getPluralTitle(NIMBLE_ARRAY),
            icon: <ObjectIcon objectType={NIMBLE_ARRAY} />,
        },
    ],
};
