const $ = require('jquery');
const App = require('app/Application');
const _ = require('underscore');
var Marionette = require('backbone.marionette');
var AsyncView = require('app/common/AsyncView');
var HeartbeatWarningsView = require('./HeartbeatWarningsView');
var AsupWarningsView = require('./AsupWarningsView');
var HardwareWarningsView = require('./HardwareWarningsView');
var tpl = require('raw-loader!./SystemDashboardView.html');
const moment = require('moment');

var View = Marionette.ItemView.extend({

    template: _.template(tpl),

    className: 'dashboard-system-view',

    templateHelpers: function () {
        var now = moment();
        return {
            arrayCount: this.collection.length,
            hardwareIssueCount: this.collection.filter(x => x.hasHardwareIssues()).length,
            hasHeartbeatWarnings: this.collection.some(x => x.hasHeartbeatWarning()),
            hasAsupWarnings: this.collection.some(x => x.hasAsupWarning()),
            upgradeAvailable: this.collection.some('upgradable'),
        };
    },

    onRender: function () {
        var popoverDefaults = {
            html: true,
            placement: 'left',
            trigger: 'focus',
            // Container needs to be the entire view so that the z-index stacking context is shared across all panels
            container: '.ops-dashboard-view',
            viewport: { selector: '.ops-dashboard-view' },

        };

        var hbWarnings = this.$('.hb-container[data-has-warnings="true"]')
            .popover(_({
                content: this.renderPopover.bind(this, HeartbeatWarningsView),
            }).defaults(popoverDefaults));
        if (hbWarnings.length > 0) {
            hbWarnings
                .data('bs.popover')
                .tip()
                .addClass('ops-dashboard-popover');
        }

        var asupWarnings = this.$('.asup-container[data-has-warnings="true"]')
            .popover(_({
                content: this.renderPopover.bind(this, AsupWarningsView),
            }).defaults(popoverDefaults));
        if (asupWarnings.length > 0) {
            asupWarnings
                .data('bs.popover')
                .tip()
                .addClass('ops-dashboard-popover');
        }

        this.$('.hardware-container [data-has-warnings="true"]')
            .popover(_({
                content: this.renderPopover.bind(this, HardwareWarningsView),
            }).defaults(popoverDefaults));
    },

    renderPopover: function (View) {
        return new View({
            collection: this.collection,
        }).render().el;
    },

    mergeTimestamps: function (asupStatusMap) {
        this.collection.each(function (assetModel) {
            var asupStatus = asupStatusMap[assetModel.get('serialNumber')];
            assetModel.set({
                asupReceived: asupStatus ? asupStatus.lastReceived : null,
                asupProcessed: asupStatus ? asupStatus.lastProcessed : null,
            });
        });
    },

});

module.exports = AsyncView.extend({
    ViewConstructor: View,

    load() {
        return $.when(
            $.getJSON(this.api().pod().account().segment('assets/asupStatus')),
            App.request('global', 'get:assets:async', 'jQuery.Deferred'),
        )
            .then((asupStatus, assetCollection) => {
                this.view.collection = assetCollection;
                this.view.mergeTimestamps.call(this.view, asupStatus);
                return this.view;
            });
    },
});
