const $ = require('jquery');
const _ = require('underscore');
var Marionette = require('backbone.marionette');
var VirtualCollection = require('backbone-virtual-collection');
var moment = require('moment');
const { buildUrlFromBase } = require('progeny/util/url');
var tpl = require('raw-loader!./HardwareWarnings.html');
var itemTpl = require('raw-loader!./HardwareWarningsItem.html');

var componentTooltipTemplate = _.template(
    'Controller <%= controller %> <b><%= component %></b> is <%= isAlerted ? "in <b>alerted</b> status" : "operating normally" %>.',
);

var diskTooltipTemplate = _.template(
    '<b><%= failed %></b> of <%= good + failed %> <%= type.toUpperCase() %>s are reporting as failed',
);

var COMPONENT_NAMES = {
    psu: 'power supply',
    fan: 'fan',
    temperature: 'temperature',
};

module.exports = Marionette.CompositeView.extend({

    template: _.template(tpl),

    className: 'hardware-warnings',

    childViewContainer: 'tbody',

    childViewOptions: function () {
        return {
            parentView: this,
        };
    },

    initialize: function () {
        this.collection = new VirtualCollection(this.collection, {
            filter: x => x.hasHardwareIssues(),
            comparator: function (asset) {
                return asset.get('hostname') + asset.get('serialNumber');
            },
        });
    },

    childView: Marionette.ItemView.extend({

        template: _.template(itemTpl),

        tagName: 'tr',

        templateHelpers: function () {
            return {
                hbMoment: moment(this.model.get('latestHeartbeat').timestamp),
                getAssetLink: buildUrlFromBase.bind(null, '/infrastructure/storage/nimble/arrays'),
            };
        },

        onRender: function () {
            var $tooltipContainer = this.getOption('parentView').$el;
            this.$('[data-component]').bs_tooltip({
                title: this.renderComponentTooltip,
                container: $tooltipContainer,
                html: true,
            });
            this.$('[data-column="ssd"] div,[data-column="hdd"] div').bs_tooltip({
                title: _(this.renderDiskTooltip).partial(this.model.get('latestHeartbeat')),
                container: $tooltipContainer,
                html: true,
            });
        },

        renderComponentTooltip: function () {
            var $el = $(this);
            return componentTooltipTemplate({
                controller: $el.closest('[data-controller]').data('controller').toUpperCase(),
                component: COMPONENT_NAMES[$el.data('component')],
                isAlerted: $el.data('status') != 'OK',
            });
        },

        renderDiskTooltip: function (hb) {
            var type = $(this).closest('[data-column]').data('column');

            return diskTooltipTemplate({
                type: type,
                failed: hb[type + 'FailedCount'],
                good: hb[type + 'GoodCount'],
            });
        },
    }),
});
