/* eslint-disable spaced-comment, no-unused-vars */
import React from 'react';
import PropTypes from 'prop-types';
import { Route, Switch, NavLink } from 'react-router-dom';
import ScrollContainer from '@infosight/elmer/dist/components/ScrollContainer';
import { StaticBold } from '@infosight/elmer/dist/components/StaticBold';
import { SecondaryNavBar, NavItem, Title } from '@infosight/elmer/dist/components/SecondaryNavBar';
import { buildUrl } from '@infosight/elmer/dist/utils/url';
import { pageBoundaryRouteRenderer } from '@infosight/elmer/dist/page';

import { AUTHORITIES, AuthorizationEvaluator, nimbleTenantAdminAccess, nimbleThirdPartyPermissionRequestAccess } from '../../user';

import UserManagementContainer from './user/UserManagementContainer';
import SelfRegistrationContainer from './selfRegistration/SelfRegistrationContainer';
import PermissionsContainer from './permissions/PermissionsContainer';
import TelemetrySettingsContainer from './telemetry/TelemetrySettingsContainer';
import AssetRegistry from '../address/components';
import NimbleUserTenantHelpers from '../tenantSelection/NimbleUserTenantHelpers';

const objects = [
    {
        label: 'Customer Registration',
        url: 'registration',
        component: SelfRegistrationContainer,
        access: {
            authorities: {
                $and: [
                    AUTHORITIES.SITE_ACCESS,
                    {
                        $not: [AUTHORITIES.PRODUCT_FAMILY_NIMBLE],
                    },
                ],
            },
        },
    },
    {
        label: 'Users',
        url: 'users',
        component: UserManagementContainer,
        access: {
            custom() {
                return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() && AuthorizationEvaluator.evaluate(nimbleTenantAdminAccess);
            },
        },
    },
    {
        label: 'Permissions',
        url: 'permissions',
        component: PermissionsContainer,
        access: {
            custom() {
                return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() &&
                    AuthorizationEvaluator.evaluate({ access: nimbleThirdPartyPermissionRequestAccess })
                    && !AuthorizationEvaluator.evaluate({ access: { authorities: AUTHORITIES.ATTRIBUTE_NIMBLE_SUPPORT_PROVIDER } });
            },
        },
    },
    {
        label: 'Telemetry',
        url: 'telemetry',
        component: TelemetrySettingsContainer,
        access: {
            custom() {
                return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() && AuthorizationEvaluator.evaluate(nimbleTenantAdminAccess);
            },
        },
    },
    {
        label: 'Asset Registry',
        url: 'assetRegistry',
        component: AssetRegistry,
        access: {
            custom() {
                return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() && AuthorizationEvaluator.evaluate({
                    access: {
                        $or: [
                            nimbleTenantAdminAccess,
                            { authorities: AUTHORITIES.ADDRESS_ADMIN },
                        ],
                    },
                });
            },
        },
    },
];

const SettingsRouter = ({ match }) => (
    <ScrollContainer>
        <ScrollContainer.Fixed>
            <SecondaryNavBar>
                <Title name="Nimble Storage" className="align-center" />
                {/* Hiding the users menu but still make it available if we have to debug or ran into issues in production
                    It will also be useful in test environments where IAM may not be available
                */}
                {AuthorizationEvaluator.filter(objects).map(({ label, url }) => url !== 'users' && (
                    <NavItem key={url}>
                        <NavLink className="flex-container align-center flex-dir-column" to={buildUrl(match.url, url)}>
                            <StaticBold parent={React.Fragment}>{label}</StaticBold>
                        </NavLink>
                    </NavItem>
                ))}
            </SecondaryNavBar>
        </ScrollContainer.Fixed>

        <ScrollContainer.Scrollable>
            <Switch>
                {AuthorizationEvaluator.filter(objects).map(({ url, label, component }) => (
                    <Route
                        key={url}
                        path={buildUrl(match.path, url)}
                        render={pageBoundaryRouteRenderer(component, label)}
                    />
                ))}
            </Switch>
        </ScrollContainer.Scrollable>
    </ScrollContainer>
);

SettingsRouter.propTypes = {
    match: PropTypes.object.isRequired,
};

export default SettingsRouter;
