const _ = require('underscore');
var Backbone = require('backbone');
var Marionette = require('backbone.marionette');
var itemTpl = require('raw-loader!./BreadcrumbItem.html');
var tpl = require('raw-loader!./BreadcrumbsView.html');

var BreadcrumbItem = Marionette.ItemView.extend({

    template: _.template(itemTpl),

    templateHelpers: function () {
        return {
            facet: this.model.getFacet().toJSON(),
        };
    },

    tagName: 'li',

    events: {
        'click [data-action="deselect-facet"]': 'deselectFacet',
    },

    deselectFacet: function (event) {
        event.preventDefault();
        this.model.set('selected', false);
    },

});

module.exports = Marionette.CompositeView.extend({

    template: _.template(tpl),

    childViewContainer: 'ul',

    childView: BreadcrumbItem,

    bindings: ['from', 'to', 'total'].reduce(function (memo, val) {
        memo['[data-bind="' + val + '"]'] = val;
        return memo;
    }, {}),

    events: {
        'click [data-action="deselect-query"]': 'deselectQuery',
        'click [data-action="clear-all"]': 'clearAll',
    },

    initialize: function (options) {
        this.pagination = options.pagination;
        this.query = options.query;
        this.facets = options.facets;
        this.collection = new Backbone.Collection(this.facets.getSelectedBuckets());

        this.model = new Backbone.Model({
            query: options.query.toJSON(),
        });
        this.refreshPagination();

        this.listenTo(this.pagination, 'change', this.refreshPagination);
    },

    onRender: function () {
        this.stickit();
    },

    refreshPagination: function () {
        this.model.set({
            from: this.pagination.get('from'),
            to: this.pagination.get('to'),
            total: this.pagination.get('total'),
        });
    },

    deselectQuery: function (event) {
        event.preventDefault();
        this.query.set('term', null);
        this.query.trigger('update');
    },

    clearAll: function (event) {
        event.preventDefault();
        this.query.set('term', null);
        this.collection.each(function (bucket) {
            bucket.set('selected', false, { silent: true });
        });
        this.facets.trigger('update');
    },

});
