var Backbone = require('backbone');

module.exports = Backbone.Model.extend({

    initialize: function (attributes, options) {
        this.collection = options.collection;
        this.set({
            total: this.collection.fullCollection.length,
        });
        this.listenTo(this.collection, 'reset', this.refresh);
        this.refresh();
    },

    refresh: function () {
        this.set({
            from: (this.collection.state.currentPage * this.collection.state.pageSize) + 1,
            to: Math.min((this.collection.state.currentPage + 1) * this.collection.state.pageSize, this.collection.fullCollection.length),
        });
    },

});
