const $ = require('jquery');
const App = require('app/Application');
const _ = require('underscore');
var Marionette = require('backbone.marionette');
var moment = require('moment');
var sprintf = require('sprintf-js').sprintf;
var ConfirmationRequestView = require('app/views/components/confirmation/ConfirmationRequestView');
var VirtualCollection = require('backbone-virtual-collection');
var GridView = require('app/views/components/grid/GridView');
var CollectionGridModel = require('app/models/grid/CollectionGridModel');
var PermissionModel = require('./PermissionModel');
var PermissionRequestForm = require('./PermissionRequestForm');
var PermissionGroupCollection = require('./PermissionGroupCollection');
var tpl = require('raw-loader!./InboundGrantsView.html');
var permissionsTpl = require('raw-loader!./InboundPermissionsCell.html');
var statusTpl = require('raw-loader!./InboundGrantStatus.html');
var FlashNotification = require('@infosight/elmer/dist/components/FlashNotification').default;
var { AUTHORITIES, AuthorizationEvaluator } = require('../../../progeny/modules/user');
var statusTemplate = _.template(statusTpl);
var permissionsTemplate = _.template(permissionsTpl);

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    regions: {
        grid: '[data-region="grid"]',
    },

    columns: [
        {
            data: 'status',
            render: function (status, type, permissionGroup) {
                var lastUpdated = _.chain(permissionGroup.permissions).sortBy('updatedAt').last().value();
                return statusTemplate({
                    status: status,
                    moment: moment,
                    updatedBy: lastUpdated.updatedBy,
                    updatedAt: lastUpdated.updatedAt,
                });
            },
        }, {
            data: 'target',
            render: function (target) {
                return target.account ? target.account.name : ('<i>' + target.opaqueName + '</i>');
            },
        }, {
            data: 'grantee.userLogin',
        }, {
            data: 'permissions',
            orderable: false,
            render: function (permissions) {
                return permissionsTemplate({
                    permissions: permissions,
                    privilegeNameMap: PermissionModel.privilegeNameMap,
                });
            },
        }, {
            data: null,
            orderable: false,
            render: function (data, type, row) {
                return '<button class="button hollow" name="delete"><i class="fa fa-trash-o"></i> Delete</button>';
            },
        },
    ],

    events: {
        'click button[name="request"]': 'onClickRequest',
        'click button[name="delete"]': 'onClickDelete',
    },

    templateHelpers: function () {
        return {
            account: this.getOption('account'),
            session: App.request('global', 'get:current:session').toJSON(),
        };
    },

    initialize: function (options) {
        var account = this.getOption('account');
        this.collection = new PermissionGroupCollection(
            new VirtualCollection(options.collection, {
                filter: function (permission) {
                    return permission.get('grantee').account.id === account.id;
                },
            }),
        );
    },

    onRender: function () {
        if (AuthorizationEvaluator.evaluate({ access: { authorities: AUTHORITIES.REALM_INTERNAL } })) {
            this.$('button[name="request"]').prop('disabled', true);
        }

        this.showChildView('grid', new GridView({
            tableTemplate: this.$('script').html(),
            model: new CollectionGridModel({
                collection: this.collection,
                columns: this.columns,
                emptyLabel: 'No permissions granted',
                pageLength: 10,
            }),
            dataTableOptions: {
                dom: 'ftip',
            },
            sort: ['Target Organization'],
        }));
    },

    onClickRequest: function () {
        App.modalRegion.show(new PermissionRequestForm({ collection: this.collection.collection }));
    },

    onClickDelete: function (event) {
        var permissionGroupId = $(event.currentTarget).closest('tr').attr('data-model-id');
        var permissionGroup = this.collection.get(permissionGroupId);
        var target = permissionGroup.get('target');
        var view = new ConfirmationRequestView({
            title: 'Delete Permission',
            action: 'Delete',
            content: sprintf('<p>Are you sure you want to delete the permission granted to user <strong>%s</strong> upon target account <strong>%s</strong>?',
                permissionGroup.get('grantee').userLogin, target.account ? target.account.name : target.opaqueName),
        });

        this.listenTo(view, 'submit', _.partial(this.deletePermissionGroup, permissionGroup));

        App.modalRegion.show(view);
    },

    deletePermissionGroup: function (permissionGroup) {
        var permissionCollection = this.collection.collection;
        var grantee = permissionGroup.get('grantee');
        var ids = _(permissionGroup.get('permissions')).pluck('id');
        $.ajax({
            type: 'DELETE',
            url: permissionCollection.url().addQuery({ id: ids }),
        })
            .done(function () {
                permissionCollection.remove(ids);
                FlashNotification.success(
                    `Successfully deleted permission granted to ${grantee.userLogin}`,
                );
            })
            .fail(function (jqxhr) {
                FlashNotification.error(
                    `Error deleting permission granted to ${grantee.userLogin}`,
                );
            });
    },

});
