import { getStore } from '../../../util/storeProvider';
import { contactSelector } from '../reducer/core';
import { userSelector } from '../../user';

import {
    setCaseParamFilterSet,
    setCaseStatusFilter,
    setCaseParamFilters,
} from '../actions/set-filters';


export function isWordChar(charCode) {
    if (!charCode) return false;
    const theChar = String.fromCharCode(charCode);
    return (/\w/gi).test(theChar);
}

// DOM helpers

export function makeDomId() {
    let intId = Math.random() * 1e12;
    intId = intId >>> 0; // eslint-disable-line
    return intId.toString(36);
}

// case helpers

function getContact() {
    const contact = contactSelector(getStore().getState());
    return contact ? contact.contact : undefined;
}

export function findCaseByNumber(supportCases, caseNumber) {
    return supportCases.find(someCase => (someCase.caseNumber === caseNumber));
}

// identity helpers
// note: internal users, will be null (guest)
export function getCurrentUserContactId() {
    const contact = getContact();
    if (!contact) return null;
    return contact.id;
}

export function getCurrentUserContact() {
    const contact = getContact();
    if (!contact) {
        return { ContactId: 'internal', ContactName: userSelector(getStore().getState()).username };
    }
    return { ContactId: contact.id, ContactName: contact.name };
}

// action dispatch helpers

export function filterStatusDispatch(status) {
    return (dispatch) => {
        dispatch(setCaseStatusFilter(status));
    };
}

export function filterParamDispatch(filterKey, filterValue) {
    let revalued = filterValue;
    if (filterKey === 'priority') revalued = parseInt(filterValue, 10) || 0;
    return (dispatch) => {
        dispatch(setCaseParamFilters(filterKey, revalued));
    };
}

export function filterSetDispatch(filterSet) {
    return (dispatch) => {
        dispatch(setCaseParamFilterSet(filterSet));
    };
}

// interfacey

export default {
    isWordChar,
    makeDomId,
    findCaseByNumber,
    filterStatusDispatch,
    filterParamDispatch,
};
