const $ = require('jquery');
var Backbone = require('backbone'),
    WellnessIssueModel = require('./WellnessIssueModel');

var doAction = function (action) {
    return $.ajax({
        url: this.api().central().account().segment('/wellness/issues').segment(action),
        type: 'PUT',
        contentType: 'application/json',
        dataType: 'json',
        data: JSON.stringify(this.pluck('id')),
    });
};

module.exports = Backbone.Collection.extend(
    /** @lends collections/WellnessIssueCollection.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name collections/WellnessIssueCollection
         * @constructor
         * @augments Backbone.Collection
         */
        model: WellnessIssueModel,

        clear: function () {
            return doAction.call(this, 'cleared');
        },

        acknowledge: function () {
            return doAction.call(this, 'acknowledged');
        },

        unacknowledge: function () {
            return doAction.call(this, 'unacknowledged');
        },

    });
