// QuickTable.js

import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';

import style from './QuickTable.scss';

const cx = classnames.bind(style);


function QuickTable(props) {
    const { caption, children } = props;
    const hasHeadline = (caption && typeof caption === 'string');
    return (
        <div className={cx('nmbl-quicktable')}>
            {hasHeadline ? <div className={cx('quicktable-caption')}>{caption}</div> : ''}
            {children}
        </div>
    );
}

QuickTable.propTypes = {
    caption: PropTypes.string,
    children: PropTypes.node.isRequired,
};


export default QuickTable;
