var Backbone = require('backbone');
var URI = require('urijs');
const { BASE_URL } = require('progeny/config');
const { buildUrl } = require('@infosight/elmer/dist/utils/url');

module.exports = Backbone.Model.extend({

    initialize: function (attrs, options) {
        this.product = options.product;
    },

    url: function () {
        return buildUrl(BASE_URL, URI('es/software/product')
            .segment(this.product.id.toString())
            .segment('_source')
            .query({
                '_source': 'eula',
            }));
    },

});
