import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link, withRouter } from 'react-router-dom';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';

import Card from '@infosight/elmer/dist/components/Card';
import Spinner from '@infosight/elmer/dist/components/Spinner';

import { tenantSelector } from '../../../user';
import style from './style.scss';

const cx = classnames.bind(style);

const Header = () => (
    <div className={classnames('h1', cx('header'))}>
        <Link to="/dashboards/nimble/wellness">Wellness</Link>
        <span>Rule Configuration</span>
    </div>
);

class WellnessRulesPage extends Component {
    constructor(props) {
        super(props);
        autobind(this);
        this.state = {
            loaded: false,
        };
    }

    componentDidMount() {
        this.setContainerHeight();
    }

    componentDidUpdate() {
        this.setContainerHeight();
    }

    onIframeLoad() {
        this.setState({
            loaded: true,
        });
    }

    setContainerHeight() {
        const bottomPadding = 30; // 20px card padding + 10px padding to bottom of viewport
        const viewportOffsetTop = Math.round(this.containerEl.getBoundingClientRect().top);
        this.containerEl.style.height = `calc(100vh - ${viewportOffsetTop + bottomPadding}px)`;
    }

    render() {
        const { tenantSelection, location: { search } } = this.props;
        const { loaded } = this.state;
        const queryParams = search ? `&${search.substr(1)}` : '';

        return (
            <div className={cx('rules-page')}>
                <Header />
                <Card className={cx('frame-container')} >
                    <div ref={((el) => { this.containerEl = el; })}>
                        {!loaded && <Spinner />}
                        <iframe
                            title="wellnessOptions"
                            className={classnames({ hide: !loaded })}
                            onLoad={this.onIframeLoad}
                            src={`/InfoSight/cgi/wellnessOptions?accountId=${tenantSelection.tenant.id}${queryParams}`}
                        />
                    </div>
                </Card>
            </div>
        );
    }
}

WellnessRulesPage.propTypes = {
    tenantSelection: PropTypes.object,

    location: PropTypes.shape({
        search: PropTypes.string.isRequired,
    }).isRequired,
};

const mapStateToProps = state => ({
    tenantSelection: tenantSelector(state),
});

export default connect(mapStateToProps)(withRouter(WellnessRulesPage));
