var Backbone = require('backbone');

module.exports = Backbone.Model.extend({

    initialize: function (attributes, options) {
        this.createNew = options.createNew || false;
    },

    defaults: {
        arrays: [],
        notes: null,
    },

    validation: {
        name: {
            required: true,
            msg: 'Name is required',
        },
        startTime: {
            fn: function (startTime, attr, computed) {
                if (!startTime) {
                    return 'Start date is required';
                }
                var patt = /invalid date/i;
                if (patt.test(startTime)) {
                    return 'Valid date/time needed, e.g. MM/DD/YYYY 1:30 PM';
                }
                if (computed.trackStartTime < new Date()) {
                    return 'Start time cannot be before current time';
                }
            },
        },
        startTimeOnly: {
            fn: function (startTime, attr, computed) {
                if (!startTime && computed.startTime) {
                    return 'Start time is required';
                }
                var patt = /invalid date/i;
                if (patt.test(computed.trackStartTime)) {
                    return 'Valid date/time needed, e.g. MM/DD/YYYY 1:30 PM';
                }
            },
        },
        endTime: {
            fn: function (value, attr, computed) {
                if (!computed.endTime) {
                    return 'End date is required';
                }
                var patt = /invalid date/i;
                if (patt.test(value)) {
                    return 'Valid date/time needed, e.g. MM/DD/YYYY 1:30 PM';
                }
                if (this.changed && this.changed.trackEndTime && (this.changed.trackEndTime < new Date())) {
                    return 'End time cannot be before current time';
                }
                if (computed.trackEndTime < computed.trackStartTime) {
                    return 'End time cannot be before start time';
                }
            },
        },
        endTimeOnly: {
            fn: function (value, attr, computed) {
                if (!value && computed.endTime) {
                    return 'End time is required';
                }
                var patt = /invalid date/i;
                if (patt.test(computed.trackEndTime)) {
                    return 'Valid date/time needed, e.g. MM/DD/YYYY 1:30 PM';
                }
            },
        },
        arrays: {
            fn: function (arrays) {
                if (!arrays || arrays.length === 0) {
                    return 'Please select at least one array for maintenance';
                }
                if (this._originalAttrs.arrays.length !== arrays.length && this._originalAttrs.endTime < new Date()) {
                    return 'You cannot edit an array list after maintenance mode has ended.';
                }
            },
        },
    },

});
