import React from 'react';
import PropTypes from 'prop-types';
import moment from 'moment-timezone';

const TAG = 'date-time';

/**
 * Formats a Date/Time as a string
 *
 * quick formatting guide:
 *
 *              2/9/17  -  M/D/YY
 *          02/09/2017  -  MM/DD/YYYY
 *        Jan 25, 2017  -  MMM DD, YYYY
 *  January 25th, 2017  -  MMMM Do, YYYY
 *
 *             1:32 PM  -  h:mm A
 *       03:15 (24hrs)  -  HH:mm
 *
 * @see https://momentjs.com/docs/#/parsing/string-format/
 */
const DateTime = ({ date, format, title }) => {
    const displayName = TAG;

    if (!date) {
        return null;
    }

    const momentized = moment(date);
    const validated = momentized.toDate();

    let isoDate = null;
    let descDate = null;
    let timeString = null;

    if (!Number.isNaN(Number(Date.parse(validated)))) { // ludicrous
        isoDate = validated.toISOString();
        descDate = title || validated.toUTCString();
        timeString = momentized.format(format || 'lll');
    } else {
        isoDate = date;
        descDate = title || date;
        timeString = String(date);
    }

    return (<time className={displayName} dateTime={isoDate} title={descDate}>{timeString}</time>);
};

DateTime.propTypes = {
    /**
     * Some date
     */
    date: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number,
    ]),

    /**
     * A [moment.js format](http://momentjs.com/docs/#/displaying/format/)
     */
    format: PropTypes.string,

    /**
     * accessibility title (optional, defaults to UTC-formatted date)
     */
    title: PropTypes.string,
};

DateTime.defaultValues = {
    format: 'lll',
};

export default DateTime;
