import React from 'react';
import PropTypes from 'prop-types';
import { Route, Switch, withRouter } from 'react-router-dom';

import WellnessDashboard from './WellnessDashboard';
import WellnessRulesPage from './WellnessRulesPage';
import MaintenanceWindowPage from './MaintenanceWindowPage';

const WellnessRouter = ({ match }) => (
    <div>
        <Switch>
            <Route path={`${match.path}/rules`} component={WellnessRulesPage} />
            <Route path={`${match.path}/maintenance-windows`} component={MaintenanceWindowPage} />
            <Route path={match.path} component={WellnessDashboard} />
        </Switch>
    </div>
);

WellnessRouter.propTypes = {
    // Injected by withRouter
    match: PropTypes.object.isRequired,
};

export default withRouter(WellnessRouter);
