import React from 'react';
import PropTypes from 'prop-types';
import { Route, Switch, NavLink } from 'react-router-dom';
import ScrollContainer from '@infosight/elmer/dist/components/ScrollContainer';
import { StaticBold } from '@infosight/elmer/dist/components/StaticBold';
import { SecondaryNavBar, NavItem, Title } from '@infosight/elmer/dist/components/SecondaryNavBar';
import { ThemeProvider } from '@infosight/elmer/dist/components/ThemeProvider';
import { buildUrl } from '@infosight/elmer/dist/utils/url';
import { pageBoundaryRouteRenderer } from '@infosight/elmer/dist/page';
import BulletinsAdminPage from '../bulletins/components/BulletinsAdminPage';
import { AUTHORITIES, AuthorizationEvaluator } from '../user';

const adminRoutes = [
    {
        label: 'Bulletins',
        url: 'bulletins',
        component: BulletinsAdminPage,
        access: {
            authorities: [
                [AUTHORITIES.REALM_INTERNAL],
                [AUTHORITIES.SITE_ADMIN],
            ],
        },
    },
];

const AdminRouter = ({ match }) => (
    <ThemeProvider>
        <ScrollContainer>
            <ScrollContainer.Fixed>
                <SecondaryNavBar>
                    <Title name="Administration" className="align-center" />
                    {AuthorizationEvaluator.filter(adminRoutes).map(({ label, url }) => (
                        <NavItem key={url}>
                            <NavLink className="flex-container align-center flex-dir-column" to={buildUrl(match.url, url)}>
                                <StaticBold parent={React.Fragment}>{label}</StaticBold>
                            </NavLink>
                        </NavItem>
                    ))}
                </SecondaryNavBar>
            </ScrollContainer.Fixed>

            <ScrollContainer.Scrollable>
                <Switch>
                    {adminRoutes.map(({ url, component, label }) => (
                        <Route
                            key={url}
                            path={buildUrl(match.path, url)}
                            render={pageBoundaryRouteRenderer(component, label)}
                        />
                    ))}
                </Switch>
            </ScrollContainer.Scrollable>
        </ScrollContainer>
    </ThemeProvider>
);

AdminRouter.propTypes = {
    match: PropTypes.object.isRequired,
};

export default AdminRouter;
