/* eslint-disable */

import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import moment from 'moment';
import { QuickTable, QuickRow } from '../../QuickTable';
import style from './CaseAttachments.scss';

const cx = classnames.bind(style);

class CaseAttachments extends React.Component {

    render() {
        const attachments = this.props.attachments;
        if (!attachments) return null;
        if (!attachments.length) return (<p>No attachments posted</p>);

        const dateTitle = 'Upload Date';
        const attachmentName = 'File Name';

        return (
            <div className={cx('attachments')}>
                <QuickTable>
                    <QuickRow key="header" className={cx('header')} columns={[dateTitle, attachmentName]} />
                    {attachments.sort((b,a) => new Date(a.createdDate) - new Date(b.createdDate)).map(attachment => {
                        attachment.ts = moment(attachment.createdDate).format('MM/DD/YYYY hh:mm A Z');
                        return <QuickRow key={attachment.id} columns={[attachment.ts, attachment.Name]} />;
                        })
                    }
                </QuickTable>
            </div>
        );
    }

}

CaseAttachments.propTypes = {
    attachments: PropTypes.array,
};

export default CaseAttachments;

