const _ = require('underscore');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');
var Marionette = require('backbone.marionette'),
    DisasterRecoveryModel = require('./DisasterRecoveryModel'),
    DataProtectionLegend = require('./DataProtectionLegend');

module.exports = Marionette.ItemView.extend(
    /** @lends views/components/dashboard/DisasterRecoveryView.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/dashboard/DisasterRecoveryView
         * @constructor
         * @augments Marionette.ItemView
         */

        template: false,

        className: 'disaster-recovery-view',

        renderChart: function (container, categories, series) {
            this.chart = new Highcharts.Chart({
                chart: {
                    type: 'column',
                    renderTo: container,
                    height: 350,
                    events: {
                        redraw: function () {

                        },
                        load: function () {
                            var chart = this;
                            _.delay(function () {
                                chart.reflow.call(chart);
                            }, 50);
                        },
                    },
                },
                credits: {
                    enabled: false,
                },
                title: {
                    text: 'Disaster Recovery',
                },
                subtitle: {
                    text: 'Applications Derived by the Volume Performance Policy',
                },
                legend: {
                    enabled: true,
                },
                xAxis: {
                    categories: categories,
                    crosshair: false,
                    labels: {
                        // pass-through formatter overrides global formatter which expects dateTimeLabelFormat
                        formatter: function () {
                            return this.value;
                        },
                    },
                },
                yAxis: {
                    min: 0,
                    max: 100,
                    title: {
                        text: '% of Application Total Data',
                    },
                    labels: {
                        enabled: true,
                    },
                },
                tooltip: {
                    enabled: true,
                    valueDecimals: 2,
                    valueSuffix: ' TiB',
                    formatter: function () {
                        var pctVolUsed = Highcharts.numberFormat(this.point.y, 0) + '%',
                            volUsed = Highcharts.numberFormat(this.point.values.volumeUsedTib, 2) + ' TiB',
                            policy = '<span style="color: ' + this.series.color + '">' + this.point.section + '</span>',
                            s = '<b>' + pctVolUsed + ' (' + volUsed + ')</b> of your total ' + policy + ' data is ';
                        var category = this.x;
                        // handle the case when grouped-category lib is used with highcharts
                        // category is a object, i.e. {"userOptions":"00:01","name":"00:01"};
                        if (typeof(category) === 'object') {
                            category = category.name;
                        }
                        var lowerCaseCategory = category.toLowerCase();
                        if (lowerCaseCategory.indexOf('local') >= 0) {
                            s += ' protected locally';
                        } else if (lowerCaseCategory.indexOf('replicated') >= 0) {
                            s += ' replicated';
                        } else if (lowerCaseCategory.indexOf('unprotected') >= 0) {
                            s += ' <span style="font-weight: bold; color: red;">' + category + '</span>';
                        } else {
                            throw new Error('Unexpected type: ' + category);
                        }
                        return s;
                    },
                },
                series: series,
                plotOptions: {
                    series: {
                        minPointLength: 3,
                    },
                },
            });
        },

        onRender: function () {

            var categories = this.model.get('categories'),
                series = this.model.get('series');

            if (series.length === 0) {
                this.$el.append('<div>No disaster recovery information available.</div>');
            } else {
                for (var i = 0; i < series.length; i++) {
                    var seriesItem = series[i];
                    seriesItem.color = DataProtectionLegend.getSeriesColor(seriesItem.name);
                }

                this.renderChart(this.el, categories, series);
            }
        },

        onDestroy: function () {
            if (this.chart) {
                this.chart.destroy();
                delete this.chart;
            }
        },

    });
