// SupportCreateCase.js
/* eslint-disable */

import React from 'react';
import PropTypes from 'prop-types';
import { Provider, connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import { Link } from 'react-router-dom';
import { buildUrl } from '@infosight/elmer/dist/utils/url';
import Dropdown from '../../../../components/Dropdown';


// import KeyCode from '../../util/KeyCode';
import SupportTab from '../SupportTab';
import KcsDocumentModal from '../KcsDocumentModal';

import SFCaseModel from '../../library/SFCaseModel';

import { postCaseDetailOk } from '../../actions/fetch-support-cases';

import {
    fetchSuggestedTerms,
    searchKcsSolutions,
    getUserProfile,
} from '../../library/support-api';

import { listSelector } from '../../../nimble/arrays/collection/reducer';
import { getArrays } from '../../../nimble/arrays/collection/actionCreators';

import { isWordChar, makeDomId } from '../../library/support-helper';

import TypeAhead from '../../../../components/TypeAhead';

import style from './SupportCreateCase.scss';
import { buildUrlFromBase } from '../../../../util/url';

const cx = classnames.bind(style);


const SEVERITY_OPTIONS = [
    {
        label: 'Critical',
        labelDesc: 'System down; no I/O being served',
        value: 'P1',
    }, {
        label: 'Major',
        labelDesc: 'Significant impact to I/O or operations',
        value: 'P2',
    }, {
        label: 'Minor',
        labelDesc: 'Some impact to I/O or operations',
        value: 'P3',
    }, {
        label: 'Low',
        labelDesc: 'No impact to I/O or operations; administrative/question/etc',
        value: 'P4',
    },
];


class SupportCreateCase extends React.Component {

    constructor(props) {
        super(props);
        autobind(this);

        this.state = {
            arrays: [],
            searching: null,
            didSearch: false,
            kcsSuggestions: null,
            kcsResults: [
                {
                    type:   'hint',
                    title:  'While you enter your case, we will also look for some solutions that could help.',
                    doc_id: null,
                }
            ],
            resultsRead: [],
            caseTitle: '',
            caseDescription: '',
            caseArray: null,
            caseSeverity: null,
            lastError: null,
            busySend: false,
            caseSubmission: null,

            visibleKcsDocId: null,
        };

        this.lastSuggestionSearch = null;

        // this.inputPlainId = makeDomId();
        this.inputTypeAheadId = makeDomId();
        this.textareaId = makeDomId();

    }

    componentWillMount() {
        this.props.getArrays();
    }

    onSummaryChange(summaryInput) {
        this.setState({ caseTitle: summaryInput });
        this.requestTermsSuggestion(summaryInput);
    }

    onDescriptionChange(e) {
        const descriptionInput = e.target.value;
        if (descriptionInput !== this.state.caseDescription) {
            this.setState({ caseDescription: descriptionInput });
        }
    }

    onArrayChange(valueSet) {
        const arrayPick = Object.assign({}, valueSet);
        this.setState({ caseArray: arrayPick });
    }

    onSeverityChange(valueSet) {
        const severityPick = Object.assign({}, valueSet);
        this.setState({ caseSeverity: severityPick.target.value });

        if (severityPick.target.value === 'P1') {
            this.setState({
                kcsResults: [{
                    type:   'hint',
                    title:  'For all P1 cases, please call Nimble support directly at 1-877-364-6253',
                    doc_id: null,
                }]
            });
        } else {
            this.setState({ kcsResults: [] });
        }
    }

    getFormErrors() {
        const { caseArray, caseSeverity, caseTitle, caseDescription } = this.state;
        const errors = [];

        if (!caseTitle.trim()) { errors.push('A case title is needed.'); }
        if (!caseDescription.trim())  { errors.push('A case requires a full description.'); }
        if (!caseArray || !caseArray.value) { errors.push('An array is required.'); }
        if (!caseSeverity) { errors.push('A severity level is required.'); }

        return errors;
    }

    checkFormErrors() {
        return (this.getFormErrors().length === 0);
    }

    eventBlur() {
        const compositeQuery = this.compileFullTextSearch();
        this.requestKcsSolutions(compositeQuery);
    }

    eventKeyUp(e) {
        const keyCode = e.which || e.keyCode;
        const theQuery = String(this.state.caseTitle);

        if (keyCode === 13) { return };

        const isPrintable = (keyCode === 8 || (keyCode >= 32 && (keyCode <= 38 && keyCode >= 40)));
        if (isPrintable && theQuery.length > 2) { this.requestTermsSuggestion(theQuery) };
    }

    eventSummaryKeyDown(e) {
        const keyCode = e.which || e.keyCode;
        const theQuery = String(this.state.caseTitle);

        if (keyCode === 13) { this.requestKcsSolutions(theQuery) };
    }

    eventDescriptionKeyDown(e) {
        // if last key was not a 'word' char, then issue a new search
        const keyCode = e.which || e.keyCode;
        if (!isWordChar(keyCode) && keyCode !== 8) { // ignore backspacing
            const compositeQuery = this.compileFullTextSearch();
            this.requestKcsSolutions(compositeQuery);
        }
    }

    eventCaseCreateSuccess(casePostResponse, stuff, what) {
        const caseSubmission = {
            type: 'success',
            caseNumber: casePostResponse.caseNumber,
            message: 'Your case has been received.',
        };
        this.props.postCaseDetailOkDispatch(casePostResponse);
        this.setState({caseSubmission, busySend: false});
    }

    eventCaseCreateFail() {
        const caseSubmission = {
            type: 'fail',
            caseNumber: null,
            message: 'Your case failed to create.',
        };
        this.setState({caseSubmission, busySend: false});
    }

    eventSubmit(e) {
        if (!e) { return false; }
        e.preventDefault();
        e.stopPropagation();

        const formErrors = this.getFormErrors();
        const hasErrors = !!formErrors.length;

        if (hasErrors) { return; }

        const caseModel = new SFCaseModel();

        let casePriority = 'P4';
        switch (this.state.caseSeverity) {
            case '3': case 'P3': casePriority = 'P3'; break;
            case '2': case 'P2':  casePriority = 'P2'; break;
            case '1': case 'P1':  casePriority = 'P1'; break;
            default:
        }

        caseModel.set({
            assetId: this.state.caseArray.value,
            priority: casePriority,
            subject: this.state.caseTitle,
            description: this.state.caseDescription,
        });

        getUserProfile().always((results) => {
            const userName = results.name || '(name not available)';
            caseModel.set({userName: userName});
            caseModel.save()
                .done(this.eventCaseCreateSuccess)
                .fail(this.eventCaseCreateFail);
        });

        this.setState({busySend: true});
        return false;
    }

    compileFullTextSearch() {
        const { caseTitle, caseDescription } = this.state;
        const searchString = `${caseTitle} ${caseDescription}`;
        return searchString.trim();
    }

    requestTermsSuggestion(theQuery) {
        if (theQuery === this.lastSuggestionSearch) { return; } // redundant

        this.lastSuggestionSearch = theQuery;

        fetchSuggestedTerms(theQuery)
        .done((terms) => {
            if (theQuery !== this.lastSuggestionSearch) { return; } // expired
            this.setState({ kcsSuggestions: terms });
        });
    }

    requestKcsSolutions(theQuery) {
        searchKcsSolutions(theQuery)
        .done((suggestedSolutions) => {
            if (!suggestedSolutions.length) {
                this.setState({ didSearch: true });
                return;
            }
            // const limitedSet = [].concat(suggestedSolutions);
            const limitedSet = suggestedSolutions.slice(0,10);
            this.setState({ kcsResults: limitedSet, didSearch: true });
        });
    }

    makeAssetDropdownOptions() {
        if (!this.props.arrays || this.props.arrays.length == 0)
        {
            return [];
        }

        return this.props.arrays
        .sort((a, b) => {
            if (a.arraySn < b.arraySn) { return -1; }
            if (a.arraySn > b.arraySn) { return 1; }
            return 0;
        })
        .map((a) => {
            const hostprint = a.hostName ? `- ${a.hostName}` : '';
            return {
                label: `${a.arraySn} ${hostprint}`,
                value: a.arrayId,
            };
        });
    }

    renderKcsSearchResults() {
        const { kcsResults, didSearch } = this.state;
        // if (!didSearch) { return null; }
        // if (!kcsResults.length) { return (<div className={cx('rs-pad')}>No results.</div>); }
        const LIMIT_RESULTS = 5;
        let resultDisplayCount = 0;
        const { resultsRead } = this.state;
        return (
            <span>
                {kcsResults.map((kcsSuggestion)=>{
                    const docId = kcsSuggestion.doc_id;
                    if (kcsSuggestion.doc_id) {
                        if (resultDisplayCount >= LIMIT_RESULTS) { return null; }
                        resultDisplayCount++;
                    }
                    return(
                        <Suggestion onClick={() => this.onClickKcsSuggestion(docId)} key={docId} docId={docId} title={kcsSuggestion.title} read={this.state.resultsRead[docId]} />
                    );
                })}
            </span>
        );
    }

    onClickKcsSuggestion(docId) {
        const { resultsRead } = this.state;
        resultsRead[docId] = true;
        this.setState(prevState => {
            const resultsRead = {
                ...prevState.resultsRead,
                [docId]: true,
            };

            return { resultsRead, visibleKcsDocId: Number(docId) };
        });
    }

    renderKcsHint() {
        return null;
    }

    renderSubmitResults() {
        const { caseSubmission } = this.state;
        const caseSuccess = (caseSubmission.type === 'success')
        if (!caseSuccess) {
            return(
                <div className={cx('case-submitted')}>
                    <h2>We were not able to process your case</h2>
                    <hr />
                    <p><Link to="/resources/nimble/support">Return to the support portal.</Link></p>
                </div>
            );
        }
        return (
            <div className={cx('case-submitted')}>
                <h2>Case <Link to={`${buildUrl('/resources/nimble/support/case', caseSubmission.caseNumber)}`}>#{caseSubmission.caseNumber}</Link> was submitted successfully</h2>
                <hr />
                <p><Link to="/resources/nimble/support">Return to the support portal.</Link></p>
            </div>
        );
    }

    cancelCase(e) {
        e.preventDefault();
        e.stopPropagation();

        // todo: switch to a modal view
        if (confirm('Are you sure you wish to abandon this case?')) {
            const url = buildUrlFromBase('/resources/nimble/support');
            window.location.href = url;
        }

        return false;
    }

    renderNewCaseForm() {
        const arrayOptions = this.makeAssetDropdownOptions();
        const formErrors = this.getFormErrors();
        const hasErrors = !!formErrors.length;

        let showErrors = false;
        if (this.state.caseSubmission) {
            showErrors = hasErrors;
        }

        const formBlocked = (this.state.caseSeverity === 'P1');
        const busySend = this.state.busySend;

        return (
            <form className={cx('measured')}>

                <h3>Severity</h3>

                <div className={cx('severity-buttons')}>
                    {SEVERITY_OPTIONS.map(option => (
                        <label key={option.value}>
                        <input type="radio" name="severity" value={option.value} onClick={this.onSeverityChange} />
                            <span>{option.label}</span> - {option.labelDesc}
                        </label>
                    ))}
                </div>

                <h3>Summary</h3>

                <TypeAhead
                    name={this.inputTypeAheadId} size="61"
                    placeholder="Describe the issue"
                    value={this.state.caseTitle} valueList={this.state.kcsSuggestions}
                    onKeyUp={this.eventKeyUp} onKeyDown={this.eventSummaryKeyDown}
                    onInput={this.onSummaryChange} onBlur={this.eventBlur}
                    disabled={formBlocked}
                />

                <h3>Description</h3>

                <textarea
                    id={this.textareaId} cols="60" rows="8"
                    value={this.state.caseDescription}
                    onKeyDown={this.eventDescriptionKeyDown}
                    onChange={this.onDescriptionChange} onBlur={this.eventBlur}
                    disabled={formBlocked}
                />

                <h3>Affected Array</h3>

                <Dropdown
                    className={cx('assets')}
                    value={this.state.caseArray}
                    options={arrayOptions}
                    onChange={this.onArrayChange}
                    disabled={formBlocked}
                />

                <button className="button" disabled={hasErrors || formBlocked || busySend} onClick={this.eventSubmit}>Submit Case</button>
                <button className="button hollow" onClick={this.cancelCase} >Cancel</button>

            </form>
        )
    }

    render() {
        const { visibleKcsDocId } = this.state;
        return (
            <SupportTab>

                <div className={cx('support-create-case')}>

                    <div className={cx('content-inset')}>
                        {this.state.caseSubmission ?
                            this.renderSubmitResults() :
                            this.renderNewCaseForm()
                        }
                    </div>

                    {/* TODO: componentize the KCS results */}
                    <div className={cx('kcs-suggestions')}>

                        {!this.state.caseSubmission ?
                            this.renderKcsSearchResults() : ''
                        }

                    </div>

                </div>
                {visibleKcsDocId && <KcsDocumentModal docId={visibleKcsDocId} closeModal={() => this.setState({ visibleKcsDocId: null })} />}
            </SupportTab>
        );
    }
}

function Suggestion(props) {
    return (
        <div
            className={props.read ? cx('read-suggestion-card') : cx('suggestion-card')}
            onClick={props.onClick}
            dangerouslySetInnerHTML={{__html: props.title}}
        />
    );
}

SupportCreateCase.propTypes = {
    arrays: PropTypes.array,
    postCaseDetailOkDispatch: PropTypes.func,
    getArrays: PropTypes.func.isRequired,
    // fetchSuggestedTermsDispatch: PropTypes.func,
    // searchKcsSolutionsDispatch: PropTypes.func,
};

function mapStateToProps(state) {
    return {
        ...listSelector(state),
    };
}

const mapDispatchToProps = dispatch => bindActionCreators({
    getArrays,
    postCaseDetailOkDispatch: postCaseDetailOk,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(SupportCreateCase);
