/* eslint-disable react/prop-types, react/no-multi-comp */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import { connect } from 'react-redux';
import Route from 'react-router-dom/Route';
import Switch from 'react-router-dom/Switch';
import Async from '@infosight/elmer/dist/components/Async';
import { buildUrl } from '@infosight/elmer/dist/utils/url';
import LabsView from '../../../../../app/modules/labs/LabsView';
import LabAppView from '../../../../../app/modules/labs/LabAppView';
import LabAppCollection from '../../../../../app/modules/labs/LabAppCollection';
import breadcrumbStyle from '../../legacyStyles/breadcrumbs.scss';
import style from './style.scss';
import Request from '../../../../services/Request';
import { awaitLegacyApp } from '../../../../containers/NimbleLegacyContainer';
import { tenantSelector } from '../../../user';

const cx = classnames.bind({ ...style, ...breadcrumbStyle });

class LandingPage extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    componentWillUnmount() {
        if (this.view) {
            this.view.destroy();
        }
    }

    renderView(el) {
        if (!this.view) {
            const { match, collection } = this.props;
            this.view = new LabsView({ collection, path: match.url });
            this.view.setElement(el).render();
        }
    }

    render() {
        return (
            <div className={cx('container')}>
                <div className="labs-view" ref={this.renderView} />
            </div>
        );
    }
}

class App extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    componentWillUnmount() {
        if (this.view) {
            this.view.destroy();
        }
    }

    renderView(el) {
        if (!this.view) {
            const { match, pod, collection } = this.props;
            this.view = new LabAppView({ pod, appid: match.params.appid, path: match.url, collection });
            this.view.setElement(el).render();
        }
    }

    render() {
        return (
            <div className={cx('container', 'legacy-breadcrumbs')}>
                <div className="lab-app" ref={this.renderView} />
            </div>
        );
    }
}

class LabsContainer extends Component {
    constructor(props) {
        super(props);

        this.state = { pod: null, collection: LabAppCollection.INSTANCE };
    }

    componentDidMount() {
        const { tenant } = this.props;
        if (!this.state.pod) {
            new Request()
                .central()
                .slash('labs/pod')
                .query({ accountId: tenant.id })
                .get()
                .then(({ data }) => this.setState({ pod: data }));
        }

        if (!LabAppCollection.INSTANCE) {
            LabAppCollection.INSTANCE = new LabAppCollection();
            LabAppCollection.INSTANCE.fetch()
                .done(() => this.setState({ collection: LabAppCollection.INSTANCE }));
        }
    }

    render() {
        const { match } = this.props;
        const { collection, pod } = this.state;
        return (
            <Async loading={!(collection && pod)}>
                {() => (
                    <Switch>
                        <Route exact path={buildUrl(match.path, ':appid')} render={props => <App {...props} {...this.state} />} />
                        <Route render={props => <LandingPage {...props} {...this.state} />} />
                    </Switch>
                )}
            </Async>
        );
    }
}

LabsContainer.propTypes = {
    tenant: PropTypes.shape({
        id: PropTypes.string.isRequired,
        name: PropTypes.string.isRequired,
    }).isRequired,
};

export default awaitLegacyApp(connect(state => tenantSelector(state))(LabsContainer));
