var Backbone = require('backbone'),
    ArrayUpgradeModel = require('./ArrayUpgradeModel');

module.exports = Backbone.Collection.extend(
    /** @lends collections/dashboard/UpgradeSummaryCollection.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name collections/dashboard/UpgradeSummaryCollection
         * @constructor
         * @augments Backbone.Collection
         */


        model: ArrayUpgradeModel,

        /**
         * Overridden url method for fetching recommendations for a given customer.
         * @return {String} The REST URL for this collection.
         */
        url: function () {
            return this.api().pod().account().segment('/dashboard/upgrade_summary');
        },
    });
