const URL_REGEX = /(\bhttps?:\/\/[-A-Z0-9+&@#/%?=~_|!:,.;]*[-A-Z0-9+&@#/%=~_|])/ig;
const ACCOUNT_REGEX = /\[account:([^\]]+)\]/;
const USER_REGEX = /\[user:([^\]]+)\]\n?/;
const USERIDNAME_REGEX = /^([^(]+)\(([^)]+)\)/;

const nimbleSupportContactId = '00580000003xTOlAAM';

const BASE_KCS_URL = buildUrl(BASE_URL, 'kcs');


function htmlEncode(value) {
    if (value !== undefined && value !== null) {
        return $('<div />').text(value).html();
    }
    return '';
}

// convert description to HTML
function convertToHTML(description) {
    let htmlDescription = htmlEncode(description);

    // convert line breaks (windows or unix compatible)
    htmlDescription = htmlDescription.replace(/(\r\n|\n|\r)/gi, '<br>');

    // convenience linkify urls in the message.
    htmlDescription = htmlDescription.replace(URL_REGEX, ' <a href="$1" target="_blank">$1</a>');

    return htmlDescription;
}

export function fetchCases() {
    return (dispatch) => {
        const caseLog = new SFCaseCollection();

        return caseLog.fetch()
            .done((stuff, status /* , xhrreq */) => {
                dispatch(fetchCaseHeadersOk(stuff, status));
            })
            .error((stuff, status /* , xhrreq */) => {
                dispatch(fetchCaseHeadersFail(stuff, status));
            });
    };
}

// TODO: move this to support helper = helper
export function parseComment(comment) {
    const c = Object.assign({}, comment);

    if (c.createdById !== nimbleSupportContactId) {
        c.createdByName = c.createdBy.name;
    }

    if (!c.commentBody.match(ACCOUNT_REGEX) && !c.commentBody.match(ACCOUNT_REGEX)) {
        c.fromNimble = true;
    }

    const attribution = c.commentBody.match(USER_REGEX); // match user first
    if (attribution) {
        const userIdName = attribution[1].match(USERIDNAME_REGEX);
        if (userIdName) {
            [,, c.createdByName] = userIdName;
            if (userIdName[1] === 'internal') {
                c.fromNimble = true;
                [,, c.createdByName] = userIdName;
            }
        }
    }
    if (!c.createdByName) { c.createdByName = 'Nimble Support'; }

    // remove any remaining metatext
    c.commentBody = c.commentBody.replace(USER_REGEX, '');
    c.commentBody = c.commentBody.replace(ACCOUNT_REGEX, '');

    return c;
}


export function fetchCase(caseId) {
    return (dispatch) => {
        const theCase = new SFCaseModel();
        theCase.set('id', caseId);

        const caseUrl = theCase.url();
        const caseDetailsFetch = theCase.fetch();
        const caseCommentsFetch = $.ajax({
            type: 'GET',
            url: `${caseUrl}/comments`,
            contentType: 'application/json',
        });

        const caseAttachmentsFetch = $.ajax({
            type: 'GET',
            url: `${caseUrl}/attachment`,
            contentType: 'application/json',
        });

        const caseRmaLabelsFetch = $.ajax({
            type: 'GET',
            url: `${caseUrl}/rmaLabels`,
            contentType: 'application/json',
        });

        function fetchSuccess(caseDetailsRes, caseCommentsRes, caseAttachmentsRes, caseRmaLabelsRes) {
            const caseData = caseDetailsRes[0];
            const caseAttachments = caseAttachmentsRes[0];
            const caseRmaLabels = caseRmaLabelsRes[0];
            let caseComments = caseCommentsRes[0];

            if (caseData.description) caseData.description = convertToHTML(caseData.description);
            caseData.$_complete = true;

            if (Array.isArray(caseComments)) caseComments = caseComments.map(parseComment);

            dispatch(fetchCaseDetailOk(caseData, caseComments, caseAttachments, caseRmaLabels));
        }

        function fetchFail(caseDetailsRes, caseCommentsRes, caseAttachmentsRes, caseRmaLabels) {
            dispatch(
                fetchCaseDetailFail(caseDetailsRes[1],
                    caseCommentsRes[1],
                    caseAttachmentsRes[1],
                    caseRmaLabels[1]));
        }

        return $.when(caseDetailsFetch, caseCommentsFetch, caseAttachmentsFetch, caseRmaLabelsFetch)
            .then(fetchSuccess, fetchFail);
    };
}

export function fetchSuggestedTerms(englishText) {
    const req = {
        type: 'POST',
        url: `${BASE_KCS_URL}/suggest/terms`,
        contentType: 'application/json',
        dataType: 'json',
        data: JSON.stringify({ query: englishText }),
    };
    return $.ajax(req);
}

export function getUserProfile() {
    const req = {
        type: 'GET',
        url: '/auth/authenticate',
        contentType: 'application/json',
    };
    return $.ajax(req);
}

export function fetchSuggestedTermsDispatch(englishText) {
    return (dispatch) => {
        this.fetchSuggestedTerms(englishText)
            .done((xhrreq, stuff, status) => {
                dispatch(fetchSuggestedTermsOk(stuff, status, englishText));
            })
            .error((xhrreq, stuff, status) => {
                dispatch(fetchSuggestedTermsFail(stuff, status, englishText));
            });
    };
}

