export const fetchSfdcContact = () => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(FETCH_SFDC_CONTACT, dispatch);
    dispatchStart();

    let req;

    if (NimbleUserTenantHelpers.getCurrentTenant() !== null) {
        req = new Request().slash('accounts', NimbleUserTenantHelpers.getCurrentTenant().id, 'cases/contacts/me');
    } else {
        req = new Request().slash('account', 'cases/contacts/me');
    }

    req
        .get()
        .then(({ data }) => data)
        .then(dispatchSuccess)
        .catch(dispatchError);
};

export function resolveCase(caseId, resolutionNotes, successCallback, errorCallback) {
    const theCase = new SFCaseModel();
    theCase.set('id', caseId);

    const caseUrl = theCase.url();

    const resolvePromise = $.ajax({
        type: 'POST',
        url: `${caseUrl}/resolve`,
        contentType: 'application/json',
        data: resolutionNotes,
    });

    resolvePromise.then(successCallback, errorCallback);
}

