const _ = require('underscore');
var Marionette = require('backbone.marionette');
var LanguagePreferenceModel = require('./LanguagePreferenceModel');
var LanguagePreferenceForm = require('./LanguagePreferenceForm');
var tpl = require('raw-loader!./LanguagePreferenceView.html');

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    className: 'language-preferences',

    ui: {
        anchor: 'a',
    },

    events: {
        'click @ui.anchor': 'showPopover',
        'click .close': 'hidePopover',
        'hidden.bs.popover @ui.anchor': 'onPopoverHidden',
    },

    initialize: function () {
        this.model = new LanguagePreferenceModel();
    },

    showPopover: function (event) {
        event.preventDefault();
        this.ui.anchor.popover('show');
    },

    hidePopover: function () {
        this.ui.anchor.popover('hide');
    },

    onPopoverHidden: function () {
        this.form.destroy();
    },

    onRender: function () {
        this.ui.anchor.popover({
            html: true,
            container: false,
            content: this.renderForm.bind(this),
            title: '<button type="button" class="close">&times;</button> Languages',
            placement: 'bottom',
            trigger: 'manual',
        });
    },

    renderForm: function () {
        this.form = new LanguagePreferenceForm({
            model: this.model,
        });
        return this.form.render().$el;
    },

});
