const _ = require('underscore');
const { buildUrl } = require('@infosight/elmer/dist/utils/url');

var Marionette = require('backbone.marionette');
var Backbone = require('backbone');
var moment = require('moment');
var itemTpl = require('raw-loader!./WhatsNewItem.html');
var tpl = require('raw-loader!./WhatsNewView.html');
var URI = require('urijs');

var NEW_CUTOFF_IN_DAYS = 90;

var ItemView = Marionette.ItemView.extend({

    tagName: 'li',

    template: _.template(itemTpl),

    templateHelpers: function () {
        var category = this.model.get('product').collection.category.toJSON();
        var product = this.model.get('product').toJSON();
        return {
            href: buildUrl('resources/nimble/software', window.encodeURI(category.name), window.encodeURI(product.name)),
            product: product,
            category: category,
            moment: moment,
        };
    },
});

module.exports = Marionette.CompositeView.extend({

    template: _.template(tpl),

    childView: ItemView,

    childViewContainer: 'ul',

    initialize: function (options) {
        var seen = {};
        this.collection = new Backbone.Collection(
            _.chain(options.categories.flattenToProductVersions())
                .filter(function (productVersion) {
                    return moment().diff(productVersion.get('releaseDate'), 'days') < NEW_CUTOFF_IN_DAYS;
                })
                .sortBy(function (productVersion) {
                    return productVersion.get('releaseDate');
                })
                .reverse()
                .filter(function (productVersion) {
                    var productId = productVersion.get('product').id;
                    try {
                        return !seen[productId];
                    } finally {
                        seen[productId] = true;
                    }
                })
                .value(),
        );
    },

    onRender: function () {
        this.$el.toggle(!this.collection.isEmpty());
    },

});
