import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';

import Button from '@infosight/elmer/dist/components/Button';
import Async from '@infosight/elmer/dist/components/Async';

import { buildUrlFromBase } from 'progeny/util/url';
import { userSelector } from '../../../user/reducer';
import Request from '../../../../services/Request';

const PreRegistration = ({ user, account, register }) => (
    <div>
        <p>
            A match for <b>{user.username}</b> has been found within the HPE Nimble Storage customer database.
        </p>
        <p>
            Please click below to associate your InfoSight profile to the <b>{account.name}</b> organization.
        </p>
        <Button
            onClick={register}
        >
            Register
        </Button>
    </div>
);

PreRegistration.propTypes = {
    user: PropTypes.object,
    account: PropTypes.object,
    register: PropTypes.func,
};

class RegistrationSuccess extends React.Component { /* eslint-disable react/no-multi-comp */
    componentDidMount() {
        window.setTimeout(() => {
            window.location.replace(buildUrlFromBase());
        }, 5000);
    }

    render() {
        return (
            <div className="callout success">
                <h4>Congratulations!  Your HPE Nimble Storage customer profile is now active.</h4>
                <p>This page will now be automatically reloaded to enable your updated profile.</p>
            </div>
        );
    }
}

const RegistrationError = ({ error }) => (
    <div className="callout error">
        <h4>Your self registration request was unable to be completed</h4>
        <p>{error}</p>
        <p>Please contact HPE Nimble Storage support.</p>
    </div>
);

RegistrationError.propTypes = {
    error: PropTypes.string,
};

class SelfRegistrationCapable extends Component {
    constructor(props) {
        super(props);
        autobind(this);
        this.state = {
            preRegistration: true,
            registrationLoading: null,
            registrationError: null,
        };
    }

    register() {
        this.setState({
            registrationLoading: true,
        });
        new Request()
            .urlSegment('auth/user/register')
            .post()
            .then(() => {
                this.setState({
                    preRegistration: false,
                    registrationLoading: false,
                });
            })
            .catch((error) => {
                this.setState({
                    preRegistration: false,
                    registrationLoading: false,
                    registrationError: error.response.data.message,
                });
            });
    }

    render() {
        const { preRegistration, registrationLoading, registrationError } = this.state;
        return (
            preRegistration ? (
                <PreRegistration register={this.register} {...this.props} />
            ) : (
                <Async loading={registrationLoading}>
                    {registrationError ? (
                        <RegistrationError error={registrationError} />
                    ) : (
                        <RegistrationSuccess />
                    )}
                </Async>
            )
        );
    }
}

SelfRegistrationCapable.propTypes = {
    user: PropTypes.object,
    account: PropTypes.object,
};

export default connect(
    state => ({
        user: userSelector(state),
    }),
    null
)(SelfRegistrationCapable);
