const _ = require('underscore');
const { buildUrl } = require('@infosight/elmer/dist/utils/url');
var Marionette = require('backbone.marionette');
var tinycolor = require('tinycolor2');
var moment = require('moment');
var numeral = require('numeral');
var ImpactScore = require('app/modules/asset_details/performance/ImpactScore');
const { buildUrlFromBase } = require('progeny/util/url');
var peerPerformanceTooltipTmpl = require('raw-loader!./PeerPerformanceChartTooltip.html');
var ONE_DAY_IN_MILLISECONDS = 24 * 3600 * 1000;

var tooltipTtemplate = _.template(peerPerformanceTooltipTmpl);

module.exports = Marionette.ItemView.extend({

    template: _.constant('<div class="chart"></div>'),

    templateHelpers() {
        return { numeral };
    },

    className: 'performance-row',

    ui: {
        chartContainer: '.chart',
    },

    onRender: function () {
        var series = this.model.get('series'),
            sn = this.model.get('serialNumber'),
            host = this.model.get('arrayHostname'),
            hostDisplay = sn + '</br>' + host;

        var displayXAxis = 'none';
        var height = 90;
        if (this.model.get('isLast')) {
            //display xAxis only for last graph and make last graph a bit bigger to accommodate the axis
            displayXAxis = 'block';
            height = 100;
        }

        var maxIops = Math.round(this.getOption('maxIops'));

        this.ui.chartContainer.highcharts({
            global: {
                useUTC: false, // https://highcharts.uservoice.com/forums/55896-highcharts-javascript-api/suggestions/2454194-useutc-as-a-configuration-per-chart
            },
            chart: {
                type: 'column',
                height: height,
                spacingLeft: 0,
                marginRight: 50,
            },
            title: {
                text: null,
            },
            plotOptions: {
                column: {
                    pointPadding: 0,
                    groupPadding: 0.1,
                    borderWidth: 0,
                    minPointLength: 3,
                    colorByPoint: true,
                },
            },
            xAxis: {
                min: this.getOption('minDate'),
                max: this.getOption('maxDate'),
                type: 'datetime',
                tickInterval: ONE_DAY_IN_MILLISECONDS,
                dateTimeLabelFormats: {
                    day: '%m/%d',
                },
                labels: {
                    style: {
                        display: displayXAxis,
                    },
                },
            },
            yAxis: [
                {
                    allowDecimals: true,
                    min: 0,
                    opposite: true,
                    showLastLabel: true,
                    showFirstLabel: false,
                    tickPositioner: function () {
                        return [0, this.dataMax];
                    },
                    tickWidth: 1,
                    title: {
                        text: 'IOPS',
                        offset: 15,
                    },
                    labels: {
                        useHTML: true,
                        formatter: function () {
                            return this.value < 1000 ? numeral(this.value).format('0') : numeral(this.value).format('0.0a');
                        },
                    },
                    gridLineWidth: 0,
                }, {
                    title: {
                        text: `<a title="${sn} : ${host}" class="history-aware" href="${buildUrlFromBase('/infrastructure/storage/nimble/arrays/', sn, '/performance')}"> ${hostDisplay}</a>`,
                        rotation: 0,
                        useHTML: true,
                        offset: 10,
                        x: -50,
                        style: {
                            'min-width': '100px',
                            'max-width': '100px',
                        },
                    },
                },
            ],
            legend: {
                enabled: false,
            },
            series: [
                {
                    name: 'Latency',
                    data: series.map(this.transformPoint),
                },
            ],
            tooltip: {
                backgroundColor: 'white',
                // borderWidth: 0,
                shadow: false,
                useHTML: true,
                style: {
                    padding: 0,
                },

                //HTML yAxis Title shows above tooltip... tried tweaking z-index but had no luck
                //Using positioner to make sure tooltip doesnt hover over yAxis title
                positioner: function (labelWidth, labelHeight, point) {
                    var tooltipX = point.plotX + this.chart.plotLeft + 20;
                    if (point.plotX + this.chart.plotLeft + labelWidth + 30 > this.chart.chartWidth) {
                        tooltipX = point.plotX + this.chart.plotLeft - labelWidth - 12;
                    }
                    var tooltipY = point.plotY - (labelHeight - (this.chart.chartHeight - this.chart.plotTop - point.plotY)) - 12;
                    return {
                        x: tooltipX,
                        y: tooltipY,
                    };
                },
                formatter: function () {
                    return tooltipTtemplate(
                        _.extend({}, this, {
                            numeral: numeral,
                            moment: moment,
                        }),
                    );
                },
            },
            credits: {
                enabled: false,
            },
        });
    },

    onAttach: function (itemView) {
        this.ui.chartContainer.highcharts().reflow();
    },

    transformPoint: function (point) {
        var impactScore = new ImpactScore({ percentile: point.severityPercentile });
        return _.extend({
            x: point.timestamp,
            y: point.iops,
            color: impactScore.color,
            impactScore: impactScore,
        }, point);
    },

});
