const Backbone = require('backbone');
const TopVMModel = require('../models/TopVMModel');
const Request = require('../../../../../progeny/services/Request').default;

module.exports = Backbone.Collection.extend({

    model: TopVMModel,

    url: function () {
        return new Request().pod().genericQuery('vmware', ['topVms']).uriBuilder().valueOf();
    },

    parse: function (response) {
        return response.data;
    },

});
