import { actionsFor } from '@infosight/elmer/dist/utils/redux';
import {
    FETCH,
    CHANGE_ROLE,
    DELETE,
    CREATE,
    RESET,
} from './constants';
import Request from '../../../../services/Request';

export const fetchUsers = () => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(FETCH, dispatch);
    dispatchStart();

    new Request()
        .urlSegment('auth').tenant().urlSegment('users')
        .get()
        .then(response => response.data)
        .then(dispatchSuccess)
        .catch(dispatchError);
};

export const changeRole = ({ user, role }) => (dispatch) => {
    new Request()
        .urlSegment('auth').tenant().urlSegments('users', user.login)
        .patch({
            role,
        })
        .then(response => dispatch({
            type: CHANGE_ROLE,
            success: true,
            user: response.data,
        }))
        .catch(error => dispatch({
            type: CHANGE_ROLE,
            success: false,
            user,
            error: error.response.data.message,
        }));
};

export const deleteUser = ({ user }) => (dispatch) => {
    new Request()
        .urlSegment('auth').tenant().urlSegments('users', user.login)
        .delete()
        .then(() => dispatch({
            type: DELETE,
            success: true,
            user,
        }))
        .catch(error => dispatch({
            type: DELETE,
            success: false,
            user,
            error: error.response.data.message,
        }));
};

export const createUser = ({ login, role }) => (dispatch) => {
    new Request()
        .urlSegment('auth').tenant().urlSegment('users')
        .post({
            login: login.toLowerCase(),
            role,
        })
        .then(response => dispatch({
            type: CREATE,
            success: true,
            login,
            user: response.data,
        }))
        .catch(error => dispatch({
            type: CREATE,
            success: false,
            login,
            error: error.response.data.message,
        }));
};

export const reset = () => ({ type: RESET });
