const _ = require('underscore');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');
var $ = require('jquery'),
    jQueryUI = require('jqueryui'),
    Backbone = require('backbone'),
    Marionette = require('backbone.marionette'),
    moment = require('moment'),
    GridView = require('app/views/components/grid/GridView'),
    GridFilterModel = require('app/views/components/grid/GridFilterModel'),
    LocalGridModel = require('app/models/grid/LocalGridModel'),
    RemoteGridModel = require('app/models/grid/RemoteGridModel'),
    BulkLoadGridModel = require('app/models/grid/BulkLoadGridModel'),
    WellnessIssueModel = require('./WellnessIssueModel'),
    WellnessIssueCollection = require('./WellnessIssueCollection'),
    ConfirmationRequestView = require('app/views/components/confirmation/ConfirmationRequestView'),
    childTpl = require('raw-loader!./WellnessChildRowTemplate.html'),
    Spinner = require('app/views/components/Spinner'),
    tpl = require('raw-loader!./WellnessIssuesView.html'),
    toolbar = require('raw-loader!./WellnessIssuesGridToolbar.html'),
    wellnessIssueClearConfirmation = require('raw-loader!./WellnessIssueClearConfirmation.html'),
    errorTpl = require('raw-loader!app/templates/components/error.html');
const App = require('app/Application');
const { buildUrlFromBase } = require('progeny/util/url');

const rulesUrl = buildUrlFromBase('/dashboards/nimble/wellness/rules');

var childTemplateRaw = _.template(childTpl);
var childTemplate = function (model) {
    _.extend(model, {
        moment: moment,
    });
    return childTemplateRaw(model);
};

module.exports = Marionette.ItemView.extend(
    /** @lends views/components/wellness/WellnessIssuesView.prototype */ {

        /**
         * @classdesc View for the Event Details on the Wellness tab
         * @name views/components/wellness/WellnessIssuesView
         * @constructor
         * @augments Backbone.View
         */

        template: _.template(tpl),

        className: 'wellness-issues-grid-view',

        mode: 'local',

        issues: {},

        events: {
            'click .toolbar button': 'onButtonClick',
        },

        ui: {
            gridContent: '.grid-content',
        },

        columns: function () {
            var accountSelection = App.request('global', 'get:account:selection');
            var configDisabled = accountSelection.isAll();

            return [
                {
                    data: 'issueDate', visible: true,
                    render: function (data, type, row) {
                        return moment(row.issueDate).format('L LT');
                    },
                },
                {
                    data: function (row, type) {
                        if (type == 'sort') {
                            return 11 + row.severityIndex;  // HACK to deal with "-1" severityIndex and DataTables string sorting
                        } else {
                            return row.severityName;
                        }
                    },
                    render: function (sev, type, row) {
                        if (type == 'display') {
                            return '<div style="height:30px;min-width:70px;line-height:30px;border-radius:2px;color:white" class="severity ' +
                                sev.replace(' ', '').toLowerCase() + '">' + sev + '</div>';
                        } else {
                            return sev;
                        }
                    },
                    visible: true,
                    filter: true,
                },
                {
                    data: 'conditionName', visible: true,
                    render: function (data, type, row) {
                        return configDisabled ? row.conditionName :
                            `<a class="history-aware"
                                title="Click to configure case creation for this condition"
                                href="${rulesUrl}?condition=${row.conditionName}">
                                ${row.conditionName}
                            </a>`
                            ;
                    },
                },
                { data: 'issueLevel', visible: false },
                {
                    data: 'arrayName', visible: true,
                    render: function (data, type, row) {
                        var rowClass = '';
                        if (row.groupName) {
                            rowClass = ' class="multi-row"';
                        }

                        var hostname;
                        var arrayName;

                        if (row.arrayHostname) {
                            arrayName = hostname = row.arrayHostname;
                        }
                        if (row.groupName) {
                            arrayName += '<br />(Group: ' + row.groupName + ')';
                            // hostname +=  row.groupName;
                        }
                        if (row.poolName) {
                            arrayName += '<br />(Pool: ' + row.poolName + ')';
                            // hostname =  row.poolName;
                        }

                        return (configDisabled ? arrayName :
                            `<a class="history-aware"
                                title="Click to configure case creation for this array"
                                href="${rulesUrl}?condition=${row.conditionName}&array_sn=${row.arraySN}&array_hostname=${row.arrayHostname}">
                                ${arrayName}
                            </a>`) +
                            '<a class="array-management-ui link" target="nimble-' + row.arraySN + '" ' +
                            'href="/InfoSight/static/nimosLauncher.html?arraySn=' + row.arraySN + '&hostname=' + hostname + '">' +
                            '<i class="fa fa-external-link"></i>' +
                            '<span>Click to launch the management interface for this array.</span>' +
                            '</a>';
                    },
                },
                { data: 'targetType', visible: true, filter: true },
                {
                    data: 'target', visible: true,
                    render: function (data, type, row) {
                        if (!configDisabled && row.caseLevel != 'Array') {
                            let targetName = row.caseLevel.toLowerCase();
                            if (row.target) {
                                let href = `${rulesUrl}?condition=${row.conditionName}&array_sn=${row.arraySN}&array_hostname=${row.arrayHostname}`;
                                if (row.volumeCollectionName) {
                                    href += `&volcol=${row.volumeCollectionName}`;
                                }
                                if (row.volumeName) {
                                    href += `&volume=${row.volumeName}`;
                                }
                                return `<a class="history-aware"
                                    title="Click to configure case creation for this target"
                                    href="${href}">
                                    ${row.target}
                                </a>`;
                            } else {
                                return row.target;
                            }
                        } else {
                            return row.target;
                        }
                    },
                },
                { data: 'caseNumber', visible: true },
                { data: 'caseStatus', visible: true },
                { data: 'severityIndex', visible: false },
                { data: 'arraySN', visible: false, filter: true },
                { data: 'arrayHostname', visible: false, filter: true },
                { data: 'groupName', visible: false },
                { data: 'poolName', visible: false },
                { data: 'volumeName', visible: false },
                { data: 'volumeCollectionName', visible: false },
                { data: 'snapshotName', visible: false },
                { data: 'caseCategory', visible: false },
                { data: 'caseCreatedDate', visible: false },
                { data: 'caseClosedDate', visible: false },
                {
                    visible: false,
                    data: function (row, type, val) {
                        if (row) {
                            if (type === 'display') {
                                row.caseDescription = row.caseDescription ?
                                    row.caseDescription.replace(/\n/g, '<br />').replace(/\t/g, '&nbsp;&nbsp;&nbsp;&nbsp;') :
                                    '(No description entered)';
                            }

                            return row.caseDescription;
                        }
                    },
                },
                { data: 'caseCreated', visible: false, filter: true },
                { data: 'caseStatusType', visible: false, filter: true },
                { data: 'lastObservedAt', visible: false },
                {
                    data: function (row, type) {
                        if (type === 'filter') {
                            return row.ackedAt ? 'Yes' : 'No';
                        } else {
                            return row.ackedAt;
                        }
                    },
                    render: {
                        display: function (data, type, row) {
                            if (!row.ackedAt) {
                                return null;
                            }
                            return `${row.ackedBy.split('@', 1)[0]}<span class="ack-timestamp">(${moment(row.ackedAt).fromNow()})</span>`;
                        },
                    },
                    filter: true,
                },
            ];
        },

        buttons: ['#ack', '#unack', '#clear'],

        actions: {

            ack: {
                title: 'Acknowledge Issue(s)',
                action: 'Acknowledge',
                content: '<p>Acknowledged issues will be <b>hidden from the view</b> (by default).</p>' +
                    '<p>Are you sure you want to acknowledge the selected wellness issues?</p>',
                submit: function () {
                    var issues = this.getSelectedIssues();
                    issues.acknowledge()
                        .done(function () {
                            App.command('notification', 'show:success', {
                                title: issues.length + ' issue(s) acknowledged',
                            });
                        })
                        .fail(function (jqxhr) {
                            App.command('notification', 'show:error', {
                                title: 'Failed to acknowledge issues:',
                                message: jqxhr.responseJSON.message,
                            });
                        })
                        .always(_.partial(this.afterAction, 'ack'));
                },
            },

            unack: {
                title: 'Unacknowledge Issue(s)',
                action: 'Unacknowledge',
                content: '<p>Are you sure you want to unacknowledge the selected wellness issues?</p>',
                submit: function () {
                    var issues = this.getSelectedIssues();
                    issues.unacknowledge()
                        .done(function () {
                            App.command('notification', 'show:success', {
                                title: issues.length + ' issue(s) unacknowledged',
                            });
                        })
                        .fail(function (jqxhr) {
                            App.command('notification', 'show:error', {
                                title: 'Failed to unacknowledge issues:',
                                message: jqxhr.responseJSON.message,
                            });
                        })
                        .always(_.partial(this.afterAction, 'unack'));
                },
            },

            clear: {
                title: 'Clear Issue(s)',
                action: 'Clear',
                content: wellnessIssueClearConfirmation,
                submit: function () {
                    var issues = this.getSelectedIssues();
                    issues.clear()
                        .done(function () {
                            App.command('notification', 'show:success', {
                                title: issues.length + ' issue(s) cleared',
                            });
                        })
                        .fail(function (jqxhr) {
                            App.command('notification', 'show:error', {
                                title: 'Failed to clear issues:',
                                message: jqxhr.responseJSON.message,
                            });
                        })
                        .always(_.partial(this.afterAction, 'clear'));
                },
            },

        },

        filterModel: null,

        /**
         * Creates the appropriate model.
         * @return {GridModel} Returns the appropriate grid model
         *                             for the current local/remote configuration.
         * @private
         */
        createModel: function () {
            if (this.mode == 'local') {
                return this.createBulkLoadModel();
            } else {
                return this.createRemoteModel();
            }
        },

        /**
         * Creates a BulkLoadGridModel
         * @return {BulkLoadGridModel} Returns a BulkLoadGridModel
         */
        createBulkLoadModel: function () {
            var bulkLoadGridModel = new BulkLoadGridModel({
                url: this.api().central().account().segment('/wellness/issues'),
                columns: this.columns(),
                emptyLabel: 'No issues found',
                showFilters: true,
                pageLength: 100,
            });
            return bulkLoadGridModel;
        },

        initialize: function () {
            _.bindAll(this, 'onRowSelectionChange', 'onCreateRow', 'afterAction');
            this.filterModel = new GridFilterModel({
                'Acknowledged': 'No',
            });
        },

        onRender: function () {
            var table = this.$el.find('TABLE'),
                $table = $(table),
                gridModel = this.createModel(),
                self = this;

            this.spinner = new Spinner();
            this.$el.find('.spinner').append(this.spinner.render().el);

            this.grid = new GridView({
                model: gridModel,
                id: 'wellness-issues-grid',
                tableTemplate: this.ui.gridContent.html(),
                childTemplate: childTemplate,
                rowSelectionMode: GridView.RowSelectionMode.MULTI,
                toolbar: toolbar,
                dataTableOptions: {
                    createdRow: this.onCreateRow,
                },
                filterModel: this.filterModel,
                sort: [
                    {
                        column: 'Creation Time',
                        order: 'desc',
                    }, {
                        column: 'Severity',
                        order: 'asc',
                    },
                ],
            });
            this.listenTo(this.grid, 'init', this.onGridLoaded);
            this.listenTo(this.grid, 'error', this.onEventsLoadFailed);
            this.listenTo(this.grid, 'row-selection:changed', this.onRowSelectionChange);

            this.$el.find('#wellness-issues-grid-container').append(this.grid.render().el);

            return this;
        },

        onCreateRow: function (row, data) {
            var $row = $(row);
            if (data.ackedAt) {
                $row.attr('data-acked', true);
            }
        },

        /**
         * Shows the Events Details Grid on the Wellness tab and
         * attaches a click event handler to the grid rows
         * @param  {grid} table The Events Details grid
         */
        onGridLoaded: function (table) {
            this.spinner.hide();
            this.$('#wellness-issues-grid-container').show();
            this.trigger('load');
        },

        /**
         * Captures grid load failure and writes error to console.log
         * @param  xhr        The request
         * @param  textStatus The request status
         * @param  textThrown The request error
         */
        onEventsLoadFailed: function (xhr, textStatus, textThrown) {
            this.spinner.hide();
            var errorTemplate = _.template(errorTpl);

            this.$el.find('#wellness-issues-grid-container').html(errorTemplate({
                message: textThrown,
            }));
            this.$el.find('#wellness-issues-grid-container').show();
        },

        onButtonClick: function (event) {
            var $button = $(event.currentTarget);

            var action = $button.attr('id');
            var actionOpts = this.actions[action];

            var view = new ConfirmationRequestView(actionOpts);
            this.listenTo(view, 'submit', _.bind(actionOpts.submit, this));

            App.modalRegion.show(view);
        },

        afterAction: function (action) {
            this.grid.refreshData();
            this.trigger('issue-action:' + action);
        },

        getSelectedIssues: function (attribute) {
            return new WellnessIssueCollection(_.map(this.grid.getSelectedRowsData(), function (data) {
                return new WellnessIssueModel(data);
            }));
        },

        onRowSelectionChange: function ($selectedRows) {
            if ($selectedRows.length === 0) {
                this.$(this.buttons.join(',')).prop('disabled', true);
            } else {
                this.$('#ack,#clear').prop('disabled', false);

                if ($selectedRows.is(':not([data-acked])')) {
                    this.$('#unack').prop('disabled', true);
                } else {
                    this.$('#unack').prop('disabled', false);
                }
            }
        },

    });
