import React from 'react';
import PropTypes from 'prop-types';

/**
 * This components is used to define the attributes of a column.
 * It should never actually be rendered.
 */
function Column() {
    if (__DEBUG__) {
        throw new Error('Component <Column /> should never render');
    }

    return <i />;
}

/* eslint-disable react/no-unused-prop-types */
Column.propTypes = {
    visible: PropTypes.bool,

    /**
     * Defaults to props.key
     */
    header: PropTypes.node,

    /**
     * Unique Key in data prop which provides the source data of this column.
     * If you don't care about a specific value, i.e. when using a custom render function, this value must at
     * least be unique across all columns and groups.
     *
     * It is also used in attributes on all `<th>` and `<td>` elements.
     */
    dataKey: PropTypes.string.isRequired,

    /**
     * Create a column filter based on unique values
     */
    summaryFilter: PropTypes.bool,

    /**
     * Create a plain-text filtering form
     */
    textFilter: PropTypes.bool,

    /**
     * Render function with signature `render(valueAtKey: Object, rowData: Object): ReactNode`
     */
    render: PropTypes.func,
};

Column.defaultProps = {
    visible: true,
    summaryFilter: false,
    textFilter: false,
};

export default Column;
