const Backbone = require('backbone');
var Marionette = require('backbone.marionette');
require('backbone.stickit');
require('backbone-validation');

module.exports = Marionette.Behavior.extend({

    validationOptions: {

        forceUpdate: true,

        valid: function (view, attr, selector) {
            var $el = view.$('[' + selector + '~="' + attr + '"]');
            var $formGroup = $el.closest('.form-group');
            $formGroup
            //.addClass('has-success')  // This is a bit too heavy of a visual effect
                .removeClass('has-error');
            $formGroup.find('.form-control-feedback')
            //.addClass('glyphicon-ok') // This is a bit too heavy of a visual effect
                .removeClass('glyphicon-remove');
            $formGroup.find('.validation-error').remove();
        },

        invalid: function (view, attr, error, selector) {
            var $el = view.$('[' + selector + '~="' + attr + '"]');
            var $formGroup = $el.closest('.form-group');
            $formGroup
            //.removeClass('has-success')
                .addClass('has-error');
            $formGroup.find('.form-control-feedback')
            //.removeClass('glyphicon-ok')
                .addClass('glyphicon-remove');
            $formGroup.find('.validation-error').remove();

            // This requires that all form inputs be nested within a .row
            // This allows the error message help-block to have a different column width than the input element
            // TODO enhance this such that it falls-back gracefully if the <input> itself is not nested in a row
            $el.closest('.row')
                .after('<div class="row validation-error"><p class="column large-24 help-block">' + error + '</p></div>');
        },
    },

    onBeforeRender: function () {
        // Ideally we would do this in initialize... see:
        // https://github.com/marionettejs/backbone.marionette/issues/1579
        // https://github.com/marionettejs/backbone.marionette/pull/1875

        Backbone.Validation.bind(this.view, this.validationOptions);
    },

    onRender: function () {
        this.view.stickit();
    },

    onDestroy: function () {
        Backbone.Validation.unbind(this);
    },

});
