const $ = require('jquery');
const App = require('app/Application');
const _ = require('underscore');
const FlashNotification = require('@infosight/elmer/dist/components/FlashNotification').default;
var Marionette = require('backbone.marionette');
var tpl = require('raw-loader!./LabsFeedBackView.html');
const { buildUrl } = require('@infosight/elmer/dist/utils/url');
const { API_BASE_URL } = require('../../../progeny/config');

module.exports = Marionette.ItemView.extend({

    className: 'lab-app-feedback',

    template: _.template(tpl),

    templateHelpers: function () {
        return {
            userLogin: App.request('global', 'get:current:session').get('userLogin'),
        };
    },

    ui: {
        uploadButton: '[data-action="uploadDocument"]',
        starduo: '.starduo',
        feedbackText: 'form.upload-labs-feedback textarea',
    },

    events: {
        'click @ui.uploadButton': 'onClickUpload',
        'mousedown @ui.starduo': 'updateRatings',
        'input @ui.feedbackText': 'adjustUploadButton',
        'change #contact-form input[type=radio]': 'updateChoice',
    },

    initialize: function () {
        this.labAppModel = this.model;
    },

    onClickUpload: function (event) {

        var appId = this.model.get('id');

        var self = this;
        var uploadForm = this.$('.upload-labs-feedback')[0];
        var formData = new FormData(uploadForm);

        this.$el.find('form .rating').each(function (idx, item) {
            var $item = $(item);
            var fieldName = $item.attr('data-score-rel'),
                fieldScore = $item.attr('data-score-current') || '0';
            formData.append(fieldName, fieldScore);
        });

        $.ajax({
            url: buildUrl(API_BASE_URL, 'labs/apps', appId, 'feedback'),
            type: 'POST',
            success() {
                FlashNotification.success('Successfully submitted the feedback.');
            },
            error() {
                FlashNotification.error('Error submitting the feedback.');
            },
            data: formData,
            contentType: false,
            processData: false,
        });
    },

    updateRatings: function (event) {
        var starSrc = $(event.target).closest('.starduo');
        var scoreKeeper = starSrc.closest('.rating');
        var score = starSrc.attr('data-score');
        scoreKeeper.attr('data-score-current', score);
        this.adjustUploadButton(event);
    },

    updateChoice: function (event) {
        var optSrc = $(event.target);
        var scoreKeeper = optSrc.closest('.rating');
        var score = optSrc.val() >>> 0;
        scoreKeeper.attr('data-score-current', score);
        this.adjustUploadButton(event);
    },

    adjustUploadButton: function (event) {
        // the form will be enabled as long as one question gets answered
        this.ui.uploadButton.prop('disabled', false);
    },

});
