const App = require('app/Application');
const _ = require('underscore');
var Marionette = require('backbone.marionette');
var TopVmsDashboardView = require('./topVms/TopVmsDashboardView');
var PeerPerformanceDashboardView = require('./performance/PeerPerformanceDashboardView');
var CapacityDashboardView = require('./capacity/CapacityDashboardView');
var ProtectionDashboardView = require('./protection/ProtectionDashboardView');
var SystemDashboardView = require('./system/SystemDashboardView');
var performanceTooltip = require('raw-loader!./PerformanceTooltip.html');
var protectionTooltip = require('raw-loader!./ProtectionTooltip.html');
var tpl = require('raw-loader!./OperationalDashboardView.html');
const { AuthorizationEvaluator, PRIVILEGES, AUTHORITIES, PERMISSION_TARGET } = require('../../../progeny/modules/user');

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    className: 'ops-dashboard-view',

    regions: {
        performance: '[data-region="performance"]',
        topVms: '[data-region="top-vms"]',
        capacity: '[data-region="capacity"]',
        protection: '[data-region="protection"]',
        system: '[data-region="system"]',
    },

    ui: {
        perfPopover: '.performance-info',
        protectionPopover: '.protection-info',
        capacityPopover: '.capacity-info',
    },

    onRender: function () {

        this.performance.show(new PeerPerformanceDashboardView());
        this.system.show(new SystemDashboardView());
        this.capacity.show(new CapacityDashboardView());
        this.protection.show(new ProtectionDashboardView());

        this.ui.perfPopover.popover({
            html: true,
            trigger: 'hover',
            placement: 'right',
            content: performanceTooltip,
            container: '.ops-dashboard-view',
            viewport: { selector: '.ops-dashboard-view' },
        }).data('bs.popover').tip().addClass('ops-dashboard-popover');

        this.ui.protectionPopover.popover({
            html: true,
            trigger: 'hover',
            placement: 'right',
            content: protectionTooltip,
            container: '.ops-dashboard-view',
            viewport: { selector: '.ops-dashboard-view' },
        }).data('bs.popover').tip().addClass('ops-dashboard-popover');

        this.ui.capacityPopover.popover({
            trigger: 'hover',
            placement: 'right',
            content: 'Arrays and Volumes grouped by the number of days until projected full.',
        });

        var account = App.request('global', 'get:account:selection').getAccount();
        const featureAccessible = AuthorizationEvaluator.evaluate({
            access: {
                $or: [
                    {
                        authorities: [AUTHORITIES.REALM_INTERNAL, AUTHORITIES.ATTRIBUTE_NIMBLE_SUPPORT_PROVIDER],
                    },
                    {
                        permissions: [
                            {
                                privilege: PRIVILEGES.PRIMARY,
                                targetType: PERMISSION_TARGET.NIMBLE_TENANT,
                                targetId: PERMISSION_TARGET.ACTIVE_TARGET_ID,
                            }, {
                                privilege: PRIVILEGES.FEATURE_VMVISION_ACCESS,
                                targetType: PERMISSION_TARGET.NIMBLE_TENANT,
                                targetId: PERMISSION_TARGET.ACTIVE_TARGET_ID,
                            },
                        ],
                    },
                ],
            },
        });

        if (featureAccessible) {
            if (account.get('vmVisionEnabled')) {
                this.topVms.show(new TopVmsDashboardView());
            } else {
                this.$el.find('.topvm-panel').hide();
            }
        } else {
            this.topVms.show(new Marionette.ItemView({
                template: `<span class="text-primary">You do not have permissions to access this customer's VM information. Please obtain authorization from your customers to access their VMs.</span>`,
            }));
        }
    },
});
