import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import classnames from 'classnames/bind';
import { Field, reduxForm, formPropTypes } from 'redux-form';

import { Modal } from '@infosight/elmer/dist/components/Modal';

import { InputWithFeedback } from './forms';

import { createUser } from './actionCreators';

import RoleField from './RoleField';

import style from './style.scss';

const cx = classnames.bind(style);

const validateEmail = value => (
    value && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)
        ? 'Invalid email address'
        : undefined
);

/* This eslint error should really be fixed at some point */
/* eslint-disable jsx-a11y/label-has-for */
const CreateUserForm = ({ handleSubmit, pristine, invalid, closeModal }) => (
    <Modal
        title="Invite User"
        primaryButtonContent="Submit"
        showCloseIcon={false}
        disablePrimaryButton={pristine || invalid}
        onClickPrimaryButton={handleSubmit}
        isStoreDriven={false}
        isOpen
        hide={closeModal}
    >
        <form className={cx('settings-popup')}>
            <div className="row">
                <div className="column small-4">
                    <label htmlFor="_">Email</label>
                </div>
                <div className="column small-10">
                    <Field name="login" component={InputWithFeedback} type="email" validate={validateEmail} />
                </div>
            </div>
            <div className="row">
                <div className="column small-4">
                    <label htmlFor="role">Role</label>
                </div>
                <div className="column small-10">
                    <RoleField />
                </div>
            </div>
        </form>
    </Modal>
);
/* eslint-enable jsx-a11y/label-has-for */

CreateUserForm.propTypes = {
    /* eslint-disable react/no-typos */
    handleSubmit: formPropTypes.handleSubmit,
    pristine: formPropTypes.pristine,
    invalid: formPropTypes.invalid,
    /* eslint-enable react/no-typos */
    closeModal: PropTypes.func.isRequired,
};

export default connect(null, { onSubmit: createUser })(reduxForm({
    form: 'createUser',
    initialValues: {
        role: 'NoSFDC',
    },
})(CreateUserForm));
