import _ from 'underscore';
import React, { Component } from 'react';
import autobind from 'react-autobind';
import { withFormik } from 'formik';
import classnames from 'classnames/bind';

// import Yup from 'yup';
import Select from 'react-select';
import 'react-select/dist/react-select.css';

import deliveryPreferences from './deliveryPreferences';

import style from './style.scss';

const cx = classnames.bind(style);

/* eslint-disable react/no-multi-comp */
/* eslint-disable jsx-a11y/label-has-for */
/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */

const formikEnhancer = withFormik({
    validate: (values, props) => {
        const errors = {};

        if (!values.assetSameDayPartDeliveryPreference__c) {
            errors.addressId = 'is required';
        }

        if (props.onValidationResult && !props.hasDoneInitialValidation) {
            props.onValidationResult(errors);
        }

        return errors;
    },
});

class DeliveryPreferenceSelect extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    handleChange(e) {
        // this is going to call setFieldValue and manually update values.topcis
        this.props.onChange('assetSameDayPartDeliveryPreference__c', e.value);
    }

    handleBlur() {
        // this is going to call setFieldTouched and manually update touched.topcis
        this.props.onBlur('assetSameDayPartDeliveryPreference__c', true);
    }

    render() {
        return (
            <Select
                id={this.props.id}
                placeholder="Select a delivery preference..."
                options={this.props.options}
                multi={false}
                clearable={false}
                onChange={this.handleChange}
                onBlur={this.handleBlur}
                value={this.props.value}
            />
        );
    }
}

class DeliveryPreferenceForm extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    componentDidMount() {
        this.props.validateForm();
    }

    render() {
        const {
            id,
            title,
            assetData,
            values,
            touched,
            dirty,
            errors,
            handleChange,
            handleBlur,
            handleSubmit,
            handleReset,
            setFieldValue,
            setFieldTouched,
            isSubmitting,
            onFieldChanged,
        } = this.props;

        const currentDeliveryPreference = values.assetSameDayPartDeliveryPreference__c;
        const is4HourSla = _.contains(['4HR', '4ON'], assetData.Support_Level__c);

        const deliveryOptions = [];
        if (!currentDeliveryPreference) {
            deliveryOptions.push({
                label: 'Select a delivery preference...',
                value: '',
            });
        }

        if (is4HourSla) {
            _.each(_.keys(deliveryPreferences.displayName), (deliveryPreference) => {
                deliveryOptions.push({
                    label: deliveryPreferences.displayName[deliveryPreference],
                    value: deliveryPreference,
                });
            });
        } else {
            _.each(_.keys(deliveryPreferences.premiumDeliveryOptions), (deliveryPreference) => {
                deliveryOptions.push({
                    label: deliveryPreferences.premiumDeliveryOptions[deliveryPreference],
                    value: deliveryPreference,
                });
            });
        }

        return (
            <div className={errors.addressId ? cx('error') : ''}>
                <div className="row">
                    <div className="column medium-14">
                        <form onSubmit={handleSubmit}>
                            <div className={cx('sla')}>
                                <i className="fa fa-info-circle" /> SLA: {assetData.SLA__c}
                            </div>
                            <label htmlFor={id}>Delivery Preference { errors.addressId }</label>
                            <DeliveryPreferenceSelect
                                title={title}
                                id={id}
                                value={values.assetSameDayPartDeliveryPreference__c}
                                options={deliveryOptions}
                                onChange={(name, value) => {
                                    setFieldValue(name, value);
                                    onFieldChanged(name, value, id);
                                }}
                                onBlur={setFieldTouched}
                                error={errors.addressId}
                                touched={touched.addressId}
                            />
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}

const FormikDeliveryPreferenceForm = formikEnhancer(DeliveryPreferenceForm);

export default FormikDeliveryPreferenceForm;
