import $ from 'jquery';
import React from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import moment from 'moment-timezone';
import classnames from 'classnames/bind';
import { Spinner } from '@infosight/elmer/dist/components/Spinner';

import SFCaseModel from '../../../library/SFCaseModel';

import style from './CaseResolver.scss';

const cx = classnames.bind(style);


class CaseResolver extends React.Component {
    constructor(props) {
        super(props);
        autobind(this);
        this.state = {
            formBusy: false,
        };
        this.textarea = null;
    }

    resultResolutionPostOk(caseData) {
        caseData.bucket = 'Resolved'; // eslint-disable-line
        caseData.assetName = this.props.theCase.assetName;
        caseData.createdDateOnly = this.props.theCase.createdDateOnly;
        caseData.ownerName = this.props.theCase.ownerName;
        caseData.comments = this.props.theCase.comments;
        caseData.attachments = this.props.theCase.attachments;
        caseData.rmaLabels = this.props.theCase.rmaLabels;

        caseData.caseLastComment__c = caseData.createdDate;
        caseData.lastUpdatedDate = moment(caseData.createdDate).format('YYYYMMDD');
        caseData.lastUpdatedTS = caseData.createdDate;

        this.textarea.value = '';
        this.setState({ formBusy: false });
        if (typeof this.props.onSave === 'function') this.props.onSave.call(null, caseData);
    }

    resultResolutionPostFail() {
        this.setState({ formBusy: false });
        console.warn('resolution failed to post.');
        // TODO: red border?
    }

    eventSubmitResolution() {
        const resolveText = this.textarea.value;

        const caseModel = new SFCaseModel();
        caseModel.set('id', this.props.theCase.id);

        const casePath = caseModel.url();

        $.ajax({
            type: 'POST',
            url: `${casePath}/resolve`,
            contentType: 'application/json',
            data: resolveText,
        })
            .then(this.resultResolutionPostOk, this.resultResolutionPostFail);

        this.setState({ formBusy: true });
    }

    unboundClear() {
        this.textarea.value = '';
    }

    render() {
        const { formBusy } = this.state;
        return (
            <div className={cx('case-resolver', { 'busy-sending': formBusy })}>
                <p>If you wish to close this case, please provide any resolution notes below and submit.</p>
                <div className={cx('textbox-wrapper')}>
                    <textarea className={cx('resolve-box')} ref={(elem) => { this.textarea = elem; }} rows="4" />
                    {formBusy && <Spinner className={cx('sending')} />}
                </div>
                <br />
                <button
                    className="button"
                    disabled={formBusy}
                    onClick={this.eventSubmitResolution}
                >Submit Resolution
                </button>

                <button
                    className="button hollow"
                    disabled={formBusy}
                    onClick={this.unboundClear}
                >Clear
                </button>
            </div>
        );
    }
}

CaseResolver.propTypes = {
    theCase: PropTypes.object.isRequired,
    onSave: PropTypes.func,
};

export default CaseResolver;
