var Backbone = require('backbone');

module.exports = Backbone.Model.extend(
    /** @lends models/toolsCentral/banners/BannerModel.prototype */ {

        /**
         * @classdesc       Backbone Model representation of an InfoSight banner.
         * @name            models/toolsCentral/banners/BannerModel
         * @constructor
         * @augments        Backbone.Model
         */

        defaults: {
            id: null,
            type: 'ReleaseNotes',
            status: 'Inactive',
            visibility: 'Internal',
            accountId: null,
            feature: null,
            expirationDate: null,
            creationTimestamp: null,
            notificationTimestamp: null,
        },

        validation: {
            title: {
                required: true,
            },
            contents: {
                required: true,
            },
            feature: function (value, attr, computedState) {
                if (computedState.visibility === 'Feature' && !value) {
                    return 'Feature is required when visibility is "Accounts with Feature"';
                }
            },
        },

    });
