const _ = require('underscore');
var Backbone = require('backbone');
var FacetCollection = require('./FacetCollection');
var FacetBucketCollection = require('./FacetBucketCollection');

var SORTBY_FACETS = ['Document Type'];

function toSortableVersion(version) {
    if (!version) {
        return '';
    }
    var pad = '000000';
    var parts = version.split(/\.|\-/);
    return parts.map(function (part) {
        return part.length >= pad.length ? part : (pad + part).slice(-pad.length);
    }).join('.');
}

module.exports = Backbone.Model.extend({

    /**
     * Build a sort key (an array of {value, order} tuples) for this hit.
     * We sort by:
     *     Score (will be 1 for all docs if no search term was entered)
     *     The Document Type field in the same order in which the facet buckets are ordered in the left panel
     *     Title
     *     Version (descending)
     */
    sortKey: function () {
        var source = this.get('_source');
        var score = this.get('_score');
        var sortKey = [];
        sortKey.push({
            value: score,
            order: 'desc',
        }, {
            value: 0,
            order: 'asc',
        }, {
            value: source.title,
            order: 'asc',
        }, {
            value: toSortableVersion(_.first(source.Version)),
            order: 'desc',
        });
        var versionFamilies = source['Version Family'];
        if (versionFamilies) {
            if (!Array.isArray(versionFamilies)) {
                versionFamilies = [versionFamilies];
            }
            versionFamilies.sort(function (a, b) {
                return -a.localeCompare(b); // Descending
            });
            sortKey.push({
                value: _.first(versionFamilies),
                order: 'desc',
            });
        }
        return sortKey;
    },

    parse: function (response) {
        if (Array.isArray(response._source['Version Family'])) {
            response._source['Version Family'] = _.without(response._source['Version Family'], null);
        }
        return response;
    },

});
