import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import classnames from 'classnames/bind';

import { Modal } from '@infosight/elmer/dist/components/Modal';

import { deleteUser } from './actionCreators';

import style from './style.scss';

const cx = classnames.bind(style);

const DeleteUserConfirmation = ({ user, deleteUser, closeModal }) => ( // eslint-disable-line no-shadow
    <Modal
        title="Confirm User Deletion"
        type="warning"
        showCloseIcon={false}
        onClickPrimaryButton={() => deleteUser({ user })}
        hide={closeModal}
        isStoreDriven={false}
        isOpen
    >
        <div className={cx('settings-popup')}>
            <p>Confirm deletion of {user.login}</p>
        </div>
    </Modal>
);

DeleteUserConfirmation.propTypes = {
    user: PropTypes.object.isRequired,
    deleteUser: PropTypes.func.isRequired,
    closeModal: PropTypes.func.isRequired,
};

export default connect(
    null, {
        deleteUser,
    }
)(DeleteUserConfirmation);
