var Backbone = require('backbone'),
    CapacityForecastModel = require('app/models/capacity/CapacityForecastModel');

module.exports = Backbone.Collection.extend(
    /** @lends collections/capacity/CapacityForecastCollection.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name collections/capacity/CapacityForecastCollection
         * @constructor
         * @augments Backbone.Collection
         */

        model: CapacityForecastModel,

        monthRange: 3,

        url: function () {
            return this.api().pod().account()
                .segment('capacity')
                .addQuery('monthRange', this.monthRange);
        },

    });
