const _ = require('underscore');
const { Highstock } = require('progeny/modules/nimble/highchartsProxy');

var BaseArrayCard = require('app/views/cards/array/BaseArrayCard'),
    Spinner = require('app/views/components/Spinner'),
    UpgradeCapacityDetailsView = require('./UpgradeCapacityDetailsView'),
    CapacityForecastChart = require('app/views/components/capacity/CapacityForecastChart'),
    CpuAreaChart = require('./CpuAreaChart'),
    CacheAreaChart = require('./CacheAreaChart'),
    tpl = require('raw-loader!./ArrayUpgradeCard.html');

var template = _.template(tpl);

module.exports = BaseArrayCard.extend({

    showDetailsLink: false,

    modelEvents: {
        'change:capacity': 'onChangeCapacity',
    },

    attributes: function () {
        return { 'data-array-type': this.model.get('arrayType') };
    },
    /**
     * Overridden onClose method to ensure we cleanup.
     * @override
     */
    onClose: function () {

    },

    onChangeCapacity: function () {
        var capacityForecastChart = new CapacityForecastChart({
            model: this.model.get('capacity'),
            chartOptions: {
                height: 150,
                tooltipsEnabled: false,
                enableMouseTracking: false,
                showYAxisLabel: false,
                showBoundsSeries: false,
                tickInterval: 24 * 3600 * 1000 * 90,
                dateTimeLabelFormats: {
                    month: '%b',
                },
            },
        });
        this.$el.find('.dashboard-capacity-chart').html(capacityForecastChart.render().el);
    },

    /**
     * Overrideen renderMainContent method to render the fluid main content region.
     * @param  {jQueryEl} $containerEl The container element to append to.
     * @override
     */
    renderContents: function ($containerEl) {
        this.spinner = new Spinner();

        $containerEl.append(template({ ...this.model.toJSON(), Highcharts: Highstock }));

        var upgradeSummaryModel = this.model.get('upgradeSummary'),
            recommendScaleOut = upgradeSummaryModel.get('recommendScaleOut'),
            recommendCache = upgradeSummaryModel.get('recommendCacheUpgrade'),
            recommendDisk = upgradeSummaryModel.get('recommendDiskUpgrade'),
            recommendController = upgradeSummaryModel.get('recommendControllerUpgrade'),
            capacityView = new UpgradeCapacityDetailsView({ model: this.model }),
            cpuView = new CpuAreaChart({ model: this.model }),
            cacheView = new CacheAreaChart({ model: this.model });

        // add the charts
        // $containerEl.find('.dashboard-capacity-chart').append( capacityView.render().el );
        $containerEl.find('.dashboard-cpu-chart').append(cpuView.render().el);
        $containerEl.find('.dashboard-cache-chart').append(cacheView.render().el);

        // this.addReadableUpgradeNote($containerEl);
    },

    addReadableUpgradeNote: function ($containerEl) {
        var upgradeSummaryModel = this.model.get('upgradeSummary'),
            currentController = this.model.get('currentController'),
            recommendedController = this.model.get('recommendedController'),
            recommendScaleOut = upgradeSummaryModel.get('recommendScaleOut'),
            recommendCache = upgradeSummaryModel.get('recommendCacheUpgrade'),
            recommendDisk = upgradeSummaryModel.get('recommendDiskUpgrade'),
            recommendController = upgradeSummaryModel.get('recommendControllerUpgrade'),
            recommendedComponents = [];

        if (recommendDisk) {
            recommendedComponents.push('capacity');
        }
        if (recommendController) {
            recommendedComponents.push('cpu');
        }
        if (recommendCache) {
            recommendedComponents.push('cache');
        }

        var s = '';
        for (var i = 0; i < recommendedComponents.length; i++) {
            s += recommendedComponents[i];

            if (i < recommendedComponents.length - 2) {
                s += ', ';
            }
            if (i < recommendedComponents.length - 1) {
                s += ' and ';
            }
        }

        var msg = null;
        if (recommendScaleOut) {
            msg = 'Note: Scale out is recommended based on your ' + s + ' needs.';
        } else if (!recommendController && currentController != recommendedController) {
            msg = 'Note: A controller upgrade is required to support the recommended ' + s + ' upgrades';
        }

        if (msg) {
            $containerEl.find('.upgrade-contents').append(
                '<div class="upgrade-note">' +
                '<span class="ui-icon ui-icon-info"></span> ' +
                '<span>' + msg + '</span>' +
                '</div>',
            );
        }
    },
});
