const _ = require('underscore');
const $ = require('jquery');
var Marionette = require('backbone.marionette');
const App = require('app/Application');
var AssetShelfCollection = require('app/collections/AssetShelfCollection');
var AssetShelfTooltip = require('./AssetShelfTooltip');
var SoftwareUpgradesView = require('./SoftwareUpgradesView');
const { buildUrlFromBase } = require('progeny/util/url');
var tpl = require('raw-loader!./AssetHeader.html');

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    templateHelpers: function () {
        return {
            hyperlinkHostname: this.options.hyperlinkHostname,
            versionSupported: _.isFunction(this.model.versionSupported) ?
                this.model.versionSupported() :
                this.model.versionSupported,
            getAssetLink: buildUrlFromBase.bind(null, '/infrastructure/storage/nimble/arrays'),
        };
    },

    className: 'array-summary-header',

    events: {
        'click .array-version a': 'onClickVersion',
    },

    onRender: function () {
        var self = this,
            expansionShelves = this.model.get('expansionShelves');
        if (expansionShelves > 0) {

            this.$el.find('.array-shelves').html(
                '(+' + expansionShelves + ' ' +
                ((expansionShelves > 1) ? 'shelves' : 'shelf') + ')');

            this.assetShelfCollection = new AssetShelfCollection([], {
                serialNumber: this.model.get('serialNumber'),
            });

            this.assetShelfTooltip = new AssetShelfTooltip({
                collection: this.assetShelfCollection,
            });

            var shelfTooltip = this.assetShelfTooltip.render().el;

            this.$el.tooltip({
                items: '.model-info-icon',
                tooltipClass: 'asset-details',
                content: function (callback) {
                    // avoid re-fetching the collection if it's already loaded
                    if (!self.assetShelfCollectionLoaded) {
                        self.assetShelfCollectionLoaded = true;
                        self.assetShelfCollection.fetch().done(function () {
                            callback(shelfTooltip.innerHTML);
                        });
                    } else {
                        callback(shelfTooltip.innerHTML);
                    }
                },
                // the below is to keep the tooltip visible while the mouse is
                // inside the tooltip (allows the user to copy and paste from the tip).
                show: null,
                close: function (event, ui) {
                    ui.tooltip.hover(
                        function () {
                            $(this).stop(true).fadeTo(400, 1);
                        },
                        function () {
                            $(this).fadeOut('400', function () {
                                $(this).remove();
                            });
                        },
                    );
                },
            });

            //Add bootstrap's popover. Used popover instead of tooltip to make it look close to other JQeuryUI tooltips used in Asset Header
            this.$('[data-toggle="popover"]').popover({
                trigger: 'hover',
                placement: 'bottom',
            });
        }
    },

    onClickVersion: function (event) {
        event.preventDefault();
        App.modalRegion.show(new SoftwareUpgradesView({ model: this.model }));
    },

});
