import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import isShallowEqual from 'is-equal-shallow';
import { withQueryParams } from '@infosight/elmer/dist/components/Router';
import DocumentationView from '../../../../app/modules/documentation/DocumentationView';
import labelsStyle from '../legacyStyles/labels.scss';
import { awaitLegacyApp } from '../../../containers/NimbleLegacyContainer';

const cx = classnames.bind(labelsStyle);

class DocumentationWrapper extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    componentDidUpdate(oldProps) {
        if (this.view && !isShallowEqual(oldProps.match.params, this.props.match.params)) {
            // Copy props because setParams mutates it input
            this.view.setParams({ ...this.props.match.params });
        }
    }

    componentWillUnmount() {
        if (this.view) {
            const { view } = this;
            // For some reason, some deeper view errored on destroy when switching to another marionette page
            // Push it onto the stack so errors don't trigger componentDidCatch
            window.setTimeout(() => view.destroy());
        }
    }

    renderView(el) {
        if (!this.view) {
            // specific parameter map for 5.x Installation / Administration Guide
            // https://nimblejira.nimblestorage.com/browse/IS-17087

            const query = this.props.match.params;

            if (query['Document Type'] === 'Installation / Administration Guide') {
                query['Document Type'] = 'Online Help';
            }

            if (query['Version Family'] === '5.0.x' ||
                query['Version Family'] === '5.x') {
                query['Version Family'] = 'NOS 5.0.x';
            }

            if (query['Version Family'] === '4.x') {
                query['Version Family'] = 'NOS 4.x';
            }

            this.view = new DocumentationView(this.props);
            this.view.setParams(query);
            this.view.setElement(el).render();
        }
    }

    render() {
        return <div className={classnames('documentation-view', cx('legacy-labels'))} ref={this.renderView} />;
    }
}

DocumentationWrapper.propTypes = {
    match: PropTypes.shape({
        params: PropTypes.object,
    }).isRequired,
};

export default awaitLegacyApp(withQueryParams(DocumentationWrapper, { includePathParams: false }));
