/*** IMPORTS FROM imports-loader ***/
var jQuery = window.nimbleShims.jQuery_1_11_1;

import { createReducersForAction, createSelector, getInitialState } from '@infosight/elmer/dist/utils/redux';
import { FETCH_BOOTSTRAP, START_LEGACY_APP } from './store/constants';

const initialState = {
    ...getInitialState('bootstrap'),
    ...getInitialState('legacy'),
};

const ACTION_HANDLERS = {
    ...createReducersForAction({ type: FETCH_BOOTSTRAP, stateKey: 'bootstrap' }),
    ...createReducersForAction({ type: START_LEGACY_APP, stateKey: 'legacy' }),
};

export function reducer(state = initialState, action) {
    const handler = ACTION_HANDLERS[action.type];
    return handler ? handler(state, action) : state;
}

// Selectors
const baseSelector = state => state.bootstrap;

export const bootstrapSelector = createSelector(baseSelector, 'bootstrap');
export const bootstrapLegacySelector = createSelector(baseSelector, 'legacy');

