const _ = require('underscore');
var Backbone = require('backbone'),
    tpl = require('raw-loader!./AssetShelfTooltipDetails.html');

var template = _.template(tpl);

module.exports = Backbone.View.extend(
    /** @lends views/components/asset/AssetShelfTooltipDetails.prototype */ {

        /**
         * @classdesc Creates the rows in the shelf table tooltip on the Asset Tab
         * @name views/components/asset/AssetShelfTooltipDetails
         * @constructor
         * @augments Backbone.View
         */

        tagName: 'tr',

        render: function () {
            var self = this,
                rawCapacity = this.model.get('rawCapacity');
            this.$el.html(template(this.model.toJSON()));
            this.$el.find('.raw-capacity-cell').html(rawCapacity ? rawCapacity + 'TB' : 'Unavailable');
            return this;
        },
    });
