const _ = require('underscore');
var Backbone = require('backbone');

function groupKey(permission) {
    var target = _(permission.target).clone();
    if (target.account) {
        // Remove opaqueName as its presence can cause permissions for the same target account to not group properly
        target.opaqueName = null;
    }
    return {
        status: permission.status,
        grantee: permission.grantee,
        target: target,
    };
}

module.exports = Backbone.Collection.extend({

    constructor: function (collection, options) {
        options = options || {};
        this.collection = collection;

        Backbone.Collection.call(this, null, options);
    },

    initialize: function () {
        this.build();

        this.listenTo(this.collection, 'add', this.onAdd);
        this.listenTo(this.collection, 'remove', this.onRemove);
        this.listenTo(this.collection, 'change', this.onChange);
        this.listenTo(this.collection, 'reset', this.build);
    },

    build: function () {
        var groups = _.chain(this.collection.toJSON())
            .groupBy(function (permission) {
                return JSON.stringify(groupKey(permission));
            })
            .pairs()
            .map(function (pair) {
                var key = JSON.parse(pair[0]);
                var permissions = pair[1];
                return _.extend(key, {
                    id: _.uniqueId('permissionGroup_'),
                    permissions: permissions,
                });
            })
            .value();

        this.reset(groups);
    },

    onRemove: function (removedPermission) {
        var matcher = _.matcher({ id: removedPermission.id });
        var permissionGroup = this.find(function (permissionGroup) {
            return _.some(permissionGroup.get('permissions'), matcher);
        });
        var newPermissions = _.reject(permissionGroup.get('permissions'), matcher);
        if (newPermissions.length) {
            permissionGroup.set('permissions', newPermissions);
        } else {
            this.remove(permissionGroup);
        }
    },

    onAdd: function (newPermissionModel) {
        var newPermission = newPermissionModel.toJSON();
        var newPermissionGroupKey = JSON.stringify(groupKey(newPermission));
        var permissionGroup = this.find(function (permissionGroup) {
            return JSON.stringify(groupKey(permissionGroup.toJSON())) === newPermissionGroupKey;
        });
        if (permissionGroup) {
            permissionGroup.set('permissions', permissionGroup.get('permissions').concat(newPermission));
        } else {
            this.add(new Backbone.Model(
                _.extend(groupKey(newPermission), {
                    id: _.uniqueId('permissionGroup_'),
                    permissions: [newPermission],
                }),
            ));
        }
    },

    onChange: function () {
        // Not needed for current implementation
        console.assert(false, 'Not implemented');
    },

});
