import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import { Route, Link } from 'react-router-dom';
import { buildUrl } from '@infosight/elmer/dist/utils/url';
import SoftwareView from '../../../../app/modules/software/SoftwareView';
import labelsStyle from '../legacyStyles/labels.scss';
import breadcrumbsStyle from '../legacyStyles/breadcrumbs.scss';
import { awaitLegacyApp } from '../../../containers/NimbleLegacyContainer';

const cx = classnames.bind({ ...labelsStyle, ...breadcrumbsStyle });

class SoftwareDownloadsMarionetteWrapper extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    componentDidMount() {
        const { params: { category, product } } = this.props.match;
        if (category && product) {
            this.view.showProduct(category, product);
        } else {
            this.view.showHome();
        }
    }

    componentDidUpdate({ match: { params: { category: oldCategory, product: oldProduct } } }) {
        const { params: { category, product } } = this.props.match;
        if (oldCategory === category && oldProduct === product) {
            return;
        }

        if (category && product) {
            this.view.showProduct(category, product);
        } else {
            this.view.showHome();
        }
    }

    componentWillUnmount() {
        if (this.view) {
            this.view.destroy();
        }
    }

    renderView(el) {
        if (!this.view) {
            this.view = new SoftwareView(this.props);
            this.view.setElement(el).render();
        }
    }

    render() {
        const { params: { category, product } } = this.props.match;
        return (
            <div className={cx('legacy-labels', 'legacy-breadcrumbs')}>
                <div className="breadcrumbs-region">
                    <ul className="breadcrumb">
                        <li><Link to="/resources/nimble/software">Software Downloads</Link></li>
                        {category && <li className="active">{category}</li>}
                        {product && <li className="active">{product}</li>}
                    </ul>
                </div>
                <div className="software-view" ref={this.renderView} />
            </div>
        );
    }
}

SoftwareDownloadsMarionetteWrapper.propTypes = {
    match: PropTypes.shape({
        url: PropTypes.string.isRequired,
        params: PropTypes.shape({
            category: PropTypes.string,
            product: PropTypes.string,
        }).isRequired,
    }).isRequired,
};

export default awaitLegacyApp(({ match }) => ( // eslint-disable-line react/prop-types
    <Route exact={false} path={buildUrl(match.url, '/:category?/:product?')} component={SoftwareDownloadsMarionetteWrapper} />
));
