import React, { Component } from 'react';
import autobind from 'react-autobind';
import { Provider } from 'react-redux';
import $ from 'jquery';
import { removeRouter, addRouter } from '@infosight/shell-api/lib/Router';
import { onUserContextChange } from '@infosight/shell-api/lib/UserProfile';
import { buildUrl } from '@infosight/elmer/dist/utils/url';
import { HighchartsProvider } from '@infosight/elmer/dist/components/ReactHighcharts';
import { ThemeProvider } from '@infosight/elmer/dist/components/ThemeProvider';
import { AuthorizationEvaluator, AUTHORITIES } from '../../user';
import NimbleInfrastructureRouter from '../NimbleInfrastructureRouter';
import NimbleDashboardsRouter from '../NimbleDashboardsRouter';
import NimbleResourcesRouter from '../NimbleResourcesRouter';
import SettingsRouter from '../settings/SettingsRouter';
import { nimbleResellerAuthorizationEvaluator } from '../tenantSelection';
import { ID } from './constants';
import { Highstock } from '../highchartsProxy';
import { getStore } from '../../../util/storeProvider';
import AdminRouter from '../AdminRouter';
import NimbleUserTenantHelpers from '../tenantSelection/NimbleUserTenantHelpers';

const checkAccess = (component) => {
    if (AuthorizationEvaluator.evaluate({
        access: {
            custom() {
                const resellerCase = nimbleResellerAuthorizationEvaluator();
                return resellerCase !== undefined ? resellerCase : NimbleUserTenantHelpers.currentTenantIsNimbleTenant() && AuthorizationEvaluator.evaluate({
                    access: {
                        $or: [
                            {
                                authorities: AUTHORITIES.PRODUCT_FAMILY_NIMBLE,
                            },
                            {
                                authorities: AUTHORITIES.SUPPORT_CASE,
                            },
                            {
                                authorities: AUTHORITIES.ADDRESS_ADMIN,
                            },
                        ],
                    },
                });
            },
        },
    })) {
        return component;
    }

    return null;
};

const provideContexts = (WrappedComponent) => {
    if (!WrappedComponent) {
        return WrappedComponent;
    }

    return class extends Component {
        componentDidMount() {
            if (document.getElementById('modal-region')) {
                return;
            }

            // Add the node needed for backbone modals.
            // Add to body because App.modalRegion is not a live binding
            $('body').append('<div id="modal-region" class="modal fade" role="dialog" data-backdrop="static" data-app-id="nimble"></div>');
        }

        componentWillUnmount() {
            const el = document.getElementById('modal-region');
            if (el) {
                el.parentNode.removeChild(el);
            }
        }

        render() {
            return (
                <Provider store={getStore()}>
                    <HighchartsProvider value={Highstock}>
                        <ThemeProvider>
                            <WrappedComponent {...this.props} />
                        </ThemeProvider>
                    </HighchartsProvider>
                </Provider>
            );
        }
    };
};

class RouterExtension extends Component { // eslint-disable-line react/no-multi-comp
    constructor(props) {
        super(props);
        autobind(this);
    }

    componentDidMount() {
        this.handleUpdate();
        this.unsubscribe = onUserContextChange(this.handleUpdate);
    }

    componentWillUnmount() {
        if (this.unsubscribe) {
            this.unsubscribe();
        }
    }

    handleUpdate() { // eslint-disable-line class-methods-use-this
        // Everything is always here so we can react to changes
        [
            { url: buildUrl('dashboards', ID), router: provideContexts(checkAccess(NimbleDashboardsRouter)), exact: false, appId: 'nimble' },
            {
                url: buildUrl('infrastructure', 'storage', ID),
                router: provideContexts(checkAccess(NimbleInfrastructureRouter)),
                exact: false,
                appId: 'nimble',
            },
            { url: buildUrl('resources', ID), router: provideContexts(NimbleResourcesRouter), exact: false, appId: 'nimble' },
            // TODO: Use the built in access control in SettingsRouter for now, but we need to make sure resellers
            // don't see irrelevant menu items
            { url: buildUrl('settings', ID), router: provideContexts(SettingsRouter), exact: false, appId: 'nimble' },
            { url: buildUrl('settings', 'admin'), router: provideContexts(AdminRouter), exact: false, appId: 'nimble' },
        ].forEach((extension) => {
            if (extension.router) {
                addRouter(extension);
            } else {
                removeRouter(extension);
            }
        });
    }

    render() {
        return null;
    }
}

export default RouterExtension;
