const $ = require('jquery');
const _ = require('underscore');
var Backbone = require('backbone');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');
const { CHART_COLORS } = require('progeny/util/chartColors');

module.exports = Backbone.View.extend({
    className: 'cache-area-chart',

    renderChart: function (container, series, maxY) {
        var self = this;
        this.chart = new Highcharts.Chart({
            chart: {
                type: 'area',
                renderTo: container,
                height: 150,
                events: {
                    redraw: function () {
                        var containerWidth = $(this.container).width();

                        // if the no data label group is present, ensure it's centered
                        if (self.svgNoDataLabelGroup) {
                            self.svgNoDataLabelGroup.translate(
                                (containerWidth / 2) - (self.svgNoDataLabelGroup.element.getBoundingClientRect().width / 2) + 20,
                                65,
                            );
                        }
                    },
                    load: function () {
                        var chart = this,
                            renderer = chart.renderer,
                            hasData = false;

                        for (var i = 0; i < chart.series.length && !hasData; i++) {
                            if (chart.series[i].data && chart.series[i].data.length > 0) {
                                hasData = true;
                            }
                        }

                        if (!hasData) {
                            self.svgNoDataLabelGroup = renderer.g().attr({ id: 'svgNoDataLabel' }).add();
                            self.svgNoDataLabelText = renderer.text('No Utilization Data Available').attr({
                                fill: '#666',
                            }).add(self.svgNoDataLabelGroup);
                        }

                        _.delay(function () {
                            chart.reflow.call(chart);
                        }, 50);
                    },
                },
            },
            credits: {
                enabled: false,
            },
            title: {
                text: null,
            },
            legend: {
                enabled: false,
            },
            xAxis: {
                type: 'datetime',
                // dateTimeLabelFormats: {
                //     day: '%e of %b'
                // },
                dateTimeLabelFormats: {
                    day: '%e. %b',
                    year: '%b',
                    month: '%b',
                },
                title: {
                    text: 'Week Ending',
                    enabled: false,
                },
                tickInterval: 24 * 3600 * 1000 * 90,
                /*,
                labels: {
                    formatter: function() {
                        console.log('formatter');
                        var tick = 0;
                        return Highcharts.dateFormat('%e. %b', this.value);
                    }
                },

                tickPositioner: function(min, max) {
                    var positions = [],
                        tick = min,
                        increment = (max - min) / 3;

                    for ( ; tick <= max; tick += increment ) {
                        positions.push( new Date(tick) );
                    }

                    positions.info = {
                        unitName: 'month',
                        higherRanks: {}
                    };

                    return positions;
                }*/
            },
            yAxis: {
                min: 0,
                max: maxY,
                title: {
                    text: null,
                },
                labels: {
                    format: '{value}%',
                },
            },
            tooltip: {
                enabled: false,
            },
            plotOptions: {
                area: {
                    animation: false,
                    enableMouseTracking: false,
                    marker: {
                        enabled: false,
                    },
                    stacking: 'normal',
                },
            },
            series: series,
        });
    },

    render: function () {
        this.$el.html(this.el);

        var data = this.model.get('utilization'),
            greenSeries = { name: 'Sufficient', id: 'cacheGreenSeries', color: CHART_COLORS.legacyGreen, data: [], pointInterval: 7 * 24 * 3600 * 1000, yAxis: 0 },
            redSeries = { name: 'Insufficient', id: 'cacheRedSeries', color: CHART_COLORS.legacyRed, data: [], pointInterval: 7 * 24 * 3600 * 1000, yAxis: 0 },
            yellowSeries = { name: 'Marginal', id: 'cacheYellowSeries', color: CHART_COLORS.legacyYellow, data: [], pointInterval: 7 * 24 * 3600 * 1000, yAxis: 0 },
            maxY = 100;

        for (var i = 0; i < data.length; i++) {
            var dataPoint = data[i];

            maxY = Math.max(maxY, dataPoint.cache);

            redSeries.data.push([dataPoint.ts, dataPoint.cacheRed]);
            yellowSeries.data.push([dataPoint.ts, dataPoint.cacheYellow]);
            greenSeries.data.push([dataPoint.ts, dataPoint.cacheGreen]);
        }

        this.renderChart(this.el, [redSeries, yellowSeries, greenSeries], maxY);

        return this;
    },

});
