import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import classnames from 'classnames/bind';

import Card from '@infosight/elmer/dist/components/Card';
import Button from '@infosight/elmer/dist/components/Button';

import UserManagementGrid from './UserManagementGrid';
import CreateUserForm from './CreateUserForm';

import { userSelector } from './reducer';
import { fetchUsers, reset } from './actionCreators';

import style from './style.scss';

const cx = classnames.bind(style);

class UserManagementContainer extends Component {
    constructor(props) {
        super(props);
        autobind(this);
        this.state = { modalVisible: false };
    }

    componentDidMount() {
        this.props.fetchUsers();
    }

    render() {
        const { loadingUsers, users } = this.props;
        return (
            <Card className={cx('user-management', 'footer-pagination')} title="User Profiles">
                <p>
                    Users within <strong>your own organization</strong> can be managed on this page.
                </p>
                <p>
                    Your organization can grant <strong>read-only</strong> access
                    to its InfoSight data using the <Link to="/settings/nimble/permissions">Third Party Permissions</Link> page.
                </p>
                <Button onClick={() => this.setState({ modalVisible: true })} style={{ marginBottom: '20px' }}>
                    <i className="fa fa-plus-circle" /> Invite User
                </Button>
                <UserManagementGrid loading={loadingUsers} users={users} />
                {this.state.modalVisible && <CreateUserForm closeModal={() => this.setState({ modalVisible: false })} />}
            </Card>
        );
    }
}

UserManagementContainer.propTypes = {
    fetchUsers: PropTypes.func.isRequired,
    loadingUsers: PropTypes.bool.isRequired,
    users: PropTypes.array,
};

const mapStateToProps = state => userSelector(state);

export default connect(mapStateToProps, { fetchUsers, reset })(UserManagementContainer);
