import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Route, withRouter } from 'react-router-dom';
import autobind from 'react-autobind';
import deparam from 'jquery-deparam';
import Card from '@infosight/elmer/dist/components/Card';
import NimbleLegacyContainer, { awaitLegacyApp } from '../../../../containers/NimbleLegacyContainer';
import WellnessView from '../../../../../app/modules/wellness/WellnessView';

/* eslint-disable react/no-multi-comp */
class DetailsRouteHandler extends Component {
    componentDidMount() {
        this.showDetails();
    }

    componentDidUpdate() {
        this.showDetails();
    }

    showDetails() {
        let filters;
        if (this.props.location.search) {
            filters = deparam(this.props.location.search.slice(1));
        }
        this.props.view.showDetails(filters);
    }

    render() {
        return null;
    }
}

DetailsRouteHandler.propTypes = {
    view: PropTypes.object.isRequired,
    location: PropTypes.shape({
        search: PropTypes.string.isRequired,
    }).isRequired,
};

const DetailsHandler = withRouter(DetailsRouteHandler);

class WellnessDashboard extends Component {
    constructor(props) {
        super(props);
        autobind(this);
        // Passing history down the component, this is not the right way, but only till better solution
        this.view = new WellnessView({ history: this.props.history });
    }

    attachView(el) {
        this.view.setElement(el);
        return this.view;
    }

    render() {
        const { match } = this.props;
        return (
            <Card>
                <NimbleLegacyContainer createLegacyView={this.attachView} />
                <Route path={`${match.path}/details`} render={() => <DetailsHandler view={this.view} />} />
            </Card>
        );
    }
}

WellnessDashboard.propTypes = {
    // Injected by withRouter
    match: PropTypes.object.isRequired,
    history: PropTypes.object.isRequired,
};

export default awaitLegacyApp(withRouter(WellnessDashboard));
