import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import classnames from 'classnames/bind';
import { Link } from 'react-router-dom';
import Card from '@infosight/elmer/dist/components/Card';
import Async from '@infosight/elmer/dist/components/Async';
import FlashNotification from '@infosight/elmer/dist/components/FlashNotification';

import { addressSelector, assetSelector } from '../reducer';
import { listSelector } from '../..//arrays/collection/reducer';
import { addAddress, editAddress, editAsset, setDeliveryPreference, setDeliveryAddress, validateAddress } from '../actionCreators';

import FormikAddressDetailForm from './FormikAddressDetailForm';
import FormikAddressForm from './FormikAddressForm';
import FormikAddressValidationForm from './FormikAddressValidationForm';
import FormikDeliveryPreferenceForm from './FormikDeliveryPreferenceForm';

import style from './style.scss';

const cx = classnames.bind(style);

const INSTALL_ADDRESS = 'INSTALL_ADDRESS';
const DELIVERY_ADDRESS = 'DELIVERY_ADDRESS';

class AssetCard extends Component {
    constructor(props) {
        super(props);
        autobind(this);

        this.state = {
            editAddressDetails: null,
            newAddressDetails: null,
            hasInstallAddressIssue: false,
            hasRmaAddressIssue: false,
            hasDeliveryPrefIssue: false,
            hasDoneInitialValidationDeliveryPref: false,
            hasDoneInitialValidationRmaAddress: false,
            hasDoneInitialValidationInstallAddress: false,
        };
    }

    getAssetDef(addressbookId) {
        const addressbookIndex = this.props.address.addressCollection
            .findIndex(item => item.id === addressbookId);
        const addressbookEntry = this.props.address.addressCollection[addressbookIndex];

        return {
            Install_Street1__c: addressbookEntry.abStreet1__c,
            Install_Street2__c: addressbookEntry.abStreet2__c,
            Install_City__c: addressbookEntry.abCity__c,
            Install_State_Province__c: addressbookEntry.abStateProvince__c,
            Install_Zip_Code__c: addressbookEntry.abPostalCode__c,
            Install_Country__c: addressbookEntry.abCountry__c,
        };
    }
    getMatchingAddressId(x) {
        const match = this.props.address.addressCollection.find(item =>
            (x.Install_Street1__c === item.abStreet1__c && x.Install_Street1__c !== null &&
            x.Install_Street2__c === item.abStreet2__c &&
            x.Install_City__c === item.abCity__c &&
            x.Install_State_Province__c === item.abStateProvince__c &&
            x.Install_Zip_Code__c === item.abPostalCode__c)
        );

        return match ? match.id : null;
    }

    openAddressEditForm(asset, assetId, newAddressDetails) {
        if (asset) {
            this.setState({
                editAddressDetails: {
                    asset,
                    assetId,
                },
            });
        } else {
            this.setState({
                newAddressDetails,
            });
        }
    }

    render() {
        const { asset, address } = this.props;

        if (!address || !address.assetCollection) {
            return null;
        }

        const displayName = (asset.System_Name__c && asset.name) ? `${asset.System_Name__c} (${asset.name})` : (asset.System_Name__c || asset.name);

        const title = (<Link to={`/infrastructure/storage/nimble/arrays/${asset.serialNumber}`}>{displayName}</Link>);

        const installAddress = asset.Install_Street1__c ||
            asset.Install_City__c ||
            asset.Install_State_Province__c ||
            asset.Install_Zip_Code__c;

        const relatedAssets = this.props.address.assetCollection.filter(n => n.assetPartShipAddressBook__c === asset.assetPartShipAddressBook__c);
        const rmaAddress = this.props.address.addressCollection.filter(n => n.id === asset.assetPartShipAddressBook__c)[0];
        const hasRmaAddress = rmaAddress && (rmaAddress.abStreet1__c || rmaAddress.abCity__c || rmaAddress.abStateProvince__c || rmaAddress.abPostalCode__c);

        // TEMPORARY HACK: match install addresses to address book entries manually
        asset.assetInstallAddressBook__c = this.getMatchingAddressId(asset);

        /*
        // Do we need this?

        if (this.props.arrays) {
            const matchingArrayList = this.props.arrays.filter(arr => arr.arraySn === asset.serialNumber);
            const matchingArray = matchingArrayList[0];
            if (!matchingArray) {
                return null;
            }
        }
        */

        const addressOptions = this.props.address.addressCollection.map(
            item => ({
                label: item.abDescription__c,
                value: item.id,
            })
        );
        addressOptions.unshift({
            label: 'Add a new address...',
            value: {
                newAddress: true,
            },
        });

        return (
            <div
                className={classnames('column small-24 medium-12 large-8', cx('equalHeight'))}
                style={{
                    order: (
                        this.state.hasDeliveryPrefIssue ||
                        this.state.hasInstallAddressIssue ||
                        this.state.hasRmaAddressIssue
                    ) ? -1 : 1,
                }}
            >
                <Card
                    title={
                        <div>
                            <h2 className={cx('addressCard')}>{title}</h2>
                            {asset.assetArenaProductCode__c}
                        </div>
                    }
                    className={classnames('flex-container', 'flex-dir-column', cx('cardAddress'))}
                >
                    {
                        <div className={cx('addressDisplay')}>
                            <FormikDeliveryPreferenceForm
                                id={`${asset.id}_delivery_pref`}
                                assetData={asset}
                                assetSameDayPartDeliveryPreference__c={asset.assetSameDayPartDeliveryPreference__c}
                                onFieldChanged={(name, value) => {
                                    this.props.setDeliveryPreference({
                                        assetSameDayPartDeliveryPreference__c: value,
                                    }, asset.id)
                                        .then(() => {
                                            FlashNotification.success(`Updated delivery preference for: ${displayName}`);
                                        })
                                        .catch((error) => {
                                            // handle error
                                            console.error('Edit address fail', error);
                                            FlashNotification.error(`Unable to update delivery preference for: ${displayName}`);
                                        });
                                }}
                                hasDoneInitialValidation={this.state.hasDoneInitialValidationDeliveryPref}
                                onValidationResult={(errors) => {
                                    if (Object.keys(errors).length) {
                                        this.setState({ hasDeliveryPrefIssue: true });
                                    } else {
                                        this.setState({ hasDeliveryPrefIssue: false });
                                    }

                                    this.setState({
                                        hasDoneInitialValidationDeliveryPref: true,
                                    });
                                }}
                            />
                        </div>
                    }
                    {
                        <div className={cx('addressDisplay')}>
                            <FormikAddressValidationForm
                                id={`${asset.id}_address_validation`}
                                assetData={asset}
                                onConfirm={() => {
                                    this.props.validateAddress(asset.id)
                                        .then(() => {
                                            FlashNotification.success(`Address confirmed for: ${displayName}`);
                                        })
                                        .catch((error) => {
                                            // handle error
                                            console.error('Edit delivery preference fail', error);
                                            FlashNotification.error(`Unable to confirm address for: ${displayName}`);
                                        });
                                }}
                            />
                            <hr className={cx('card')} />
                        </div>
                    }

                    <div className={cx('addressDisplay')}>
                        {
                            <FormikAddressForm
                                id={`${asset.id}_install`}
                                title="Install Address"
                                addressOptions={addressOptions}
                                addressId={asset.assetInstallAddressBook__c || installAddress}
                                onFieldChanged={(name, value) => {
                                    if (value && value.newAddress) {
                                        this.openAddressEditForm(null, null, INSTALL_ADDRESS);
                                        return;
                                    }

                                    const addressbookId = value;

                                    const assetDef = this.getAssetDef(addressbookId);

                                    this.setState({
                                        installAddressUpdating: true,
                                    });

                                    this.props.editAsset(assetDef, asset.id)
                                        .then(() => {
                                            FlashNotification.success(`Updated install address for: ${displayName}`);
                                            this.setState({
                                                installAddressUpdating: false,
                                            });
                                        })
                                        .catch((error) => {
                                            // handle error
                                            console.error('Edit address fail', error);
                                            FlashNotification.error(`Unable to update install address for: ${displayName}`);
                                        });
                                }}
                                onEdit={() => {
                                    if (asset.assetInstallAddressBook__c) {
                                        const match = this.props.address.addressCollection.find(item => item.id === asset.assetInstallAddressBook__c);
                                        asset.description = match.abDescription__c;
                                    }
                                    this.openAddressEditForm(asset, asset.id);
                                    // this.setState({
                                    //     hasDoneInitialValidationInstallAddress: false,
                                    // });
                                }}
                                hasDoneInitialValidation={this.state.hasDoneInitialValidationInstallAddress}
                                addressUpdating={this.state.installAddressUpdating}
                                onValidationResult={(errors) => {
                                    if (Object.keys(errors).length) {
                                        this.setState({ hasInstallAddressIssue: true });
                                    } else {
                                        this.setState({ hasInstallAddressIssue: false });
                                    }
                                    this.setState({
                                        hasDoneInitialValidationInstallAddress: true,
                                    });
                                }}
                            />
                        }
                        <div>
                            {
                                (installAddress && (
                                    <Async loading={this.state.installAddressUpdating}>
                                        <div className={cx('addressText')}>
                                            {asset.Install_Street1__c} <br />
                                            {asset.Install_Street2__c}{asset.Install_Street2__c ? <br /> : null}
                                            {asset.Install_City__c} {asset.Install_State_Province__c} {asset.Install_Zip_Code__c} <br />
                                            {asset.Install_Country__c}
                                        </div>
                                    </Async>
                                ))
                            }
                            <hr className={cx('card')} />
                        </div>
                    </div>

                    <div className={cx('addressDisplay')}>
                        <FormikAddressForm
                            id={`${asset.id}_shipping`}
                            title="RMA Part Delivery Address"
                            addressOptions={addressOptions}
                            addressId={hasRmaAddress ? rmaAddress.id : null}
                            onFieldChanged={(name, value) => {
                                if (value && value.newAddress) {
                                    this.openAddressEditForm(null, null, DELIVERY_ADDRESS);
                                    return;
                                }

                                this.setState({
                                    deliveryAddressUpdating: true,
                                });

                                this.props.setDeliveryAddress({
                                    assetPartShipAddressBook__c: value,
                                }, asset.id)
                                    .then(() => {
                                        FlashNotification.success(`Updated RMA address for: ${displayName}`);
                                        this.setState({
                                            deliveryAddressUpdating: false,
                                        });
                                    })
                                    .catch((error) => {
                                        // handle error
                                        console.error('Edit address fail', error);
                                        FlashNotification.error(`Unable to update RMA address for: ${displayName}`);
                                    });
                            }}
                            onEdit={() => {
                                this.openAddressEditForm(
                                    {
                                        description: rmaAddress.abDescription__c,
                                        Install_Street1__c: rmaAddress.abStreet1__c,
                                        Install_Street2__c: rmaAddress.abStreet2__c,
                                        Install_City__c: rmaAddress.abCity__c,
                                        Install_State_Province__c: rmaAddress.abStateProvince__c,
                                        Install_Zip_Code__c: rmaAddress.abPostalCode__c,
                                        Install_Country__c: rmaAddress.abCountry__c,
                                        assetPartShipAddressBook__c: rmaAddress.id,
                                    }
                                );
                            }}
                            hasDoneInitialValidation={this.state.hasDoneInitialValidationRmaAddress}
                            addressUpdating={this.state.deliveryAddressUpdating}
                            onValidationResult={(errors) => {
                                if (Object.keys(errors).length) {
                                    this.setState({ hasRmaAddressIssue: true });
                                } else {
                                    this.setState({ hasRmaAddressIssue: false });
                                }
                                this.setState({
                                    hasDoneInitialValidationRmaAddress: true,
                                });
                            }}
                        />
                    </div>
                    {
                        (hasRmaAddress && (
                            <Async loading={this.state.deliveryAddressUpdating}>
                                <div className={cx('addressDisplay')}>
                                    <div className={cx('addressText')}>
                                        {rmaAddress.abStreet1__c} <br />
                                        {rmaAddress.abStreet2__c}{rmaAddress.abStreet2__c ? <br /> : null}
                                        {rmaAddress.abCity__c} {rmaAddress.abStateProvince__c} {rmaAddress.abPostalCode__c} <br />
                                        {rmaAddress.abCountry__c}
                                    </div>
                                </div>
                            </Async>
                        ))
                    }
                </Card>

                {
                    this.state.newAddressDetails &&
                    <FormikAddressDetailForm
                        editAddress={(values) => {
                            const addressbookEntry = {
                                abDescription__c: values.description,
                                abStreet1__c: values.Install_Street1__c,
                                abStreet2__c: values.Install_Street2__c,
                                abCity__c: values.Install_City__c,
                                abStateProvince__c: values.Install_State_Province__c,
                                abPostalCode__c: values.Install_Zip_Code__c,
                                abCountry__c: values.Install_Country__c,
                            };

                            const updatePromise = this.props.addAddress(addressbookEntry);

                            // grab now because it will be destroyed by the closing modal
                            const { newAddressDetails } = this.state;

                            updatePromise
                                .then((response) => {
                                    const value = response.data.id;
                                    let applyNewAddressPromise;
                                    if (newAddressDetails === INSTALL_ADDRESS) {
                                        const addressbookId = value;
                                        const assetDef = this.getAssetDef(addressbookId);
                                        applyNewAddressPromise = this.props.editAsset(assetDef, asset.id);
                                    } else if (newAddressDetails === DELIVERY_ADDRESS) {
                                        applyNewAddressPromise = this.props.setDeliveryAddress({
                                            assetPartShipAddressBook__c: value,
                                        }, asset.id);
                                    }

                                    return applyNewAddressPromise;
                                })
                                .then(() => {
                                    FlashNotification.success(`Updated address for: ${displayName}`);
                                })
                                .catch((error) => {
                                    // handle error
                                    console.error('Edit address fail', error);
                                    FlashNotification.error(`Unable to update address for: ${displayName}`);
                                });
                        }}
                        closeModal={() => this.setState({ newAddressDetails: null })}
                    />
                }

                {
                    this.state.editAddressDetails &&
                    <FormikAddressDetailForm
                        id={this.state.editAddressDetails.asset.assetPartShipAddressBook__c}
                        relatedAssets={this.state.editAddressDetails.assetId ? null : relatedAssets}
                        editAddress={(values) => {
                            const { assetId } = this.state.editAddressDetails;

                            const updatePromises = [];

                            // asset needs to edit both the address entry
                            // and the raw asset fields (to match)
                            if (assetId) {
                                const allowedAssetFields = [
                                    'contactId',
                                    'assetPartShipAddressBook__c',
                                    'assetSameDayPartDeliveryPreference__c',
                                    'assetPartShipLastValidatedDatetime__c',
                                    'Install_Street1__c',
                                    'Install_Street2__c',
                                    'Install_City__c',
                                    'Install_State_Province__c',
                                    'Install_Zip_Code__c',
                                    'Install_Country__c',
                                ];

                                const assetValues = {};
                                allowedAssetFields.forEach(prop => assetValues[prop] = values[prop]);

                                updatePromises.push(this.props.editAsset(assetValues, assetId));
                            }

                            const addressbookId = assetId ? asset.assetInstallAddressBook__c : asset.assetPartShipAddressBook__c;

                            if (addressbookId) {
                                // convert asset address model property names to address book model property names
                                const addressbookEntry = {
                                    abDescription__c: values.description,
                                    abStreet1__c: values.Install_Street1__c,
                                    abStreet2__c: values.Install_Street2__c,
                                    abCity__c: values.Install_City__c,
                                    abStateProvince__c: values.Install_State_Province__c,
                                    abPostalCode__c: values.Install_Zip_Code__c,
                                    abCountry__c: values.Install_Country__c,
                                };

                                updatePromises.push(this.props.editAddress(addressbookEntry, addressbookId));
                            }

                            Promise.all(updatePromises)
                                .then(() => {
                                    FlashNotification.success(`Updated address for: ${displayName}`);
                                })
                                .catch((error) => {
                                    // handle error
                                    console.error('Edit address fail', error);
                                    FlashNotification.error(`Unable to update address for: ${displayName}`);
                                });
                        }}
                        closeModal={() => this.setState({ editAddressDetails: null })}
                        {...this.state.editAddressDetails.asset}
                    />
                }

            </div>
        );
    }
}

AssetCard.propTypes = {
    asset: PropTypes.object,
    arrays: PropTypes.arrayOf(PropTypes.object),
    address: PropTypes.object,
    addAddress: PropTypes.func,
    editAddress: PropTypes.func,
    editAsset: PropTypes.func,
    setDeliveryPreference: PropTypes.func,
    setDeliveryAddress: PropTypes.func,
    validateAddress: PropTypes.func,
};

const mapStateToProps = state => ({
    ...addressSelector(state),
    ...assetSelector(state),
    ...listSelector(state),
});

const mapDispatchToProps = dispatch => bindActionCreators({
    addAddress,
    editAddress,
    editAsset,
    setDeliveryPreference,
    setDeliveryAddress,
    validateAddress,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(AssetCard);
