import React, { Component } from 'react';
import classnames from 'classnames/bind';
import { withFormik, Field } from 'formik';

import Select from 'react-select';
import 'react-select/dist/react-select.css';

import { Modal } from '@infosight/elmer/dist/components/Modal';

import countries from '../../../../util/countries';

import style from './style.scss';

const cx = classnames.bind(style);

/* eslint-disable react/no-multi-comp */
/* eslint-disable jsx-a11y/label-has-for */
/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */

const formikEnhancer = withFormik({
    validate: (values) => {
        const errors = {};
        if (!values.description) {
            errors.description = 'is required';
        }
        if (!values.Install_Street1__c) {
            errors.Install_Street1__c = 'is required';
        }
        if (!values.Install_City__c) {
            errors.Install_City__c = 'is required';
        }
        if (!values.Install_State_Province__c) {
            errors.Install_State_Province__c = 'is required';
        }
        if (!values.Install_Zip_Code__c) {
            errors.Install_Zip_Code__c = 'is required';
        }
        if (!values.Install_Country__c) {
            errors.Install_Country__c = 'is required';
        }

        return errors;
    },
});

/* eslint-disable react/prefer-stateless-function */
class AddressDetailForm extends Component {
    componentDidMount() {
        this.props.validateForm();
    }

    renderField(fieldName, label) {
        const {
            touched,
            dirty,
            errors,
            handleBlur,
            handleChange,
            values,
        } = this.props;

        return (
            <div className="form-group">
                <label htmlFor={fieldName} className={cx('form-label', (errors[fieldName] && touched[fieldName] ? 'error' : null))}>
                    {label}
                    {
                        errors[fieldName] && touched[fieldName] && (
                            <span> {errors[fieldName]}</span>
                        )
                    }
                </label>
                <Field
                    id={fieldName}
                    name={fieldName}
                    type="text"
                    onChange={handleChange}
                    value={values[fieldName]}
                    className={errors[fieldName] && touched[fieldName] ? 'form-control error' : 'form-control'}
                />
            </div>
        );
    }

    render() {
        const {
            id,
            values,
            touched,
            closeModal,
            dirty,
            errors,
            handleBlur,
            handleChange,
            setFieldValue,
            setTouched,
            editAddress,
            relatedAssets,
        } = this.props;

        const title = (id === undefined) ? 'Create New Address' : 'Edit Address';

        return (
            <Modal
                size="tiny"
                type="primary"
                hideModalOnPrimaryButtonClick
                hideModalOnSecondaryButtonClick
                primaryButtonContent="Save"
                secondaryButtonContent="Cancel"
                disablePrimaryButton={Object.keys(errors).length > 0}
                title={title}
                onClickPrimaryButton={() => {
                    editAddress(values, id);
                }}
                onClickSecondaryButton
                bodyClassName={cx('body')}
                footerClassName={cx('footer')}
                hide={closeModal}
            >
                <div className={cx('addressDetailForm')}>
                    <form
                        className="form-horizontal"
                    >
                        { this.renderField('description', 'Description') }
                        { this.renderField('Install_Street1__c', 'Street 1') }
                        { this.renderField('Install_Street2__c', 'Street 2') }
                        { this.renderField('Install_City__c', 'City') }
                        { this.renderField('Install_State_Province__c', 'State/Province') }
                        { this.renderField('Install_Zip_Code__c', 'Postal Code') }

                        <div className="form-group">
                            <label
                                htmlFor="Install_Country__c"
                                className={cx('form-label', (errors.Install_Country__c && touched.Install_Country__c ? 'error' : null))}
                            >
                                Country
                                {
                                    errors.Install_Country__c && touched.Install_Country__c && (
                                        <span> {errors.Install_Country__c}</span>
                                    )
                                }
                            </label>
                            <Field
                                component={Select}
                                id="Install_Country__c"
                                name="Install_Country__c"
                                multi={false}
                                clearable={false}
                                className={errors.Install_Country__c && touched.Install_Country__c ? 'form-control error' : 'form-control'}
                                options={countries.map(x => ({
                                    label: x,
                                    value: x,
                                }))}
                                onChange={(e) => {
                                    setFieldValue('Install_Country__c', e.value);
                                }}
                                onClose={(e) => {
                                    setTouched(Object.assign({
                                        Install_Country__c: true,
                                    }, touched));
                                }}
                                value={values.Install_Country__c}
                            />
                        </div>
                    </form>
                    {
                        relatedAssets &&
                        <div>
                            <hr />
                            <h2><b>Important:</b> Editing this entry will change the address of the following assets</h2>
                            <ul>
                                {
                                    relatedAssets.map(asset => <li>{asset.name}</li>)
                                }
                            </ul>
                        </div>
                    }
                </div>
            </Modal>
        );
    }
}

const FormikAddressDetailForm = formikEnhancer(AddressDetailForm);

export default FormikAddressDetailForm;
