import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import autobind from 'react-autobind';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import { buildUrl } from '@infosight/elmer/dist/utils/url';
import { Link } from 'react-router-dom';

import { listSelector } from '../../../../nimble/arrays/collection/reducer';
import { getArrays } from '../../../../nimble/arrays/collection/actionCreators';

import { hasSupportCase } from '../../../../user/reducer';

import { QuickTable, QuickRow } from '../../QuickTable';

import style from '../PanelStyles.scss';

const cx = classnames.bind(style);


/**
 * Renders a panel displaying array information
 */

class PanelArrayMeta extends React.Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    componentWillMount() {
        this.props.getArrays();
    }

    render() {
        const { theAsset, assetId, serialNumber, arrays, isSupportCaseAccess } = this.props;

        let arrayAsset = null;

        if (isSupportCaseAccess) return (null);

        if (!arrays) return (<span>Loading Array: {assetId} ... <i className="fa fa-spinner fa-spin" /></span>);

        if (theAsset) arrayAsset = theAsset;
        else if (assetId && arrays) {
            arrayAsset = arrays
                .find(a => a.arrayId === assetId);
        } else if (serialNumber && arrays) {
            arrayAsset = arrays
                .find(a => a.arraySn === serialNumber);
        }

        if (!arrayAsset) return (<span>Invalid Array: {assetId}</span>);

        let version = 'n/a';
        if (arrayAsset.softwareVersion) {
            [version] = arrayAsset.softwareVersion.split('-');
        }

        return (
            <div className={cx('case-panel')}>

                <div className={cx('panel-content')}>

                    <QuickTable>
                        <QuickRow
                            columns={[
                                'Serial No:',
                                <Link to={`${buildUrl('/infrastructure/storage/nimble/arrays/', arrayAsset.arraySn)}`}>
                                    {arrayAsset.arraySn}
                                </Link>,
                            ]}
                        />
                        <QuickRow columns={['Model:', arrayAsset.model || 'n/a']} />
                        <QuickRow columns={['Version:', version]} />
                    </QuickTable>

                    <QuickTable>
                        <QuickRow columns={['Hostname:', arrayAsset.hostName || 'n/a']} />
                        <QuickRow columns={['Group:', arrayAsset.groupName || 'n/a']} />
                        {/* <QuickRow columns={['Pool:', arrayAsset.pool || 'n/a']} /> */}
                    </QuickTable>

                    <QuickTable>
                        <QuickRow columns={['Coverage:', arrayAsset.supportStatus || 'n/a']} />
                    </QuickTable>

                </div>

            </div>
        );
    }
}

PanelArrayMeta.propTypes = {
    assetId: PropTypes.string,
    serialNumber: PropTypes.string,
    theAsset: PropTypes.object,
    arrays: PropTypes.array,
    getArrays: PropTypes.func.isRequired,
    isSupportCaseAccess: PropTypes.bool,
};

function mapStateToProps(state) {
    return {
        ...listSelector(state),
        isSupportCaseAccess: hasSupportCase(state),
    };
}

const mapDispatchToProps = dispatch => bindActionCreators({
    getArrays,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(PanelArrayMeta);

