const $ = require('jquery');
const _ = require('underscore');
var Marionette = require('backbone.marionette');
var tpl = require('raw-loader!./PaginationView.html');

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    bindings: ['from', 'to', 'total'].reduce(function (memo, val) {
        memo['[data-bind="' + val + '"]'] = val;
        return memo;
    }, {}),

    ui: {
        previous: '[data-action="previous"]',
        next: '[data-action="next"]',
    },

    events: {
        'click @ui.previous': 'previousPage',
        'click @ui.next': 'nextPage',
    },

    collectionEvents: {
        'reset': 'adjustLinkState scrollToTop',
    },

    initialize: function () {
        this.collection = this.model.collection;
    },

    onRender: function () {
        this.stickit();
        this.adjustLinkState();
    },

    nextPage: function (event) {
        event.preventDefault();
        this.collection.getNextPage();
        this.adjustLinkState();
    },

    previousPage: function (event) {
        event.preventDefault();
        this.collection.getPreviousPage();
        this.adjustLinkState();
    },

    adjustLinkState: function () {
        this.ui.previous.toggleClass('disabled', !this.collection.hasPreviousPage());
        this.ui.next.toggleClass('disabled', !this.collection.hasNextPage());
    },

    scrollToTop: function () {
        $('html,body').animate({ scrollTop: 0 }, 500);
    },

});
