const _ = require('underscore');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');
var Marionette = require('backbone.marionette'),
    tpl = require('raw-loader!./SpaceSavings.html'),
    SpaceSavingsDonutView = require('./SpaceSavingsDonutView'),
    SpaceSavingsBarChart = require('./SpaceSavingsBarChart'),
    SpaceSavingsWaterfallView = require('./SpaceSavingsWaterfallView'),
    SpaceUsageChart = require('./SpaceUsageChart');

var template = _.template(tpl);

module.exports = Marionette.ItemView.extend({

    /**
     * @classdesc <span style="color: red;">Developer description to be added.</span>
     * @name views/components/dashboard/./SpaceSavingsView
     * @constructor
     * @augments Backbone.View
     */

    template: _.template(tpl),

    className: 'space-savings-view',

    initialize: function (options) {
        this.excludeDonut = options.excludeDonut;
    },

    onRender: function () {

        // the summary message
        var pctSaved = this.model.get('spaceSavings') / this.model.get('traditionalSpace') * 100;
        pctSaved = Highcharts.numberFormat(pctSaved, 1);
        this.$el.find('.percent-saved').append('(' + pctSaved + '%)');

        // the waterfall view
        this.spaceSavingsView = new SpaceSavingsWaterfallView({
            model: this.model,
        });
        this.$el.find('.space-savings-waterfall').append(
            this.spaceSavingsView.render().el);

        if (!this.excludeDonut) {
            // the donut summary view
            this.spaceSavingsDonut = new SpaceSavingsDonutView({
                model: this.model,
            });
            this.$el.find('.space-savings-donut').append(
                this.spaceSavingsDonut.render().el);
        }
    },

});
