import React, { Component } from 'react';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';

import Async from '@infosight/elmer/dist/components/Async';
import Card from '@infosight/elmer/dist/components/Card';

import Request from '../../../../services/Request';

import SelfRegistrationCapable from './SelfRegistrationCapable';
import SelfRegistrationIncapable from './SelfRegistrationIncapable';

import style from './style.scss';

const cx = classnames.bind(style);

class SelfRegistrationContainer extends Component {
    constructor(props) {
        super(props);
        autobind(this);
        this.state = {
            account: undefined,
        };
    }

    componentDidMount() {
        this.evaluateSelfRegistrationCapability();
    }

    evaluateSelfRegistrationCapability() {
        new Request()
            .urlSegment('auth/user/selfRegistrationCapability')
            .get()
            .then(response => response.data)
            .then(account => this.setState({ account }))
            .catch((error) => {
                this.setState({
                    account: null,
                    error: error.response.data.message,
                });
            });
    }

    render() {
        const { account, error } = this.state;
        return (
            <Card className={cx('container')} title="Nimble Storage Customer Registration">
                <Async loading={account === undefined}>
                    {() => (
                        account ?
                            <SelfRegistrationCapable account={account} />
                            : <SelfRegistrationIncapable error={error} />
                    )}
                </Async>
            </Card>
        );
    }
}

export default SelfRegistrationContainer;
