var Backbone = require('backbone');
module.exports = Backbone.Model.extend({
    defaults: {
        email: '',
    },
    idAttribute: 'email',
    validation: {
        email: [
            {
                required: true,
                msg: 'An email address is required',
            }, {
                pattern: 'email',
                msg: 'The provided email address is not well-formed',
            },
        ],
    },
});
