import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Route, Switch, withRouter } from 'react-router-dom';
import { pageBoundaryRouteRenderer } from '@infosight/elmer/dist/page';

import SupportListCases from './components/SupportListCases';
import SupportCreateCase from './components/SupportCreateCase';
import SupportExamineCase from './components/SupportExamineCase';
import SupportContactPage from './contact/SupportContactPage';

import { AuthorizationEvaluator, AUTHORITIES, PRIVILEGES, PERMISSION_TARGET } from '../user';

const SupportRouter = ({ match, hasSupportPermission }) => (
    <React.Fragment>
        {!hasSupportPermission ? (
            <SupportContactPage />
        ) : (
            <Switch>
                <Route path={`${match.path}/newcase`} render={pageBoundaryRouteRenderer(SupportCreateCase, 'Create a case')} />
                <Route
                    path={`${match.path}/case/:caseNumber/kcs/:docId`}
                    render={pageBoundaryRouteRenderer(SupportExamineCase, 'View case')}
                />
                <Route path={`${match.path}/case/:caseNumber`} render={pageBoundaryRouteRenderer(SupportExamineCase, 'View case')} />
                <Route render={pageBoundaryRouteRenderer(SupportListCases, 'Support')} />
            </Switch>
        )}
    </React.Fragment>
);

SupportRouter.propTypes = {
    match: PropTypes.object,
    hasSupportPermission: PropTypes.bool,
};

export default withRouter(connect(
    () => ({
        hasSupportPermission: AuthorizationEvaluator.evaluate({
            access: {
                $or: [
                    {
                        authorities: [AUTHORITIES.SUPPORT_CASE],
                    },
                    {
                        permissions: {
                            privilege: PRIVILEGES.FEATURE_SUPPORT_CASE_MANAGE,
                            targetType: PERMISSION_TARGET.NIMBLE_TENANT,
                            targetId: PERMISSION_TARGET.ACTIVE_TARGET_ID,
                        },
                    },
                ],
            },
        }),
    })
)(SupportRouter));
