import React from 'react';
import moment from 'moment-timezone';

import RealtimeDeliveryCell from './RealtimeDeliveryCell';
import VmwareContentCell from './VmwareContentCell';

/* eslint-disable react/prop-types */
const telemetryConfig = handleChange =>
    ({
        categories: ['default'],
        columns: [
            {
                Header: 'Array',
                accessor: 'arraySearch',
                filterable: true,
            },
            {
                Header: 'Diagnostics Delivery',
                accessor: 'delivery',
                headerClassName: 'text-center',
                columns: [{
                    Header: 'Heartbeats',
                    accessor: 'lastHeartbeat',
                    Cell: ({ original: { lastHeartbeat } }) => lastHeartbeat && moment((lastHeartbeat * -1)).fromNow(),
                }, {
                    Header: 'Daily',
                    accessor: 'lastDaily',
                    Cell: ({ original: { lastDaily } }) => lastDaily && moment((lastDaily * -1)).fromNow(),
                }, {
                    Header: 'Streaming',
                    accessor: 'realtimeSearch',
                    Cell: ({ original: { arraySn, realtime } }) => (<RealtimeDeliveryCell arraySn={arraySn} handleChange={handleChange} {...realtime} />),
                }],
            },
            {
                Header: 'Diagnostics Content',
                accessor: 'content',
                headerClassName: 'text-center',
                columns: [{
                    accessor: 'vmwareSearch',
                    Header: 'VMware',
                    Cell: ({ original: { arraySn, vmware } }) => (<VmwareContentCell arraySn={arraySn} handleChange={handleChange} {...vmware} />),
                }],
            },
        ],
    });

export { telemetryConfig as default };

