import $ from 'jquery';
import React from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import moment from 'moment-timezone';
import classnames from 'classnames/bind';
import { Spinner } from '@infosight/elmer/dist/components/Spinner';

import SFCaseModel from '../../../library/SFCaseModel';
import { parseComment } from '../../../library/support-api';
import { getCurrentUserContact } from '../../../library/support-helper';

import style from './AddComment.scss';

const cx = classnames.bind(style);


class AddComment extends React.Component {
    constructor(props) {
        super(props);
        autobind(this);
        this.state = {
            formBusy: false,
        };
        this.textarea = null;
    }

    resultCommentPostOk(commentPosted) {
        const uiComment = parseComment(commentPosted);

        this.props.theCase.caseLastComment__c = uiComment.createdDate;
        this.props.theCase.lastUpdatedDate = moment(uiComment.createdDate).format('YYYYMMDD');
        this.props.theCase.lastUpdatedTS = uiComment.createdDate;

        this.textarea.value = '';
        this.setState({ formBusy: false });

        if (typeof this.props.onSave === 'function') this.props.onSave.call(null, uiComment);
        // maybe TODO: hide the comment box until ready to use, put it away after
    }

    resultCommentPostFail() {
        this.setState({ formBusy: false });
        console.warn('comment failed to post.');
        // TODO: red border?
    }

    eventSubmitComment() {
        const commentText = this.textarea.value;
        if (commentText) {
            const session = getCurrentUserContact();
            const taggedText = `[user:${session.ContactId}(${session.ContactName})]\n${commentText}`; // tag the user

            const caseModel = new SFCaseModel();
            caseModel.set('id', this.props.theCase.id);

            const casePath = caseModel.url();

            $.ajax({
                type: 'POST',
                url: `${casePath}/comments`,
                contentType: 'application/json',
                data: JSON.stringify({ commentBody: taggedText }),
            })
                .then(this.resultCommentPostOk, this.resultCommentPostFail);

            this.setState({ formBusy: true });
        }
    }

    unboundClear() {
        this.textarea.value = '';
    }

    render() {
        const { formBusy } = this.state;
        return (
            <div className={cx('add-comment', { 'busy-sending': formBusy })}>

                <div className={cx('textbox-wrapper')}>
                    <textarea className={cx('comment-box')} ref={(elem) => { this.textarea = elem; }} rows="6" />
                    {formBusy && <Spinner className={cx('sending')} />}
                </div>
                <br />
                <button
                    className="button"
                    disabled={formBusy}
                    onClick={this.eventSubmitComment}
                >Post Comment
                </button>
                <button
                    className="button hollow"
                    disabled={formBusy}
                    onClick={this.unboundClear}
                >Clear
                </button>
            </div>
        );
    }
}

AddComment.propTypes = {
    theCase: PropTypes.object.isRequired,
    onSave: PropTypes.func,
};

export default AddComment;
