var Backbone = require('backbone');

module.exports = Backbone.Model.extend({

    url: function () {
        return this.api().central().account().segment('admin/permissions/request');
    },

    validation: {
        arraySn: {
            pattern: /^[A-Z]{2}-[0-9]{6}$/,
            msg: 'The provided input is not a properly formatted Array SN',
        },
    },

});
