const $ = require('jquery');
const _ = require('underscore');
const Marionette = require('backbone.marionette');
const Backbone = require('backbone');
const GridView = require('app/views/components/grid/GridView');
const CollectionGridModel = require('app/models/grid/CollectionGridModel');
const Spinner = require('app/views/components/Spinner');
const moment = require('moment');
const tpl = require('raw-loader!./templates/AuditLogView.html');

const template = _.template(tpl);

module.exports = Marionette.View.extend(
    /** @lends views/toolsCentral/auditlog/AuditLogCollectionView.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/toolsCentral/auditlog/AuditLogCollectionView
         * @constructor
         * @augments Backbone.View
         */
        className: 'audit-log-detailed-grid',

        mode: 'local',

        events: {},

        columns: [
            { data: 'username', filter: false },
            {
                data: 'timestamp',
                render: {
                    display: function (data) {
                        return moment(data).format('L LT');
                    },
                },
            },
            { data: 'category', filter: true },
            { data: 'description' },
            { data: 'status', filter: true },
            { data: 'details' },
        ],

        createModel: function () {
            return new CollectionGridModel({
                collection: new Backbone.Collection(this.getOption('collection')),
                columns: this.columns,
                emptyLabel: 'No audit entries found',
                showFilters: true,
            });
        },

        render: function () {
            this.$el.html(template());

            var table = this.$('TABLE'),
                $table = $(table),
                gridModel = this.createModel();

            this.spinner = new Spinner();
            this.$('.spinner').append(this.spinner.render().el);

            this.grid = new GridView({
                model: gridModel,
                id: 'audit-log-detail-grid',
                tableTemplate: table,
                sort: [
                    {
                        column: 'Timestamp',
                        order: 'desc',
                    },
                ],
            });
            this.listenTo(this.grid, 'error', this.onGridError);
            this.$('#audit-log-detailed-grid-container').append(this.grid.render().el);
            return this;
        },

        onGridError: function (resp, error, message) {
            this.$('#audit-log-detailed-grid-container').hide();
            this.$('#audit-log-unavailable').show();
        },
    });
