const $ = require('jquery');
const _ = require('underscore');
var Marionette = require('backbone.marionette');
var ArrayCapacityDashboardView = require('./ArrayCapacityDashboardView');
var VolumeCapacityDashboardView = require('./VolumeCapacityDashboardView');
var tpl = require('raw-loader!./CapacityDashboardView.html');

var spinnerTpl = require('raw-loader!app/common/Spinner.html');

var SpinnerView = Marionette.ItemView.extend({
    template: _.template(spinnerTpl),
});

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    className: 'dashboard-capacity-view',

    regions: {
        spinner: '.spinner-view',
        array: '.dashboard-array-capacity-view',
        volume: '.dashboard-volume-capacity-view',
    },

    ui: {
        headerLabels: '.headerLabels',
    },

    initialize: function () {
        _.bindAll(this, 'render');

        this.arrayView = new ArrayCapacityDashboardView();
        this.volumeView = new VolumeCapacityDashboardView();

        this.arrayView.$el.hide();
        this.volumeView.$el.hide();
    },

    onRender: function () {
        this.showChildView('array', this.arrayView);
        this.showChildView('volume', this.volumeView);

        this.ui.headerLabels.hide();

        this.spinner.show(new SpinnerView());

        var self = this;

        $.when(this.arrayView.deferred, this.arrayView.deferred)
            .then(function () {
                self.onChildrenLoaded();
            });
    },

    onChildrenLoaded: function (view) {
        this.spinner.empty();

        this.$el.addClass('animated fadeIn');
        this.ui.headerLabels.show();
        this.arrayView.$el.show();
        this.volumeView.$el.show();
    },

});
