/**
 * Represents an aggregation of all upgrade recommendations.  Contains all the
 * business-logic related to showing recommendations (many of the recommendations
 * are have dependencies).
 */
var Backbone = require('backbone');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');

module.exports = Backbone.Model.extend(
    /** @lends models/dashboard/UpgradeSummaryModel.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name models/dashboard/UpgradeSummaryModel
         * @constructor
         * @augments Backbone.Model
         */

        defaults: {
            currentArrayModel: null,

            currentCacheExpNonAfsGB: null,
            currentCacheHeadGB: null,
            currentCacheSystemTotalGB: null,
            currentCacheTotalAfsGB: null,
            currentController: null,
            currentHddExpansionShelves: null,
            currentHddGB: null,
            recommendCacheUpgrade: false,
            recommendControllerUpgrade: false,
            recommendDiskUpgrade: false,
            recommendScaleOut: false,
            recommendedAddShelf: false,
            recommendedArrayModel: null,
            recommendedController: null,
            recommendedCacheHeadGB: null,
            recommendedCacheSystemTotalGB: null,
            recommendedCacheTotalAfsGB: null,
            recommendedHddExpansionShelves: null,
            serialNumber: null,
            arrayType: null,
            addAfsShelf: null,

            // these properties are not sent by the server, but instead are set
            // during initialize() so they can be conveniently accessed via templates.
            cpuRecommendationObject: null,
            cacheTotalSystemRecommendationObject: null,
            cacheHeadShelfRecommendationObject: null,
            cacheSSDShelfRecommendationObject: null,
            cacheHDDShelfRecommendationObject: null,
            capacityRecommendationObject: null,
            upgradeNotes: [],
        },

        /**
         * Overridden Backbone initialize method.
         * @param  {Object} options Parameterized object containing the initial attribute values.
         */
        initialize: function (options) {

            var upgradeNotes = [],
                recommendScaleOut = this.get('recommendScaleOut'),
                recommendController = this.get('recommendControllerUpgrade'),
                recommendCache = this.get('recommendCacheUpgrade'),
                recommendDisk = this.get('recommendDiskUpgrade'),
                currentController = this.get('currentController'),
                recommendedController = this.get('recommendedController');
            var addAfsShelf = this.get('addAfsShelf');
            var arrayType = this.get('arrayType');

            // scale out specific logic
            if (recommendScaleOut) {

                if (recommendController) {
                    this.set('cpuRecommendationObject', this.buildRecommendation('CPU', 'CPU upgrade recommended'));
                }

                if (recommendCache) {
                    this.set('cacheRecommendationObject', this.buildRecommendation('Cache', 'Cache upgrade recommended'));
                }

                if (recommendDisk) {
                    this.set('capacityRecommendationObject', this.buildRecommendation('Disk', 'Additional storage recommended'));
                }
            }
            // non-scale out
            else {

                // cpu upgrades
                if (recommendController) {
                    this.set('cpuRecommendationObject', this.buildRecommendation('Controller', 'CPU upgrade recommended',
                        currentController, recommendedController));
                }

                // cache upgrades
                if (recommendCache) {
                    var cacheMessages = [],
                        currentCacheExpNonAfsGB = this.get('currentCacheExpNonAfsGB'),
                        currentCacheHeadGB = this.get('currentCacheHeadGB'),
                        currentCacheSystemTotalGB = this.get('currentCacheSystemTotalGB'),
                        currentCacheTotalAfsGB = this.get('currentCacheTotalAfsGB'),
                        recommendedCacheHeadGB = this.get('recommendedCacheHeadGB'),
                        recommendedCacheSystemTotalGB = this.get('recommendedCacheSystemTotalGB'),
                        recommendedCacheTotalAfsGB = this.get('recommendedCacheTotalAfsGB');

                    // total system cache
                    if (recommendedCacheSystemTotalGB != currentCacheSystemTotalGB) {
                        this.set('cacheTotalSystemRecommendationObject',
                            this.buildRecommendation(
                                'Cache',
                                'Total cache upgrade recommended',
                                Highcharts.numberFormat(currentCacheSystemTotalGB, 0) + ' GB',
                                Highcharts.numberFormat(recommendedCacheSystemTotalGB, 0) + ' GB'));

                        this.set('cacheHeadShelfRecommendationObject',
                            this.buildRecommendation(
                                'Cache',
                                'Head shelf cache upgrade recommended',
                                Highcharts.numberFormat(currentCacheHeadGB, 0) + ' GB',
                                Highcharts.numberFormat(recommendedCacheHeadGB, 0) + ' GB'));

                        if (currentCacheTotalAfsGB !== null) {
                            this.set('cacheSSDShelfRecommendationObject',
                                this.buildRecommendation(
                                    'Cache',
                                    'SSD shelf cache upgrade recommended',
                                    Highcharts.numberFormat(currentCacheTotalAfsGB, 0) + ' GB',
                                    Highcharts.numberFormat(recommendedCacheTotalAfsGB, 0) + ' GB'));
                        }

                        this.set('cacheHDDShelfRecommendationObject',
                            this.buildRecommendation(
                                'Cache',
                                'HDD shelf cache upgrade recommended',
                                Highcharts.numberFormat(currentCacheExpNonAfsGB, 0) + ' GB',
                                Highcharts.numberFormat(currentCacheExpNonAfsGB, 0) + ' GB'));
                    }

                    if (arrayType === 'all-flash' && addAfsShelf) {
                        this.set('capacityRecommendationObject', this.buildRecommendation('AFS', 'Capacity upgrade recommended',
                            '0' + ' shelf', '+1 shelf'));
                    }

                    if (arrayType === 'all-flash' && !addAfsShelf) {
                        this.set('capacityRecommendationObject', this.buildRecommendation('AFS', 'Capacity upgrade recommended',
                            '0' + ' shelf', 'Populate Bank B'));
                    }
                }
                // disk upgrades
                if (recommendDisk) {
                    var diskMessages = [],
                        currentShelves = this.get('currentHddExpansionShelves'),
                        recommendedShelves = this.get('recommendedHddExpansionShelves'),
                        additionalShelves = recommendedShelves - currentShelves,
                        diskRecommendationText = '+' + additionalShelves + (additionalShelves > 1 ? ' shelves' : ' shelf');

                    this.set('capacityRecommendationObject', this.buildRecommendation('Disk', 'Capacity upgrade recommended',
                        currentShelves + ' shelf', diskRecommendationText));
                }
            }

            this.setUpgradeNotes();
        },

        buildRecommendation: function (category, summary, current, recommended) {
            return {
                category: category,
                summary: summary,
                current: current,
                recommended: recommended,
            };
        },

        /**
         * Helper method for setting the upgrade notes on the model.
         * @private
         */
        setUpgradeNotes: function () {
            var recommendedComponents = [],
                recommendScaleOut = this.get('recommendScaleOut'),
                recommendController = this.get('recommendControllerUpgrade'),
                recommendCache = this.get('recommendCacheUpgrade'),
                recommendDisk = this.get('recommendDiskUpgrade'),
                currentController = this.get('currentController'),
                recommendedController = this.get('recommendedController'),
                s = '',
                msg = null;

            // build up the upgrade notes
            if (recommendDisk) {
                recommendedComponents.push('capacity');
            }
            if (recommendController) {
                recommendedComponents.push('cpu');
            }
            if (recommendCache) {
                recommendedComponents.push('cache');
            }

            // make the recommended components string readable
            for (var i = 0; i < recommendedComponents.length; i++) {
                s += recommendedComponents[i];

                if (i < recommendedComponents.length - 2) {
                    s += ', ';
                }
                if (i < recommendedComponents.length - 1) {
                    s += ' and ';
                }
            }

            // set the appropriate upgrade notes
            if (recommendScaleOut) {
                msg = 'Scale-Out is recommended based on your ' + s + ' needs.';
            } else if (!recommendController && currentController != recommendedController) {
                msg = 'A controller upgrade is required to support the recommended ' + s + ' upgrades';
            }
            if (msg) {
                this.set('upgradeNotes', [msg]);
            }
        },

    });
