// CommentItem.js

import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import objectHash from 'object-hash';

import DateTime from '../../../../../components/DateTime';


import style from './CommentItem.scss';

const cx = classnames.bind(style);


function CommentItem({ comment }) {
    return (
        <div
            key={comment.id || objectHash(comment)}
            className={cx('comment-item', { 'from-nimble': comment.fromNimble })}
        >

            <div className={cx('attribution')}>

                <div className={cx('caret')} />

                <div className={cx('user-icon')}>
                    <i className="fa fa-user" />
                </div>
                <span>
                    {comment.createdByName ?
                        comment.createdByName :
                        comment.createdBy.id
                    }
                </span><br />
                <DateTime date={comment.createdDate} format="M/D/Y h:mm A" />
            </div>

            <div className={cx('comment-text')}>
                <p>{comment.commentBody}</p>
            </div>

        </div>
    );
}

CommentItem.propTypes = {
    comment: PropTypes.object,
};

export default CommentItem;
