const $ = require('jquery');
var Marionette = require('backbone.marionette');
var AsyncView = require('app/common/AsyncView');
var ProtectionModel = require('./ProtectionModel');
var numeral = require('numeral');

var LOCAL_PROTECTED_COLOR = '#95cfdb',
    REMOTE_PROTECTED_COLOR = '#529fae',
    UNPROTECTED_COLOR = '#ffffff',
    UNPROTECTED_BORDER_COLOR = '#999999';

var View = Marionette.ItemView.extend({

    template: false,

    className: 'dashboard-protection-view protection-donut',

    initialize: function () {
        this.model = new ProtectionModel();
    },

    onRender: function () {
        var model = this.model;
        var totalVolumeCount = model.get('accountTotalVolumeCount');

        if (totalVolumeCount !== 0) {
            this.$el.highcharts({
                // Highcharts.chart({
                chart: {
                    type: 'pie',
                    spacingTop: 0,
                    spacingBottom: 0,
                    spacingLeft: 0,
                    spacingRight: 0,
                    marginLeft: 0,
                },
                title: {
                    useHTML: true,
                    text: function () {
                        var percentProtected = model.get('percentProtected');
                        if (percentProtected <= 1) {
                            return '<span class="large-number">' + numeral(percentProtected * 100).format('0') + '</span><span>%</span>' +
                                '<p class="ops-label">volumes</br>protected</p>';
                        } else {
                            return null;
                        }
                    }(),
                    align: 'center',
                    verticalAlign: 'middle',
                    y: -27,
                    x: -80,
                },
                xAxis: {
                    type: 'category',
                },
                legend: {
                    enabled: true,
                    useHTML: true,
                    labelFormatter() {
                        let result = `<div class="protection-legend-item"><span>${this.name}</span><span>${this.y}</span></div>`;

                        if (model.get('accountTotalVolumesLagging') !== 0 && this.name === 'UNPROTECTED') {
                            result += `<div class="protection-legend-item lag">
                                    <span class="text-right"><i class="fa fa-exclamation-triangle text-warning"></i> LAG</span>
                                    <span>${model.get('accountTotalVolumesLagging')}</span>
                                </div>`;
                        }

                        return result;
                    },
                    layout: 'vertical',
                    align: 'right',
                    verticalAlign: 'middle',
                    padding: 0,
                    margin: 0,
                    x: -30,
                    itemMarginTop: 3,
                    itemMarginBottom: 3,
                },
                tooltip: {
                    enabled: false,
                },
                plotOptions: {
                    pie: {
                        dataLabels: {
                            enabled: false,
                        },
                        center: [75, '50%'],
                        point: {
                            events: {
                                legendItemClick: function (event) {
                                    event.preventDefault();
                                },
                            },
                        },
                    },
                },
                series: [
                    {
                        showInLegend: true,
                        size: '100%',
                        innerSize: '85%',
                        borderColor: UNPROTECTED_BORDER_COLOR,
                        data: [
                            { name: 'LOCAL ONLY', y: this.model.get('localProtectedCount'), color: LOCAL_PROTECTED_COLOR },
                            { name: 'REMOTE', y: this.model.get('remoteProtectedCount'), color: REMOTE_PROTECTED_COLOR },
                            { name: 'UNPROTECTED', y: this.model.get('unprotectedCount'), color: UNPROTECTED_COLOR, lag: this.lag },
                        ],
                        states: {
                            hover: {
                                enabled: false,
                            },
                        },
                    },
                ],
                credits: {
                    enabled: false,
                },
            });

            // Legend borders
            $(this.$el.highcharts().series[0].data).each(function (i, seriesData) {
                if (seriesData.legendSymbol) {
                    $(seriesData.legendSymbol.element).attr('stroke-width', '1');
                    $(seriesData.legendSymbol.element).attr('stroke', UNPROTECTED_BORDER_COLOR);
                }
            });

        } else {
            this.$el.append('<span><p>No volumes have been provisioned</p></span>');
        }

    },

    onAttach: function () {
        if (this.$el.highcharts()) {
            this.$el.highcharts().reflow();
        }
    },

});

module.exports = AsyncView.extend({
    ViewConstructor: View,
});
