/**
 * Development-time warning message to alert abuse or misuse
 */
export default function warn(message, ...extra) {
    if (__DEBUG__ && console) {
        const logger = console.warn || console.log;
        logger(message, ...extra);
    }
}

/**
 * Development-time warning message to alert abuse or misuse is expression is falsy
 */
export function assert(expression, message, ...extra) {
    if (!expression) {
        warn(message, ...extra);
    }
}
