import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';

import CommentItem from '../CommentItem';

import style from './CaseComments.scss';

import moment from 'moment-timezone';

const cx = classnames.bind(style);

const ACCOUNT_REGEX = /\[account:([0-9A-Za-z]+)\]/;
const USER_REGEX = /\[user:([0-9A-Za-z]+)\]/;

class CaseComments extends React.Component {

    render() {
        const comments = this.props.comments;
        if (!comments) return null;
        if (!comments.length) return (<p>No comments posted</p>);
        return (
            <div className={cx('case-comments')}>
                {comments
                    .sort((b,a) => moment(a.createdDate).valueOf() - moment(b.createdDate).valueOf())
                    .map(comment => <CommentItem key={comment.id} comment={comment} />)
                }
            </div>
        );
    }

}

CaseComments.propTypes = {
    comments: PropTypes.array,
};

export default CaseComments;

