import React, { Component } from 'react';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import Card from '@infosight/elmer/dist/components/Card';
import BannersView from '../../../../app/modules/tools/tools_central/modules/banners/BannersView';
import labelsStyle from '../../nimble/legacyStyles/labels.scss';
import '../../../../sass/toolsCentral/modules/banners/_banners.scss';
import { awaitLegacyApp } from '../../../containers/NimbleLegacyContainer';

const cx = classnames.bind(labelsStyle);

class BulletinsAdminPage extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    componentWillUnmount() {
        if (this.view) {
            const { view } = this;
            window.setTimeout(() => view.destroy());
        }
    }

    renderView(el) {
        if (!this.view) {
            this.view = new BannersView(this.props);
            this.view.setElement(el).render();
        }
    }

    render() {
        return (
            <Card title="Bulletin Management">
                <div className={classnames(cx('legacy-labels'))} ref={this.renderView} />
            </Card>
        );
    }
}

export default awaitLegacyApp(BulletinsAdminPage);
