export const sortThis = (a, b, direction) => {
    if (a < b) {
        return direction === 'asc' ? -1 : 1;
    } else if (a > b) {
        return direction === 'asc' ? 1 : -1;
    }
    return 0;
};

export const compareVersion = (a, b) => {
    // strip out version number
    const nameA = a.replace(/\s*\d(\.\d)*\.x\s*/, '');
    const nameB = b.replace(/\s*\d(\.\d)*\.x\s*/, '');

    // strip out all letters and just keep number
    const numA = a.replace(/[^\d]/g, '');
    const numB = b.replace(/[^\d]/g, '');

    // NOS versions come first (newest to oldest)
    // then other named versions alpabetically (newest to oldest)
    // then numbered versions (newest to oldest)

    if (nameA === 'nos' && nameB === 'nos') {
        return sortThis(numA, numB, 'desc');
    }
    if (nameA === 'nos' && nameB !== 'nos') {
        return -1;
    }
    if (nameA !== 'nos' && nameB === 'nos') {
        return 1;
    }
    if (nameA !== '' && nameB !== '') {
        if (nameA === nameB) {
            return sortThis(numA, numB, 'desc');
        }
        return sortThis(nameA, nameB, 'asc');
    }
    if (nameA !== '' && nameB === '') {
        return -1;
    }
    if (nameA === '' && nameB !== '') {
        return 1;
    }
    return sortThis(numA, numB, 'desc');
};
