const $ = require('jquery');
const _ = require('underscore');
const { buildUrlFromBase } = require('progeny/util/url');
const { buildUrl } = require('@infosight/elmer/dist/utils/url');
var Marionette = require('backbone.marionette');
var AsyncView = require('app/common/AsyncView');
var VolumeForecastBuckets = require('./VolumeForecastBuckets');

var tpl = require('raw-loader!./VolumeCapacityDashboardView.html');

var View = Marionette.ItemView.extend({

    template: _.template(tpl),

    animateOnLoad: false,

    templateHelpers: function () {
        return {
            _,
            getVolumeFilterLink: function (filter) {
                // TODO: Add filters back to this URL when Elmer's Table is enhanced to have Range Filters
                return `${buildUrlFromBase('/infrastructure/storage/nimble/volumes')}?fieldSet=space&${(filter)}`;
            },
            volumeBuckets: this.volumeBuckets.toJSON(),
        };
    },

    className: 'dashboard-volume-capacity-view',

    ui: {
        info: '.info',
    },

    initialize: function () {
        this.volumeBuckets = new VolumeForecastBuckets();
    },

    onRender: function () {
        this.ui.info.popover({
            trigger: 'hover',
            content: 'Timeframes are estimated',
        });
        // Disable links when count is 0
        this.$('a[data-count="0"]').attr('href', null);
    },
});

module.exports = AsyncView.extend({
    ViewConstructor: View,

    useSpinner: false,

    load: function () {
        this.deferred = $.Deferred();

        $.when(
            this.view.volumeBuckets.fetch(),
        )
            .fail(_.bind(this.deferred.reject, this.deferred))
            .done(_.bind(this.deferred.resolve, this.deferred, this.view));

        return this.deferred;
    },

});
