import React from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';

import Spinner from '@infosight/elmer/dist/components/Spinner';

import DateTime from '../../../../components/DateTime';
import { grabKcsDocument } from '../../library/support-api';

import style from './KcsDocument.scss';

const cx = classnames.bind(style);

class KcsDocument extends React.Component {
    constructor(props) {
        super(props);
        autobind(this);
        this.state = {
            kcsDocument: null,
        };
    }

    componentWillMount() {
        try {
            grabKcsDocument(this.props.docId)
                .done((kcsDocument) => {
                    this.setState({ kcsDocument });
                })
                .error((x) => {
                    if (x.responseJSON.error) {
                        this.props.onError({
                            message: 'Since your initial query, the solution you are trying to view ' +
                                'has been withdrawn from public access. You will be now be redirected to the ' +
                                'InfoSight portal, where you may search for similar knowledge under the ' +
                                '"Resources  Documentation" tab.',
                        });
                    }
                });
        } catch (e) {
            console.log('err');
        }
    }

    render() {
        const kcsDoc = this.state.kcsDocument;

        if (!kcsDoc) {
            return <Spinner />;
        }

        return (
            <div className={cx('kcs-document')}>
                <div className="callout">
                    <h2>{kcsDoc.title}</h2>
                    <p>Created on: <DateTime date={kcsDoc.created_at} format="MM/DD/Y HH:mm" /></p>
                </div>
                <div className={cx('kcs-body-text')}>
                    <p
                        dangerouslySetInnerHTML={{ __html: kcsDoc.body_html }} // eslint-disable-line react/no-danger
                    />
                </div>
            </div>
        );
    }
}

KcsDocument.propTypes = {
    docId: PropTypes.number.isRequired,
    onError: PropTypes.func,
};

export default KcsDocument;
