const _ = require('underscore');
const Backbone = require('backbone');
var Marionette = require('backbone.marionette');
var stickit = require('backbone.stickit');
var trackit = require('backbone.trackit');
var sprintf = require('sprintf-js').sprintf;
var FormBehavior = require('app/common/FormBehavior');
var AsyncView = require('app/common/AsyncView');
var MaintenanceWindowFormView = require('./MaintenanceWindowFormView');
var MaintenanceWindowDetailsView = require('./MaintenanceWindowDetailsView');
var MaintenanceWindowSidePanelView = require('./MaintenanceWindowSidePanelView');
var MaintenanceWindowModel = require('./MaintenanceWindowModel');
var MaintenanceWindowCollection = require('./MaintenanceWindowCollection');
var tpl = require('raw-loader!./MaintenanceWindowView.html');
var emptyDetailsTpl = require('raw-loader!./MaintenanceWindowEmptyDetailsView.html');
const App = require('app/Application');

var EmptyDetailsView = Marionette.ItemView.extend({
    template: _.template(emptyDetailsTpl),
    className: 'empty-details',
});

var View = Marionette.LayoutView.extend({

    className: 'maintenance-window-view',

    template: _.template(tpl),

    regions: {
        sidePanelRegion: '#side-panel-region',
        detailsPanelRegion: '#details-panel-region',
    },

    ui: {
        newButton: 'button[name="new"]',
    },

    events: {
        'click @ui.newButton': 'onClickNew',
    },

    initialize: function () {
        this.collection = new MaintenanceWindowCollection();
        this.sidePanelView = new MaintenanceWindowSidePanelView({
            collection: this.collection,
        });
        this.listenTo(this.sidePanelView, 'childview:selected', this.onItemSelect);
    },

    onRender: function () {
        var self = this;

        this.sidePanelRegion.show(this.sidePanelView);
        this.showEmptyDetailsView();
    },

    showEmptyDetailsView: function () {
        this.detailsPanelRegion.show(new EmptyDetailsView({
            model: new Backbone.Model({
                maintenanceWindowCount: this.collection.length,
            }),
        }));
    },

    showDetailsView: function (model) {
        var detailsView = new MaintenanceWindowDetailsView({
            model: model,
        });
        this.listenTo(detailsView, 'edit', this.onItemEdit);
        this.listenTo(detailsView, 'delete', this.onItemDelete);
        this.detailsPanelRegion.show(detailsView);
    },

    showFormView: function (model) {
        model.startTracking();
        var formView = new MaintenanceWindowFormView({
            model: model,
        });
        this.listenTo(formView, 'cancel', this.onFormCancel);
        this.listenTo(formView, 'submit', this.onFormSubmit);
        this.detailsPanelRegion.show(formView);
    },

    onItemSelect: function (itemView) {
        this.showDetailsView(itemView.model);
    },

    onItemEdit: function (detailsView) {
        this.showFormView(detailsView.model);
    },

    onItemDelete: function (detailsView) {
        var model = detailsView.model;
        model.destroy()
            .done(function (response) {
                App.command('notification', 'show:success', {
                    title: sprintf('Successfully deleted maintenance window "%s"', model.get('name')),
                });
            })
            .fail(function (jqxhr) {
                App.command('notification', 'show:error', {
                    title: sprintf('Error deleting maintenance window "%s"', model.get('name')),
                    message: jqxhr.responseJSON.message,
                });
            });

        this.showEmptyDetailsView();
    },

    onFormCancel: function (formView) {
        var model = formView.model;
        model.resetAttributes();
        if (model.isNew()) {
            this.showEmptyDetailsView();
        } else {
            this.showDetailsView(model);
        }
    },

    onFormSubmit: function (formView) {
        var self = this;
        var model = formView.model;
        var wasNew = model.isNew();

        // trackStartTime and trackEndTime will always have the correct timestamp
        model.set('startTime', model.get('trackStartTime'));
        model.set('endTime', model.get('trackEndTime'));

        // these are needed just for form values and validation
        model.unset('trackStartTime');
        model.unset('trackEndTime');
        model.unset('startTimeOnly');
        model.unset('endTimeOnly');

        // Proactively show the details view for the new model
        // If the save fails (unlikely), we'll replace it with the EmptyDetailsView on failure
        this.showDetailsView(model);

        model.save()
            .done(function (response) {
                App.command('notification', 'show:success', {
                    title: sprintf('Successfully %s maintenance window "%s"',
                        wasNew ? 'created' : 'updated', model.get('name')),
                });
                model.collection.add(model);
                self.sidePanelView.selectOne(model);
            })
            .fail(function (jqxhr) {
                App.command('notification', 'show:error', {
                    title: sprintf('Error %s maintenance window "%s"',
                        wasNew ? 'creating' : 'updating', model.get('name')),
                    message: jqxhr.responseJSON.message,
                });

                self.showEmptyDetailsView();
            });
    },

    onClickNew: function () {
        this.sidePanelView.unselectAll();
        this.showFormView(new MaintenanceWindowModel({}, {
            collection: this.collection,
        }));
    },

    openPrepopulatedForm: function (arraySn) {
        this.showFormView(new MaintenanceWindowModel({
            arrays: [arraySn],
        }, {
            collection: this.collection,
        }));
    },

});

module.exports = AsyncView.extend({
    ViewConstructor: View,

    openPrepopulatedForm: function (arraySn) {
        if (this.view.isRendered) {
            this.view.openPrepopulatedForm(arraySn);
        } else {
            this.listenToOnce(this.view, 'render', this.view.openPrepopulatedForm.bind(this.view, arraySn));
        }
    },
});
