const _ = require('underscore');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');
var Backbone = require('backbone');

module.exports = Backbone.View.extend(
    /** @lends views/components/dashboard/SpaceUsageChart.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/dashboard/SpaceUsageChart
         * @constructor
         * @augments Backbone.View
         */


        className: 'space-usage-chart',

        renderChart: function (container, series) {
            var self = this;
            this.chart = new Highcharts.Chart({
                chart: {
                    type: 'pie',
                    renderTo: container,
                    height: 250,
                    width: 300,
                    events: {
                        load: function () {
                            var chart = this;

                            // reflow the chart to ensure it fills the container
                            _.delay(function () {
                                chart.reflow.call(chart);
                            }, 50);
                        },
                    },
                },
                title: {
                    text: null,
                },
                xAxis: {
                    type: 'category',
                },
                yAxis: {
                    title: {
                        text: 'Data (TiB)',
                    },
                    labels: {
                        overflow: 'justify',
                    },
                },
                credits: {
                    enabled: false,
                },
                legend: {
                    enabled: true,
                },
                tooltip: {
                    useHTML: true,
                    formatter: function () {
                        var point = this.point,
                            series = this.series,
                            pctSaved = '';

                        // if ( point.name != 'Traditional Storage' ) {
                        //     var traditionalSpace = self.model.get('traditionalSpace'),
                        //         pct = Highcharts.numberFormat((this.y / traditionalSpace * 100), 1);
                        //     pctSaved = ' (' + pct + '%)';
                        // }

                        return '<span style="color:' + point.color + '">' + point.name + '</span>' +
                            ':</br><b>' + Highcharts.numberFormat(point.y, 2) + ' TiB</b>' + pctSaved + '<br/>';
                    },//,
                    // pointFormat: '<span style="color:{series.color}">{series.name}</span>: <b>{point.y} TiB</b><br/>'
                },
                plotOptions: {
                    pie: {
                        dataLabels: {
                            enabled: false,//,
                            // size: 50
                        },
                        showInLegend: true,
                    },
                },
                series: series,
            });
        },

        render: function () {
            this.$el.html();

            var series = {
                name: 'Space Usage',
                data: [
                    // ['Free', this.model.get('free')],
                    {
                        name: 'Volume Usage',
                        y: this.model.get('volumes'),
                        sliced: true,
                        selected: true,
                    }, {
                        name: 'Snapshots',
                        y: this.model.get('snapshots'),
                        color: '#78c639',//,
                        // sliced: true,
                        // selected: true
                    }, {
                        name: 'Free',
                        y: this.model.get('free'),
                        color: '#CCC',
                    },
                ],
            };

            // if ( this.model.get('compression') > 0 ) {
            //     series.data.push({
            //         name: 'Compression Savings',
            //         y: this.model.get('compression'),
            //         // color: '#2f7ed8'
            //         color: 'rgba(47, 126, 216, 0.5)'
            //     });
            // }

            // if ( this.model.get('thinProvisionedSpace') > 0 ) {
            //     series.data.push({
            //         name: 'Thin Provisioned',
            //         y: this.model.get('thinProvisionedSpace'),
            //         // color: '#2f7ed8'
            //         color: 'rgba(47, 126, 216, 0.7)'
            //     });
            // }

            // if ( this.model.get('zeroCopyClones') > 0 ) {
            //     series.data.push({
            //         name: 'Zero Copy Clones',
            //         y: this.model.get('zeroCopyClones'),
            //         // color: '#2f7ed8'
            //         color: 'rgba(47, 126, 216, 0.9)'
            //     });
            // }

            this.renderChart(this.el, [series]);

            return this;
        },

    });
