/* eslint-disable */
import Backbone from 'backbone';
import moment from 'moment-timezone';

export default Backbone.Model.extend({

    defaults: {

        // these are the header record fields (SFCase.java)
        id: null,
        createdDate: null,
        createdDateOnly: null,
        caseNumber: null,
        status: null,
        bucket: null,
        subject: null,
        type: null,

        assetId: null,
        accountId: null,
        createdById: null,
        contactId: null,
        priority: null,
        closedDate: null,
        Auto_Open__c: null,
        Auto_Close__c: null,
        owner: {},
        ownerName: null,
        caseLastComment__c: null,
        lastUpdatedDate: null,
        lastUpdatedTS: null,

        // these fields belong to the full record (SFCaseDetail.java)
        description: null,
        comments: null,
        reason: null,
        ownerId: null,
        suppliedEmail: null,
        origin: null,
        isEscalated: null,
        closed_By__c: null,
        casePortalResolutionNotes__c: '',
        caseClosureCaseReason__c: null,
        casCatClosureRootCause__c: null,
        caseClosureCategory__c: null,
        technology_Area__c: null,
        sub_Technology_Area__c: null
    },

    url: function() {
        if (!this.id) {
            return this.api().account().segment('cases').search({ userName: this.get('userName') }); // for POSTs
        }
        return this.api().account().segment('cases') + '/' + this.id; // for GETs
    },

    parse: function(md) {

        // normalize status
        switch (md.status) {
            /* falls through */
            // jshint ignore: start
            case 'Resolved':
                md.bucket = 'Resolved';
                break;

            case 'Closed':
            case 'Closed(Duplicate)':
                md.bucket = 'Closed';
                break;

            case 'New':
            case 'On Hold':
            case 'Open':
            case 'Awaiting Customer':
            case 'Awaiting Engineering':
            case 'Wait-Fix':
            case 'Handover':
            default:
                md.bucket = 'Open';
            // jshint ignore: end
        }

        // normalize dates
        // if (md.closedDate && typeof md.closedDate === 'string') {
        //     md.closedDate = moment(md.closedDate).toDate();
        // }
        // if (md.createdDate && typeof md.createdDate === 'string') {
        //     md.createdDate = moment(md.createdDate).toDate();
        // }
        // if (md.systemModstamp && typeof md.systemModstamp === 'string') {
        //     md.systemModstamp = moment(md.systemModstamp).toDate();
        // }

        // normalize resolution notes
        if (!md.casePortalResolutionNotes__c) {
            md.casePortalResolutionNotes__c = '';
        }

        // assign owner name
        if (md.owner) {
            const queueRegex = /queue/i;
            md.ownerName = md.owner.name.match(queueRegex) ? '(pending)' : md.owner.name;
            if (md.Auto_Close__c && md.ownerName === '(pending)') { md.ownerName = 'N/A'; }
        }

        // get date string
        md.createdDateOnly = moment(md.createdDate).format('YYYYMMDD');
        md.lastUpdatedDate = (md.caseLastComment__c) ? moment(md.caseLastComment__c).format('YYYYMMDD') : md.createdDateOnly;
        md.lastUpdatedTS = (md.caseLastComment__c) ? md.caseLastComment__c : md.createdDate;

        return md;
    },

    initialize: function(opt) {

    }

});
