const $ = require('jquery');
const App = require('app/Application');
const _ = require('underscore');
const { BASE_URL } = require('progeny/config');
const { buildUrl } = require('@infosight/elmer/dist/utils/url');

var Marionette = require('backbone.marionette');
var AsyncView = require('app/common/AsyncView');
var NimbleOsReleaseNotesCollection = require('./NimbleOsReleaseNotesCollection');
var tpl = require('raw-loader!./NimbleOsReleasesView.html');
var childTpl = require('raw-loader!./NimbleOsRelease.html');
var VirtualCollection = require('backbone-virtual-collection');
var moment = require('moment');

var ChildView = Marionette.ItemView.extend({
    template: _.template(childTpl),

    tagName: 'tr',

    templateHelpers: function () {
        return {
            _,
            moment,
            mediaUrl(...path) {
                return buildUrl(BASE_URL, 'media', ...path);
            },
            releaseNotes: this.model.get('releaseNotes') ? this.model.get('releaseNotes').toJSON() : null,
        };
    },
});

var View = Marionette.CompositeView.extend({

    template: _.template(tpl),

    childView: ChildView,

    childViewContainer: 'tbody',

    initialize: function () {
        this.releaseNotesCollection = new NimbleOsReleaseNotesCollection();
    },

    mergeCollections: function (releases) {
        var self = this;
        this.collection = new VirtualCollection(releases, {
            filter: function (release) {
                var status = release.attributes.displayStatus;
                var code = release.attributes.status.code;

                // https://nimblejira.nimblestorage.com/browse/IS-9978
                // All GA releases in a stream should appear (e.g., 2.3.16.0 and 2.3.9.2 since 2.3.9.2 is used as an update
                // hop from some older releases that cannot update to 2.3.16.0 directly).
                // An EOSS release that is still marked GA should appear.
                // For the same reason as above, these releases can be used as an update hop from previous releases.
                // If EOSS is not marked GA, it should not appear.
                if (code === 'GA' || code === 'LTSR') {
                    return true;
                }

                // If there is no GA release in the stream, then only an EA release with 'Status' populated with
                // "Initial Production Release" or "GA Candidate" should appear. For versions we want customers to see, there should always
                // be at least a GA version or an EA version with IPR/GAC status in a release stream.
                if (code === 'EA' && _(['Initial Production Release', 'GA Candidate']).contains(status)) {
                    return true;
                }

                // Deprecated and staging releases should not appear in this table.
                if (code === 'DEP' || code === 'STG') {
                    return false;
                }

                return false;
            },
        });

        this.collection.each(function (release) {
            // the old behavior didn't account for an optional 'NOS' prefix
            // so our findWhere becomes a filter...

            // var releaseNotes = self.releaseNotesCollection.findWhere({ version: release.get('shortVersion') });

            var shortVersion = release.get('shortVersion');
            var releaseNotes = self.releaseNotesCollection.filter(function (item) {
                var notesVersion = item.get('version');
                if (notesVersion == shortVersion || notesVersion == 'NOS ' + shortVersion) {
                    return item;
                }
            });

            if (releaseNotes) {
                release.set('releaseNotes', releaseNotes[0]); // only return the first result
            }
        });
    },

});

module.exports = AsyncView.extend({
    ViewConstructor: View,

    load: function () {
        return $.when(
            App.request('global', 'get:nimbleosreleases:async', 'jQuery.Deferred'),
            this.view.releaseNotesCollection.fetch(),
        )
            .then((releases) => {
                this.view.mergeCollections(releases);
                return this.view;
            });
    },
});
