import React, { Component } from 'react';
import autobind from 'react-autobind';
import Card from '@infosight/elmer/dist/components/Card';
import Async from '@infosight/elmer/dist/components/Async';
import AuditLogView from '../../../../app/modules/admin/auditlog/AuditLogView';
import Request from '../../../services/Request';
import style from './AuditLogView.scss';
import { awaitLegacyApp } from '../../../containers/NimbleLegacyContainer';

class AuditReportMarionetteWrapper extends Component {
    constructor(props) {
        super(props);
        autobind(this);

        // eslint-disable-next-line react/no-unused-state
        this.state = { data: null, error: null };
    }

    /* eslint-disable react/no-did-mount-set-state */
    async componentDidMount() {
        try {
            const { data } = await new Request().slash('auth').tenant().slash('audits')
                .get();
            // eslint-disable-next-line react/no-unused-state
            this.setState({ data, error: null });
        } catch (error) {
            // eslint-disable-next-line react/no-unused-state
            this.setState({ error });
        }
    }

    componentWillUnmount() {
        if (this.view) {
            const { view } = this;
            window.setTimeout(() => view.destroy());
        }
    }

    renderView(el) {
        if (!this.view) {
            this.view = new AuditLogView({ collection: this.state.data });
            this.view.setElement(el).render();
        }
    }

    render() {
        return (
            <Async loading={!this.state.data}>
                {() => (
                    <Card title="Audit Report">
                        <div className={style.container} ref={this.renderView} />
                    </Card>
                )}
            </Async>
        );
    }
}

export default awaitLegacyApp(AuditReportMarionetteWrapper);
