import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import { ClientControlledTable } from '@infosight/elmer/dist/components/Table';
import { connect } from 'react-redux';
import { userSelector } from '../../../user/reducer';

import RoleCell from './RoleCell';

class UserManagementGrid extends Component {
    constructor(props) {
        super(props);
        autobind(this);
        this.state = { modal: null };

        this.config = {
            categories: ['default'],
            columns: [
                {
                    Header: 'Login',
                    accessor: 'login',
                    filterable: true,
                },
                {
                    Header: 'Role',
                    accessor: 'role',
                    Cell: ({ original }) => <RoleCell user={original} />,
                },
            ],
        };
    }

    render() {
        const { users, userName } = this.props;
        const { modal } = this.state;

        return (
            <React.Fragment>
                <ClientControlledTable
                    tableId={`${userName}_NIMBLE_USERS`}
                    data={users || []}
                    columns={this.config.columns}
                    categories={this.config.categories}
                />
                {modal && React.createElement(modal.type, { ...modal, closeModal: () => this.setState({ modal: null }) })}
            </React.Fragment>
        );
    }
}

UserManagementGrid.propTypes = {
    users: PropTypes.array.isRequired,
    userName: PropTypes.string,
};

const mapStateToProps = state => ({
    userName: userSelector(state).username,
});

export default connect(mapStateToProps)(UserManagementGrid);
