// SupportExamineCase.js

import React from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import autobind from 'react-autobind';

import { Spinner } from '@infosight/elmer/dist/components/Spinner';

import { caseListSelector } from '../../reducer/support-cases';
import { fetchCase, fetchCases, fetchSfdcContact } from '../../library/support-api';
import { findCaseByNumber } from '../../library/support-helper';
import { awaitLegacyApp } from '../../../../containers/NimbleLegacyContainer';
import { contactSelector } from '../../reducer/core';
import { postCaseCommentOk, postCaseResolveOk, postCaseAttachmentOk } from '../../actions/fetch-support-cases';

import KcsDocumentModal from '../KcsDocumentModal';
import SupportTab from '../SupportTab';

import CaseNotFound from '../CaseNotFound';
import CaseInformation from './CaseInformation';

class SupportExamineCase extends React.Component {
    constructor(props) {
        super(props);
        autobind(this);
        this.state = { kcsModal: null };
    }

    componentDidMount() {
        const { cases, match } = this.props;

        const {
            caseNumber,
        } = match.params;

        // TODO this should not be done in this method. Take care of it in render(). It seems it might already be done
        if (!caseNumber) {
            console.error('no case number'); // todo: human-friendly error helper
            return;
        }

        const { contact } = this.props;
        if (!contact || (!contact.loaded && !contact.loading)) {
            this.props.fetchSfdcContact();
        }

        // TODO what happens when there is an error fetching cases?
        if (!cases.value) {
            this.props.fetchCases().done(this.requestFullCase);
            return;
        }

        this.requestFullCase();
    }

    requestFullCase() {
        const { cases, match } = this.props;

        const {
            caseNumber,
            docId,
        } = match.params;

        if (!cases.loaded) return;

        const theCase = findCaseByNumber(cases.value, caseNumber);
        if (!theCase) return; // cannot find either header or full record (presume case does not exist)

        if (docId) {
            this.setState({
                kcsModal: {
                    caseId: theCase.id,
                    docId: Number(docId),
                },
            });
        }

        // now we need to verify if we have only the header, or the full record
        // in theory, we could check for any field that only exists in the complete record
        // or we can define a semaphore (in this case $_complete)
        if (!theCase.description || !theCase.$_complete) {
            this.props.fetchCase(theCase.id);
        }
    }

    render() {
        const { match } = this.props;
        const { kcsModal } = this.state;
        const {
            caseNumber,
        } = match.params;

        const { cases } = this.props;
        if (!cases.loaded) return (<Spinner />);

        const theCase = findCaseByNumber(cases.value, caseNumber);

        return (
            <SupportTab>

                {theCase ?
                    <CaseInformation
                        theCase={theCase}
                        commentDispatch={this.props.postCaseCommentOkDispatch}
                        attachmentDispatch={this.props.postCaseAttachmentOkDispatch}
                        resolveDispatch={this.props.postCaseResolveOkDispatch}
                    /> :
                    <CaseNotFound />
                }
                {kcsModal && <KcsDocumentModal {...kcsModal} closeModal={() => this.setState({ kcsModal: null })} />}
            </SupportTab>
        );
    }
}

SupportExamineCase.propTypes = {
    cases: PropTypes.object,
    // caseNumber: PropTypes.string,
    fetchCase: PropTypes.func,
    fetchCases: PropTypes.func,
    match: PropTypes.object.isRequired,
    postCaseCommentOkDispatch: PropTypes.func,
    postCaseResolveOkDispatch: PropTypes.func,
    postCaseAttachmentOkDispatch: PropTypes.func,
    contact: PropTypes.object,
    fetchSfdcContact: PropTypes.func,
};

function mapStateToProps(state) {
    return {
        cases: caseListSelector(state),
        contact: contactSelector(state),
    };
}

const mapDispatchToProps = dispatch => bindActionCreators({
    fetchCase,
    fetchCases,
    fetchSfdcContact,
    postCaseCommentOkDispatch: postCaseCommentOk,
    postCaseResolveOkDispatch: postCaseResolveOk,
    postCaseAttachmentOkDispatch: postCaseAttachmentOk,
}, dispatch);

export default awaitLegacyApp(connect(mapStateToProps, mapDispatchToProps)(SupportExamineCase));
