const _ = require('underscore');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');
var Backbone = require('backbone');

module.exports = Backbone.View.extend(
    /** @lends views/components/dashboard/CaseStatisticsDonutView.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/dashboard/CaseStatisticsDonutView
         * @constructor
         * @augments Backbone.View
         */


        className: 'case-statistics-donut-view',

        renderChart: function (container, series, percentAutoClosed) {
            var self = this;
            this.chart = new Highcharts.Chart({
                chart: {
                    type: 'pie',
                    renderTo: container,
                    height: '300',
                    // width: 200,
                    events: {
                        load: function () {
                            var chart = this;

                            // reflow the chart to ensure it fills the container
                            _.delay(function () {
                                chart.reflow.call(chart);
                            }, 50);
                        },
                    },
                },
                title: {
                    text: '<span style="color: #78c639">' + percentAutoClosed + '%' +
                        '</span><br/><span>Auto-Closed</span>',
                    align: 'center',
                    verticalAlign: 'middle',
                    y: 3,
                },
                xAxis: {
                    type: 'category',
                },
                yAxis: {
                    title: {
                        text: 'Data (TiB)',
                    },
                    labels: {
                        overflow: 'justify',
                    },
                },
                credits: {
                    enabled: false,
                },
                legend: {
                    enabled: false,
                },
                tooltip: {
                    useHTML: true,
                    formatter: function () {
                        var point = this.point,
                            series = this.series,
                            pctAutoClosed = '';

                        var totalCases = self.model.get('totalCases'),
                            pct = Highcharts.numberFormat((this.y / totalCases * 100), 0);
                        pctAutoClosed = ' (' + pct + '%)';

                        return '<span style="color:' + point.color + '">' + point.name + '</span>' +
                            ':</br><b>' + Highcharts.numberFormat(point.y, 0) + '</b>' + pctAutoClosed + '<br/>';
                    },//,
                    // pointFormat: '<span style="color:{series.color}">{series.name}</span>: <b>{point.y} TiB</b><br/>'
                },
                plotOptions: {
                    pie: {
                        dataLabels: {
                            enabled: false,
                            // distance: -50,
                            //
                            size: 50,
                            style: {
                                // fontWeight: 'bold',
                                // color: 'white',
                                // textShadow: '0px 1px 2px black'
                            },
                        },
                        // startAngle: -90,
                        // endAngle: 90,
                        center: ['50%', '50%'],
                    },
                },
                series: series,
            });
        },

        render: function () {
            this.$el.html();

            var percentAutoClosed = 0,
                series = {
                    name: 'Space Savings',
                    innerSize: '75%',
                    data: [],
                };

            percentAutoClosed = this.model.get('automaticallyClosed') /
                this.model.get('totalCases') * 100;

            if (this.model.get('automaticallyClosed') > 0) {
                series.data.push({
                    name: 'Automatically Closed',
                    y: this.model.get('automaticallyClosed'),
                    // color: '#2f7ed8'
                    color: 'rgba(47, 126, 216, 0.7)',
                });
            }

            if (this.model.get('manuallyClosed') > 0) {
                series.data.push({
                    name: 'Manually Closed',
                    y: this.model.get('manuallyClosed'),
                    // color: '#2f7ed8'
                    // color: 'rgba(47, 126, 216, 0.5)'
                    color: 'rgba(13, 35, 58, 0.75)',
                });
            }

            this.renderChart(this.el, [series],
                Highcharts.numberFormat(percentAutoClosed, 0));

            return this;
        },

    });
