// CaseSection.js

import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';

import style from './CaseSection.scss';

const cx = classnames.bind(style);


/**
 * Wraps case content with a header
 */
function CaseSection(props) {
    const { name, children, text } = props;
    return (
        <div id={name} className={cx('case-section')}>

            <h2>{name}</h2><span className={cx('sub-text')}>{text}</span><div className={cx('extend-line')} />

            <div className={cx('child-content')}>
                {children}
            </div>
        </div>
    );
}

CaseSection.propTypes = {
    name: PropTypes.string,
    text: PropTypes.string,
    children: PropTypes.node,
};

export default CaseSection;
