import React, { Component } from 'react';
import autobind from 'react-autobind';
import SupportMatrixView from '../../../../app/modules/supportmatrix/SupportMatrixView';
import { awaitLegacyApp } from '../../../containers/NimbleLegacyContainer';

class ValidatedConfigMatrixMarionetteWrapper extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    componentWillUnmount() {
        if (this.view) {
            const { view } = this;
            window.setTimeout(() => view.destroy());
        }
    }

    renderView(el) {
        if (!this.view) {
            this.view = new SupportMatrixView(this.props);
            this.view.setElement(el).render();
        }
    }

    render() {
        return <div ref={this.renderView} />;
    }
}

export default awaitLegacyApp(ValidatedConfigMatrixMarionetteWrapper);
