import React from 'react';
import PropTypes from 'prop-types';
import { Route, Switch, NavLink } from 'react-router-dom';
import ScrollContainer from '@infosight/elmer/dist/components/ScrollContainer';
import { StaticBold } from '@infosight/elmer/dist/components/StaticBold';
import { SecondaryNavBar, NavItem, Title } from '@infosight/elmer/dist/components/SecondaryNavBar';
import { pageBoundaryRouteRenderer } from '@infosight/elmer/dist/page';
import { buildUrl } from '@infosight/elmer/dist/utils/url';
import OperationalDashboard from './operationalDashboard/components';
import ExecutiveDashboard from './executiveDashboard/components';
import WellnessDashboard from './wellnessDashboard/components';
import Labs from './labs/components';
import ResellerDashboard from './resellerDashboard/components';
import CapacityContainer from '../capacityReport/components/containers/CapacityContainer';
import NimbleUserTenantHelpers from './tenantSelection/NimbleUserTenantHelpers';

const NimbleDashboardsRouter = ({ match }) => {
    const objects = [
        { label: 'Reseller', url: 'resellerdashboard', component: ResellerDashboard, visible: NimbleUserTenantHelpers.isReseller() },
        { label: 'Operational', url: 'operational', component: OperationalDashboard, visible: true },
        { label: 'Executive', url: 'executive', component: ExecutiveDashboard, visible: true },
        { label: 'Wellness', url: 'wellness', component: WellnessDashboard, visible: true },
        { label: 'Capacity', url: 'capacity', component: CapacityContainer, visible: true },
        { label: 'Labs', url: 'labs', component: Labs, visible: true },
    ];

    return (
        <ScrollContainer>
            <ScrollContainer.Fixed>
                <SecondaryNavBar>
                    <Title name="Nimble Storage" className="align-center" />
                    {objects.filter(x => x.visible).map(({ label, url }) => (
                        <NavItem key={url}>
                            <NavLink className="flex-container align-center flex-dir-column" to={buildUrl(match.url, url)}>
                                <StaticBold parent={React.Fragment}>{label}</StaticBold>
                            </NavLink>
                        </NavItem>
                    ))}
                </SecondaryNavBar>
            </ScrollContainer.Fixed>

            <ScrollContainer.Scrollable>
                <Switch>
                    {objects.map(({ url, component, label }) => {
                        const title = component === Labs ? 'Labs' : `${label} Dashboard`;
                        return <Route key={url} path={buildUrl(match.path, url)} render={pageBoundaryRouteRenderer(component, title)} />;
                    })}
                    <Route render={pageBoundaryRouteRenderer(objects[0].component, objects[0].title)} />
                </Switch>
            </ScrollContainer.Scrollable>
        </ScrollContainer>
    );
};

NimbleDashboardsRouter.propTypes = {
    match: PropTypes.object.isRequired,
};

export default NimbleDashboardsRouter;
