const _ = require('underscore');
var URI = require('urijs');
var Marionette = require('backbone.marionette');
var esClient = require('app/modules/elasticsearch/ElasticsearchClient');
var SearchForm = require('./SearchForm');
var QueryModel = require('./QueryModel');
var HitCollection = require('./HitCollection');
var FacetCollection = require('./FacetCollection');
var ResultsView = require('./ResultsView');
var EmptyHitsView = require('./EmptyHitsView');
var HomeView = require('./HomeView');
var FacetPanel = require('./FacetPanel');
var LanguagePreferenceModel = require('./LanguagePreferenceModel');
var LanguagePreferenceView = require('./LanguagePreferenceView');
var tpl = require('raw-loader!./DocumentationView.html');

var MAX_RESULTS = 500;

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    className: 'documentation-view',

    regions: {
        languageRegion: '[data-region="language"]',
        searchRegion: '[data-region="search"]',
        mainRegion: '[data-region="main"]',
        facetsRegion: '[data-region="facets"]',
    },

    ui: {
        spinner: '.spinner',
        results: '.search-results',
    },

    initialize: function () {
        _.bindAll(this, 'processHits', 'processFacets');

        this.query = new QueryModel();
        this.facets = new FacetCollection();
        this.languageFilter = new LanguagePreferenceModel();
        this.languagePreferenceView = new LanguagePreferenceView();
    },

    onRender: function () {
        this.languageRegion.show(this.languagePreferenceView);
        this.searchRegion.show(new SearchForm({ model: this.query }));
        this.facetsRegion.show(new FacetPanel({ collection: this.facets }));

        this.listenTo(this.query, 'update', this.doQuery);
        this.listenTo(this.facets, 'update', this.doQuery);
        this.listenTo(this.facets, 'change', this.doQuery);
        //this.listenTo(this.facets, 'reset', this.onFacetChange);
        this.listenTo(this.languagePreferenceView.model, 'change', this.doQuery);

        this.doQuery();
    },

    doQuery: function () {
        var self = this;

        var params = _.extend({},
            this.query.toQueryParamMap(),
            this.facets.toQueryParamMap(),
        );

        const history = this.getOption('history');
        const match = this.getOption('match');
        const uri = new URI(match.url).setQuery(params).valueOf();

        if (!URI(uri.valueOf()).equals(URI(`${history.location.pathname}${history.location.search}`))) {
            history.replace(uri);
        }

        this.ui.results.hide();
        this.ui.spinner.show();
        this.mainRegion.empty();

        esClient.searchTemplate({
            index: 'docs',
            template: 'docs',
            params: {
                size: this.hasQueryOrFilter() ? MAX_RESULTS : 0,
                term: this.query.get('term'),
                languages: this.languagePreferenceView.model.get('languages'),
                filters: this.facets.reduce(function (memo, facet) {
                    var bucket = facet.getSelectedBucket();
                    if (bucket) {
                        var termFilter = {};
                        termFilter[facet.get('field')] = bucket.get('value');
                        memo.push({ term: termFilter });
                    }
                    return memo;
                }, []),
            },
        })
            .done(this.processFacets)
            .done(this.processHits)
            .always(function () {
                self.ui.spinner.hide();
                self.ui.results.show();
            });
    },

    hasQueryOrFilter: function () {
        return this.query.get('term') || this.facets.hasSelected();
    },

    processHits: function (response) {

        // Create a new collection here instead of resetting since Backbone.Paginator doesn't handle reset properly
        // See https://github.com/backbone-paginator/backbone.paginator/issues/265
        var hits = new HitCollection(response.hits.hits, { parse: true });

        var mainView;
        if (!this.hasQueryOrFilter()) {
            mainView = new HomeView();
        } else {
            mainView = new ResultsView({
                query: this.query,
                facets: this.facets,
                hits: hits,
            });
        }
        this.mainRegion.show(mainView);
    },

    processFacets: function (response) {
        var self = this;
        this.facets.reset(
            _.chain(response.aggregations)
                .map(function (aggregation, aggName) {
                    var facet = self.facets.findWhere({ field: aggName });
                    return {
                        field: aggName,
                        buckets: _.chain(aggregation.buckets)
                            .map(function (bucket) {
                                return {
                                    value: bucket.key,
                                    count: bucket.doc_count,
                                    selected: facet && facet.isSelected(bucket.key),
                                };
                            })
                            // Include selected buckets even if there are no documents within that bucket in the response.
                            // This handles the IS-7559 case where a search/filter combination may yield no results,
                            // but the filter selection must remain visible
                            .union(!facet ? [] : facet.getSelectedBuckets().map(function (bucketModel) {
                                return bucketModel.toJSON();
                            }))
                            // Need to dedupe buckets in the (normal) case where there are documents within the selected bucket
                            .unique(false, 'value')
                            .value(),
                    };
                })
                .filter(function (facet) {
                    return !_.isEmpty(facet.buckets);
                })
                .value(),
            { parse: true });
    },

    setParams: function (params) {
        this.query.set('term', params.term);
        delete params.term;
        this.facets.reset(_.map(params, function (bucket, facet) {
            return {
                field: facet,
                buckets: [
                    {
                        value: bucket,
                        selected: true,
                    },
                ],
            };
        }), { parse: true });
        if (this.isRendered) {
            this.doQuery();
        }
    },
});
