const _ = require('underscore');
var Backbone = require('backbone');
var esClient = require('app/modules/elasticsearch/ElasticsearchClient');
var CategoryModel = require('./CategoryModel');

module.exports = Backbone.Collection.extend({

    model: CategoryModel,

    fetch: function () {
        return esClient.searchTemplate({
            index: 'software',
            template: 'software',
        }).done(this.load.bind(this));
    },

    load: function (response) {
        var categories = _.chain(response.hits.hits)
            .pluck('_source')
            .groupBy('category')
            .reduce(function (memo, products, category) {
                memo.push({
                    name: category,
                    products: products,
                });
                return memo;
            }, [])
            .value();
        this.reset(categories, { parse: true, prioritizedCategories: this.prioritizedCategories });
    },

    unselectAllProducts: function () {
        this.pluck('products').forEach(function (products) {
            products.forEach(function (product) {
                product.set('selected', false);
            });
        });
    },

    flattenToProductVersions: function () {
        return this.pluck('products').reduce(function (result, products) {
            return products.pluck('versions').reduce(function (result, productVersions) {
                return result.concat(productVersions.map(function (productVersion) {
                    var model = productVersion.clone();
                    model.set('product', productVersion.collection.product);
                    return model;
                }));
            }, result);
        }, []);
    },

    comparator: function (model) {
        const index = this.prioritizedCategories.findIndex(x => x.name === model.get('name'));
        return index === -1 ? Number.POSITIVE_INFINITY : index;
    },

});
