const App = require('app/Application');
const _ = require('underscore');
var Marionette = require('backbone.marionette');
var ProductVersionTable = require('./ProductVersionTable');
var ProductVersionCollection = require('./ProductVersionCollection');
var EulaView = require('./EulaView');
var ProductDocumentationView = require('./ProductDocumentationView');
var ProductDocumentCollection = require('./ProductDocumentCollection');
var tpl = require('raw-loader!./ProductView.html');
var UserPreferencesController = require('app/modules/preferences/UserPreferencesController');

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    className: 'product-view animated fadeIn',

    ui: {
        otherVersionsToggle: 'a[href="#otherVersionsContainer"]',
        otherVersionsToggleContainer: '.other-versions-link-container',
    },

    events: {
        'show.bs.collapse #otherVersionsContainer': 'onShowOtherVersions',
        'hide.bs.collapse #otherVersionsContainer': 'onHideOtherVersions',
    },

    regions: {
        documentation: '[data-region="documentation"]',
        currentVersion: '[data-region="currentVersion"]',
        otherVersions: '[data-region="otherVersions"]',
    },

    initialize: function () {
        this.documents = new ProductDocumentCollection([], {
            productName: this.model.get('name'),
        });
    },

    onRender: function () {
        var session = App.request('global', 'get:current:session');
        if (this.model.get('hasEula') &&
            session.isExternal() &&
            !UserPreferencesController.isEulaAccepted(this.model.get('name'))) {

            this.showEulaView();
        } else {
            this.showProductView();
        }
    },

    showProductView: function () {
        var self = this;
        this.$el.show();
        // TODO enforce same column widths for both tables ?

        var versions = this.model.get('versions');
        var defaultVersions = new ProductVersionCollection(versions.where({ 'default': true }), { product: this.model });
        var otherVersions = new ProductVersionCollection(versions.where({ 'default': false }), { product: this.model });

        this.showChildView('currentVersion', new ProductVersionTable({
            collection: defaultVersions,
        }));
        this.showChildView('otherVersions', new ProductVersionTable({
            collection: otherVersions,
        }));
        this.ui.otherVersionsToggleContainer.toggle(!otherVersions.isEmpty());

        this.documents.fetch().done(function () {
            if (!self.documents.isEmpty()) {
                self.showChildView('documentation', new ProductDocumentationView({
                    collection: self.documents,
                }));
            }
        });
    },

    showEulaView: function () {
        this.$el.hide();
        var view = new EulaView({
            product: this.model,
        });
        this.listenTo(view, 'accept', this.showProductView);
        App.modalRegion.show(view);
    },

    onShowOtherVersions: function () {
        this.ui.otherVersionsToggle.text('Hide other versions');
    },

    onHideOtherVersions: function () {
        this.ui.otherVersionsToggle.text('Show other versions');
    },

});
