import React from 'react';
import autobind from 'react-autobind';
import { ObjectIcon } from '@infosight/elmer/dist/infrastructure';
import URI from 'urijs';
import SearchWorker from './search.worker';

const IS_READY_CHECK_INTERVAL_MS = 2000;

export default class LocalObjectSearchSubject {
    constructor({ productInterface, OBJECTS }) {
        autobind(this);

        // Private
        this.productInterface = productInterface;
        this.turn = 0;
        this.worker = new SearchWorker();
        this.worker.addEventListener('message', this.onReceiveResults);

        // Public
        this.id = `${productInterface.id.toLowerCase()}-infrastructure-objects`;
        this.name = productInterface.attributes.name;
        this.topics = Object.values(OBJECTS).map(objectType => ({
            id: objectType,
            singularName: this.productInterface.getSingularTitle(objectType),
            pluralName: this.productInterface.getPluralTitle(objectType),
            icon: <ObjectIcon objectType={objectType} />,
        }));
    }

    /**
     * @public
     */
    destroy() {
        if (this.worker) {
            this.worker.terminate();
        }
    }

    /**
     * @private
     */
    onReceiveResults(e) {
        // Manually stringifying result between worker and main thread is faster than allowing it to serialize
        const data = JSON.parse(e.data);

        // Make sure we do nothing because we don't care about this request.
        if (data.turn !== this.turn) {
            return;
        }

        if (!data || (!data.results && !data.error)) {
            this.handlers.noOp();
            return;
        }

        let hasResults = false;

        if (data.error) {
            this.handlers.error(data.error);
            return;
        }

        const hits = Object
            .entries(data.results)
            .reduce((memo, [type, ids]) => {
                const { productInterface } = this;
                if (!productInterface.hasDetailView(type)) {
                    return memo;
                }

                ids.forEach((id) => {
                    const object = productInterface.findObject(type, id);
                    if (!object) {
                        return;
                    }

                    const { pathname, search } = productInterface.getLinkPropsForView(productInterface.getDetailView(type), { id }, null, window.location).to;
                    memo.push({
                        id,
                        name: object.name,
                        topic: object.type,
                        url: new URI('').path(pathname).search(search).valueOf(),
                    });

                    hasResults = true;
                });

                return memo;
            }, [])
            .sort((a, b) => (a.name.toLowerCase() <= b.name.toLowerCase() ? -1 : 1));

        if (hasResults) {
            this.handlers.hits(hits);
        } else {
            this.handlers.noOp();
        }
    }

    /**
     * @public
     */
    search({ query, submit }) {
        this.turn += 1;
        this.handlers = submit;

        const tryUntilReady = () => {
            if (this.productInterface.isReadyForTopologySearch()) {
                this.worker.postMessage({ productFamily: this.productInterface.id, query, turn: this.turn });
            } else {
                window.setTimeout(tryUntilReady, IS_READY_CHECK_INTERVAL_MS);
            }
        };

        tryUntilReady();
    }
}
