/* eslint-disable spaced-comment, no-unused-vars */
import React from 'react';
import PropTypes from 'prop-types';
import { Route, Switch, NavLink } from 'react-router-dom';
import ScrollContainer from '@infosight/elmer/dist/components/ScrollContainer';
import { StaticBold } from '@infosight/elmer/dist/components/StaticBold';
import { SecondaryNavBar, NavItem, Title } from '@infosight/elmer/dist/components/SecondaryNavBar';
import { buildUrl } from '@infosight/elmer/dist/utils/url';
import { pageBoundaryRouteRenderer } from '@infosight/elmer/dist/page';
import Documentation from './documentation/DocumentationMarionetteWrapper';
import SoftwareDownloads from './documentation/SoftwareDownloadsMarionetteWrapper';
import ValidatedConfigMatrixMarionetteWrapper from './documentation/ValidatedConfigMatrixMarionetteWrapper';
import AuditReportMarionetteWrapper from './documentation/AuditReportMarionetteWrapper';
import SupportRouter from '../support/SupportRouter';
import VmarrayLicenseManagementContainer from './vmarray/VmarrayLicenseManagementContainer';
import { AuthorizationEvaluator, AUTHORITIES, nimbleThirdPartyPermissionRequestAccess } from '../user';
import NimbleUserTenantHelpers from './tenantSelection/NimbleUserTenantHelpers';

const objects = [
    {
        label: 'Support',
        url: 'support',
        component: SupportRouter,
        visible: true,
        access: {
            custom() {
                return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() && AuthorizationEvaluator.evaluate({
                    access: {
                        $or: [
                            {
                                authorities: AUTHORITIES.PRODUCT_FAMILY_NIMBLE,
                            },
                            {
                                authorities: [AUTHORITIES.SUPPORT_CASE],
                            },
                        ],
                    },
                });
            },
        },
    },
    {
        label: 'Documentation',
        url: 'docs',
        component: Documentation,
        visible: true,
        access: {
            authorities: [AUTHORITIES.PRODUCT_FAMILY_NIMBLE, AUTHORITIES.PRODUCT_FAMILY_STOREONCE, AUTHORITIES.PRODUCT_FAMILY_STORESERV],
        },
    },
    {
        label: 'Software Downloads',
        url: 'software',
        component: SoftwareDownloads,
        visible: true,
        access: {
            custom() {
                return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() && AuthorizationEvaluator.evaluate({
                    access: {
                        authorities: AUTHORITIES.PRODUCT_FAMILY_NIMBLE,
                    },
                });
            },
        },
    },
    {
        label: 'Virtual Array Licenses',
        url: 'vmarray',
        component: VmarrayLicenseManagementContainer,
        visible: true,
        access: {
            custom() {
                return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() && AuthorizationEvaluator.evaluate({
                    access: {
                        authorities: [AUTHORITIES.VIRTUAL_ARRAY_ACCESS],
                    },
                });
            },
        },
    },
    {
        label: 'Validated Configuration Matrix',
        url: 'validated-configuration-matrix',
        component: ValidatedConfigMatrixMarionetteWrapper,
        visible: true,
        access: {
            authorities: [AUTHORITIES.PRODUCT_FAMILY_NIMBLE, AUTHORITIES.REALM_INTERNAL],
        },
    },
    {
        label: 'Audit Report',
        url: 'audit-report',
        component: AuditReportMarionetteWrapper,
        visible: true,
        access: {
            custom() {
                return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() &&
                    AuthorizationEvaluator.evaluate({ access: nimbleThirdPartyPermissionRequestAccess })
                    && !AuthorizationEvaluator.evaluate({ access: { authorities: AUTHORITIES.ATTRIBUTE_NIMBLE_SUPPORT_PROVIDER } });
            },
        },
    },
];

// Go directly to SupportRouter where pageBoundaryRouteRenderer will
// be called from there.
// Need to use uniqKey prop so that PageBoundary (Analytics.trackPageView)
// will re-mount each time a support page is clicked.
const renderComponent = (RouteComponent, uniqKey) => props => (
    <RouteComponent key={uniqKey} {...props} />
);

const ResourcesRouter = ({ match, location }) => (
    <ScrollContainer>
        <ScrollContainer.Fixed>
            <SecondaryNavBar>
                <Title name="Nimble Storage" className="align-center" />
                {AuthorizationEvaluator.filter(objects).filter(x => x.visible).map(({ label, url }) => (
                    <NavItem key={url}>
                        <NavLink className="flex-container align-center flex-dir-column" to={buildUrl(match.url, url)}>
                            <StaticBold parent={React.Fragment}>{label}</StaticBold>
                        </NavLink>
                    </NavItem>
                ))}
            </SecondaryNavBar>
        </ScrollContainer.Fixed>

        <ScrollContainer.Scrollable>
            <Switch>
                {AuthorizationEvaluator.filter(objects).map(({ url, component, label }) => (
                    <Route
                        key={url}
                        path={buildUrl(match.path, url)}
                        render={label === 'Support'
                            ? renderComponent(component, location.key)
                            : pageBoundaryRouteRenderer(component, label)
                        }
                    />
                ))}
            </Switch>
        </ScrollContainer.Scrollable>
    </ScrollContainer>
);

ResourcesRouter.propTypes = {
    match: PropTypes.object.isRequired,
    location: PropTypes.object,
};

export default ResourcesRouter;
