const $ = require('jquery');
const _ = require('underscore');
var Marionette = require('backbone.marionette');
var tpl = require('raw-loader!./WellnessSummaryWrapper.html');
var wellnessSummaryTpl = require('raw-loader!./WellnessSummaryView.html');
var errorTpl = require('raw-loader!app/templates/components/error.html');
var WellnessSummaryModel = require('./WellnessSummaryModel');
var Spinner = require('app/views/components/Spinner');

var template = _.template(tpl);
var summaryTemplate = _.template(wellnessSummaryTpl, undefined, { variable: 'matrix' });

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    className: 'wellness-summary-view',

    ui: {
        wellnessContent: '.wellness-summary-content',
        wellnessSummaryEmpty: '.wellness-summary-empty',
        spinner: '.spinner',
    },

    events: {
        'click .highlight-badge': 'onCellClick',
    },

    initialize: function () {
        _.bindAll(this, 'onLoad', 'changeSummaryFilter');
    },

    onRender: function () {
        var self = this;

        this.$contentEl = this.$el.find('#wellness-summary-wrapper');

        this.ui.spinner.append(new Spinner().render().el);

        var handleError = function (response) {

            self.trigger('summary-error');
            var message = '';

            if (response.responseText && response.responseText.length > 0) {

                message = response.responseText;
            } else {
                message = 'Server currently unavailable.';
            }
            var errorTemplate = _.template(errorTpl);
            self.$contentEl.append(errorTemplate({
                message: message,
            }));
            $('.wellness-summary').find('.sub-title').hide();
            $('.wellness-summary').show();
        };

        this.model.fetch()
            .always(function () {
                self.ui.spinner.hide();
            })
            .done(this.onLoad)
            .fail(handleError);

        return this;
    },

    onLoad: function (results) {

        var modelJSON = this.model.toJSON();

        // show empty results
        if (_.keys(modelJSON).length === 0) {
            this.ui.wellnessContent.hide();
            this.ui.wellnessSummaryEmpty.show();
        }
        // show the results template
        else {
            this.ui.wellnessContent.show();
            this.ui.wellnessSummaryEmpty.hide();

            this.$contentEl.append(summaryTemplate(modelJSON));
            this.$('#summary-filter INPUT').change(this.changeSummaryFilter);
        }

        this.trigger('summary-load', results);
    },

    changeSummaryFilter: function () {
        var filter = this.$('#summary-filter INPUT').is(':checked') ? 'total' : 'unacked';
        this.$('.wellness-summary-table .highlight-badge').each(function () {
            var $this = $(this);
            var newCount = $this.data('count-' + filter);
            $this.attr('data-count', newCount);
            $this.find('SPAN.highlight-item-text').text(newCount);
        });
    },

    onCellClick: function (event) {
        event.preventDefault();
        var $cell = $(event.currentTarget);

        var filter = {
            'Severity': $cell.attr('data-severity'),
            'Target Type': $cell.attr('data-level'),
            'Acknowledged': this.$('#summary-filter INPUT').is(':checked') ? '' : 'No',
        };

        if (this.model.inventoryFilter && this.model.inventoryFilter.get('arraySn')) {
            filter['Array SN'] = this.model.inventoryFilter.get('arraySn');
        }

        this.options.history.push(`/dashboards/nimble/wellness/details?${$.param(filter)}`);
    },

});
