
import * as types from './constants';

/*
FILTER_CASE_STATUS
FILTER_CASE_PRIORITY
FILTER_CASE_PARAMS
*/

export function setCaseStatusFilter(caseStatus) {
    return {
        type: types.FILTER_CASE_STATUS,
        caseStatus,
    };
}

export function setCasePriorityFilter(casePriority) {
    const priority = parseInt(casePriority, 10) || 0;
    return {
        type: types.FILTER_CASE_PRIORITY,
        priority,
    };
}

export function setCaseParamFilters(filterKey, filterValue) {
    const filterParams = {};
    filterParams[filterKey] = filterValue;
    return {
        type: types.FILTER_CASE_PARAMS,
        filterParams,
    };
}

export function setCaseParamFilterSet(filterSet) {
    const filterParams = Object.assign({}, filterSet);
    return {
        type: types.FILTER_CASE_PARAMS,
        filterParams,
    };
}
