const $ = require('jquery');
const App = require('app/Application');
const _ = require('underscore');
var Marionette = require('backbone.marionette');
var stickit = require('backbone.stickit');
var FormBehavior = require('app/common/FormBehavior');
var AsyncView = require('app/common/AsyncView');
var tpl = require('raw-loader!./ExecutiveDashboardEmailSettingsView.html');
var ExecutiveDashboardEmailView = require('./ExecutiveDashboardEmailView');
var ExecutiveDashboardEmailModel = require('./ExecutiveDashboardEmailModel');
var ExecutiveDashboardEmailCollection = require('./ExecutiveDashboardEmailCollection');
var ExecutiveDashboardEmailSettingsModel = require('./ExecutiveDashboardEmailSettingsModel');
var layoutTpl = require('raw-loader!./ExecutiveDashboardEmailSettingsLayoutView.html');

var EmptyEmailListView = Marionette.ItemView.extend({
    template: _.template('<li><b> No email subscription is set up <i name="deleteButton"  class="fa fa-exclamation-triangle" style="color:red"></i> </b></li>'),
});

var EmailSettingsView = Marionette.CompositeView.extend({
    className: 'email-settings-view',
    template: _.template(tpl),
    fetchModel: true,
    model: ExecutiveDashboardEmailSettingsModel,
    collection: ExecutiveDashboardEmailCollection,
    childView: ExecutiveDashboardEmailView,
    emptyView: EmptyEmailListView,
    childViewContainer: 'ul',
    readonly: false,
    behaviors: {
        FormBehavior: {
            behaviorClass: FormBehavior,
        },
    },
    bindings: {
        'input[name="frequencyDays"]': {
            observe: 'frequencyDays',
            setOptions: {
                validate: true,
            },
        },

        '#enabled': {
            observe: 'enabled',
        },
        'input[name="email"]': {
            observe: 'email',
            setOptions: {
                validate: true,
            },
        },
    },
    ui: {
        form: 'form',
        messageArea: '#message',
        emailInput: 'input[name="email"]',
        testEmailButton: 'button[name="testEmail"]',
        addEmailButton: 'button[name="addEmail"]',
    },

    events: {
        'click @ui.addEmailButton': 'addEmail',
        'click @ui.testEmailButton': 'testEmail',
        'focus @ui.emailInput': 'resetEmailButton',
        'click @ui.submitButton': 'submit',
    },
    collectionEvents: {
        'remove': 'collectionModelDeleted',
    },
    initialize: function () {
        this.model = new ExecutiveDashboardEmailSettingsModel();
        this.collection = new ExecutiveDashboardEmailCollection();

        this.listenTo(this.model, 'validated', this.onValidate);
        this.onEmailListLoad();
    },

    collectionModelDeleted: function (event) {
        this.saveCollection();
    },

    onEmailListLoad: function () {
        this.collection = this.model.get('emailList');
        this.render();
    },

    onValidate: function (isValid) {
        var emailValue = this.model.get('email');
        if (this.model.get('email').trim() !== '') {
            this.ui.addEmailButton.prop('disabled', !isValid);
            this.ui.testEmailButton.prop('disabled', !isValid);
        } else {
            // handle the case when email address is replaced with a few whitespace
            this.ui.addEmailButton.prop('disabled', true);
            this.ui.testEmailButton.prop('disabled', true);
        }
    },

    onRender: function () {
        this.stickit();
        if (App.request('global', 'get:current:session').isInternal()) {
            // The form should be made read-only for internal users
            this.decorateAsReadonly();
        } else {
            this.$('.deleteEmailButton').show();
        }
        // disable both test and add button first
        this.ui.testEmailButton.prop('disabled', true);
        this.ui.addEmailButton.prop('disabled', true);
    },

    decorateAsReadonly: function () {
        $('#executive-dashboard-email-modal-title').append('<strong>(Read Only)</strong>');
        // Disable all buttons except 'OK'
        this.ui.testEmailButton.prop('disabled', true);
        this.ui.addEmailButton.prop('disabled', true);
        this.ui.emailInput.prop('disabled', true);
        this.$('.deleteEmailButton').hide();
        this.$('#frequencyDays').prop('disabled', true);
        this.$('#enabled').prop('disabled', true);
        this.readonly = true;
    },

    resetEmailButton: function () {
        if (!this.readonly) {
            this.ui.testEmailButton.bs_button('reset');
            this.ui.testEmailButton.prop('disabled', true);
            this.ui.messageArea.hide();
        }
    },

    testEmail: function (event) {

        event.preventDefault();
        var newEmail = this.addEmailModel.get('email');
        var self = this;
        self.ui.testEmailButton.bs_button('loading');
        var toBeSend = JSON.stringify(_.pick(this.addEmailModel.toJSON(), 'email'));
        $.ajax({
            type: 'POST',
            url: this.api().pod().account().segment('executiveDashboard/emailSettings/test'),
            contentType: 'application/json',
            dataType: 'json',
            data: JSON.stringify(_.pick(this.addEmailModel.toJSON(), 'email')),
        }).done(function () {
            self.ui.testEmailButton.bs_button('success');
        }).error(function (jqxhr) {
            App.command('notification', 'show:error', {
                title: 'Error sending test email to ' + newEmail,
                message: jqxhr.responseJSON.message,
            });
        }).always(function () {
            _.defer(_.bind(self.ui.testEmailButton.prop, self.ui.testEmailButton, 'disabled', true));
        });
    },

    // add new email from input
    addEmail: function (event) {
        var newEmail = this.ui.emailInput.val();
        var newEmailModel = new ExecutiveDashboardEmailModel({
            'email': newEmail,
        });

        // check if the email already exist
        var foundEmailModel = this.collection.findWhere({
            'email': newEmail,
        });
        if (!foundEmailModel) {
            this.collection.add(newEmailModel);
            this.saveCollection();
            //clear the email field and disable add button
            this.ui.emailInput.val('');
            this.model.set('email', '');
            this.ui.addEmailButton.prop('disabled', true);
            this.ui.testEmailButton.prop('disabled', true);
        } else {
            // email already exist, display error text
            this.ui.messageArea.text('This email is already subscribed');
            this.ui.messageArea.css('color', 'red');
            this.ui.messageArea.show();
        }
    },

    saveCollection: function () {
        this.model.set('emailList', this.collection);
    },

    submit: function () {
        this.saveCollection();
        this.model.save()
            .done(function () {
                App.command('notification', 'show:success', { title: 'executive dashboard email settings successfully updated' });
            })
            .fail(function (jqxhr) {
                App.command('notification', 'show:error', {
                    title: 'Failed to update executive dashboard email settings:',
                    message: jqxhr.responseJSON.message,
                });
            });
    },

});

module.exports = Marionette.LayoutView.extend({
    className: 'email-settings-dialog',
    template: _.template(layoutTpl),
    emailSettingsView: null,
    regions: {
        bodyRegion: '.modal-body',
    },
    ui: {
        submitButton: 'button[name="submit"]',
        cancelButton: 'button[name="cancel"]',
    },
    events: {
        'click @ui.submitButton': 'onSubmit',
    },
    onRender: function () {
        this.emailSettingsView = new EmailSettingsView();
        this.bodyRegion.show(AsyncView.wrap(this.emailSettingsView));
        if (App.request('global', 'get:current:session').isInternal()) {
            // The form should be made read-only for internal users
            this.ui.submitButton.hide();
            this.ui.cancelButton.text('Close');
        }
    },
    onSubmit: function () {
        this.emailSettingsView.submit();
    },

});
