import $ from 'jquery';
/* eslint-disable react/no-unused-state */
import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';

// import TypeAheadList from './TypeAheadList';

import style from './style.scss';

const cx = classnames.bind(style);


// FIXME: this impostor event object is enough to fool the receivers into accepting
// our dispatched e.target.value, but it won't go much further than that.
// function generatePseudoEvent(type, element) {
//     return {
//         _targetInst: null,
//         bubbles: true,
//         cancelable: false,
//         target: element,
//         currentTarget: element,
//         defaultPrevented: false,
//         dispatchConfig: null,
//         eventPhase: null,
//         isTrusted: false,
//         nativeEvent: {},
//         timeStamp: Date.now(),
//         type,
//     };
// }


class TypeAhead extends React.Component {
    constructor(props) {
        super(props);
        // autobind(this);

        this.state = {
            inputValue: props.value,
            suggestions: props.valueList || [],
            savedValues: [], // TODO: keep a tally of user phrases
            showSuggestions: false,
            listIndex: -1,
        };

        this.inputElem = null; // DOM reference
        this.listElem = null; // DOM reference

        this.eventChange = this.eventChange.bind(this);
        this.eventBlur = this.eventBlur.bind(this);
        // this.eventInput = this.eventInput.bind(this);
        this.eventKeyDown = this.eventKeyDown.bind(this);
        this.eventMouseOver = this.eventMouseOver.bind(this);

        this.outsideClick = this.outsideClick.bind(this);
    }

    componentDidMount() {
        $(window).on('click', this.outsideClick);
    }

    componentWillReceiveProps(nextProps) {
        const nstate = {};
        if ('value' in nextProps) nstate.inputValue = nextProps.value;
        if (Array.isArray(nextProps.valueList)) {
            if (this.suggestionsChanged(nextProps.valueList)) {
                nstate.showSuggestions = true;
                nstate.suggestions = nextProps.valueList;
            }
        }
        this.setState(nstate);
    }

    componentWillUnmount() {
        $(window).off('click', this.outsideClick);
    }

    suggestionsChanged(newSuggestions) {
        const { suggestions } = this.state;
        if (newSuggestions.length !== suggestions.length) return true;
        for (let i = 0, l = suggestions.length; i < l; i++) {  // eslint-disable-line
            if (suggestions[i] !== newSuggestions[i]) return true;
        }
        return false;
    }

    outsideClick(e) {
        if (!this.state.showSuggestions) return;

        const targetElem = $(e.target);
        const treeElements = targetElem.closest(this.rootElem);

        if (!treeElements.length) {
            this.setState({ showSuggestions: false, listIndex: -1 });
            this.eventBlur(e);
        }
    }

    eventMouseOver(e) {
        let listIndex = e.currentTarget.getAttribute('data-list-index'); // e.currentTarget.dataset.listIndex?
        listIndex = parseInt(listIndex, 10);
        if (Number.isNaN(Number(listIndex))) listIndex = -1;
        this.setState({ listIndex });
    }

    eventChange(e) {
        const val = e.target.value;
        const self = this;

        if (self.changeId) {
            clearTimeout(self.changeId);
        }

        self.changeId = setTimeout(() => {
            self.setState({ inputValue: val }, () => {
                // then.... since this component is an interceptor patterm, we need to pass along the event
                self.dispatchChanges(val);
            });
            self.changeId = null;
        }, 200);
    }

    dispatchChanges(val) {
        if (typeof this.props.onInput !== 'function') return;

        this.props.onInput(val); // eslint-disable-line
        // note: the above was triggering a misleading warning: 'onChange.call' is missing in props validation
    }

    // eventInput(e) {
    //     this.setState({inputValue: e.target.value});
    //     if (typeof this.props.onInput === 'function') this.props.onInput.call(null,e);
    // }

    eventBlur(e) {
        // FIXME - conflict on blur event when mouse picking from list
        // this.setState({showSuggestions: false});
        if (typeof this.props.onBlur === 'function') this.props.onBlur.call(null, e); // eslint-disable-line
    }

    eventKeyDown(e) {
        const leState = this.state;
        const suggestionsLength = leState.suggestions.length;
        const keyCode = e.keyCode || e.which;

        let newIndex = leState.listIndex;

        // select with enter key
        if (keyCode === 13 && leState.showSuggestions && suggestionsLength && leState.listIndex > -1) {
            const pickText = leState.suggestions[leState.listIndex];
            this.setState({ inputValue: pickText, showSuggestions: false, listIndex: -1 });
            // this is an antipattern, but it's needed so that we can send an onChange event to the parent component
            this.inputElem.value = pickText; // it will be overwritten with the same data soon (restoring sync)
            this.dispatchChanges(pickText);
        } else {
            // key nav
            if (keyCode === 38) newIndex -= 1;
            else if (keyCode === 40) newIndex += 1;

            if (newIndex < 0) newIndex = -1;
            if (newIndex >= suggestionsLength) newIndex = suggestionsLength - 1;

            const inList = (newIndex > -1);
            this.setState({ listIndex: newIndex, showSuggestions: inList });
            if (inList) this.centerScroll(newIndex);
        }

        // and....  since this component runs the interceptor pattern, we need to forward the event
        // do not dispatch if it was a list pick
        if (leState.listIndex < 0) {
            if (typeof this.props.onKeyDown === 'function') this.props.onKeyDown.call(null, e);
        }
    }

    centerScroll(selectedIndex) {
        const { listElem } = this;
        if (!listElem) return; // list is closed

        const items = Array.from(listElem.childNodes);
        const selected = items.find((i, idx) => (idx === selectedIndex));

        if (!selected) return;

        const itemPosition = selected.offsetTop;
        const itemHeight = selected.scrollHeight;

        const listVisibleHeight = listElem.offsetHeight;
        // const listScroll = listElem.scrollTop;
        // const listScrollHeight = listElem.scrollHeight;

        listElem.scrollTop = (itemPosition + itemHeight) - (listVisibleHeight / 2);
    }

    filteredProps(pp) {
        const p = Object.assign({}, this.props, pp);
        delete p.valueList;
        return p;
    }

    renderResults() {
        const component = this;
        const leState = this.state;
        const { suggestions } = leState;
        if (!suggestions.length || !leState.showSuggestions) return null;
        return (
            /* eslint-disable */
            <ul className={cx('type-ahead-results')} ref={(elem) => this.listElem = elem} >
            {/* eslint-enable */}
                {suggestions.map((pt, pti) => {
                    const liClass = {};
                    const anchorClass = {};
                    if (leState.listIndex === pti) anchorClass.selected = true;
                    const key = `ta-${pti}`;
                    function injectValue(e) {
                        e.preventDefault();
                        component.setState({ inputValue: pt, showSuggestions: false, listIndex: -1 }, () => {
                            component.inputElem.value = pt;
                            component.dispatchChanges(pt);
                        });

                        return false;
                    }
                    return (
                        <li
                            key={key}
                            className={cx(liClass)}
                            onMouseOver={component.eventMouseOver}
                            data-list-index={pti}
                        >
                            <a href="#support" className={cx(anchorClass)} onClick={injectValue}>{pt}</a>
                        </li>
                    );
                })}
            </ul>
        );
    }

    render() {
        const component = this;
        function bindElem(elem) {
            component.inputElem = elem;
            return elem;
        }

        return (
            <span className={cx('type-ahead-input')}>
                <input
                    type="text"
                    ref={bindElem}
                    onInput={this.eventChange}
                    onKeyDown={this.eventKeyDown}
                    onBlur={this.eventBlur}
                />
                {/* <TypeAheadList theList={this.state.valueList} /> */}
                {this.renderResults()}
            </span>
        );
    }
}

TypeAhead.propTypes = {
    value: PropTypes.string,
    valueList: PropTypes.array,
    onBlur: PropTypes.func,
    onInput: PropTypes.func,
    onKeyDown: PropTypes.func,
};

export default TypeAhead;
