const _ = require('underscore');
var Marionette = require('backbone.marionette');
var Api = require('app/modules/api/Api');
var FormBehavior = require('app/common/FormBehavior');
var PermissionRequestModel = require('./PermissionRequestModel');
var PermissionModel = require('./PermissionModel');
var tpl = require('raw-loader!./PermissionRequestForm.html');
var FlashNotification = require('@infosight/elmer/dist/components/FlashNotification').default;

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    behaviors: {
        FormBehavior: {
            behaviorClass: FormBehavior,
        },
    },

    ui: {
        submitButton: 'button[name="submit"]',
    },

    events: {
        'click @ui.submitButton': 'submit',
    },

    bindings: {
        'input[name="arraySn"]': {
            observe: 'arraySn',
            setOptions: {
                validate: true,
            },
            onSet: function (val) {
                return val.toUpperCase();
            },
        },
    },

    initialize: function () {
        this.model = new PermissionRequestModel();
        this.listenTo(this.model, 'validated', this.onValidated);
    },

    onValidated: function (isValid) {
        this.ui.submitButton.prop('disabled', !isValid);
    },

    submit: function () {
        var self = this;
        this.model.save()
            .done(function (response) {
                if (response.grantee.affiliation) {
                    FlashNotification.success(
                        `Successfully requested permission for ${response.target.opaqueName}`,
                    );
                } else {
                    FlashNotification.warning(
                        'Customer affiliation not found within purchase records. ' +
                        `Permission request will still be sent to ${response.target.opaqueName} however it will be annotated as coming from an unaffiliated third party.`,
                    );
                }
            })
            .done(function (response) {
                self.collection.add(new PermissionModel(response));
            })
            .fail(function (jqxhr) {
                FlashNotification.error(
                    `Failed to request permission: ${jqxhr.responseJSON.message}`,
                );
            });
    },

});
