const _ = require('underscore');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');
var $ = require('jquery'),
    jQueryUI = require('jqueryui'),
    Backbone = require('backbone');

module.exports = Backbone.View.extend(
    /** @lends views/components/dashboard/CaseStatisticsChart.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/dashboard/CaseStatisticsChart
         * @constructor
         * @augments Backbone.View
         */


        className: 'case-statistics-chart',

        renderChart: function (container, series, numberOfDataPoints) {
            this.chart = new Highcharts.Chart({
                chart: {
                    type: 'area',
                    renderTo: container,
                    height: 350,
                    events: {
                        load: function () {
                            var chart = this;
                            // reflow the chart to ensure it fills the container
                            _.delay(function () {
                                chart.reflow.call(chart);
                            }, 50);
                        },
                    },
                },
                title: {
                    text: 'Operational Efficiencies',
                },
                subtitle: {
                    text: 'Cases closed automatically vs manually',
                },
                credits: {
                    enabled: false,
                },
                xAxis: {
                    type: 'datetime',
                    dateTimeLabelFormats: {
                        day: '%e of %b',
                        month: '%b',
                    },
                },
                yAxis: {
                    title: {
                        text: 'Cases Resolved',
                    },
                },
                tooltip: {
                    shared: true,
                    formatter: function () {
                        var tip = Highcharts.dateFormat('Week of %A, %b %e, %Y', this.points[0].x),
                            totalCases = 0;

                        for (var i = 0; i < this.points.length; i++) {
                            var point = this.points[i],
                                color = point.series.color,
                                cases = point.y;
                            tip += '<br/><span style="color: ' + color + '">' +
                                point.series.name + ':</span> ' + cases;
                            totalCases += cases;
                        }

                        tip += '<br/>Total Cases: ' + totalCases;

                        return tip;
                    },
                },
                plotOptions: {
                    area: {
                        animation: false,
                        stacking: 'normal',
                        lineColor: '#666666',
                        lineWidth: 1,
                        marker: {
                            enabled: numberOfDataPoints < 5 ? true : false,
                        },
                        format: '{point.y:.1f} TiB',
                    },
                },
                series: series,
            });
        },

        render: function () {
            // this.$el.html( template(this.model.toJSON()) );
            this.$el.html(this.el);

            // this.model
            var caseStats = this.model.get('caseStats'),
                categories = [],
                autoClosed = this.model.get('automaticallyClosed'),
                manualClosed = this.model.get('manuallyClosed'),
                totalClosed = this.model.get('totalCases'),
                automaticSeries = {
                    name: 'Automatically Closed',
                    data: [],
                },
                manualSeries = {
                    name: 'Manually Closed',
                    data: [],
                };

            for (var i = 0; i < caseStats.length; i++) {
                var dp = caseStats[i],
                    auto = dp.automatic,
                    manual = dp.manual,
                    closeWeek = dp.closeWeek;

                // categories.push(closeWeek);
                automaticSeries.data.push([closeWeek, auto]);
                manualSeries.data.push([closeWeek, manual]);
            }

            var summaryText = '';
            if (totalClosed > 0 && autoClosed > 0) {
                var autoClosedPct = Highcharts.numberFormat(autoClosed / totalClosed * 100, 1);
                summaryText = autoClosedPct + '% of your resolved cases were closed automatically.';
            }

            var successMessage = this.$el.find('.dashboard-success-message');
            if (summaryText.length > 0) {
                successMessage.html(summaryText);
                successMessage.show();
            } else {
                successMessage.hide();
            }

            this.renderChart(this.el, [automaticSeries, manualSeries], caseStats.length);

            return this;
        },

    });
