const _ = require('underscore');
var Marionette = require('backbone.marionette'),
    tpl = require('raw-loader!./DashboardROIView.html'),
    errorTpl = require('raw-loader!app/templates/components/error.html'),
    SpaceSavingsModel = require('./SpaceSavingsModel'),
    CaseStatisticsModel = require('./CaseStatisticsModel'),
    CaseStatisticsView = require('./CaseStatisticsView'),
    SpaceSavingsView = require('./SpaceSavingsView'),
    SpaceUsageView = require('./SpaceUsageView'),
    Spinner = require('app/views/components/Spinner');

module.exports = Marionette.ItemView.extend(
    /** @lends views/components/dashboard/DashboardROIView.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/dashboard/DashboardROIView
         * @constructor
         * @augments Marionette.ItemView
         */

        template: _.template(tpl),

        className: 'dashboard-space-view',

        onRender: function () {

            var self = this,
                spaceSavingsModel = new SpaceSavingsModel({}),
                caseStatsModel = new CaseStatisticsModel({});
            var handleErrorSpaceSavings = function (response) {
                var message = 'Space Savings Information is not available.';
                var errorTemplate = _.template(errorTpl);
                self.$el.find('.space-savings-header').after(errorTemplate({
                    message: message,
                }));
            };

            var handleErrorOpEfficiencies = function (response) {
                var message = 'Operational Efficiency Information is not available.';
                var errorTemplate = _.template(errorTpl);
                self.$el.find('.space-usage-header').after(errorTemplate({
                    message: message,
                }));
            };

            this.spaceSavings = new SpaceSavingsView({
                model: spaceSavingsModel,
            });

            // add a loading spinner
            this.spaceSpinner = new Spinner();
            this.$el.find('#space-savings-spinner').append(this.spaceSpinner.render().el);

            this.caseSpinner = new Spinner();
            this.$el.find('#case-efficiency-spinner').append(this.caseSpinner.render().el);

            spaceSavingsModel.fetch()
                .always(function () {
                    self.spaceSpinner.hide();
                })
                .done(function () {
                    // space savings section
                    self.$el.find('.space-savings-container').append(
                        self.spaceSavings.render().el);
                }).fail(handleErrorSpaceSavings);

            // hiding space usage for now
            // spaceUsageModel.fetch({
            //     success: function() {
            //         // space usage section
            //         self.spaceUsageView = new SpaceUsageView({
            //             model: spaceUsageModel
            //         });
            //         self.$el.find('.space-usage-container').append(
            //             self.spaceUsageView.render().el );
            //     }
            // });
            caseStatsModel.fetch()
                .always(function () {
                    self.caseSpinner.hide();
                })
                .done(function () {
                    self.caseStatisticsView = new CaseStatisticsView({
                        model: caseStatsModel,
                    });
                    self.$el.find('.operational-efficiency-container').append(
                        self.caseStatisticsView.render().el);
                }).fail(handleErrorOpEfficiencies);
        },

        onDestroy: function () {
            if (this.chart) {
                this.chart.destroy();
                delete this.chart;
            }
        },

    });
