const $ = require('jquery');
const App = require('app/Application');
const _ = require('underscore');
var Backbone = require('backbone'),
    Validate = require('jquery-validation'),
    moment = require('moment'),
    GridView = require('app/views/components/grid/GridView'),
    GridFilterModel = require('app/views/components/grid/GridFilterModel'),
    BulkLoadGridModel = require('app/models/grid/BulkLoadGridModel'),
    LocalGridModel = require('app/models/grid/LocalGridModel'),
    BannerModel = require('./BannerModel'),
    BannerCollection = require('./BannerCollection'),
    BannerDialogView = require('./BannerDialogView'),
    BannerPreviewView = require('./BannerPreviewView'),
    BannerNotifyView = require('./BannerNotifyView'),
    tpl = require('raw-loader!./BannersView.html'),
    React = require('react'),
    ReactDOM = require('react-dom'),
    BulletinsModal = require('../../../../../../progeny/modules/bulletins/components/BulletinsModal').default;

const { ThemeProvider } = require('@infosight/elmer/dist/components/ThemeProvider');
const { Provider } = require('react-redux');
const { getStore } = require('../../../../../../progeny/util/storeProvider');


var template = _.template(tpl);

var splitCamelCase = function (string) {
    return string.replace(/([a-z])([A-Z])/g, '$1 $2');
};

module.exports = Backbone.View.extend({

    bannerCollection: null,

    rowButtons: ['#edit', '#delete', '#preview', '#notify'],

    columns: [
        {
            data: 'id',
            visible: false,
        }, {
            data: 'title',
        }, {
            data: 'type',
            filter: true,
            render: {
                display: splitCamelCase,
            },
        }, {
            data: 'status',
            filter: true,
            render: {
                display: splitCamelCase,
            },
        }, {
            data: 'visibility',
            filter: true,
            render: {
                display: splitCamelCase,
            },
        }, {
            data: 'accountId',
        }, {
            data: 'feature',
        }, {
            data: 'creationTimestamp',
            render: {
                display: function (data) {
                    return moment(data).format('L LT');
                },
            },
        }, {
            data: 'notificationTimestamp',
            render: {
                display: function (data) {
                    return !data ? '' : moment(data).format('L LT');
                },
            },
        }, {
            data: 'expirationDate',
            visible: false,
        },
    ],

    /**
     * Overridden Backbone initialize function.
     * @override
     */
    initialize: function () {
        _.bindAll(this,
            'enableToolbarActions', 'disableToolbarActions',
            'reloadGrid', 'onPreview', 'onNotify', 'onOpenDialog');
    },

    /**
     * Overridden Backbone render function.
     * @return {BannerView} Returns this view.
     */
    render: function () {
        var self = this;
        this.$el.html(template());

        this.$('#banners-grid-container').hide();

        this.setupAndLoadCollection();

        $(document).click(function () {
            var menu = $('.ui-menu');
            if (menu.is(':visible')) {
                menu.hide();
            }
        });

        // create the jquery ui buttons and set up the appropriate handlers
        this.$el.find('.toolbar BUTTON').click(function () {
            var button = $(this);
            switch (button.attr('id')) {
            case 'create':
                self.onOpenDialog(new BannerModel());
                break;
            case 'edit':
                self.onOpenDialog(self.getSelectedModel());
                break;
            case 'delete':
                self.getSelectedModel().destroy({ wait: true });
                break;
            case 'preview':
                self.onPreview(self.getSelectedModel());
                break;
            case 'notify':
                self.onNotify(self.getSelectedModel());
                break;
            default:
                throw new Error('Unexpected button with id: ' + button.attr('id'));
            }
        });

        return this;
    },

    setupAndLoadCollection: function () {
        var self = this;
        this.bannerCollection = new BannerCollection();
        this.bannerCollection.fetch()
            .done(function () {
                // this could be improved by having the grid model take in a backbone collection
                // then listen for sync events.
                var gridModel = new LocalGridModel({
                    data: self.bannerCollection.toJSON(),
                    columns: self.columns,
                    emptyLabel: 'No banners found',
                    showFilters: true,
                    pageLength: 25,
                });

                self.grid = new GridView({
                    model: gridModel,
                    id: 'banners-grid',
                    tableTemplate: self.$el.find('TABLE'),
                    rowSelectionMode: GridView.RowSelectionMode.SINGLE,
                    filterModel: new GridFilterModel({
                        'Status': 'Active',
                    }),
                    sort: [
                        {
                            column: 'Created',
                            order: 'desc',
                        },
                    ],
                });
                self.listenTo(self.grid, 'row-selection:changed', function ($selectedRows) {
                    if ($selectedRows.length === 0) {
                        self.disableToolbarActions();
                    } else {
                        self.enableToolbarActions();
                    }
                });

                self.$el.find('#banners-grid-container').append(self.grid.render().el);

                self.listenTo(self.bannerCollection, 'remove', self.reloadGrid);
                self.listenTo(self.bannerCollection, 'sync', self.reloadGrid);

                self.$('#banners-grid-container').show();
            });
    },

    reloadGrid: function () {
        // we should just be able to call .data() to set the new data,
        // but that appears to be broken in this version of datatables.
        // resorting to clearing and adding.
        this.grid.getDataTableApi()
            .clear()
            .rows
            .add(this.bannerCollection.toJSON())
            .draw();
        this.disableToolbarActions();
    },

    enableToolbarActions: function () {
        this.$el.find(this.rowButtons.join(',')).prop('disabled', false);
    },

    disableToolbarActions: function () {
        this.$el.find(this.rowButtons.join(',')).prop('disabled', true);
    },

    /**
     * Handler for opening the report create/edit dialog.
     * @param  {BannerModel} model The BannerModel.
     *                                      Can be a new (empty) model
     *                                      or an existing model to edit.
     */
    onOpenDialog: function (model) {
        var self = this,
            dialog = null,
            $el = this.$el;

        var dialogView = new BannerDialogView({
            model: model,
        });

        App.modalRegion.show(dialogView);

        this.listenTo(dialogView, 'submit', function (args) {
            var updatedModel = args.model;
            self.bannerCollection.add(updatedModel);
            updatedModel.save()
                .done(function () {
                    App.command('notification', 'show:success', {
                        title: 'Banner "' + updatedModel.get('title') + '" successfully saved',
                    });

                    self.grid.filterView.clearFilters();
                    self.bannerCollection.fetch();
                    self.grid.getDataTableApi().one('draw', function () {
                        self.grid.eachRow(function (row) {
                            if (row.data().id == model.get('id')) {
                                $(row.node()).effect('highlight', { color: '#78c639' }, 3000);
                            }
                        });
                    });
                })
                .fail(function (jqxhr) {
                    App.command('notification', 'show:error', {
                        title: 'Failed to save banner "' + updatedModel.get('title') + '":',
                        message: jqxhr.responseJSON.message,
                    });
                });
        });
    },

    onPreview: function (model) {
        ReactDOM.render(<ThemeProvider><Provider store={getStore()}><BulletinsModal closeModal={this.onPreviewClose} bulletins={[model.attributes]} hidePrimaryButton /></Provider></ThemeProvider>,
            document.getElementById('modal-region'));
    },

    onPreviewClose: function() {
        ReactDOM.unmountComponentAtNode(document.getElementById('modal-region'))
    },

    onNotify: function (model) {
        var self = this;
        var notifyView = new BannerNotifyView({
            model: model,
        });
        $('body').append(notifyView.render().el);
        notifyView.showDialog();
        this.listenTo(notifyView, 'close', function () {
            notifyView.remove();
            self.bannerCollection.fetch();
        });
    },

    getSelectedModel: function () {
        var rowData = this.grid.getSelectedRowData();
        return this.bannerCollection.findWhere({ id: rowData.id });
    },

});
