var Backbone = require('backbone');
var FacetModel = require('./FacetModel');
var deepGet = require('utils-deep-get');

const ORDERING = ['Document Type', 'Nimble Software and Solutions', 'Version Family', 'Integration', 'Platform'];

module.exports = Backbone.Collection.extend({

    model: FacetModel,

    toQueryParamMap: function () {
        return this.getSelectedBuckets().reduce(function (memo, bucket) {
            memo[bucket.getFacet().get('field')] = bucket.get('value');
            return memo;
        }, {});
    },

    getSelectedBuckets: function () {
        return this.reduce(function (memo, facet) {
            var bucket = facet.getSelectedBucket();
            if (bucket) {
                memo.push(bucket);
            }
            return memo;
        }, []);
    },

    hasSelected: function () {
        return this.some(function (facet) {
            return facet.hasSelected();
        });
    },

    comparator: function (model) {
        var index = ORDERING.indexOf(model.get('field'));
        return index === -1 ? Number.POSITIVE_INFINITY : index;
    },

});
