var Backbone = require('backbone');

module.exports = Backbone.Model.extend({

    url: function () {
        return this.api().pod().account().segment('/opsDashboard/protection');
    },

    parse: function (response) {
        response.percentProtected = (response.localProtectedCount + response.remoteProtectedCount) / response.accountTotalVolumeCount;
        return response;
    },

});
