var Marionette = require('backbone.marionette');
var htmlEncode = require('app/util/html').htmlEncode;

module.exports = Marionette.ItemView.extend({

    template: false,

    tagName: 'tr',

    className: 'list-group-item banner',

    render: function () {
        var value = this.model.get('contents');
        this.attachElContent(value);
    },

});
