const _ = require('underscore');
var Marionette = require('backbone.marionette');
var AsyncView = require('app/common/AsyncView');
var PermissionCollection = require('./PermissionCollection');
var OutboundGrantsView = require('./OutboundGrantsView');
var InboundGrantsView = require('./InboundGrantsView');
var RequestedGrantsView = require('./RequestedGrantsView');
var tpl = require('raw-loader!./PermissionsView.html');

var View = Marionette.LayoutView.extend({

    template: _.template(tpl),

    className: 'permissions-view',

    regions: {
        inbound: '[data-region="inbound"]',
        outbound: '[data-region="outbound"]',
        requested: '[data-region="requested"]',
    },

    initialize: function () {
        this.collection = new PermissionCollection();
    },

    onRender: function () {
        this.$el.popover({
            selector: '[data-toggle="popover"]',
            trigger: 'hover',
        });

        var account = this.getOption('account');

        if (account.resellerEquivalent) {
            this.showChildView('inbound', new InboundGrantsView({ collection: this.collection, account }));
        }

        this.showChildView('requested', new RequestedGrantsView({ collection: this.collection, account }));
        this.showChildView('outbound', new OutboundGrantsView({ collection: this.collection, account }));
    },

});

module.exports = AsyncView.extend({
    ViewConstructor: View,
});
