var Marionette = require('backbone.marionette');
var AsyncView = require('app/common/AsyncView');
var PeerPerformanceChartView = require('./PeerPerformanceChartView');
var PeerPerformanceCollection = require('./PeerPerformanceCollection');

var fiveDays = 5 * 24 * 60 * 60 * 1000;

var View = Marionette.CollectionView.extend({

    childView: PeerPerformanceChartView,

    className: 'dashboard-performance-view',

    collectionEvents: {
        reset: 'onFetch',
    },

    emptyView: Marionette.ItemView.extend({ template: '<span>No data available</span>' }),

    initialize: function () {
        this.collection = new PeerPerformanceCollection();
    },

    getChartOptions: function () {
        var chartOptions = this.collection.reduce(function (memo, model) {

            var min = model.get('minTimestamp');
            var minDate = new Date(min);
            var max = model.get('maxTimestamp');
            var maxDate = new Date(max);

            return {
                minDate: Math.min(memo.minDate, min),
                maxDate: Math.max(memo.maxDate, max),
                maxLatency: Math.max(memo.maxLatency, model.get('maxLatency')),
                maxIops: Math.max(memo.maxIops, model.get('maxIops')),
            };
        }, {
            minDate: new Date(),
            maxDate: new Date(0),
            maxLatency: Number.MIN_VALUE,
            maxIops: Number.MIN_VALUE,
        });

        chartOptions.minDate = Math.min(chartOptions.minDate, chartOptions.maxDate - fiveDays);

        return chartOptions;
    },

    onFetch: function () {
        if (this.collection.last()) {
            this.collection.last().set('isLast', true);
        }
        this.childViewOptions = this.getChartOptions();
    },
});

module.exports = AsyncView.extend({
    ViewConstructor: View,
    animateOnLoad: false,
});
