import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';

import Card from '@infosight/elmer/dist/components/Card';
import NimbleLegacyContainer from '../../../../containers/NimbleLegacyContainer';
import PermissionsView from '../../../../../app/modules/permissions/PermissionsView';

import { tenantSelector } from '../../../user';
import style from './style.scss';

const cx = classnames.bind(style);

const PermissionsContainer = ({ tenant }) => (
    <Card title="Third Party Permissions">
        <NimbleLegacyContainer className={cx('container')} createLegacyView={el => new PermissionsView({ el, account: tenant })} />
    </Card>
);

PermissionsContainer.propTypes = {
    tenant: PropTypes.object,
};

export default connect(state => ({ tenant: tenantSelector(state).tenant }))(PermissionsContainer);
