const _ = require('underscore');
var Marionette = require('backbone.marionette');
var moment = require('moment');
var VirtualCollection = require('backbone-virtual-collection');
var tpl = require('raw-loader!./RequestedGrantsView.html');
var itemTpl = require('raw-loader!./RequestedGrantsItemView.html');

var FlashNotification = require('@infosight/elmer/dist/components/FlashNotification').default;

var ItemView = Marionette.ItemView.extend({

    template: _.template(itemTpl),

    tagName: 'div',

    className: 'callout',

    events: {
        'click button[name="approve"]': 'approve',
        'click button[name="deny"]': 'deny',
    },

    approve: function () {
        this.model.save({ status: 'Granted' }, { patch: true, emulateHTTP: true, wait: true })
            .done(function () {
                FlashNotification.success('Permission request approved');
            })
            .fail(function (jqxhr) {
                FlashNotification.error(`Failed to approve request: ${jqxhr.responseJSON.message}`);
            });
    },

    deny: function () {
        this.model.save({ status: 'Denied' }, { patch: true, emulateHTTP: true, wait: true })
            .done(function () {
                FlashNotification.success('Permission request denied');
            })
            .fail(function (jqxhr) {
                FlashNotification.error(`Failed to deny request: ${jqxhr.responseJSON.message}`);
            });
    },

});

module.exports = Marionette.CompositeView.extend({

    template: _.template(tpl),

    childView: ItemView,

    childViewContainer: '.child-container',

    className: 'requested-grants',

    collectionEvents: {
        remove: 'updateVisibility',
    },

    initialize: function (options) {
        var account = this.getOption('account');

        this.collection = new VirtualCollection(options.collection, {
            filter: function (permission) {
                return permission.get('target').account && permission.get('target').account.id === account.id &&
                    permission.get('status') === 'Requested';
            },
        });

        this.onRender();
    },

    onRender: function () {
        this.updateVisibility();
    },

    updateVisibility: function () {
        this.$el.toggle(!this.collection.isEmpty());
    },

});
