const _ = require('underscore');
var Backbone = require('backbone');
var Marionette = require('backbone.marionette');
var moment = require('moment');
var VirtualCollection = require('backbone-virtual-collection');
var GridView = require('app/views/components/grid/GridView');
var CollectionGridModel = require('app/models/grid/CollectionGridModel');
var PermissionGroupCollection = require('./PermissionGroupCollection');
var OutboundPermissionsCellView = require('./OutboundPermissionsCellView');
var tpl = require('raw-loader!./OutboundGrantsView.html');

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    regions: {
        grid: '[data-region="grid"]',
    },

    columns: [
        {
            data: 'grantee.account.name',
        }, {
            data: 'grantee.userLogin',
        }, {
            data: 'permissions',
            orderable: false,
            render: function () {
                return '';
            },
            createdCell: function (el, permissions, permissionGroup) {
                var initOptions = this.api().init();
                var view = new OutboundPermissionsCellView({
                    collection: new Backbone.Collection(permissions),
                    permissionCollection: initOptions.permissionGroupCollection.collection,
                    permissionGroup: initOptions.permissionGroupCollection.get(permissionGroup.id),
                });
                view.render();
                el.appendChild(view.el);
            },
        },
    ],

    collectionEvents: {
        update: 'updateVisibility',
    },

    templateHelpers: function () {
        return {
            account: this.getOption('account'),
        };
    },

    initialize: function (options) {
        var account = this.getOption('account');
        this.collection = new PermissionGroupCollection(
            new VirtualCollection(options.collection, {
                filter: function (permission) {
                    return permission.get('target').account && permission.get('target').account.id === account.id &&
                        permission.get('status') === 'Granted';
                },
            }),
        );

        this.isResellerEquivalent = account.resellerEquivalent;
    },

    onRender: function () {
        this.showChildView('grid', new GridView({
            tableTemplate: this.$('script').html(),
            model: new CollectionGridModel({
                collection: this.collection,
                columns: this.columns,
                emptyLabel: 'No permissions granted',
                pageLength: 10,
            }),
            dataTableOptions: {
                dom: 'ftip',
                permissionGroupCollection: this.collection,
            },
            sort: ['Organization'],
        }));

        this.updateVisibility();
    },

    updateVisibility: function () {
        if (this.isResellerEquivalent) {
            this.$el.toggle(!this.collection.isEmpty());
        }
    },

});
