const _ = require('underscore');
const { buildUrl } = require('@infosight/elmer/dist/utils/url');

var Marionette = require('backbone.marionette');
var productTpl = require('raw-loader!./CategoryPanelProduct.html');
var tpl = require('raw-loader!./CategoryPanel.html');

var ProductView = Marionette.ItemView.extend({

    tagName: 'li',

    template: _.template(productTpl),

    templateHelpers: function () {
        return {
            href: buildUrl(
                'resources/nimble/software',
                window.encodeURI(this.model.collection.category.get('name')),
                window.encodeURI(this.model.get('name')),
            ),
        };
    },

    modelEvents: {
        'change': 'render',
    },

});

module.exports = Marionette.CompositeView.extend({

    template: _.template(tpl),

    tagName: 'li',

    className: 'software-category panel panel-default',

    childView: ProductView,

    childViewContainer: 'ul',

    initialize: function () {
        this.collection = this.model.get('products');
    },

});
