const _ = require('underscore');
var Backbone = require('backbone');
var ExecutiveDashboardEmailCollection = require('./ExecutiveDashboardEmailCollection');

module.exports = Backbone.Model.extend({

    defaults: {
        enabled: false,
        frequencyDays: 30,
        email: '',
        emailList: new ExecutiveDashboardEmailCollection(),
    },

    validation: {
        email: {
            pattern: 'email',
            required: false,
        },
    },
    initialize: function () {
    },

    isNew: function () {
        return false;
    },

    url: function () {
        return this.api().central().account().segment('admin/executiveDashboard/emailSettings');
    },

    parse: function (obj) {
        // update the inner collection
        this.get('emailList').reset(obj.emailList);
        return obj;
    },

    // Overwrite save function
    save: function (attrs, options) {
        options = {};
        attrs = _.clone(this.attributes);

        // Filter the data to send to the server
        delete attrs.email;

        options.data = JSON.stringify(attrs);
        options.contentType = 'application/json';

        // Proxy the call to the original save function
        return Backbone.Model.prototype.save.call(this, attrs, options);
    },
});
