import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import classnames from 'classnames/bind';
import { reduxForm, formPropTypes } from 'redux-form';
import { Modal } from '@infosight/elmer/dist/components/Modal';

import RoleField from './RoleField';

import { changeRole } from './actionCreators';

import style from './style.scss';

const cx = classnames.bind(style);

/* eslint-disable jsx-a11y/label-has-for */
const ChangeRoleForm = ({ user, changeRole, handleSubmit, pristine, invalid, closeModal }) => ( // eslint-disable-line no-shadow
    <Modal
        title="Change Role"
        primaryButtonContent="Submit"
        disablePrimaryButton={pristine || invalid}
        showCloseIcon={false}
        onClickPrimaryButton={handleSubmit(({ role }) => changeRole({ user, role }))}
        hide={closeModal}
        isStoreDriven={false}
        isOpen
    >
        <form className={cx('settings-popup')}>
            <div className="row">
                <div className="column small-4">
                    <label htmlFor="_">User</label>
                </div>
                <div className="column">
                    {user.login}
                </div>
            </div>
            <div className="row">
                <div className="column small-4">
                    <label htmlFor="role">Role</label>
                </div>
                <div className="column small-10">
                    <RoleField />
                </div>
            </div>
        </form>
    </Modal>
);
/* eslint-enable jsx-a11y/label-has-for */

ChangeRoleForm.propTypes = {
    user: PropTypes.object.isRequired,
    changeRole: PropTypes.func.isRequired,
    /* eslint-disable react/no-typos */
    handleSubmit: formPropTypes.handleSubmit,
    pristine: formPropTypes.pristine,
    invalid: formPropTypes.invalid,
    /* eslint-enable react/no-typos */
    closeModal: PropTypes.bool.isRequired,
};

export default connect(
    (state, { user }) => ({
        initialValues: {
            role: user.role,
        },
    }),
    {
        changeRole,
    }
)(reduxForm({
    form: 'changeRole',
})(ChangeRoleForm));
