import React from 'react';
import { withRouter } from 'react-router';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import { Modal } from '@infosight/elmer/dist/components/Modal';

import { redirectToLogin } from '../../../../util/url';

import {
    likeASolution,
    dislikeASolution,
    resolveCase,
} from '../../library/support-api';


import KcsDocument from '../KcsDocument';


class KcsDocumentModal extends React.Component {
    constructor(props) {
        super(props);
        autobind(this);

        this.state = {
            error: null,
            resultsLiked: [],
            resultsDisliked: [],
        };
    }

    onKcsError(error) {
        this.setState({ error });
    }

    docSolvedMyIssue(docId) {
        const { resultsLiked } = this.state;
        if (!resultsLiked[docId]) {
            resultsLiked[docId] = true;
            this.setState({ resultsLiked });
            likeASolution(docId);
            resolveCase(this.props.caseId, `User accepted solution in KCS Document ${docId}`);
        }
    }

    docNotHelpful(docId) {
        const { resultsDisliked } = this.state;
        if (!resultsDisliked[docId]) {
            resultsDisliked[docId] = true;
            this.setState({ resultsDisliked });
            dislikeASolution(docId);
        }
    }

    redirectToDocs() {
        const { history } = this.props;
        history.push('/resources/nimble/docs');

        if (this.props.isSupportCaseAccess) {
            redirectToLogin();
        }
    }

    render() {
        if (this.state.error) {
            return (
                <Modal
                    size="large"
                    hideModalOnPrimaryButtonClick
                    hideSecondaryButton
                    primaryButtonContent="Ok"
                    title={`Solution #${this.props.docId} Unavailable`}
                    onClickPrimaryButton={() => this.redirectToDocs()}
                    hide={this.props.closeModal}
                    isStoreDriven={false}
                    isOpen
                >
                    <h1 className="no-margin"><i className="fa fa-exclamation-triangle text-danger" /> {this.state.error.message}</h1>
                </Modal>
            );
        }

        return (
            <Modal
                size="large"
                hideModalOnPrimaryButtonClick
                hideModalOnSecondaryButtonClick
                primaryButtonContent="This solved my issue"
                secondaryButtonContent="Not helpful"
                title={`Solution #${this.props.docId}`}
                onClickPrimaryButton={() => this.docSolvedMyIssue(this.props.docId)}
                onClickSecondaryButton={() => this.docNotHelpful(this.props.docId)}
                hide={this.props.closeModal}
                isStoreDriven={false}
                isOpen
            >
                <KcsDocument
                    docId={this.props.docId}
                    onError={e => this.onKcsError(e)}
                />
            </Modal>);
    }
}

KcsDocumentModal.propTypes = {
    caseId: PropTypes.string,
    docId: PropTypes.number.isRequired,
    // Injected by withRouter
    history: PropTypes.object,
    isSupportCaseAccess: PropTypes.bool,
    closeModal: PropTypes.func.isRequired,
};

export default withRouter((KcsDocumentModal));
