const _ = require('underscore');
var Marionette = require('backbone.marionette');
var ConfirmationRequestModel = require('./ConfirmationRequestModel');
var tpl = require('raw-loader!./ConfirmationRequestView.html');

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    triggers: {
        'click button[name="submit"]': {
            event: 'submit',
            stopPropagation: false,
        },
    },

    ui: {
        modalBody: '.modal-body',
    },

    initialize: function (options) {
        this.model = new ConfirmationRequestModel(_.pick(options, 'action', 'title'));
        _.extend(this, _.pick(options, ['content']));

        console.assert(this.content, 'content option is required');
    },

    onRender: function () {
        this.ui.modalBody.append(_.result(this, 'content'));
    },

});
