var jsPDF = require('jspdf');
require('jspdf-autotable');

function generatePdf(matrix, columnDef) {
    var grid = matrix.grid;
    var data = [];

    var rawData = grid.rawRows();

    rawData.every(function () {
        var row = this.data();
        var item = [];

        for (var i = 0, len = columnDef.length; i < len; i++) {
            item.push(row[columnDef[i].data]);
        }

        data.push(item);
    });

    var columns = [];
    for (var i = 0, len = columnDef.length; i < len; i++) {
        columns.push(columnDef[i].data);
    }

    var doc = new jsPDF();
    doc.setFontSize(10);
    var y = 10;

    // Logo
    var scale = 0.05;
    var logoW = Math.round(600 * scale); // native 600
    var logoH = Math.round(253 * scale); // native 253
    doc.addImage(logo, 'png', 10, y, logoW, logoH);

    y += logoH + 10;

    doc.setFontSize(18);
    doc.text('HPE Nimble Storage Validated Configuration Matrix', 20 + logoW, 15);
    doc.setFontSize(10);

    var titleNotes = (matrix.getTitlenotes(matrix.type) || '').replace('<br>', '\n');
    var splitTitle = '';
    if (titleNotes) {
        splitTitle = doc.splitTextToSize(titleNotes, 160);
    }

    if (splitTitle !== '') {
        doc.setFontSize(12);
        doc.text(10, y, splitTitle);
        doc.setFontSize(10);
        y += splitTitle.length * 5 + 5;
    }

    var pageContent = function (data) {
        console.log('pageContent', data);
        var left = data.settings.margin.left + 15;

        var startY = data.table.pageStartY - 15;

        // Header
        doc.setFillColor(120, 198, 57);
        doc.rect(left / 2 - .5, startY, 181.75, 15, 'F');
        doc.text(matrix.type, left / 2 + 2, 9.5 + startY);

        // Footer
        doc.setFontSize(7);
        doc.text(' Copyright 2018 Hewlett-Packard Development Company, L.P.', left / 2, doc.internal.pageSize.height - 10);
        var generationDateText = 'PDF Generated: ' + (new Date()).toString() + '.';
        doc.text(generationDateText, doc.internal.pageSize.width - left / 2, doc.internal.pageSize.height - 10, 'right');
        var referenceUrlText = 'See https://infosight.hpe.com/resources/nimble/validated-configuration-matrix for the latest updates.';
        doc.text(referenceUrlText, left / 2, doc.internal.pageSize.height - 6);
        doc.setFontSize(10);
    };

    doc.autoTable(columns, data, {
        startY: y + 10, // + 10 + (splitTitle.length + 1) * 5.5,
        margin: { top: 25, bottom: 20 },
        addPageContent: pageContent,
        theme: 'striped',
        headerStyles: {
            textColor: 0,
            fillColor: [120, 198, 57],
        },
        bodyStyles: { valign: 'top' },
        styles: {
            fontSize: 8,
            overflow: 'linebreak',
            columnWidth: 'auto',
        },
    });

    doc.addPage();

    y = 20; // Math.ceil(doc.autoTable.previous.finalY) + 10

    var footnotes = matrix.getFootnotes(matrix.type);

    doc.setFontSize(10);

    if (footnotes && footnotes.length) {
        for (var j = 0, footnotesLen = footnotes.length; j < footnotesLen; j++) {
            var footnote = footnotes[j];

            var splitFootnote = doc.splitTextToSize(footnote, 180);

            doc.text(splitFootnote, 10, y);
            y += (4 * splitFootnote.length) + 5;
        }
    }

    doc.output('save', 'HPE Nimble Storage Validated Configuration Matrix: ' + matrix.type + '.pdf');
}

