const _ = require('underscore');
var Marionette = require('backbone.marionette');
var EulaModel = require('./EulaModel');
var Spinner = require('app/views/components/Spinner');
var tpl = require('raw-loader!./EulaView.html');
var UserPreferencesController = require('app/modules/preferences/UserPreferencesController');

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    regions: {
        spinner: '[data-region="spinner"]',
    },

    ui: {
        eulaWrapper: '.eula-wrapper',
    },

    events: {
        'click [name="submit"]': 'accept',
        'click [name="cancel"]': 'cancel',
    },

    modelEvents: {
        'sync': 'onFetch',
    },

    initialize: function (options) {
        this.product = options.product;
        this.acceptanceModel =
            this.model = new EulaModel({}, {
                product: this.product,
            });
    },

    onRender: function () {
        this.showChildView('spinner', new Spinner({
            message: 'Loading license agreement...',
        }));
        this.ui.eulaWrapper.hide();
        this.model.fetch();
    },

    onFetch: function () {
        this.getRegion('spinner').empty();

        var iframe = this.$('iframe')[0];
        iframe.contentWindow.document.open();
        iframe.contentWindow.document.write(this.model.get('eula'));
        iframe.contentWindow.document.close();

        this.ui.eulaWrapper.show();
    },

    accept: function () {
        UserPreferencesController.acceptEula(this.product.get('name'));
        this.trigger('accept');
    },

    cancel: _ => _,

});
