const _ = require('underscore');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');
var Marionette = require('backbone.marionette'),
    LocalRetentionModel = require('./LocalRetentionModel'),
    DataProtectionLegend = require('./DataProtectionLegend');

module.exports = Marionette.ItemView.extend(
    /** @lends views/components/dashboard/RPOView.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/dashboard/RPOView
         * @constructor
         * @augments Marionette.ItemView
         */

        template: false,

        className: 'rpo-view',

        renderChart: function (container, categories, series) {
            var self = this;
            this.chart = new Highcharts.Chart({
                chart: {
                    type: 'column',
                    renderTo: container,
                    height: 350,
                    events: {
                        redraw: function () {

                        },
                        load: function () {
                            var chart = this;
                            _.delay(function () {
                                chart.reflow.call(chart);
                            }, 50);
                        },
                    },
                },
                credits: {
                    enabled: false,
                },
                title: {
                    text: 'RPO',
                },
                subtitle: {
                    text: 'Applications Derived by the Volume Performance Policy',
                },
                legend: {
                    enabled: true,
                },
                xAxis: {
                    type: 'category',
                    title: {
                        text: 'Snapshot Schedule Intervals',
                    },
                    categories: categories,
                    crosshair: false,
                    labels: {
                        formatter: function () {
                            return DataProtectionLegend.scheduleCategoryLabelFormatter(this.value);
                        },
                    },
                },
                yAxis: {
                    min: 0,
                    max: 100,
                    title: {
                        text: '% of Application Total Data',
                    },
                    labels: {
                        enabled: true,
                    },
                },
                tooltip: {
                    enabled: true,
                    valueDecimals: 2,
                    valueSuffix: ' TiB',
                    formatter: function () {
                        var s = '<b>' + Highcharts.numberFormat(this.point.y, 0) + '% (' +
                            Highcharts.numberFormat(this.point.values.volumeUsedTib, 2) + ' TiB)</b> of your ' +
                            'total <span style="color: ' + this.series.color + '">' +
                            this.point.section +
                            '</span>' +
                            '<span> data has an RPO of <b>' +
                            DataProtectionLegend.scheduleCategoryLabelFormatter(this.x) +
                            '</b></span>';
                        return s;
                    },
                },
                plotOptions: {
                    series: {
                        dataLabels: {
                            enabled: false,
                            formatter: function () {
                                return Highcharts.numberFormat(this.y, 2) + ' TiB';
                            },
                        },
                        minPointLength: 3,
                    },
                },
                series: series,
            });
        },

        onRender: function () {

            var categories = this.model.get('categories'),
                series = this.model.get('series');

            if (series.length === 0) {
                this.$el.append('<div>No RPO information available.</div>');
            } else {
                for (var i = 0; i < series.length; i++) {
                    var seriesItem = series[i];
                    seriesItem.color = DataProtectionLegend.getSeriesColor(seriesItem.name);
                }

                this.renderChart(this.el, categories, series);
            }
        },

        onDestroy: function () {
            if (this.chart) {
                this.chart.destroy();
                delete this.chart;
            }
        },

    });
