// PanelCaseMeta.js

import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';

// import PriorityLabel from '../../PriorityLabel';
import DateTime from '../../../../../components/DateTime';

import style from '../PanelStyles.scss';

import { QuickTable, QuickRow } from '../../QuickTable';

const cx = classnames.bind(style);


/**
 * Renders a panel displaying array information
 */
function PanelCaseMeta(props) {
    const { theCase } = props;
    return (
        <div className={cx('case-panel')}>

            <div className={cx('panel-content')}>

                <QuickTable>
                    <QuickRow
                        columns={[
                            'Date Opened',
                            <DateTime date={theCase.createdDate} format="M/D/YYYY h:mm A" />,
                        ]}
                    />
                    {theCase.closedDate ?
                        <QuickRow
                            columns={[
                                'Date Closed',
                                <DateTime date={theCase.closedDate} format="M/D/YYYY h:mm A" />,
                            ]}
                        /> : ''
                    }
                </QuickTable>

                <QuickTable>
                    <QuickRow columns={['Status', theCase.status]} />
                    <QuickRow columns={['Priority', theCase.priority]} />
                </QuickTable>

                {/*
                <QuickTable>
                    <QuickRow columns={['Auto Open?', (theCase.Auto_Open__c ? 'YES' : 'NO')]} />
                    <QuickRow columns={['Auto Close?', (theCase.Auto_Close__c ? 'YES' : 'NO')]} />
                </QuickTable>
                */}

                {/*
                <QuickTable>
                    <QuickRow columns={['accountId', theCase.accountId]} />
                    {theCase.suppliedEmail ?
                        <QuickRow
                            columns={[
                                'Reply to:',
                                <a href={`mailto:${theCase.suppliedEmail}`}>{theCase.suppliedEmail}</a>,
                            ]}
                        /> : ''
                    }
                    <QuickRow columns={['createdById', theCase.createdById]} />
                    <QuickRow columns={['ownerId', theCase.ownerId]} />
                    <QuickRow columns={['contactId', theCase.contactId]} />
                </QuickTable>
                */}

            </div>

        </div>
    );
}

PanelCaseMeta.propTypes = {
    theCase: PropTypes.object.isRequired,
};


export default PanelCaseMeta;
