const _ = require('underscore');
var Marionette = require('backbone.marionette'),
    tpl = require('raw-loader!./WellnessView.html'),
    errorTpl = require('raw-loader!app/templates/components/error.html'),
    WellnessSummaryView = require('./WellnessSummaryView'),
    WellnessIssuesView = require('./WellnessIssuesView'),
    WellnessSummaryModel = require('./WellnessSummaryModel'),
    WellnessEmailSettingsView = require('./WellnessEmailSettingsView'),
    MaintenanceWindowStatusView = require('./maintenanceWindow/MaintenanceWindowStatusView');
const App = require('app/Application');
const { buildUrlFromBase } = require('progeny/util/url');
const { NimbleUserTenantHelpers } = require('../../../progeny/modules/nimble/tenantSelection');

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    templateHelpers: {
        getNestedLink: buildUrlFromBase.bind(null, '/dashboards/nimble/wellness'),
    },

    ui: {
        downloadLink: '.download-link',
        spinner: '.loading-spinner2',
        contentEl: '.wellness-summary',
        caseCreateBtn: '#wellness-case-creation-btn',
        dailyEmailBtn: '#wellness-daily-email-btn',
        wellnessEmailInfo: '#wellness-info .email-info',
        caseCreateInfo: '#wellness-info .case-creation-info',
        emptyResults: '.empty-wellness',
        issuesEl: '#wellness-issues-container',
        viewMaintenanceWindowBtn: '#view-maintenance-window-btn',
    },

    events: {
        'click @ui.dailyEmailBtn': 'onClickDailyEmail',
        'click @ui.viewMaintenanceWindowBtn': 'onClickMaintenanceWindow',
    },

    regions: {
        maintenanceWindowStatusRegion: '.maintenance-window-status-region',
    },

    initialize: function () {
        this.wellnessSummaryModel = new WellnessSummaryModel();
        this.wellnessSummary = new WellnessSummaryView({ model: this.wellnessSummaryModel, history: this.options.history });
        this.wellnessIssues = new WellnessIssuesView({ model: this.model });
    },

    onClickDailyEmail: function (event) {
        event.preventDefault();
        App.modalRegion.show(new WellnessEmailSettingsView());
    },

    onRender: function () {
        var self = this;

        this.ui.contentEl.append(this.wellnessSummary.render().el);
        this.ui.issuesEl.append(this.wellnessIssues.render().el);

        if (NimbleUserTenantHelpers.isExternal() && !NimbleUserTenantHelpers.isPrimaryTenant()) {
            this.ui.dailyEmailBtn.hide();
            this.ui.wellnessEmailInfo.hide();
        }

        this.getRegion('maintenanceWindowStatusRegion').show(new MaintenanceWindowStatusView());
        this.ui.downloadLink.attr('href', this.api().central().account().segment('/wellness/export'));

        this.listenTo(this.wellnessIssues, 'issue-action:clear', this.wellnessSummary.refresh);
        this.listenTo(this.wellnessIssues, 'issue-action:ack', this.wellnessSummary.refresh);
        this.listenTo(this.wellnessIssues, 'issue-action:unack', this.wellnessSummary.refresh);

        this.listenTo(this.wellnessSummary, 'summary-error', function () {
            self.ui.spinner.hide();
        });
        this.listenTo(this.wellnessSummary, 'summary-load', function (results) {

            self.ui.spinner.hide();
            self.$('#wellness-summary-toolbar').show();

            if (_.isEmpty(results)) {
                self.ui.emptyResults.show();
            } else {
                self.ui.contentEl.show();
                self.ui.issuesEl.show();
            }
        });
    },

    onDestroy: function () {
        if (this.wellnessSummary) {
            this.wellnessSummary.destroy();
            delete this.wellnessSummary;
        }
        if (this.wellnessIssues) {
            this.wellnessIssues.destroy();
            delete this.wellnessIssues;
        }
    },

    showDetails: function (filter) {
        var self = this;
        var scrollFunc = function () {
            self.ui.issuesEl[0].scrollIntoView({ block: 'start', behavior: 'smooth' });
            if (!_.isEmpty(filter)) {
                self.ui.issuesEl.find('.grid-filter-wrapper').effect('highlight', { color: '#78c639' }, 2000);
            }
        };

        this.wellnessIssues.filterModel.clearValues();
        this.wellnessIssues.filterModel.set(filter);

        if (this.isRendered && this.ui.contentEl.is(':visible')) {
            scrollFunc();
        } else {
            this.listenToOnce(this.wellnessIssues, 'load', scrollFunc);
        }
    },

});
