import { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import { withRouter } from 'react-router-dom';
import { addMenu, removeMenu } from '@infosight/shell-api/lib/PrimaryNav';
import { onUserContextChange } from '@infosight/shell-api/lib/UserProfile';
import { buildUrl } from '@infosight/elmer/dist/utils/url';
import { AuthorizationEvaluator, AUTHORITIES, nimbleTenantAdminAccess, nimbleThirdPartyPermissionRequestAccess } from '../../user';
import { nimbleResellerAuthorizationEvaluator, NimbleUserTenantHelpers } from '../tenantSelection';
import { productInterface as nimble } from '../config';
import { BASE_URL } from '../../../config';
import { ID } from './constants';

class PrimaryNavExtension extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    componentDidMount() {
        this.handleUpdate();
        this.unsubscribe = onUserContextChange(this.handleUpdate);
    }

    componentWillUnmount() {
        if (this.unsubscribe) {
            this.unsubscribe();
        }
    }

    getDashboard({ match }) { // eslint-disable-line class-methods-use-this
        // TODO the global user should be injected via some API or via commands
        const options = AuthorizationEvaluator.filter([
            {
                id: 'nimble', // Don't change the id properties without updating the nav sorting
                title: 'Nimble Storage',
                options: AuthorizationEvaluator.filter([
                    {
                        id: 'operational',
                        title: 'Operational',
                        url: buildUrl(match.url, '/dashboards/nimble/operational'),
                    },
                    {
                        id: 'executive',
                        title: 'Executive',
                        url: buildUrl(match.url, '/dashboards/nimble/executive'),
                    },
                    {
                        id: 'wellness',
                        title: 'Wellness',
                        url: buildUrl(match.url, '/dashboards/nimble/wellness'),
                    },
                    {
                        id: 'capacity',
                        title: 'Capacity',
                        url: buildUrl(match.url, '/dashboards/nimble/capacity'),
                    },
                    {
                        id: 'labs',
                        title: 'Labs',
                        url: buildUrl(match.url, '/dashboards/nimble/labs'),
                    },
                ]),
                access: {
                    custom() {
                        const resellerCase = nimbleResellerAuthorizationEvaluator();
                        if (resellerCase !== undefined) {
                            return resellerCase;
                        }

                        return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() && AuthorizationEvaluator.evaluate({
                            access: {
                                authorities: AUTHORITIES.PRODUCT_FAMILY_NIMBLE,
                            },
                        });
                    },
                },
            },
        ]);

        /* When we switch tenant we call loadAuthorizationEvaluator to get authorization for new tenant, till the time
         * this API call is not completed AuthorizationEvaluator filters all the options that are passed.
         * As default value of NimbleUserTenantHelpers.isReseller() is false, so during this switch who is not authorized as reseller
         * will not see this option.
         */
        if (NimbleUserTenantHelpers.isReseller()) {
            options[0].options.unshift({
                id: 'resellerdashboard',
                title: 'Reseller',
                url: buildUrl(match.url, '/dashboards/nimble/resellerdashboard'),
            });
        }

        return options.length ? options : null;
    }

    getInfrastructure({ match }) { // eslint-disable-line class-methods-use-this
        const config = nimble;
        const options = AuthorizationEvaluator.filter([
            {
                id: 'storage/nimble', // Don't change the id properties without updating the nav sorting
                title: config.attributes.name,

                options: config.getNavRoutes().map(({ type, route }) => ({
                    id: route,
                    url: buildUrl(match.url, 'infrastructure', 'storage', 'nimble', route),
                    title: config.getPluralTitle(type),
                    preserveQueries: config.attributes.preserveQueries,
                })),
                access: {
                    custom() {
                        const resellerCase = nimbleResellerAuthorizationEvaluator();
                        if (resellerCase !== undefined) {
                            return resellerCase;
                        }

                        return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() && AuthorizationEvaluator.evaluate({
                            access: {
                                authorities: AUTHORITIES.PRODUCT_FAMILY_NIMBLE,
                            },
                        });
                    },
                },
            },
        ]);

        if (!options.length) {
            return null;
        }

        return options.map(x => ({ tier: 'Storage', options: x }));
    }

    getResources({ match }) { // eslint-disable-line class-methods-use-this
        const options = AuthorizationEvaluator.filter([
            {
                id: 'nimble', // Don't change the id properties without updating the nav sorting
                title: 'Nimble Storage',
                depth: 1,
                options: AuthorizationEvaluator.filter([
                    {
                        id: 'support',
                        title: 'Support',
                        url: buildUrl(match.url, '/resources/nimble/support'),
                        access: {
                            custom() {
                                return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() && AuthorizationEvaluator.evaluate({
                                    access: {
                                        $or: [
                                            {
                                                authorities: AUTHORITIES.PRODUCT_FAMILY_NIMBLE,
                                            },
                                            {
                                                authorities: [AUTHORITIES.SUPPORT_CASE],
                                            },
                                        ],
                                    },
                                });
                            },
                        },
                    },
                    {
                        id: 'docs',
                        title: 'Documentation',
                        url: buildUrl(match.url, '/resources/nimble/docs'),
                        access: {
                            authorities: [AUTHORITIES.PRODUCT_FAMILY_NIMBLE, AUTHORITIES.PRODUCT_FAMILY_STOREONCE, AUTHORITIES.PRODUCT_FAMILY_STORESERV],
                        },
                    },
                    {
                        id: 'software',
                        title: 'Software Downloads',
                        url: buildUrl(match.url, '/resources/nimble/software'),
                        access: {
                            custom() {
                                return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() && AuthorizationEvaluator.evaluate({
                                    access: {
                                        authorities: AUTHORITIES.PRODUCT_FAMILY_NIMBLE,
                                    },
                                });
                            },
                        },
                    },
                    {
                        id: 'vmarray',
                        title: 'Virtual Array Licenses',
                        url: buildUrl(match.url, '/resources/nimble/vmarray'),
                        access: {
                            custom() {
                                return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() && AuthorizationEvaluator.evaluate({
                                    access: {
                                        authorities: [AUTHORITIES.VIRTUAL_ARRAY_ACCESS],
                                    },
                                });
                            },
                        },
                    },
                    {
                        id: 'validated-configuration-matrix',
                        title: 'Validated Configuration Matrix',
                        url: '/resources/nimble/validated-configuration-matrix',
                        access: {
                            authorities: [AUTHORITIES.PRODUCT_FAMILY_NIMBLE, AUTHORITIES.REALM_INTERNAL],
                        },
                    },
                    {
                        id: 'sizing-primary',
                        title: 'Primary Storage Sizing Tool',
                        url: `${buildUrl(BASE_URL, 'labs/apps/sizing_tools/primary_storage')}/`,
                        nodeType: 'external',
                        access: {
                            authorities: [AUTHORITIES.REALM_INTERNAL, AUTHORITIES.ATTRIBUTE_NIMBLE_RESELLER],
                        },
                    },
                    {
                        id: 'sizing-sfa',
                        title: 'Backup Storage Sizing Tool',
                        url: `${buildUrl(BASE_URL, 'labs/apps/sizing_tools/secondary_storage')}/`,
                        nodeType: 'external',
                        access: {
                            authorities: [AUTHORITIES.REALM_INTERNAL, AUTHORITIES.ATTRIBUTE_NIMBLE_RESELLER],
                        },
                    },
                    {
                        id: 'connect',
                        title: 'Nimble Connect',
                        url: 'https://community.hpe.com/t5/HPE-Nimble-Storage/ct-p/nimble-storage',
                        nodeType: 'external',
                    },
                    {
                        id: 'audit-report',
                        title: 'Audit Report',
                        url: buildUrl(match.url, '/resources/nimble/audit-report'),
                        access: {
                            custom() {
                                return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() &&
                                    AuthorizationEvaluator.evaluate({ access: nimbleThirdPartyPermissionRequestAccess })
                                    && !AuthorizationEvaluator.evaluate({ access: { authorities: AUTHORITIES.ATTRIBUTE_NIMBLE_SUPPORT_PROVIDER } });
                            },
                        },
                        nodeType: 'internal',
                        divider: true,
                    },
                ]),
                collapseHeader: false,
            },
        ]);

        return options.length ? options : null;
    }

    getSettings({ match }) { // eslint-disable-line class-methods-use-this
        const options = AuthorizationEvaluator.filter([
            {
                id: 'nsis', // Don't change the id properties without updating the nav sorting
                title: 'Nimble Storage',
                options: AuthorizationEvaluator.filter([
                    {
                        id: 'selfRegistration',
                        title: 'Customer Registration',
                        url: buildUrl(match.url, '/settings/nimble/registration'),
                        access: {
                            authorities: {
                                $not: [AUTHORITIES.PRODUCT_FAMILY_NIMBLE],
                            },
                        },
                        nodeType: 'internal',
                    },
                    {
                        id: 'permissions',
                        title: 'Permissions',
                        url: buildUrl(match.url, '/settings/nimble/permissions'),
                        access: {
                            custom() {
                                return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() &&
                                    AuthorizationEvaluator.evaluate({ access: nimbleThirdPartyPermissionRequestAccess })
                                    && !AuthorizationEvaluator.evaluate({ access: { authorities: AUTHORITIES.ATTRIBUTE_NIMBLE_SUPPORT_PROVIDER } });
                            },
                        },
                        nodeType: 'internal',
                    },
                    {
                        id: 'telemetry',
                        title: 'Telemetry Settings',
                        url: buildUrl(match.url, '/settings/nimble/telemetry'),
                        access: {
                            custom() {
                                return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() && AuthorizationEvaluator.evaluate(nimbleTenantAdminAccess);
                            },
                        },
                        nodeType: 'internal',
                    },
                    {
                        id: 'registry',
                        title: 'Asset Registry',
                        url: buildUrl(match.url, '/settings/nimble/assetRegistry'),
                        access: {
                            custom() {
                                return NimbleUserTenantHelpers.currentTenantIsNimbleTenant() && AuthorizationEvaluator.evaluate({
                                    access: {
                                        $or: [
                                            nimbleTenantAdminAccess,
                                            { authorities: AUTHORITIES.ADDRESS_ADMIN },
                                        ],
                                    },
                                });
                            },
                        },
                        nodeType: 'internal',
                    },
                ]),
                collapseHeader: false,
            },
        ]).filter(({ options: innerOptions }) => !innerOptions || innerOptions.length);

        return options.length ? options : null;
    }

    handleUpdate() { // eslint-disable-line class-methods-use-this
        // TODO this stuff needs to be passed in from something whether its via an extension or passed down via props.
        // Consider wrapping up all extensions into a component that is a sibling of ExtensionPoints
        const externals = { match: this.props.match };

        // Everything is always here so we can react to changes
        [
            { menuId: 'dashboards', id: ID, content: this.getDashboard(externals) },
            { menuId: 'infrastructure', id: ID, content: this.getInfrastructure(externals) },
            { menuId: 'resources', id: ID, content: this.getResources(externals) },
            { menuId: 'settings', id: ID, content: this.getSettings(externals) },
        ].forEach((extension) => {
            if (extension.content) {
                addMenu(extension);
            } else {
                removeMenu(extension);
            }
        });
    }

    render() {
        return null;
    }
}

PrimaryNavExtension.propTypes = {
    match: PropTypes.object.isRequired,
};

export default withRouter(PrimaryNavExtension);
