import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import { findRole } from './roles';
import ChangeRoleForm from './ChangeRoleForm';
import DeleteUserConfirmation from './DeleteUserConfirmation';

class RoleCell extends Component {
    constructor(...args) {
        super(...args);
        autobind(this);

        this.state = { modal: false };
    }

    changeRole(e) {
        e.preventDefault();
        this.setState({ modal: ChangeRoleForm });
    }

    deleteUser(e) {
        e.preventDefault();
        this.setState({ modal: DeleteUserConfirmation });
    }

    closeModal() {
        this.setState({ modal: null });
    }

    render() {
        const { modal } = this.state;
        const { user } = this.props;
        return (
            <React.Fragment>
                {findRole(user.role).name} (
                <a href="#changeRole" onClick={this.changeRole}>Change</a>
                &nbsp;|&nbsp;
                <a href="#deleteUser" onClick={this.deleteUser}>Delete</a>
                )
                {modal && React.createElement(modal, { user, closeModal: this.closeModal })}
            </React.Fragment>
        );
    }
}

RoleCell.propTypes = {
    user: PropTypes.shape({
        role: PropTypes.string.isRequired,
    }),
};

RoleCell.defaultProps = {};

export default RoleCell;
