var _ = require('underscore'),
    Backbone = require('backbone'),
    Marionette = require('backbone.marionette'),
    jqueryui = require('jqueryui'),
    VirtualArraysLicenseKeysGridView = require('./VirtualArraysLicenseKeysGridView'),
    VirtualArrayCollection = require('./VirtualArrayCollection'),
    tpl = require('raw-loader!./VirtualArraysLicenseKeysView.html');
const App = require('app/Application');

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    templateHelpers: function () {
        var session = App.request('global', 'get:current:session');
        return {
            vmImageLicenseLimit: session.get('vmImageLicenseLimit'),
            login: session.get('username'),
        };
    },

    regions: {
        licenseKeysGridRegion: '#license-keys-grid',
    },

    initialize: function () {
        this.collection = new VirtualArrayCollection();
    },

    onRender: function () {
        this.licenseKeysGridRegion.show(new VirtualArraysLicenseKeysGridView({
            collection: this.collection,
        }));
    },
});
