const $ = require('jquery');
const App = require('app/Application');
const _ = require('underscore');
const { SERVER_BASE_URL, BASE_URL } = require('progeny/config');
const { buildUrl } = require('@infosight/elmer/dist/utils/url');

var Marionette = require('backbone.marionette');
var tpl = require('raw-loader!./LabAppView.html');
var LabsFeedBackView = require('./LabsFeedBackView');
var LabAppUserGuide = require('./LabAppUserGuide');

module.exports = Marionette.ItemView.extend({

    className: 'lab-app',

    template: _.template(tpl),

    templateHelpers: function () {
        var reportId = this.popReportIdFromUrl();
        var currentAccount = App.request('global', 'get:account:selection');
        if (currentAccount) {
            var accountModel = currentAccount.getAccount();
            if (accountModel) {
                return {
                    id: reportId,
                    account: accountModel.toJSON(),
                    pod: this.getOption('pod'),
                    url(...args) {
                        return buildUrl(BASE_URL, ...args);
                    },
                };
            }
        }
        // template fail-safe
        return {
            id: reportId,
            account: { id: 'invalid' },
            url(...args) {
                return buildUrl(BASE_URL, ...args);
            },
        };
    },

    ui: {
        feedbackData: '.labsFeedback',
    },

    events: {
        'click .labsFeedback': 'onClickLabsFeedback',
    },

    userManualAdded: false,

    popReportIdFromUrl: function () {
        return this.getOption('appid');
    },

    onRender: function () {

        var srcElem = this.$el,
            labFrame = srcElem.find('.shiny-container iframe');

        labFrame.on('load error ready', function () {
            srcElem.find('.shiny-container .lab-app-loading').hide();
            labFrame.show();

        });

        labFrame.hide();
        this.setHeight();
    },

    setHeight() {
        let subtract = 0;
        const el = this.el.querySelector('.shiny-container');
        if (!el) {
            return;
        }

        const footer = $('#infosight-app-footer');
        if (footer.length) {
            // Subtract the height of the footer
            subtract += $(document).height() - footer.get(0).getBoundingClientRect().top;
        }

        // Subtract anything above the element
        subtract += el.getBoundingClientRect().top;

        // A little extra for padding and borders
        subtract += 10;

        el.style.height = `calc(100vh - ${subtract}px)`;
    },

    onDestroy: function () {
        var target = $('ul.breadcrumb li').last();
        target.find('a.fa-info-circle').off('click');
    },

    onClickLabsFeedback: function (event) {
        App.modalRegion.show(new LabsFeedBackView({
                model: this.model,
            }),
        );
    },

    collectionReady: function () {
        var thisView = this;
        var reportId = thisView.popReportIdFromUrl();
        var reportModel = this.collection.find({ id: reportId });
        thisView.model = reportModel;

        if (thisView.userManualAdded) {
            return;
        }

        thisView.userManualAdded = true;

        // breadcrumb post-render hack
        setTimeout(function () {

            var target = $('ul.breadcrumb li').last();
            target.find('a.fa-info-circle').remove(); // ensure a clean start

            target.append('<a href="#" class="fa fa-info-circle">&nbsp;</a>')
                .on('click', function (e) {
                    e.preventDefault();
                    App.modalRegion.show(new LabAppUserGuide({ model: reportModel }));
                    return false;
                });

        }, 100);
    },

    initialize: function () {
        // prevent access with "All Accounts" mode
        var accountSelection = App.request('global', 'get:account:selection');
        if (!accountSelection.isSingle()) {
            setTimeout(function () {
                window.location = buildUrl(SERVER_BASE_URL, '/#assets');
            }, 50);
            return;
        }

        this.userManualAdded = false;

        // if we navigated from within the app, the content region will load the model
        // but if the user hit the route, directly, then we need to setup the context.
        if (!this.model) {

            if (this.collection.length) {
                this.collectionReady.call(this);
            } else {
                this.collection.bind('change reset add remove', this.collectionReady, this);
            }

        }

    },

});
