const _ = require('underscore');
var Backbone = require('backbone'),
    tpl = require('raw-loader!./AssetShelfTooltip.html'),
    AssetShelfTooltipDetails = require('app/views/components/asset/AssetShelfTooltipDetails'),
    Spinner = require('app/views/components/Spinner');

var template = _.template(tpl);

module.exports = Backbone.View.extend(
    /** @lends views/components/asset/AssetShelfTooltip.prototype */ {

        /**
         * @classdesc Creates a tooltip on the Asset Tab with asset shelf information
         * @name views/components/asset/AssetShelfTooltip
         * @constructor
         * @augments Backbone.View
         */

        initialize: function (options) {
            this.listenTo(this.collection, 'sync', this.onCollectionLoaded);
            this.listenTo(this.collection, 'error', this.onLoadFailed);
        },

        /**
         * Populates the shelf info in the asset tab shelf tooltip
         */
        onCollectionLoaded: function () {
            console.log('onCollectionLoaded!');

            var container = document.createDocumentFragment();

            // add a view for each model to the document fragment
            this.collection.each(function (model) {
                var row = new AssetShelfTooltipDetails({ model: model });
                container.appendChild(row.render().el);
            });

            this.spinner.hide();

            // finally append the fragment
            this.$el.find('.shelf-container').append(container);
        },

        /**
         * Displays error msg in the asset tab shelf tooltip
         */
        onLoadFailed: function () {
            this.spinner.hide();
            this.$el.append('<div>Unable to fetch shelves at this time.</div>');
        },

        /**
         * Overridden Backbone render method.
         * @return {AssetShelfTooltip} Returns this instance (useful for chaining).
         */
        render: function () {
            this.$el.html(template());
            this.spinner = new Spinner();
            this.$el.find('.spinner').append(this.spinner.render().el);
            return this;
        },
    });
