const _ = require('underscore');
var Backbone = require('backbone'),
    tpl = require('raw-loader!./CaseStatisticsView.html'),
    CaseStatisticsChart = require('./CaseStatisticsChart'),
    CaseStatisticsDonutView = require('./CaseStatisticsDonutView');

var template = _.template(tpl);

module.exports = Backbone.View.extend(
    /** @lends views/components/dashboard/CaseStatisticsView.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/dashboard/CaseStatisticsView
         * @constructor
         * @augments Backbone.View
         */


        className: 'case-statistics-view',

        render: function () {
            this.$el.html(template(this.model.toJSON()));

            this.caseStatisticsChart = new CaseStatisticsChart({
                model: this.model,
            });
            this.$el.find('.chart-container').append(
                this.caseStatisticsChart.render().el);

            this.caseStatisticsDonutView = new CaseStatisticsDonutView({
                model: this.model,
            });
            this.$el.find('.donut-container').append(
                this.caseStatisticsDonutView.render().el);

            return this;
        },

    });
