const $ = require('jquery');
const App = require('app/Application');
const _ = require('underscore');
var Marionette = require('backbone.marionette'),
    moment = require('moment');
require('backbone.stickit');
var tpl = require('raw-loader!app/modules/virtualArrays/VirtualArraysExpirationDateModal.html');

var FlashNotification = require('@infosight/elmer/dist/components/FlashNotification').default;

module.exports = Marionette.LayoutView.extend({

    className: 'virtual-arrays-dialog',

    template: _.template(tpl),

    templateHelpers: function () {
        return {
            licenseKey: this.model.get('licenseKey'),
        };
    },

    bindings: {
        '#extension-in-days': {
            observe: 'expirationDate',
            onSet: function (value, options) {
                var expirationDate = moment().add((value || 0), 'day').unix();
                // Convert to millis.
                return expirationDate * 1000;
            },
            updateView: false,
        },
    },

    ui: {
        extensionInDays: '#extension-in-days',
        submit: 'button[name="submit"]',
    },

    events: {
        'click @ui.submit': 'submit',
    },

    initialize: function () {
        _.bindAll(this, 'showDialog');
    },

    showDialog: function () {
        App.modalRegion.show(this);
    },

    submit: function () {
        var self = this,
            licenseKey = self.model.get('licenseKey');

        if (!self.model.get('expirationDate')) {
            var expirationDate = moment().add((this.ui.extensionInDays.val() || 0), 'day').unix();
            this.model.set('expirationDate', expirationDate * 1000);
        }
        $.ajax({
            url: this.api().central().segment('vmarrays').segment(licenseKey),
            type: 'PUT',
            contentType: 'application/json',
            dataType: 'json',
            data: JSON.stringify(this.model.pick('expirationDate')),
        })
            .done(function () {
                FlashNotification.success(
                    `Expiration date of license key ${licenseKey} was updated`,
                );
            })
            .fail(function (jqxhr) {
                FlashNotification.error(
                    `Could not update expiration date of license key ${licenseKey}: ${jqxhr.responseJSON.message}`,
                );
            })
            .always(function () {
                self.trigger('expiration-date:updated');
            });
    },

    onRender: function () {
        this.stickit();
    },
});
