var Backbone = require('backbone');
var FacetBucketModel = require('./FacetBucketModel');
const { compareVersion } = require('./documents');

module.exports = Backbone.Collection.extend({

    model: FacetBucketModel,

    initialize: function (models, options) {
        console.assert(options.facetField);
        this.facet = options.facet;
        this.facetField = options.facetField;
    },

    comparator: function (a, b) {
        var lowerA = a.get('value').toLowerCase();
        var lowerB = b.get('value').toLowerCase();

        if (this.facetField === 'Version Family') {
            return compareVersion(lowerA, lowerB);
        }
        // alphabetical order
        return lowerA < lowerB ? -1 : lowerA > lowerB ? 1 : 0;
    },

});
