import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import PopoverTrigger from '@infosight/elmer/dist/components/PopoverTrigger';
import Switch from './Switch';
import style from './style.scss';

const cx = classnames.bind(style);

const statusMap = {
    OK: {
        isError: false,
        description: 'Registered',
        remediation: null,
    },
    NoVCentersFound: {
        isError: true,
        description: 'No vCenters found',
        remediation: 'No vCenters were found.  Please re-register the vCenter plugin using the System Management UI',
    },
    OtherCollectionError: {
        isError: true,
        description: 'Unable to collect inventory',
        remediation: 'An unexpected error occurred collecting information from vCenter.  Please contact Nimble support',
    },
    '<null>': {
        isError: true,
        description: 'Not Registered',
        remediation: 'Please register the vCenter plugin using the System Management UI',
    },
};

const popover = status => (
    <div>
        <h5>vCenter Plugin Status: <b>{status.description}</b></h5>
        <p>{status.remediation}</p>
    </div>
);

export default class VmwareContentCell extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    handleChange(event) {
        this.props.handleChange({
            arraySn: this.props.arraySn,
            setting: 'vmware',
            enabled: event.target.checked,
        });
    }

    renderStatus() {
        const status = statusMap[this.props.status || '<null>'];
        const iconClass = cx({
            fa: true,
            'fa-lg': true,
            'fa-warning': status.isError,
            'text-alert': status.isError,
            'fa-check-circle': !status.isError,
            'text-success': !status.isError,
        });
        return (
            <PopoverTrigger popover={popover(status)}>
                <i className={iconClass} />
            </PopoverTrigger>
        );
    }

    render() {
        return (
            <div className={cx('switch-with-status')}>
                <Switch handleChange={this.handleChange} on={this.props.enabled} />
                {this.props.enabled && this.renderStatus()}
            </div>
        );
    }
}

VmwareContentCell.propTypes = {
    handleChange: PropTypes.func.isRequired,
    arraySn: PropTypes.string.isRequired,
    status: PropTypes.string,
    enabled: PropTypes.bool,
};
