var Backbone = require('backbone');
var ProductVersionModel = require('./ProductVersionModel');

function padVersionParts(version) {
    var pad = '000000';
    var parts = version.split(/\.|\-/);
    return parts.map(function (part) {
        return part.length >= pad.length ? part : (pad + part).slice(-pad.length);
    }).join('.');
}

module.exports = Backbone.Collection.extend({

    model: ProductVersionModel,

    initialize: function (models, options) {
        console.assert(options.product);
        this.product = options.product;
    },

    comparator: function (a, b) {
        var aVersion = padVersionParts(a.get('version'));
        var bVersion = padVersionParts(b.get('version'));
        return bVersion.localeCompare(aVersion); // b.localCompare(a) to sort in descending order
    },

});
