const _ = require('underscore');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');
var Backbone = require('backbone'),
    FormattingUtils = require('app/util/FormattingUtils');

module.exports = Backbone.View.extend(
    /** @lends views/components/dashboard/./SpaceSavingsDonutView.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/dashboard/./SpaceSavingsDonutView
         * @constructor
         * @augments Backbone.View
         */


        className: 'space-savings-donut-view',

        renderChart: function (container, spaceSaved, pctSaved, series) {
            var self = this;
            this.chart = new Highcharts.Chart({
                chart: {
                    type: 'pie',
                    renderTo: container,
                    height: 300,
                    // width: 200,
                    events: {
                        load: function () {
                            var chart = this;

                            // reflow the chart to ensure it fills the container
                            _.delay(function () {
                                chart.reflow.call(chart);
                            }, 50);
                        },
                    },
                },
                title: {
                    text: '<span style="color: #78c639">' + pctSaved + 'x</span><br/>' +
                        '<span>Reduction</span>',
                    align: 'center',
                    verticalAlign: 'middle',
                    y: 2,
                },
                xAxis: {
                    type: 'category',
                },
                yAxis: {
                    title: {
                        text: 'Data',
                    },
                    labels: {
                        overflow: 'justify',
                    },
                },
                credits: {
                    enabled: false,
                },
                legend: {
                    enabled: false,
                },
                tooltip: {
                    useHTML: true,
                    formatter: function () {
                        var point = this.point,
                            series = this.series,
                            pctSaved = '';

                        if (point.name != 'Capacity Used<br>(Pre-Savings)') {
                            var spaceSavings = self.model.get('spaceSavings'),
                                pct = Highcharts.numberFormat((this.y / spaceSavings * 100), 2);
                            pctSaved = ' (' + pct + '%)';
                        }

                        return '<span style="color:' + point.color + '">' + point.name + '</span>' +
                            ':</br><b>' + FormattingUtils.formatTB(point.y) + '</b>' + pctSaved + '<br/>';

                    },//,
                },
                plotOptions: {
                    pie: {
                        dataLabels: {
                            enabled: false,
                            size: 50,
                        },
                        center: ['50%', '50%'],
                    },
                },
                series: series,
            });
        },

        render: function () {
            this.$el.html();

            var series = {
                    name: 'Space Savings',
                    innerSize: '75%',
                    data: [],
                },
                spaceSavings = this.model.get('spaceSavings'),
                savingsFactor = this.model.get('savingsFactor'),
                traditionalSpace = this.model.get('traditionalSpace'),
                pctSaved = Highcharts.numberFormat(spaceSavings / traditionalSpace * 100, 0);

            if (this.model.get('compression') > 0) {
                series.data.push({
                    name: 'Compression Savings',
                    y: this.model.get('compression'),
                    color: 'rgba(47, 126, 216, 0.4)',
                });
            }

            if (this.model.get('zeroCopyClones') > 0) {
                series.data.push({
                    name: 'Clone Savings',
                    y: this.model.get('zeroCopyClones'),
                    color: 'rgba(47, 126, 216, 1)',
                });
            }

            if (this.model.get('deduplication') > 0) {
                series.data.push({
                    name: 'Deduplication Savings',
                    y: this.model.get('deduplication'),
                    color: 'rgba(47, 126, 216, 0.8)',
                });
            }
            this.renderChart(this.el, spaceSavings, savingsFactor, [series]);

            return this;
        },

    });
