const _ = require('underscore');
var Backbone = require('backbone');
var FacetBucketCollection = require('./FacetBucketCollection');

var TITLE_MAP = {
    'Version Family': 'Software Version',
};

module.exports = Backbone.Model.extend({

    defaults: {
        field: null,
    },

    idAttribute: 'field',

    parse: function (data) {
        data.buckets = new FacetBucketCollection(data.buckets, {
            parse: true,
            facet: this,
            facetField: data.field,
        });
        this.listenTo(data.buckets, 'change', this.onBucketChange);
        return data;
    },

    onBucketChange: function () {
        this.trigger('change', this, {});
    },

    getSelectedBucket: function () {
        return this.get('buckets').findWhere({ selected: true });
    },

    getSelectedBuckets: function () {
        return this.get('buckets').where({ selected: true });
    },

    selectBucket: function (bucketValue) {
        var bucket = this.get('buckets').findWhere({ value: bucketValue });
        if (bucket) {
            bucket.set('selected', true);
        }
    },

    clearSelection: function () {
        this.get('buckets').where({ selected: true }).forEach(function (bucket) {
            bucket.set('selected', false);
        });
    },

    isSelected: function (bucketValue) {
        var bucket = this.get('buckets').findWhere({ value: bucketValue });
        return bucket && bucket.get('selected');
    },

    hasSelected: function () {
        return _.some(this.get('buckets').pluck('selected'));
    },

    getTitle: function () {
        var field = this.get('field');
        return TITLE_MAP[field] || field;
    },

});
