const _ = require('underscore');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');
const MBtoGB = 1024;

module.exports = {

    formatSize: function (valueMB, decimalPlaces) {

        var value = valueMB,
            suffix = 'MB';

        // TiB
        if (valueMB > 1048576) {
            value = valueMB / 1048576;
            suffix = 'TiB';
            // force the precision to keep at least 1 or 2 decimal places of precision
            if (decimalPlaces === 0) {
                decimalPlaces = 2;
            }
        }
        // GB
        else if (valueMB > 1024) {
            value = valueMB / 1024;
            suffix = 'GiB';
        }
        // MB
        else {
            value = valueMB;
            suffix = 'MB';
        }

        return Highcharts.numberFormat(value,
            _.isNumber(decimalPlaces) ? decimalPlaces : 2) + ' ' + suffix;
    },

    formatGB: function (valueGB, decimalPlaces) {
        var value = valueGB,
            suffix = 'GiB';

        // TB
        if ((valueGB >= 1024) || (valueGB === 0) || (valueGB <= -1024)) {
            value = valueGB / 1024;
            suffix = 'TiB';
        }

        return Highcharts.numberFormat(value,
            _.isNumber(decimalPlaces) ? decimalPlaces : 2) + ' ' + suffix;
    },

    formatTB: function (valueTB, decimalPlaces) {
        var valueGB = valueTB * 1024,
            suffix = 'GiB';
        var value = valueGB;
        // Larger than 1TB
        if ((valueGB >= 1024) || (valueGB === 0) || (valueGB <= -1024)) {
            value = valueTB;
            suffix = 'TiB';
        }

        return Highcharts.numberFormat(value,
            _.isNumber(decimalPlaces) ? decimalPlaces : 2) + ' ' + suffix;
    },

};
