import React from 'react';
import PropTypes from 'prop-types';
import { compact } from 'underscore';
import GridView from './GridView';
import Column from './Column';
import ColumnGroup from './ColumnGroup';
import Pagination from '../Pagination';

const toColumn = ({ dataKey, summaryFilter, textFilter, ...props }) => ({
    ...props,
    key: dataKey,
    subquery: summaryFilter ? 'summary' : !!textFilter,
});

const toColumnGroup = ({ children, dataKey, ...props }) => ({
    ...props,
    key: dataKey,
    columns: compact(React.Children.map(
        children,
        ({ type, props: childProps }) => (type === Column ? toColumn(childProps) : undefined))
    ),
});

const toPagination = ({ numberPerPage, dataOffset }) => ({
    perPage: numberPerPage,
    offset: dataOffset,
});

/**
 * Define GridView attributes in JSX instead of nested objects.
 */
function SimpleGridView({ children, disablePagination, ...props }) {
    const columns = [];

    // Empty object lets GridView use default pagination settings
    let pagination = !disablePagination;

    React.Children.forEach(children, ({ type, props: childProps }) => {
        if (type === Column) {
            columns.push(toColumn(childProps));
        } else if (type === ColumnGroup) {
            columns.push(toColumnGroup(childProps));
        } else if (type === Pagination && !disablePagination) {
            pagination = toPagination(childProps);
        }
    });

    return <GridView columns={compact(columns)} pagination={pagination} {...props} />;
}

SimpleGridView.propTypes = {
    data: PropTypes.arrayOf(PropTypes.object).isRequired,

    /**
     * Children are not rendered. The following components are used to describe aspects of the Gridview:
     * `Column`, `Pagination`
     */
    children: PropTypes.any.isRequired,

    /**
     * Pagination is enabled by default, manually disable it. To override defaults, add a `Pagination` component to
     * `props.children`
     */
    disablePagination: PropTypes.bool,

    /**
     * Query to search across all columns
     */
    tableQuery: PropTypes.string,

    /**
     * Key in data hash that uniquely identifies the row. If this is not unique, React will not render the row.
     */
    rowKey: PropTypes.string.isRequired,
};

SimpleGridView.defaultProps = {
    noPagination: false,
};

export default SimpleGridView;
