import numeral from 'numeral';
import { formatValue } from '../../../util/formatter/index';
import { FORMAT_BYTE_PRECISION } from '../../../util/formatter/constants';

export const arrayStatus = {
    EXPIRED: {
        textClass: 'arrayWarrantyExpiring',
        icon: 'warning',
        text: 'EXPIRED',
    },
};
export const iconSize = 17;

/**
 * will take array's freespace and capacity
 * find out the spaced used and convert it into percentage
 * @param freeSpace
 * @param capacity
 * @example
 * // Output: 30%
 * arrayUsedSpaceInPercentage(30, 100);
 * @returns {String} Returns string of numeric value and percentage sign
 */
export const arrayUsedSpaceInPercentage = (freeSpace, capacity) => (numeral((capacity - freeSpace) / capacity).format('0%'));

export const transformDataForCSV = rawData => rawData.map(row => ({
    'Customer Name': row.customerName,
    'Account ID': row.accountId,
    'Array Capacity': formatValue(row.arrayCapacityBytes, FORMAT_BYTE_PRECISION) || '',
    'Array Free Space': formatValue(row.arrayFreeBytes, FORMAT_BYTE_PRECISION) || '',
    'Array Free Space in %': arrayUsedSpaceInPercentage(row.arrayFreeBytes, row.arrayCapacityBytes) || '',
    'Array Serial Number': row.arraySN,
    'Array Model': row.model || '',
    'Software Version': row.softwareVersion || '',
    'Is All Flash': row.isAllFlash || '',
    'Support Agreement': row.supportAgrmt || '',
    'Support End Date': row.supportEndDate || '',
    'Hardware Upgrade Recommended': row.hasHardwareRecommendations ? 'YES' : 'NO',
    'Software Upgrade Recommended': row.hasSoftwareRecommendations ? 'YES' : 'NO',
}));

/**
 * will convert object to string in comma separated values format
 * keys will be taken as header and values as row values
 * @param objArray
 * @example
 * // Name,Age
 * // Adarsh,32
 * // Ram, 32
 * convertObjectToCSV([{name: 'Adarsh', age: 32},{name: 'Ram', age: 32},]);
 * @returns {String} Returns string with comma separated values
 */
export const convertObjectToCSV = (objArray) => {
    const array = typeof objArray !== 'object' ? JSON.parse(objArray) : objArray;
    return array.reduce((str, next) => {
        str += `${Object.values(next).map(value => `"${value}"`).join(',')} \r\n`;
        return str;
    }, `${Object.keys(array[0]).map(value => `"${value}"`).join(',')} \r\n`);
};
