const $ = require('jquery');
const _ = require('underscore');
var Marionette = require('backbone.marionette');
var tpl = require('raw-loader!./LanguagePreferenceForm.html');

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    events: {
        'change :checkbox': 'onChange',
    },

    onRender: function () {
        var self = this;
        this.model.get('languages').forEach(function (lang) {
            self.$(':checkbox[value="' + lang + '"]').prop('checked', true);
        });
    },

    onChange: function () {
        this.model.set('languages',
            this.$(':checkbox:checked').map(function () {
                return $(this).val();
            }).get(),
        );
    },

});
