const _ = require('underscore');
var Marionette = require('backbone.marionette');
var WhatsNewView = require('./WhatsNewView');
var NimbleOsReleasesView = require('./NimbleOsReleasesView');
var tpl = require('raw-loader!./HomeView.html');

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    regions: {
        whatsNew: '[data-region="whatsNew"]',
        nimbleOsReleases: '[data-region="nimbleOsReleases"]',
    },

    onRender: function () {
        this.showChildView('whatsNew', new WhatsNewView({
            categories: this.collection,
        }));
        this.showChildView('nimbleOsReleases', new NimbleOsReleasesView({
            categories: this.collection,
        }));
    },

});
