import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import style from './style.scss';

const cx = classnames.bind(style);

export default class Toggle extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    onClick(event) {
        const index = Number(event.target.value);
        const childAtIndex = React.Children.toArray(this.props.children)[index];
        this.props.onChange(childAtIndex.props.value);
    }

    render() {
        const count = React.Children.count(this.props.children) - 1;

        const kids = React.Children.map(this.props.children, (child, index) => {
            const { value, className, ...childProps } = child.props;

            const newChildClassName = classnames(className, 'text-uppercase', cx('nmbl-Toggle__button', {
                'nmbl-Toggle__first': index === 0,
                'nmbl-Toggle__last': index === count,
                'nmbl-Toggle--selected': value === this.props.value,
            }));

            return <button {...childProps} className={newChildClassName} value={index} onClick={this.onClick} />;
        });

        return <div className={classnames(this.props.className, cx('nmbl-Toggle'))}>{kids}</div>;
    }
}

Toggle.propTypes = {
    /**
     * Fragment containing only `<button>` elements.
     */
    children: PropTypes.node.isRequired,

    /**
     * Current value. This should match the `props.value` of one of the child `Button` elements.
     */
    value: PropTypes.any.isRequired,

    /**
     * @param {*} value Value of selected element from `child[].props.value`
     */
    onChange: PropTypes.func.isRequired,
};
