const _ = require('underscore');
var $ = require('jquery'),
    jQueryUI = require('jqueryui'),
    Marionette = require('backbone.marionette'),
    AssetHeader = require('app/views/components/asset/AssetHeader'),
    tpl = require('raw-loader!app/templates/cards/array/BaseArrayCard.html');

module.exports = Marionette.LayoutView.extend({

    template: _.template(tpl),

    className: 'array-card',

    regions: {
        headerRegion: '.header-region',
        footerRegion: '.array-card-footer',
    },

    /**
     * An optional parameter that can be specified to control whether
     * the "View Details" link is shown.  True by default.
     */
    showDetailsLink: true,

    /**
     * An optional parameter to control whether the footer is shown or not.
     * True by default
     */
    showFooter: true,

    onClose: function () {
        console.log('BaseArrayCard::onClose()');
        if (this.recommendationChart) {
            this.recommendationChart.close();
        }
    },

    /**
     * Overridden Backbone render method.
     * @return {BaseArrayCard} Returns this instance (useful for chaining).
     */
    onRender: function () {
        this.headerRegion.show(new AssetHeader({
            hyperlinkHostname: true,
            model: this.model,
        }));

        // delegate to subclass to render main and sidebar contents
        this.renderContents(this.$el.find('.array-card-contents'));

        if (this.getOption('footerView')) {
            this.footerRegion.show(this.getOption('footerView'));
        }

        var detailsLink = this.$el.find('.details-link');
        if (!this.showDetailsLink) {
            detailsLink.hide();
        } else {
            detailsLink.tooltip({
                tooltipClass: 'details-link-tooltip',
            });
        }

        return this;
    },

    /**
     * Intended to be overridden by subclasses.
     * @param  {jQueryEl} $containerEl The jQuery element to append content to.
     * @required
     */
    renderContents: function ($containerEl) {
        // to be overidden by subclasses...
        throw new Error('renderContents must be overridden');
    },

});
