import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { ClientControlledTable } from '@infosight/elmer/dist/components/Table';
import { userSelector } from '../../../user/reducer';

import telemetryConfig from './telemetryConfig';

const TelemetrySettingsGrid = ({ loading, data, handleChange, userName }) => {
    const { columns, categories } = telemetryConfig(handleChange);
    return (
        <ClientControlledTable
            tableId={`${userName}_NIMBLE_TELEMETRY`}
            data={data || []}
            columns={columns}
            categories={categories}
            loading={loading}
        />
    );
};

TelemetrySettingsGrid.propTypes = {
    data: PropTypes.array.isRequired,
    handleChange: PropTypes.func.isRequired,
    userName: PropTypes.string,
    loading: PropTypes.bool.isRequired,
};

const mapStateToProps = state => ({
    userName: userSelector(state).username,
});

export default connect(mapStateToProps)(TelemetrySettingsGrid);
