const $ = require('jquery');
const _ = require('underscore');
const { API_BASE_URL } = require('progeny/config');
const { buildUrl } = require('@infosight/elmer/dist/utils/url');

var Backbone = require('backbone'),
    moment = require('moment'),
    tpl = require('raw-loader!./BannerNotifyView.html');

var template = _.template(tpl);

var waitingClasses = 'fa-spinner fa-spin';
var successClasses = 'fa-check';
var errorClasses = 'error fa-times';

module.exports = Backbone.View.extend({

    initialize: function () {
        _.bindAll(this, 'showDialog', 'notify');
    },

    showDialog: function () {
        var self = this;

        this.dialog = this.$('#banner-notify').dialog({
            appendTo: this.el,
            autoOpen: true,
            height: 'auto',
            width: 600,
            modal: true,
            title: 'Email Notification: ' + this.model.get('title'),
            close: function () {
                self.trigger('close');
            },
            dialogClass: 'banner-notify-dialog',
        });
    },

    render: function () {
        var self = this;

        this.$el.html(template(_.extend(this.model.toJSON(), {
            moment: moment,
        })));

        this.$status = this.$('#status');
        this.$notify = this.$('#notify');
        this.$confirmation = this.$('#confirmation');

        this.$('#cancel').button().click(function () {
            self.dialog.dialog('close');
        });

        this.$('#confirm').button().click(function () {
            self.$confirmation.fadeOut({
                done: self.notify,
            });
        });

        this.$notify.hide();
        this.$confirmation.show();

        return this;
    },

    notify: function () {
        var self = this;

        this.$notify.fadeIn();
        this.$status.addClass(waitingClasses);

        $.post(buildUrl(API_BASE_URL, 'admin/banners', self.model.get('id'), '/notify'))
            .done(function () {
                self.$status.removeClass(waitingClasses).addClass(successClasses);
                self.$('#success').show();
            })
            .fail(function (jqXHR, textStatus, errorThrown) {
                self.$status.removeClass(waitingClasses).addClass(errorClasses);
                self.$('#banner-notify-error').text(errorThrown);
                self.$('#error').show();
            });
    },

});
