import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import { fieldPropTypes } from 'redux-form';

import style from './style.scss';

const cx = classnames.bind(style);

// TODO Move these into elmer/form or some other reusable area

export const InputWithFeedback = ({
    input,
    type,
    meta: {
        touched,
        error,
    },
}) => (
    <div>
        <input {...input} type={type} />
        {touched && error && <span className={cx('form-error')}>{error}</span>}
    </div>
);

InputWithFeedback.propTypes = {
    // eslint-disable-next-line react/no-typos
    input: fieldPropTypes.input,
    type: PropTypes.string,
    // eslint-disable-next-line react/no-typos
    meta: fieldPropTypes.meta,
};

export const SelectWithFeedback = ({
    input,
    meta: {
        touched,
        error,
    },
    children,
}) => (
    <div>
        <select {...input}>
            {children}
        </select>
        {touched && error && <span className={cx('form-error')}>{error}</span>}
    </div>
);

SelectWithFeedback.propTypes = {
    // eslint-disable-next-line react/no-typos
    input: fieldPropTypes.input,
    type: PropTypes.string,
    // eslint-disable-next-line react/no-typos
    meta: fieldPropTypes.meta,
    children: PropTypes.element,
};
