import $ from 'jquery';
import React from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import classnames from 'classnames/bind';
import Button from '@infosight/elmer/dist/components/Button';
import { Link } from 'react-router-dom';

import { filterSetDispatch } from '../../library/support-helper';

import Toggle from '../../../../components/Toggle';

import style from './SearchNav.scss';

const cx = classnames.bind(style);


class SearchNav extends React.Component {
    constructor(props) {
        super(props);
        autobind(this);
        this.state = {
            casesBy: 'all',
            isOpenNav: false,
            searchString: '',
        };
        this.navStuff = null;
    }

    setMyCases() {
        this.setState({ casesBy: 'justMine' });
        this.props.filterSetDispatch({ justMine: true });
    }

    setOpenCases(e) {
        this.props.filterSetDispatch({ justMine: false, caseStatus: 'Open' });
        return this.redirect(e);
    }

    setClosedCases(e) {
        this.props.filterSetDispatch({ justMine: false, caseStatus: 'Closed' });
        return this.redirect(e);
    }

    setResolvedCases(e) {
        this.props.filterSetDispatch({ justMine: false, caseStatus: 'Resolved' });
        return this.redirect(e);
    }

    setAllCases() {
        this.setState({ casesBy: 'all' });
        this.props.filterSetDispatch({ justMine: false, caseStatus: null });
    }

    hideAutoCases(e) {
        const { currentFilters } = this.props;
        currentFilters.hideAutoCases = e.target.checked;
        this.props.filterSetDispatch(currentFilters);
    }

    redirect(e) {
        e.preventDefault();
        const hashDestination = e.target.hash;
        this.setState({ isOpenNav: false });
        window.setTimeout(() => { window.location.hash = hashDestination; }, 50);
        return false;
    }

    offClick(e) {
        const clickTarget = e.target;
        if (this.navStuff === clickTarget) return true;

        const foundInNav = $(this.navStuff).find(clickTarget);
        if (foundInNav.length) return true;

        this.setState({ isOpenNav: false });
        return false;
    }

    toggleState(e) {
        e.preventDefault();
        e.stopPropagation();
        const unOpen = !this.state.isOpenNav;
        this.setState({ isOpenNav: unOpen });
        return false;
    }

    forwardSearch(e) {
        const searchTerms = e.target.value;
        if (searchTerms) this.setState({ searchString: searchTerms });
        else this.setState({ searchString: '' });

        if (typeof this.props.onSearch === 'function') this.props.onSearch.call(null, searchTerms);
    }

    eraseSearch(e) {
        // note: the click event from A will also expectedly trigger a blur event on INPUT
        e.preventDefault();
        e.stopPropagation();
        this.setState({ searchString: '' });
        if (typeof this.props.onSearch === 'function') this.props.onSearch.call(null, '');
        return false;
    }

    showCasesBy(value) {
        if (value === 'all') {
            this.setAllCases();
        } else {
            this.setMyCases();
        }
    }

    render() {
        const { isInternal } = this.props;

        return (
            <div className={cx('search-nav', { 'is-internal': isInternal })}>

                {!isInternal &&
                <div className={cx('case-owner')}>
                    <Toggle className="cases-by" value={1} onChange={this.showCasesBy}>
                        <Button
                            className={cx({ active: (this.state.casesBy === 'all') }, 'button')}
                            value="all"
                        >All Cases
                        </Button>
                        <Button
                            className={cx({ active: (this.state.casesBy === 'justMine') }, 'button')}
                            value="justMine"
                        >My Cases
                        </Button>
                    </Toggle>
                </div>
                }

                <div className={cx('search-stuff')}>
                    <input
                        type="text"
                        className={cx('search-bar')}
                        // onClick={this.activateSearch}
                        value={this.state.searchString}
                        onChange={this.forwardSearch}
                        // onBlur={this.onBlur}
                        placeholder="Search all case fields..."
                    />
                    <a href="#support" className={cx('cancel-search')} onClick={this.eraseSearch}>{'\u00D7'}</a>
                </div>


                <div className={cx('hide-auto-cases')}>
                    <input type="checkbox" onChange={this.hideAutoCases} />Hide auto-created cases
                </div>

                <div className={cx('actions-options')}>
                    {!isInternal &&
                    <Link to="/resources/nimble/support/newcase">
                        <Button
                            className="button"
                        >
                            <i className="fa fa-plus" />
                            New Case
                        </Button>
                    </Link>
                    }
                </div>

            </div>
        );
    }
}

SearchNav.propTypes = {
    onSearch: PropTypes.func,
    filterSetDispatch: PropTypes.func,
    currentFilters: PropTypes.object,
    isInternal: PropTypes.bool,
};

function mapStateToProps(state) {
    return {
        filtering: state.supportTab.filtering,
    };
}

const mapDispatchToProps = dispatch => bindActionCreators({
    filterSetDispatch,
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(SearchNav);

/* eslint-enable */
