const _ = require('underscore');
var Backbone = require('backbone'),
    ArrayModel = require('app/models/ArrayModel'),
    ArrayEventsCollection = require('app/collections/events/ArrayEventsCollection');

module.exports = ArrayModel.extend({

    idAttribute: 'serialNumber',

    defaults: _.defaults({
        data: [],
        events: new ArrayEventsCollection(), // note: not sent via server
        possiblyExceedsThreshold: null,
        probablyExceedsThreshold: null,
        unmanagedSnapshots: 0,
        capacity: null,
        free: null,
    }, ArrayModel.prototype.defaults),

    monthRange: undefined,

    initialize: function (attributes, options) {
        if (options) {
            this.monthRange = options.monthRange;
        }
        this.constructor.__super__.initialize.call(this, attributes, options);
    },

    /**
     * URL used when fetching this model directly.
     * In this scenario, the arraySn parameter must be set.
     */
    url: function () {
        var url = this.api().segment('capacity');
        if (this.monthRange) {
            url = url.query({ monthRange: this.monthRange });
        }
        return url;
    },

});
