// QuickRow.js

import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';

import style from './QuickTable.scss';

const cx = classnames.bind(style);


function rowIterator(colData, ci) {
    if (ci) return (<div key={ci} className={cx('qtrc-next')}>{colData}</div>);
    return null;
}

function QuickRow(props) {
    const { columns } = props;
    return (
        <div className={cx('quicktable-row')}>
            <div className={cx('qtrc-first')}>{columns[0]}</div>
            {columns.map(rowIterator)}
        </div>
    );
}

QuickRow.propTypes = {
    columns: PropTypes.array.isRequired,
};


export default QuickRow;
