const _ = require('underscore');
const Request = require('../../../../../../progeny/services/Request').default;
const Marionette = require('backbone.marionette');
const FormBehavior = require('app/common/FormBehavior');
const tpl = require('raw-loader!./BannerDialogView.html');
require('backbone.stickit');

const SPECIFIC_ACCOUNT = 'SpecificAccount';
const FEATURE = 'Feature';

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    behaviors: {
        FormBehavior: {
            behaviorClass: FormBehavior,
        },
    },

    ui: {
        submitButton: 'button[name="submit"]',
        accountIdContainer: '#accountId-container',
        featureContainer: '#feature-container',
    },

    triggers: {
        'click @ui.submitButton': {
            event: 'submit',
            stopPropagation: false,
        },
    },

    bindings: {
        '#title': {
            observe: 'title',
            setOptions: { validate: true },
        },
        '#type': {
            observe: 'type',
            setOptions: { validate: true },
        },
        '#status': {
            observe: 'status',
            setOptions: { validate: true },
        },
        '#visibility': {
            observe: 'visibility',
            setOptions: { validate: true },
        },
        '#accountId': {
            observe: 'accountId',
            setOptions: { validate: true },
        },
        '#contents': {
            observe: 'contents',
            setOptions: { validate: true },
        },
        '#feature': {
            observe: 'feature',
            setOptions: { validate: true },
        },
    },

    modelEvents: {
        'change:visibility': 'onChangeVisibility',
        'validated': 'onValidated',
    },

    onValidated: function (isValid) {
        this.ui.submitButton.prop('disabled', !isValid);
    },

    onRender: function () {
        var self = this;

        this.$('#accountName').autocomplete({
            appendTo: '#accountId-container',
            source: function (request, response) {
                return new Request().central().slash('accounts').query({ term: request.term })
                    .get()
                    .then(function (accounts) {
                        response(accounts.data.map(account => ({ label: account.name, value: account.id })));
                    })
                    .catch(() => response(['An error has occurred.']));

            },
            select: function (event, ui) {
                self.$('#accountName').val(ui.item.label);
                self.model.set('accountId', ui.item.value, { validate: true });

                return false;
            },
        });

        if (this.model.get('visibility') !== SPECIFIC_ACCOUNT) {
            this.ui.accountIdContainer.hide();
        }

        if (this.model.get('visibility') !== FEATURE) {
            this.ui.featureContainer.hide();
        }
    },

    onChangeVisibility: function (model, visibility) {
        if (visibility === SPECIFIC_ACCOUNT) {
            this.ui.accountIdContainer.slideDown('fast');
        } else {
            this.ui.accountIdContainer.slideUp('fast');
            this.model.set('accountId', null, { validate: true });
            this.$('#accountName').val(null);
        }

        if (visibility === FEATURE) {
            this.ui.featureContainer.slideDown('fast');
        } else {
            this.ui.featureContainer.slideUp('fast');
            this.model.set('feature', null, { validate: true });
            this.$('#feature').val(null);
        }
    },

});
