var Backbone = require('backbone');
var esClient = require('app/modules/elasticsearch/ElasticsearchClient');
var HitCollection = require('../documentation/HitCollection');

module.exports = HitCollection.extend({

    initialize: function (models, options) {
        this.productName = options.productName;
    },

    fetch: function () {
        return esClient.searchTemplate({
            index: 'docs',
            template: 'docs',
            params: {
                size: 50,
                filters: [
                    {
                        term: {
                            'Nimble Software and Solutions': this.productName,
                        },
                    }, {
                        terms: {
                            'Document Type': ['Release Notes', 'Installation / Administration Guide', 'Integration / Solution Guide'],
                        },
                    },
                ],
            },
        }).done(this.load.bind(this));
    },

    load: function (response) {
        this.reset(response.hits.hits, { parse: true });
    },

});
