import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { userSelector } from '../../../user/reducer';

const SelfRegistrationIncapable = ({ user, error }) => (
    <div>
        <p>
            Your login <b>{user.username}</b> cannot be automatically associated to an HPE Nimble Storage customer account.
        </p>
        <div className="callout error">
            <h4>{error}</h4>
        </div>
        <p>
            Please request that your local InfoSight administrator invite you to join your team&rsquo;s InfoSight organization.

            If no one within your organization is already registered for InfoSight, please contact HPE Nimble Storage
            support to assist with new customer registration.
        </p>
    </div>
);

SelfRegistrationIncapable.propTypes = {
    user: PropTypes.object,
    error: PropTypes.string,
};

export default connect(
    state => ({
        user: userSelector(state),
    }),
    null
)(SelfRegistrationIncapable);
