var Backbone = require('backbone');
var ProductCollection = require('./ProductCollection');

module.exports = Backbone.Model.extend({

    idAttribute: 'name',

    parse: function (response, { prioritizedCategories }) {
        response.products = new ProductCollection(response.products, {
            prioritizedCategories,
            category: this,
            parse: true,
        });
        return response;
    },

});
