const _ = require('underscore');
var Marionette = require('backbone.marionette');
var tpl = require('raw-loader!./FacetBucketView.html');

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    tagName: 'li',

    className: function () {
        return 'facet-bucket flex-container' + (this.model.get('selected') ? ' selected' : '');
    },

    events: {
        'click [data-action="select"]': 'selectBucket',
        'click [data-action="deselect"]': 'deselectBucket',
    },

    selectBucket: function (event) {
        event.preventDefault();
        this.model.set('selected', true);
    },

    deselectBucket: function (event) {
        event.preventDefault();
        this.model.set('selected', false);
    },

});
