const _ = require('underscore');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');
var $ = require('jquery'),
    jQueryUI = require('jqueryui'),
    Backbone = require('backbone'),
    tpl = require('raw-loader!./SpaceSavingsBarChart.html');

var template = _.template(tpl);

module.exports = Backbone.View.extend(
    /** @lends views/components/dashboard/./SpaceSavingsBarChart.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/dashboard/./SpaceSavingsBarChart
         * @constructor
         * @augments Backbone.View
         */


        className: 'space-savings-view',

        renderChart: function (container, spaceUsed, spaceSaved, traditionalSpace) {
            this.chart = new Highcharts.Chart({
                chart: {
                    type: 'bar',
                    renderTo: container,
                    height: 175,
                    events: {
                        load: function () {
                            var chart = this;
                            // reflow the chart to ensure it fills the container
                            _.delay(function () {
                                chart.reflow.call(chart);
                            }, 50);
                        },
                    },
                },
                title: {
                    text: null,
                },
                xAxis: {
                    categories: ['Nimble', 'Traditional Storage'],
                },
                yAxis: {
                    min: 0,
                    title: {
                        text: null,
                    },
                },
                credits: {
                    enabled: false,
                },
                legend: {
                    backgroundColor: '#FFFFFF',
                    reversed: true,
                },
                plotOptions: {
                    series: {
                        stacking: 'normal',
                        dataLabels: {
                            enabled: true,
                            // align: 'right',
                            color: '#FFFFFF',//,
                            //format: '{point.y}TiB'
                            // formatter: function() {
                            //     if ( this.series)
                            // }
                            // x: -10
                        },
                    },
                },
                series: [
                    {
                        name: 'Space Savings',
                        data: [spaceSaved],
                    }, {
                        name: 'Space Used',
                        color: '#8bbc21',
                        data: [spaceUsed],
                    }, {
                        name: 'Traditional',
                        color: 'gray',
                        data: [null, traditionalSpace],
                    },
                ],
            });
        },

        render: function () {
            this.$el.html(template(this.model.toJSON()));
            this.renderChart(this.$el.find('.space-savings-chart')[0],
                this.model.get('spaceUsed'), this.model.get('spaceSavings'),
                this.model.get('traditionalSpace'));
            return this;
        },

    });
