const $ = require('jquery');
const _ = require('underscore');
var Marionette = require('backbone.marionette');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');

module.exports = Marionette.ItemView.extend(
    /** @lends views/components/dashboard/UpgradeCapacityDetailsView.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/dashboard/UpgradeCapacityDetailsView
         * @constructor
         * @augments Backbone.View
         */


        className: 'upgrade-capacity-details-view',

        renderChart: function (container, series, maxY) {
            var self = this;
            this.chart = new Highcharts.Chart({
                chart: {
                    type: 'line',
                    renderTo: container,
                    height: 150,
                    events: {
                        redraw: function () {
                            var containerWidth = $(this.container).width();

                            // if the no data label group is present, ensure it's centered
                            if (self.svgNoDataLabelGroup) {
                                self.svgNoDataLabelGroup.translate(
                                    (containerWidth / 2) - (self.svgNoDataLabelGroup.element.getBoundingClientRect().width / 2) + 20,
                                    65,
                                );
                            }
                        },
                        load: function () {
                            var chart = this,
                                renderer = chart.renderer;

                            if (chart.series[0].data && chart.series[0].data.length === 0) {
                                self.svgNoDataLabelGroup = renderer.g().attr({ id: 'svgNoDataLabel' }).add();
                                self.svgNoDataLabelText = renderer.text('No Capacity Data Available').attr({
                                    fill: '#666',
                                }).add(self.svgNoDataLabelGroup);
                            }

                            _.delay(function () {
                                chart.reflow.call(chart);
                            }, 50);
                        },
                    },
                },
                credits: {
                    enabled: false,
                },
                title: {
                    text: null,
                },
                legend: {
                    enabled: false,
                },
                xAxis: {
                    categories: ['-90 Days', 'Current', '+90 Days'],
                },
                yAxis: {
                    min: 0,
                    max: maxY,
                    title: {
                        text: 'Array Usage (%)',
                    },
                    labels: {
                        enabled: true,
                        format: '{value}%',
                    },
                },
                tooltip: {
                    enabled: true,
                    valueDecimals: 1,
                    valueSuffix: ' TiB',
                    formatter: function () {
                        var arrayUsedPct = this.point.y / 100 * this.point.arrayCapacity,
                            s =
                                '<span style="color: ' + this.series.color + '">' +
                                this.x +
                                '</span>' +
                                '<br/>Capacity Used: <b>' + Highcharts.numberFormat(arrayUsedPct, 2) + ' TiB</b>' +
                                '<br/>Array Usage: <b>' + Highcharts.numberFormat(this.point.y, 2) + ' %</b>';
                        return s;
                    },
                },
                plotOptions: {
                    line: {
                        dataLabels: {
                            // crop: false,
                            // overflow: 'none',
                            enabled: false,
                            // format: '{y} %',
                            formatter: function () {
                                return Highcharts.numberFormat(this.y, 2) + ' TiB';
                            },
                        },
                    },
                },
                series: series,
            });
        },

        render: function () {
            this.$el.html(this.el);

            var data = this.model.get('capacity'),
                series = { name: 'Capacity Used', data: [] },
                maxY = 100;

            for (var i = 0; i < data.length; i++) {
                var dataPoint = data[i],
                    pctUsed = dataPoint.arrayUsedPct,
                    arrayCapacity = dataPoint.capacityUsed;

                maxY = Math.max(pctUsed, maxY);

                series.data.push({
                    y: pctUsed,
                    pctUsed: pctUsed,
                    arrayCapacity: arrayCapacity / 1024 / 1024,
                });
            }

            this.renderChart(this.el, [series], maxY);

            return this;
        },

        onDestroy: function () {
            console.log('UpgradeCapacityDetailsView:onDestroy()');
            if (this.chart) {
                this.chart.destroy();
                delete this.chart;
            }
        },

    });
