const App = require('app/Application');
const _ = require('underscore');
var Marionette = require('backbone.marionette');
var CategoryCollection = require('./CategoryCollection');
var AsyncView = require('app/common/AsyncView');
var CategoriesView = require('./CategoriesView');
var WhitelistCategoryView = require('./WhitelistCategoryView');
var WhitelistCollection = require('./WhitelistCollection');
var ProductView = require('./ProductView');
var HomeView = require('./HomeView');
var tpl = require('raw-loader!./SoftwareView.html');
const Request = require('../../../progeny/services/Request').default;
const $ = require('jquery');

var View = Marionette.LayoutView.extend({

    template: _.template(tpl),

    className: 'software-view',

    regions: {
        sidePanel: '[data-region="sidePanel"]',
        whitelistCategoryPanel: '[data-region="whitelistCategoryPanel"]',
        mainPanel: '[data-region="mainPanel"]',
    },

    ui: {},

    initialize: function (attribute) {
        this.collection = new CategoryCollection();
    },

    onDestroy: function () {
        App._getChannel('software').reset();
    },

    onRender: function () {
        var self = this;
        this.whitelistCollection = new WhitelistCollection();
        self.whitelistCategoryPanel = new WhitelistCategoryView({
            collection: this.whitelistCollection,
        });
        this.whitelistCollection.bind('change reset add remove', function() {
            if (self.whitelistCollection.length < 0) {
                self.whitelistCategoryPanel.$el.hide();
            } else {
                self.whitelistCategoryPanel.$el.show();
            }
        });

        this.showChildView('whitelistCategoryPanel', this.whitelistCategoryPanel);
        self.whitelistCategoryPanel.$el.hide();

        this.showChildView('sidePanel', new CategoriesView({
            collection: this.collection,
        }));
    },

    showProduct: function (category, productName) {
        this.collection.unselectAllProducts();
        var product = this.collection
            .findWhere({ name: category })
            .get('products')
            .findWhere({ name: productName });

        if (product) {
            product.set('selected', true);

            this.showChildView('mainPanel', new ProductView({
                model: product,
            }));
        }
    },

    showHome: function () {
        this.collection.unselectAllProducts();

        this.showChildView('mainPanel', new HomeView({
            collection: this.collection,
        }));
    },

});

module.exports = AsyncView.extend({
    ViewConstructor: View,

    // TODO put this in redux
    loadCategories() {
        const deferred = $.Deferred();
        new Request().urlSegments('software', 'categories')
            .get()
            .then((categories) => {
                this.view.collection.prioritizedCategories = categories.data;
            })
            .then(deferred.resolve)
            .catch(deferred.reject);
        return deferred;
    },

    load() {
        return this.loadCategories()
            .then(() => this.view.collection.fetch())
            .then(() => this.view);
    },

    invokePostRender: function (fn) {
        if (this.view.isRendered) {
            fn();
        } else {
            this.listenToOnce(this.view, 'render', fn);
        }
    },

    showProduct: function (category, product) {
        this.invokePostRender(this.view.showProduct.bind(this.view, category, product));
    },

    showHome: function () {
        this.invokePostRender(this.view.showHome.bind(this.view));
    },
});
