const App = require('app/Application');
const _ = require('underscore');
var $ = require('jquery'),
    jQueryUI = require('jqueryui'),
    Marionette = require('backbone.marionette'),
    tpl = require('raw-loader!./ExecutiveDashboardReportView.html'),
    DashboardROIView = require('./DashboardROIView'),
    DataProtectionView = require('./DataProtectionView'),
    UpgradeSummaryCollectionView = require('./UpgradeSummaryCollectionView'),
    UpgradesView = require('./UpgradesView'),
    ExecutiveDashboardEmailSettingsView = require('./notification/ExecutiveDashboardEmailSettingsView');
const { NimbleUserTenantHelpers } = require('../../../../progeny/modules/nimble/tenantSelection');

module.exports = Marionette.ItemView.extend(
    /** @lends views/tabs/DashboardView.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/tabs/DashboardView
         * @constructor
         * @augments Marionette.ItemView
         */

        template: _.template(tpl),

        className: 'dashboard-view',

        ui: {
            executiveDashboardEmailBtn: '#executive-dashboard-email-btn',
        },

        events: {
            'click @ui.executiveDashboardEmailBtn': 'onClickExecutiveDashboardEmail',
        },

        /**
         * Overridden Backbone render method.
         * @return {DashboardView} Returns this instance (useful for chaining).
         */
        onRender: function () {
            this.renderROIView();
            this.renderDataProtection();
            this.renderCaseStatistics();
            this.renderUpgrades();
            // hide email settings for resellers
            if (NimbleUserTenantHelpers.isExternal() && !NimbleUserTenantHelpers.isPrimaryTenant()) {
                this.ui.executiveDashboardEmailBtn.hide();
            }
        },

        onDestroy: function () {
            if (this.dashboardROIView) {
                this.dashboardROIView.destroy();
                delete this.dashboardROIView;
            }
            if (this.dataProtectionView) {
                this.dataProtectionView.destroy();
                delete this.dataProtectionView;
            }
            if (this.upgradesView) {
                this.upgradesView.destroy();
                delete this.upgradesView;
            }
        },

        /**
         * Render the space savings content.
         * @return {DashboardView} Returns this instance (useful for chaining).
         */
        renderROIView: function () {
            this.dashboardROIView = new DashboardROIView({
                model: this.model,
            });
            this.$el.find('.dashboard-space-tab').append(
                this.dashboardROIView.render().el);
            return this;
        },

        /**
         * Renders the data protection section of the dashboard
         * @return {DashboardView} Returns this instance (useful for chaining).
         */
        renderDataProtection: function () {
            var self = this;

            this.dataProtectionView = new DataProtectionView({
                model: this.model,
            });

            this.$el.find('.data-protection-container.dashboard-section').append(
                this.dataProtectionView.render().el);

            return this;
        },

        /**
         * Render the case statistics content.
         * @return {DashboardView} Returns this instance (useful for chaining).
         */
        renderCaseStatistics: function () {
            return this;
        },

        /**
         * Renders the upgrade summary list.
         * @return {DashboardView} Returns this instance (useful for chaining).
         */
        renderUpgrades: function () {
            this.upgradesView = new UpgradesView({ model: this.model });
            this.$el.find('.dashboard-arrays-tab').append(
                this.upgradesView.render().el);
        },

        onClickExecutiveDashboardEmail: function (event) {
            event.preventDefault();
            App.modalRegion.show(new ExecutiveDashboardEmailSettingsView());
        },

    });
