const $ = require('jquery');

var seriesColors = {
    'Exchange': '#2f7ed8',
    'Oracle': '#910000',
    'SharePoint': '#8085e9',
    'VMware ESX': '#C4C243',
    'VMware VDI': '#6E6C20',
    'VMware Other': '#8C8B27',
    'SQL Server': '#434348',
    'SQL Other': '#75828F',
    'Nimble Default': '#8bbc21',
    'Hyper-V': '#f15c80',
    'MySQL': '#e4d354',
    'Windows': '#2b908f',
    'Custom': 'gray',
};

module.exports = {

    /**
     * Helper method for getting the appropriate series color for a given series.
     * @param  {String} seriesName The name of the series.
     * @return {String}            Returns the color string.
     */
    getSeriesColor: function (seriesName) {
        var color = seriesColors[seriesName];
        if (!color) {
            throw new Error('Series "' + seriesName + '" does not have a mapped color.');
        }
        return color;
    },

    /**
     * Helper method for retrieving a user-friendly formatted version of the category.
     * The given category can be in the following forms:
     *   <li>hh:mm  (where hh is hours and mm is minutes)
     *   <li>n      (where n is the number of days)
     *   <li>string (a string to display)
     * @param  {String} category The category.
     * @return {String}          The user-friendly category.
     */
    scheduleCategoryLabelFormatter: function (category) {
        var s = '';

        // handle the case when grouped-category lib is used with highcharts
        // category is a object, i.e. {"userOptions":"00:01","name":"00:01"};
        if (typeof(category) === 'object') {
            category = category.name;
        }
        // days
        if ($.isNumeric(category)) {
            if (category % 7 === 0) {
                var weeks = category / 7;
                s = weeks + (weeks > 1 ? ' weeks' : ' week');
            } else {
                s = category + (category > 1 ? ' days' : ' day');
            }
        }
        // some combination of hours:mins
        else if (category.indexOf(':') >= 0) {
            var timePieces = category.split(':'),
                hours = parseInt(timePieces[0], 10),
                mins = parseInt(timePieces[1], 10);
            if (hours === 0) {
                return mins + (mins > 1 ? ' mins' : ' min');
            } else if (mins === 0) {
                return hours + (hours > 1 ? ' hours' : ' hour');
            }
            // partial hours and minutes
            else {
                return hours + '.' + (mins / 60) + ' hours';
            }
        }
        // other string based category
        else {
            s = category;
        }

        return s;
    },
};
