const $ = require('jquery');
const _ = require('underscore');
var Marionette = require('backbone.marionette'),
    tpl = require('raw-loader!./UpgradesView.html'),
    errorTpl = require('raw-loader!app/templates/components/error.html'),
    UpgradeSummaryCollection = require('./UpgradeSummaryCollection'),
    CapacityForecastCollection = require('app/modules/capacity_report/collections/CapacityForecastCollection'),
    CardCollectionView = require('app/views/cards/CardCollectionView'),
    ArrayUpgradeCard = require('./ArrayUpgradeCard');

module.exports = Marionette.ItemView.extend({

    template: _.template(tpl),

    className: 'upgrades-view',

    upgradeSummaryCollection: null,

    ui: {
        downloadLink: '.download-link',
        spinner: '.loading-spinner2',
    },

    /**
     * Overridden Backbone render method.
     * @return {UpgradesView} Returns this UpgradesView instance (useful for chaining).
     */
    onRender: function () {
        this.ui.spinner.show();

        this.upgradeSummaryCollection = new UpgradeSummaryCollection([], {});

        // we reuse the existing capacity forecast collection (used on the capacity tab),
        // but an optimization would be to only fetch capacity info for arrays that need upgrades
        this.capacityCollection = new CapacityForecastCollection([], {});

        this.ui.downloadLink
            .attr('href', this.api().pod().account().segment('/dashboard/upgrade_export'));

        this.loadCollections();
    },

    onDestroy: function () {
        if (this.collectionView) {
            this.collectionView.destroy();
            delete this.collectionView;
        }
        if (this.upgradeSummaryCollection) {
            this.upgradeSummaryCollection.reset();
            delete this.upgradeSummaryCollection;
        }
        if (this.capacityCollection) {
            this.capacityCollection.reset();
            delete this.capacityCollection;
        }
    },

    loadCollections: function () {
        var self = this;

        var handleError = function (response) {
            var message = 'Upgrade Information is not available.';
            var errorTemplate = _.template(errorTpl);
            self.$el.append(errorTemplate({
                message: message,
            }));
        };

        $.when(
            this.upgradeSummaryCollection.fetch()
                .done(function () {
                    self.renderCollection();
                })
                .fail(function () {

                }),
            this.capacityCollection.fetch()
                .done(function () {

                })
                .fail(function () {

                }),
        ).done(function () {

            var arrayUpgradeMap = {};
            self.upgradeSummaryCollection.each(function (arrayUpgradeModel) {
                arrayUpgradeMap[arrayUpgradeModel.get('serialNumber')] = arrayUpgradeModel;
            });

            self.capacityCollection.each(function (capacityForecastModel) {
                // if this array has a recommended upgrade
                var arrayUpgradeModel = arrayUpgradeMap[capacityForecastModel.get('serialNumber')];
                if (arrayUpgradeModel) {
                    arrayUpgradeModel.set('capacity', capacityForecastModel);
                }
            });

        }).always(function () {
            self.ui.spinner.hide();
        }).fail(handleError);
    },

    renderCollection: function () {
        var contentEl = this.$('.upgrade-collection-view');
        this.collectionView = new CardCollectionView({
            collection: this.upgradeSummaryCollection,
            cardClass: ArrayUpgradeCard,
            emptyCollectionMessage: 'No upgrades recommended.',
            // ,
            // footer: capacityViewLegendTemplate()
        });
        contentEl.empty();
        contentEl.append(this.collectionView.render().el);

        if (this.upgradeSummaryCollection.isEmpty()) {
            $('div.upgrade-section').addClass('hidden-print');
        }
    },

});
