const _ = require('underscore');
var $ = require('jquery'),
    jQueryUI = require('jqueryui'),
    Marionette = require('backbone.marionette'),
    tpl = require('raw-loader!app/templates/cards/CardCollectionView.html'),
    Spinner = require('app/views/components/Spinner');

var template = _.template(tpl);

module.exports = Marionette.ItemView.extend(
    /** @lends views/cards/CardCollectionView.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/cards/CardCollectionView
         * @constructor
         * @augments Backbone.View
         */


        /**
         * Required parameters that must be specified in the constructor.
         * @type {Array}
         */
        requiredParams: [
            /**
             * The card class to instantiate.
             */
            'cardClass',

            /**
             * The message to be displayed if the collection is empty.
             */
            'emptyCollectionMessage',
        ],

        /**
         * Optional parameters that can be included for additional functionality.
         * @type {Array}
         */
        optionalParams: [

            /**
             * A header string to be prepended to the template before the main collection contents.
             */
            'header',

            /**
             * A footer string to be appended to the template after the main collection contents.
             */
            'footer',
        ],

        currentView: null,

        className: 'card-collection-view',

        renderedViews: [],

        /**
         * Overridden Backbone initialize method.
         * @param  {Object} options Options param object.
         * @override
         */
        initialize: function (options) {
            _.bindAll(this, 'renderList');

            // validate each of the required parameters
            _.each(this.requiredParams, function (param) {
                if (!options[param]) {
                    throw new Error(param + ' must be specified.');
                }
                this[param] = options[param];
            }, this);

            // add each of the optional parameters
            _.each(this.optionalParams, function (param) {
                this[param] = options[param];
            }, this);
        },

        onDestroy: function () {
            console.warn('CardCollectionView:onDestroy()');
            this.removeSubviews();
            if (this.spinner) {
                this.spinner.destroy();
            }
        },

        /**
         * Overridden Backbone render method.
         * @return {CardCollectionView} Returns this instance (useful for chaining).
         * @override
         */
        render: function () {
            this.$el.html(template({
                emptyCollectionMessage: this.emptyCollectionMessage,
            }));

            // render the loading spinner
            this.spinner = new Spinner();
            this.$el.find('.spinner').append(this.spinner.render().el);

            // render the list of items
            this.renderList();

            return this;
        },

        /**
         * Renders the list of cards.
         * @return {CardCollectionView} Returns this instance (useful for chaining).
         * @private
         */
        renderList: function () {
            // optimize the rendering of the collection by first rendering to a DocumentFragment
            // then appending it to avoid triggering multiple reflows.
            var self = this;

            if (this.collection.isEmpty()) {
                this.$el.find('.empty-collection').show();
                this.$el.find('.nonempty-collection').hide();
            } else {

                if (this.header) {
                    this.$el.find('.header').append(this.header);
                }

                var container = document.createDocumentFragment();

                // add a view for each model to the document fragment
                this.collection.each(function (model) {

                    // create a new
                    var row = new self.cardClass({ model: model });
                    container.appendChild(row.render().el);

                    // track the rendered view so we can remove it
                    self.renderedViews.push(row);
                });

                // finally append the fragment
                this.$el.find('.card-collection-contents').append(container);

                if (this.footer) {
                    this.$el.find('.footer').append(this.footer);
                }
            }

            this.spinner.hide();

            return this;
        },

        /**
         * Helper method to ensure proper closing of each rendered sub view.
         * @private
         */
        removeSubviews: function () {
            for (var i = this.renderedViews.length - 1; i >= 0; i--) {
                this.renderedViews[i].destroy();
            }
        },
    });
