/**
 * A mixin for a Backbone.Collection or Backbone.Model that is an subresource of the /arrays resource
 * Provides code reuse for handling the 'arraySn' option and constructing the appropriate base URL.
 */
module.exports = {

    arraySn: undefined,

    initialize: function (arg1, options) {
        if (options) {
            this.arraySn = options.arraySn;
        }

        this.listenTo(this, 'api:create', this.decorateApi);
    },

    decorateApi: function (api) {
        console.assert(this.arraySn, 'Missing arraySn parameter');
        return api.array(this.arraySn);
    },

};
