import React, { Component } from 'react';
import classnames from 'classnames/bind';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { ClientControlledTable } from '@infosight/elmer/dist/components/Table';
import { StatusIndicator } from '@infosight/elmer/dist/components/StatusIndicator';
import { ProgressWithValue, Progress } from '@infosight/elmer/dist/components/Progress';
import { Link } from 'react-router-dom';
import { saveAs } from 'file-saver';
import moment from 'moment-timezone';
import { buildNimbleTenantUrl } from '../../tenantSelection';
import style from './style.scss';
import { ArrayHardwareStatus } from '../../arrays/collection/components/list/ArrayHealth';
import { getArraysFromAllAccounts, reset } from '../actionCreators';
import { accountsCollectionSelector } from '../reducer';
import { userSelector } from '../../../user/reducer';
import { formatValue, formatDateTime } from '../../../../util/formatter';
import { FORMAT_BYTE_PRECISION, FORMAT_MOMENT_CALENDAR_DAY } from '../../../../util/formatter/constants';
import { arrayStatus, iconSize, transformDataForCSV, arrayUsedSpaceInPercentage, convertObjectToCSV } from '../helper';
import { config } from '../../config';

const cx = classnames.bind(style);
const hasArrayWarrantyExpired = arrayWarrantyStatus => (arrayWarrantyStatus === arrayStatus.EXPIRED.text);

export class ResellerDashboard extends Component {
    componentDidMount() {
        this.props.getArraysFromAllAccounts();
    }
    componentWillUnmount() {
        this.props.resetResellerDashboard();
    }
    render() {
        const { accountsArrays, userName } = this.props;
        const columns = [
            {
                Header: 'Customer',
                accessor: 'customerName',
                filterable: true,
                Cell: ({ original: { customerName, accountId } }) => (
                    <Link to={buildNimbleTenantUrl(accountId, 'nimble')} title={customerName}>{customerName}</Link>
                ),
            },
            {
                Header: 'Array',
                headerClassName: 'text-center',
                columns: [
                    {
                        Header: 'Name',
                        accessor: 'arraySN',
                        filterable: true,
                        Cell: ({ original: { arraySN, accountId, arrayHostName } }) => (
                            <Link
                                to={buildNimbleTenantUrl(accountId, `${config.attributes.rootPath}/arrays/${arraySN}`)}
                                title={arrayHostName}
                            >
                                {arrayHostName}
                            </Link>
                        ),
                    },
                    {
                        Header: 'Model',
                        accessor: 'model',
                        filterable: true,
                    },
                ],
            },
            {
                Header: 'Usable Space',
                headerClassName: 'text-center',
                columns: [
                    {
                        Header: 'Total',
                        accessor: 'arrayCapacityBytes', // Array total capacity in bytes
                        filterable: true,
                        headerClassName: 'text-right',
                        className: 'text-right',
                        Cell: ({ original: { arrayCapacityBytes } }) => formatValue(arrayCapacityBytes, FORMAT_BYTE_PRECISION),
                    },
                    {
                        Header: '% used',
                        accessor: 'arrayFreeBytes',
                        filterable: true,
                        headerClassName: 'text-right',
                        className: 'text-right',
                        Cell: ({ original: { arrayCapacityBytes, arrayFreeBytes } }) => (
                            /*
                             * arrayCapacityBytes is array's total capacity in bytes and arrayFreeBytes is array's available space in bytes
                             * arrayUsedSpaceInPercentage is used to show array used space in percentage
                             */
                            <ProgressWithValue
                                leftLabel={arrayUsedSpaceInPercentage(arrayFreeBytes, arrayCapacityBytes)}
                            >
                                <Progress max={100} min={0} value={((arrayCapacityBytes - arrayFreeBytes) / arrayCapacityBytes) * 100} />
                            </ProgressWithValue>
                        ),
                    },
                ],
            },
            {
                Header: 'Hardware Status',
                accessor: 'hardware',
                headerClassName: 'text-right',
                className: 'text-right',
                Cell: ({ original: { latestHeartbeat, isAllFlash } }) =>
                    <ArrayHardwareStatus latestHeartbeat={latestHeartbeat} isAllFlash={isAllFlash} />,
            },
            {
                Header: 'S/W Version',
                accessor: 'softwareVersion',
                filterable: true,
                headerClassName: 'text-right',
                className: 'text-right',
                Cell: ({ original: { softwareVersion } }) => softwareVersion && softwareVersion.split('-')[0],
            },
            {
                Header: 'Support end date',
                accessor: 'supportEndDate',
                filterable: true,
                Cell: ({ original: { supportEndDate, supportStatus } }) => (
                    <div>
                        <div className={cx('warrantyCol')}>{ formatDateTime(supportEndDate, FORMAT_MOMENT_CALENDAR_DAY, true) }</div>
                        {
                            /*
                             * Cannot reuse ArraySupportStatus from arrays list page as requirement was to show icon as well
                             * along with support end date
                             */
                            hasArrayWarrantyExpired(supportStatus) ?
                                <div className={cx('warrantyCol', 'icon')}>
                                    <StatusIndicator size={iconSize} status={arrayStatus.EXPIRED.icon} tooltip="Warranty Expired" />
                                </div> : null
                        }
                    </div>
                ),
            },
            {
                Header: 'Upgrade Recommendations',
                headerClassName: 'text-center',
                columns: [
                    {
                        Header: 'Hardware',
                        accessor: 'upgrade',
                        filterable: true,
                        Cell: ({ original: { hasHardwareRecommendations, accountId, arraySN } }) => (
                            <div>
                                {hasHardwareRecommendations ?
                                    <Link to={buildNimbleTenantUrl(accountId, `${config.attributes.rootPath}/arrays/${arraySN}`)}>YES</Link> :
                                    'NO'}
                            </div>
                        ),
                    },
                    {
                        Header: 'Software',
                        accessor: 'upgrade',
                        filterable: true,
                        Cell: ({ original: { hasSoftwareRecommendations, accountId, arraySN } }) => (
                            <div>
                                {hasSoftwareRecommendations ?
                                    <Link to={buildNimbleTenantUrl(accountId, `${config.attributes.rootPath}/arrays/${arraySN}`)}>YES</Link> :
                                    'NO'}
                            </div>
                        ),
                    },
                ],
            },
        ];
        return (
            <ClientControlledTable
                tableId={`${userName}_NIMBLE_RESELLER_ARRAYS`}
                data={accountsArrays || []}
                loading={!accountsArrays}
                columns={columns}
                defaultSorted={[{ id: 'groupName', desc: false }]}
                getTrProps={(state, rowInfo) => (
                    {
                        className: hasArrayWarrantyExpired(rowInfo.original.supportStatus) ?
                            cx(arrayStatus[rowInfo.original.supportStatus].textClass) : '',
                    }
                )}
                downloadCsv={() =>
                    saveAs(new Blob([convertObjectToCSV(transformDataForCSV(accountsArrays))], { type: 'application/json' }),
                        `HPE InfoSight Arrays ${moment().format('L').replace(/\\|\//g, '-')}.csv`)
                }
            />);
    }
}

ResellerDashboard.propTypes = {
    resetResellerDashboard: PropTypes.func.isRequired, // reset Redux state for reseller
    getArraysFromAllAccounts: PropTypes.func.isRequired, // API that gets details of all arrays
    accountsArrays: PropTypes.array, // coming from accountsCollectionSelector, it is used for displaying data in table
    userName: PropTypes.string,
};

function mapStateToProps(state) {
    return {
        ...accountsCollectionSelector(state),
        userName: userSelector(state).username,
    };
}

const mapDispatchToProps = dispatch => bindActionCreators({ getArraysFromAllAccounts, resetResellerDashboard: reset }, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(ResellerDashboard);
