// CaseRmaLabels.js
/* eslint-disable */

import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';
import moment from 'moment';
import { buildUrl } from '@infosight/elmer/dist/utils/url';
import { BASE_URL } from '../../../../../config';
import { QuickTable, QuickRow } from '../../QuickTable';
import style from './CaseRmaLabels.scss';

const cx = classnames.bind(style);

class CaseRmaLabels extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {
        const theCase = this.props.theCase;
        const caseId = theCase.id;
        const rmaLabels = theCase.rmaLabels;
        const accountId = theCase.accountId;
        const internal = this.props.userIsInternal;

        if (!rmaLabels) return null;
        if (!rmaLabels.length) return (<p>No RMA labels available</p>);

        const componentTitle = 'Component';
        const rmaTitle = 'RMA Label';
        const dateTitle = 'Created Date';
        let url = '';

        return (
            <div className={cx('rma-labels')}>
                <div>All historical RMA labels generated are shown, most recent label supersedes duplicates.</div>
                <QuickTable>
                    <QuickRow key="header" className={cx('header')} columns={[dateTitle, rmaTitle, componentTitle]} />
                    {rmaLabels.sort((b,a) => new Date(a.createdDate) - new Date(b.createdDate)).map(rmaLabel => {
                        if (internal) {
                            url =  buildUrl(BASE_URL, `api/accounts/${accountId}/cases/${caseId}/rmaLabel?body=${rmaLabel.Body}&fileName=${rmaLabel.Name}`);
                        } else {
                            url = buildUrl(BASE_URL, `api/account/cases/${caseId}/rmaLabel?body=${rmaLabel.Body}&fileName=${rmaLabel.Name}`);
                        }

                        rmaLabel.ts = moment(rmaLabel.createdDate).format('MM/DD/YYYY hh:mm A Z');
                        return <QuickRow
                                    key={rmaLabel.id}
                                    columns={[rmaLabel.ts,
                                    <a href={url} download>{rmaLabel.Name}</a>,
                                    rmaLabel.description
                                    ]}
                                />;
                        })
                    }
                </QuickTable>
            </div>
        );
    }
}

CaseRmaLabels.propTypes = {
    rmaLabels: PropTypes.array,
};

export default CaseRmaLabels;

