const $ = require('jquery');
const _ = require('underscore');
var Marionette = require('backbone.marionette');
var stickit = require('backbone.stickit');
var validation = require('backbone-validation');
var FormBehavior = require('app/common/FormBehavior');
require('bootstrap-datepicker');
//require('bootstrap-timepicker');
const moment = require('moment');
var tpl = require('raw-loader!./MaintenanceWindowFormView.html');
const AsyncView = require('../../../common/AsyncView');
const App = require('app/Application');

const View = Marionette.LayoutView.extend({

    className: 'maintenance-window-details animated fadeIn',

    template: _.template(tpl),

    templateHelpers: function () {
        return {
            isNew: this.model.isNew(),
        };
    },

    behaviors: {
        FormBehavior: {
            behaviorClass: FormBehavior,
        },
    },

    bindings: {
        '[name="name"]': {
            observe: 'name',
            setOptions: { validate: true },
        },
        '[name="notes"]': {
            observe: 'notes',
        },
        '[name="startTime"]': {
            observe: 'startTime',
            onSet: function (val) {
                var newStartTime = new Date(val + ' ' + this.$('[name="startTimeOnly"]').val());
                this.model.set('trackStartTime', newStartTime);
                return !val ? null : newStartTime;
            },
            onGet: function (d) {
                var startTimeOnly = !d ? '' : moment(d).format('hh:mm A');
                this.model.set('startTimeOnly', startTimeOnly);
                this.model.set('trackStartTime', d);
                return !d ? '' : moment(d).format('MM/DD/YYYY');
            },
            setOptions: { validate: true },
        },
        '[name="startTimeOnly"]': {
            observe: 'startTimeOnly',
            onSet: function (val) {
                var currentStartDate = this.$('[name="startTime"]').val();
                var newStartTime = new Date(currentStartDate + ' ' + val);
                this.model.set('trackStartTime', newStartTime);
                return val;
            },
            setOptions: { validate: true },
        },
        '[name="endTime"]': {
            observe: 'endTime',
            onSet: function (val) {
                var newEndTime = new Date(val + ' ' + this.$('[name="endTimeOnly"]').val());
                this.model.set('trackEndTime', newEndTime);
                return !val ? null : newEndTime;
            },
            onGet: function (d) {
                var endTimeOnly = !d ? '' : moment(d).format('hh:mm A');
                this.model.set('endTimeOnly', endTimeOnly);
                this.model.set('trackEndTime', d);
                return !d ? '' : moment(d).format('MM/DD/YYYY');
            },
            setOptions: { validate: true },
        },
        '[name="endTimeOnly"]': {
            observe: 'endTimeOnly',
            onSet: function (val) {
                var currentEndTime = this.$('[name="endTime"]').val();
                var newEndTime = new Date(currentEndTime + ' ' + val);
                this.model.set('trackEndTime', newEndTime);
                return val;
            },
            setOptions: { validate: true },
        },
    },

    ui: {
        submitButton: 'button[name="submit"]',
        unselectedArrays: 'select[name="arrays-unselected"]',
        selectedArrays: 'select[name="arrays"]',
        selectArray: 'button[name="array-select"]',
        unselectArray: 'button[name="array-unselect"]',
    },

    events: {
        'click @ui.selectArray': 'selectArray',
        'click @ui.unselectArray': 'unselectArray',
    },

    triggers: {
        'click button[name="submit"]': 'submit',
        'click button[name="cancel"]': 'cancel',
    },

    initialize: function (options) {
        this.listenTo(this.model, 'validated', this.onValidated);
    },

    onRender: function () {
        var self = this;

        // Need to eager init so first click on time icon works
        //this.$('[data-provide="timepicker"]').timepicker();
        const { arrays } = this;
        arrays.each(function (array) {
            var hostName = !array.get('hostname') ? '' : array.get('hostname');
            self.ui.unselectedArrays.append(
                $('<option>')
                    .text(hostName + ' (' + array.get('serialNumber') + ')')
                    .val(array.get('serialNumber')),
            );
        });

        this.model.get('arrays').forEach(function (arraySn) {
            var hostName = arrays.findWhere({ serialNumber: arraySn }).get('hostname') || '';
            self.ui.selectedArrays.append(
                $('<option>')
                    .text(hostName + ' (' + arraySn + ')')
                    .val(arraySn),
            );
        });

        if (this.model.isNew()) {
            this.onValidated(false);
        }
    },

    selectArray: function (event) {
        event.preventDefault();
        this.ui.selectedArrays.append(this.ui.unselectedArrays.find('option:selected'));
        this.refreshArraysInModel();
    },

    unselectArray: function (event) {
        event.preventDefault();
        this.ui.unselectedArrays.append(this.ui.selectedArrays.find('option:selected'));
        this.refreshArraysInModel();
    },

    refreshArraysInModel: function () {
        this.model.set('arrays',
            this.ui.selectedArrays.find('option')
                .map(function () {
                    return this.value;
                })
                .get(), { validate: true });
    },

    onValidated: function (isValid) {
        this.ui.submitButton.prop('disabled', !isValid);
        this.trigger('validated', isValid);
    },

});

module.exports = AsyncView.extend({
    ViewConstructor: View,

    load() {
        const deferred = $.Deferred();
        const { view } = this;

        App.request('global', 'get:assets:async')
            .then((arrays) => {
                view.arrays = arrays;
                deferred.resolve(view);
            })
            .catch((...args) => deferred.reject(...args));

        return deferred;
    },
});
