var Backbone = require('backbone'),
    MaintenanceWindowModel = require('./MaintenanceWindowModel');

module.exports = Backbone.Collection.extend({

    model: MaintenanceWindowModel,

    url: function () {
        return this.api().central().account().segment('maintenancewindow');
    },

    parse(response) {
        return response.sort((a, b) => -(a.startTime - b.startTime));
    },
});
