import React, { Component } from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import moment from 'moment-timezone';
import classnames from 'classnames/bind';
import Switch from './Switch';
import style from './style.scss';

const cx = classnames.bind(style);

export default class RealtimeDeliveryCell extends Component {
    constructor(props) {
        super(props);
        autobind(this);
    }

    handleChange(event) {
        this.props.handleChange({
            arraySn: this.props.arraySn,
            setting: 'realtime',
            enabled: event.target.checked,
        });
    }

    renderStatus() {
        return (
            <span>{this.props.latestDelivery && moment(this.props.latestDelivery).fromNow()}</span>
        );
    }

    render() {
        return (
            <div className={cx('switch-with-status')}>
                <Switch handleChange={this.handleChange} on={this.props.enabled} />
                {this.props.enabled && this.renderStatus()}
            </div>
        );
    }
}

RealtimeDeliveryCell.propTypes = {
    handleChange: PropTypes.func.isRequired,
    arraySn: PropTypes.string.isRequired,
    latestDelivery: PropTypes.any,
    enabled: PropTypes.bool,
};
