import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Async from '@infosight/elmer/dist/components/Async';
import NoData from '@infosight/elmer/dist/components/NoData';
import { InfrastructurePages } from '@infosight/elmer/dist/infrastructure';
import { fetchInventory, reset, inventorySelector } from './inventory';
import { productInterface as config } from './config'; // eslint-disable-line import/no-named-as-default
import { bootstrapSelector } from '../../bootstrapReducer';

class NimbleInfrastructureRouter extends Component {
    componentDidMount() {
        const { loadedObjects, loadingObjects, loadedBootstrap } = this.props;
        if (!loadedObjects && !loadingObjects && loadedBootstrap) {
            this.props.fetchInventory();
        }
    }

    render() {
        const { loadedObjects, loadingObjects } = this.props;
        return (
            <Async loading={loadingObjects}>
                <NoData hasData={loadedObjects}>
                    {() => <InfrastructurePages productInterface={config} />}
                </NoData>
            </Async>
        );
    }
}

NimbleInfrastructureRouter.propTypes = {
    fetchInventory: PropTypes.func.isRequired,
    loadingObjects: PropTypes.bool,
    loadedObjects: PropTypes.bool,
    loadedBootstrap: PropTypes.bool,
};

const mapStateToProps = state => ({
    ...inventorySelector(state),
    ...bootstrapSelector(state),
});
const mapDispatchToProps = { fetchInventory, resetInventory: reset };

export default connect(mapStateToProps, mapDispatchToProps)(NimbleInfrastructureRouter);
