/**
 * A mixin for a Backbone.Collection or Backbone.Model that supports being filtered
 * by an "inventory" object (group, array, volume, etc.)
 */
var InventoryFilter = require('./InventoryFilter');
var Api = require('app/modules/api/Api');

module.exports = {

    initialize: function (arg1, options) {
        /*jshint expr:true */
        options || (options = {});

        this.inventoryFilter = options.inventoryFilter || new InventoryFilter();

        this.listenTo(this, 'api:create', this.decorateApi);

        // TODO listen for 'change' events on the inventoryFilter model and
        // trigger a refetch of this collection/model
    },

    decorateApi: function (api) {
        api.addQuery(this.inventoryFilter.toJSON());
    },

};
