var Backbone = require('backbone');
var PageableCollection = require('backbone.paginator');
var HitModel = require('./HitModel');

module.exports = PageableCollection.extend({

    model: HitModel,

    mode: 'client',

    state: {
        firstPage: 0,
        pageSize: 50,
    },

    comparator: function (a, b) {
        var aKey = a.sortKey();
        var bKey = b.sortKey();
        for (var i = 0; i < Math.min(aKey.length, bKey.length); i++) {
            if (aKey[i].value > bKey[i].value) {
                return aKey[i].order == 'asc' ? 1 : -1;
            } else if (aKey[i].value < bKey[i].value) {
                return aKey[i].order == 'asc' ? -1 : 1;
            }
        }
        return 0;
    },

});
