// DatePicker.js

// import $ from 'jquery';

import React from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';

import Flatpickr from 'flatpickr';
import 'flatpickr/dist/flatpickr.css';

import getSingleDate from './getSingleDate';
import DateTime from '../../components/DateTime';

import style from './style.scss';

const cx = classnames.bind(style);


class DatePicker extends React.Component {
    constructor(props) {
        super(props);
        autobind(this);

        this.inputElem = null; // DOM reference
        this.datePicker = null; // Flatpickr instance
    }

    componentDidMount() {
        const p = this.props;

        const enableTime = ('enableTime' in p) ? !!p.enableTime : false;
        const allowInput = ('allowInput' in p) ? !!p.allowInput : true;

        const { mode, altFormat, minDate } = p;

        const config = {
            minDate,
            enableTime,
            allowInput,
            altInput: false,
            altFormat: altFormat || 'F j, Y h:i K',
            animate: false,
            dateFormat: altFormat || 'F j, Y h:i K',
            mode: mode || 'single', // single, multiple, or range
            onChange: this.eventChange,
            onClose: this.eventClose,
            onOpen: this.eventOpen,
            onReady: this.eventReady,
        };
        this.datePicker = new Flatpickr(this.inputElem, config);
    }

    shouldComponentUpdate() {
        return false;
    }

    componentWillUnmount() {
        this.datePicker.destroy();
    }

    eventChange(dates) {
        this.datePicker.close();
        const dateString = getSingleDate(dates);
        const dateObj = DateTime({ date: dateString, format: 'YYYYMMDD' });
        if (typeof this.props.onChange === 'function') {
            if (dateObj) {
                this.props.onChange.call(null, { date: dateObj.props.children, subfilter: this.props.subfilter });
            } else {
                this.props.onChange.call(null, { date: null, subfilter: this.props.subfilter });
            }
        }
    }

    // eventKeyDown(e) {
    // const dateString = getSingleDate(dates);
    // if (typeof this.props.onChange === 'function') this.props.onChange.call(null, dateString);
    // }

    eventClose(stuff) {
        if (typeof this.props.onClose === 'function') this.props.onClose.call(null, stuff);
    }

    eventOpen(stuff) {
        if (typeof this.props.onOpen === 'function') this.props.onOpen.call(null, stuff);
    }

    eventReady(stuff) {
        if (typeof this.props.onReady === 'function') this.props.onReady.call(null, stuff);
    }

    eventClear(e) {
        e.preventDefault();
        this.datePicker.clear();
        if (typeof this.props.onChange === 'function') this.props.onChange.call(null, '');
        return false;
    }

    render() {
        return (
            <span>
                {/* note: do not bind things to this element as it will confuse the library */}
                <input
                    className={cx('nmbl-date-picker')}
                    ref={(eref) => { this.inputElem = eref; }}
                    id={this.props.id}
                    type="text"
                    placeholder="Select Date..."
                    // onKeyPress={this.eventKeyDown}
                />
                <button className={cx('clear', 'close')} onClick={this.eventClear}>x</button>
            </span>
        );
    }
}

DatePicker.propTypes = {
    id: PropTypes.string,
    subfilter: PropTypes.string,
    // enableTime: PropTypes.bool,
    // mode: PropTypes.string,
    // minDate: PropTypes.string,
    // minDate: PropTypes.string,
    onChange: PropTypes.func,
    onClose: PropTypes.func,
    onOpen: PropTypes.func,
    onReady: PropTypes.func,
};

export default DatePicker;
