const $ = require('jquery');
const _ = require('underscore');
var Marionette = require('backbone.marionette'),
    tpl = require('raw-loader!./DataProtectionView.html'),
    errorTpl = require('raw-loader!app/templates/components/error.html'),
    DisasterRecoveryModel = require('./DisasterRecoveryModel'),
    LocalRetentionModel = require('./LocalRetentionModel'),
    RPOModel = require('./RPOModel'),
    DisasterRecoveryView = require('./DisasterRecoveryView'),
    LocalRetentionView = require('./LocalRetentionView'),
    RPOView = require('./RPOView'),
    Spinner = require('app/views/components/Spinner');

var template = _.template(tpl);

module.exports = Marionette.ItemView.extend(
    /** @lends views/components/dashboard/DataProtectionView.prototype */ {

        /**
         * @classdesc Renders the Data Protection section of the Dashboard.
         * @name views/components/dashboard/DataProtectionView
         * @constructor
         * @augments Marionette.ItemView
         */

        className: 'data-protection-view',

        render: function () {
            this.$el.html(template());

            var self = this;

            this.disasterRecoveryModel = new DisasterRecoveryModel({});
            this.localRetentionModel = new LocalRetentionModel({});
            this.rpoModel = new RPOModel({});

            var handleErrorRPO = function (response) {
                var message = 'RPO Information is not available.';
                var errorTemplate = _.template(errorTpl);
                self.$el.prepend(errorTemplate({
                    message: message,
                }));
            };
            var handleErrorLR = function (response) {
                var message = 'Local Retention Information is not available.';
                var errorTemplate = _.template(errorTpl);
                self.$el.prepend(errorTemplate({
                    message: message,
                }));
            };
            var handleErrorDR = function (response) {
                var message = 'DR Information is not available.';
                var errorTemplate = _.template(errorTpl);
                self.$el.prepend(errorTemplate({
                    message: message,
                }));
            };

            this.spinner = new Spinner();
            this.$el.find('#data-protection-spinner').append(this.spinner.render().el);

            $.when(
                // fetch RPO
                this.rpoModel.fetch()
                    .done(function () {
                        self.rpoView = new RPOView({ model: self.rpoModel });
                        self.$el.find('.rpo-container').append(self.rpoView.render().el);
                    }).fail(handleErrorRPO),
                // fetch loca retention
                this.localRetentionModel.fetch()
                    .done(function () {
                        self.localRetentionView = new LocalRetentionView({
                            model: self.localRetentionModel,
                        });
                        self.$el.find('.local-retention-container').append(
                            self.localRetentionView.render().el);
                    }).fail(handleErrorLR),
                // fetch DR
                this.disasterRecoveryModel.fetch()
                    .done(function () {
                        self.disasterRecoveryView = new DisasterRecoveryView({
                            model: self.disasterRecoveryModel,
                        });
                        self.$el.find('.disaster-recovery-container').append(
                            self.disasterRecoveryView.render().el);
                    }).fail(handleErrorDR),
            ).always(function () {
                self.spinner.hide();
            });

            return this;
        },

        onDestroy: function () {

            // cleanup models
            if (this.disasterRecoveryModel) {
                this.disasterRecoveryModel.clear({ silent: true });
                delete this.disasterRecoveryModel;
            }
            if (this.localRetentionModel) {
                this.localRetentionModel.clear({ silent: true });
                delete this.localRetentionModel;
            }
            if (this.rpoModel) {
                this.rpoModel.clear({ silent: true });
                delete this.rpoModel;
            }

            // cleanup views
            if (this.rpoView) {
                this.rpoView.destroy();
                delete this.rpoView;
            }
            if (this.localRetentionView) {
                this.localRetentionView.destroy();
                delete this.localRetentionView;
            }
            if (this.disasterRecoveryView) {
                this.disasterRecoveryView.destroy();
                delete this.disasterRecoveryView;
            }
        },

    });
