const _ = require('underscore');
var Marionette = require('backbone.marionette'),
    SpinnerModel = require('./SpinnerModel'),
    tpl = require('raw-loader!app/templates/components/Spinner.html');

module.exports = Marionette.ItemView.extend(
    /** @lends views/components/Spinner.prototype */ {

        /**
         * @classdesc <span style="color: red;">Developer description to be added.</span>
         * @name views/components/Spinner
         * @constructor
         * @augments Backbone.View
         */

        template: _.template(tpl),

        className: 'spinner-view',

        initialize: function (options) {
            this.model = new SpinnerModel();
            if (options && options.message) {
                this.model.set('message', options.message);
            }
        },

        show: function () {
            this.$el.show();
        },

        hide: function () {
            this.$el.hide();
        },

    });
