const $ = require('jquery');
const App = require('app/Application');
const _ = require('underscore');
const { SERVER_BASE_URL } = require('progeny/config');
const { buildUrl } = require('@infosight/elmer/dist/utils/url');

var Marionette = require('backbone.marionette');
var LabAppCollection = require('./LabAppCollection');
var itemTpl = require('raw-loader!./LabsItemView.html');
var tpl = require('raw-loader!./LabsView.html');
var moment = require('moment');

var ItemView = Marionette.ItemView.extend({

    tagName: 'li',
    class: 'lab-app',

    template: _.template(itemTpl),

    attributes: function () {
        return {
            'data-app-id': this.model.id,
        };
    },

    templateHelpers() {
        return {
            moment,
            url: (...args) => buildUrl(SERVER_BASE_URL, this.getOption('path'), ...args),
        };
    },
});

module.exports = Marionette.CompositeView.extend({

    template: _.template(tpl),

    className: 'labs-view',

    childView: ItemView,

    childViewOptions() {
        return { path: this.getOption('path') };
    },

    childViewContainer: 'ul.labs-apps',

    events: {
        'click a[data-category]': 'filterLabApps',
    },

    selectedCategory: 'all',

    filterLabApps: function (e) {
        // debugger; // jshint ignore: line

        var thisView = this;

        var btn = $(e.currentTarget);
        thisView.selectedCategory = btn.attr('data-category');

        var recollection = this.getOption('collection').filter(function (a, b, c) {
            if (thisView.selectedCategory === 'all') {
                return true;
            }
            return a.get('category') === thisView.selectedCategory;
        });

        thisView.collection = new LabAppCollection(recollection);
        thisView.render();

        thisView.$el.find('ul.labs-nav li a').removeClass('selected');
        thisView.$el.find('ul.labs-nav li a[data-category=' + this.selectedCategory + ']').addClass('selected');
    },

    initialize: function () {

        // prevent access with "All Accounts" mode
        var accountSelection = App.request('global', 'get:account:selection');
        if (!accountSelection.isSingle()) {
            setTimeout(function () {
                window.location = buildUrl(SERVER_BASE_URL, '/#assets');
            }, 50);
        }
    },

});
