// CaseInformation.js
/* eslint-disable */

import React from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import { buildUrl } from '@infosight/elmer/dist/utils/url';
import { Link } from 'react-router-dom';

import DateTime from '../../../../../components/DateTime'

// import BackBanner from '../../BackBanner';

import CaseComments from '../CaseComments';
import CaseAttachments from '../CaseAttachments';
import CaseRmaLabels from '../CaseRmaLabels';
import CaseSection from '../CaseSection';
import PanelArrayMeta from '../PanelArrayMeta';
import PanelCaseMeta from '../PanelCaseMeta';
import AddComment from '../AddComment';
import AddAttachment from '../AddAttachment';
import CaseResolver from '../CaseResolver';

import style from './CaseInformation.scss';
import { AUTHORITIES, AuthorizationEvaluator } from '../../../../user';

const cx = classnames.bind(style);


class CaseInformation extends React.Component {

    constructor(props) {
        super(props);
        autobind(this);
        this.state = {
            busyCloseCase: false,
        }
    }

    commentWasPosted(comment) {
        document.getElementById('Comments').scrollIntoView(true);
        this.props.commentDispatch(comment);
    }

    attachmentWasPosted(attachment) {
        document.getElementById('Attachments').scrollIntoView(true);
        this.props.attachmentDispatch(attachment);
    }

    caseWasResolved(updatedCase) {
        const commentsData = updatedCase.comments;
        const attachmentsData = updatedCase.attachments;
        const rmaLabelsData = updatedCase.rmaLabels;

        this.props.resolveDispatch(updatedCase, commentsData, attachmentsData, rmaLabelsData);
        window.scrollTo(0,0);
    }

    renderDescription() {
        const theCase = this.props.theCase;
        const desc = theCase.description || 'No description entered';
        return (<div dangerouslySetInnerHTML={{__html: desc}} />)
    }

    goToResolve() {
        const resolveBox = document.getElementsByClassName('resolve-box')[0];
        resolveBox.scrollIntoView(true);
        resolveBox.focus();
    }

    goToComment() {
        const commentBox = document.getElementsByClassName('comment-box')[0];
        commentBox.scrollIntoView(true);
        commentBox.focus();
    }

    goToAttachment() {
        const attachments = document.getElementById('Attachments');
        attachments.scrollIntoView(true);
    }

    render() {

        const theCase = this.props.theCase;
        const isClosed = !!(theCase.bucket === 'Closed' || theCase.bucket === 'Resolved');
        const userIsInternal = AuthorizationEvaluator.evaluate({ access: { authorities: AUTHORITIES.REALM_INTERNAL } });

        return (
            <div className={cx('case-information')}>

                <div className={cx('case-banner')}>
                    <h4>Case {theCase.caseNumber}</h4>
                    <h2>{theCase.subject}</h2>
                </div>


                <div className={cx('page-inset')}>

                    {(!isClosed && !userIsInternal) ?
                        <div className={cx('action-set')}>
                            <button className="button hollow" onClick={this.goToComment}><i className="fa fa-comment-o" /> Comment</button>
                            <button className="button hollow" onClick={this.goToAttachment}><i className="fa fa-paperclip" /> Attach</button>
                            <button className="button hollow" onClick={this.goToResolve}><i className="fa fa-handshake-o" /> Resolve</button>
                        </div> : ''
                    }

                    <CaseSection>

                        <PanelArrayMeta assetId={theCase.assetId} />
                        <PanelCaseMeta theCase={theCase} />

                    </CaseSection>


                    <CaseSection name="Description">
                        {!("description" in theCase) ?
                            <p>Loading description....<i className="fa fa-spinner fa-spin" /></p> :
                            this.renderDescription()
                        }
                    </CaseSection>


                    <CaseSection name="Comments">
                            {theCase.comments === null ?
                                <p>Loading comments.... <i className="fa fa-spinner fa-spin" /></p> :
                                <CaseComments comments={theCase.comments} />
                            }
                            {( isClosed && !userIsInternal ) &&
                                <p>Further commenting is not allowed for closed cases.</p>
                            }
                            {( !isClosed && !userIsInternal ) &&
                                <AddComment theCase={theCase} onSave={this.commentWasPosted} />
                            }
                    </CaseSection>

                    <CaseSection name="Attachments" text="(25MB size limit)">
                            {theCase.attachments === null ?
                                <p>Loading attachments.... <i className="fa fa-spinner fa-spin" /></p> :
                                <CaseAttachments attachments={theCase.attachments} />
                            }
                            {( isClosed && !userIsInternal ) &&
                                <p>Adding attachments is not allowed for closed cases.</p>
                            }
                            {( !isClosed && !userIsInternal ) &&
                                <AddAttachment theCase={theCase} onSave={this.attachmentWasPosted} />
                            }
                    </CaseSection>

                    <CaseSection name="RMA Labels" text="(may take up to 24 hours to show)">
                            {theCase.rmaLabels === null ?
                                <p>Loading RMA labels.... <i className="fa fa-spinner fa-spin" /></p> :
                                <CaseRmaLabels userIsInternal={userIsInternal} theCase={theCase} />
                            }
                    </CaseSection>

                    {(!userIsInternal || (userIsInternal && isClosed)) &&
                        <CaseSection name="Resolution">
                            {isClosed &&
                                <p>{theCase.casePortalResolutionNotes__c || 'No resolution notes entered.'}</p>
                            }
                            {(!isClosed && !userIsInternal) &&
                                <CaseResolver theCase={theCase} onSave={this.caseWasResolved} />
                            }
                        </CaseSection>
                    }

                    <hr />

                    <p>
                        <Link to="/resources/nimble/support">Return to the support portal.</Link>
                    </p>
                    <br/><br/>
                </div>
            </div>
        );
    }

}

CaseInformation.propTypes = {
    theCase: PropTypes.object.isRequired,
    commentDispatch: PropTypes.func.isRequired,
    attachmentDispatch: PropTypes.func.isRequired,
    resolveDispatch: PropTypes.func.isRequired,
};

export default CaseInformation;
/* eslint-enable */
