import $ from 'jquery';
import React from 'react';
import PropTypes from 'prop-types';
import autobind from 'react-autobind';
import classnames from 'classnames/bind';
import { Spinner } from '@infosight/elmer/dist/components/Spinner';

import SFCaseModel from '../../../library/SFCaseModel';

import style from './AddAttachment.scss';

const cx = classnames.bind(style);


class AddAttachment extends React.Component {
    constructor(props) {
        super(props);
        autobind(this);
        this.state = {
            formBusy: false,
            formBlocked: true,
            fileTooBig: false,
        };
    }

    attachmentPostOk(attachment) {
        document.getElementById('file-chooser').value = null;
        this.setState({ formBusy: false });
        this.setState({ formBlocked: false });

        if (typeof this.props.onSave === 'function') this.props.onSave.call(null, attachment);
    }

    attachmentPostFail() {
        this.setState({ formBusy: false });
        console.warn('attachment failed to post.');
    }

    eventAttachDoc() {
        let fileName = document.getElementById('file-chooser').value;

        if (!fileName) {
            return;
        }

        const caseModel = new SFCaseModel();
        caseModel.set('id', this.props.theCase.id);
        this.casePath = caseModel.url();

        const FILEPATH_REGEX = /.*\\/;
        fileName = fileName.replace(FILEPATH_REGEX, '');

        const file = document.getElementById('file-chooser').files[0];

        const formData = new FormData();
        formData.append('caseId', this.props.theCase.id);
        formData.append('attachment', file, fileName);

        $.ajax({
            type: 'POST',
            url: `${this.casePath}/attachment`,
            data: formData,
            contentType: false,
            processData: false,
        })
            .then(this.attachmentPostOk, this.attachmentPostFail);

        this.setState({ formBlocked: true, formBusy: true });
    }

    filePicked() {
        const file = document.getElementById('file-chooser');
        const fileName = file.value;
        const fileSize = file.files[0].size;
        const fileSizeLimit = 25 * (1024 ** 2);

        // SF limits attachments to 25MB
        if (fileSize > fileSizeLimit) {
            this.setState({ fileTooBig: true, formBlocked: true });
        } else if (fileName) {
            this.setState({ fileTooBig: false, formBlocked: false });
        }
    }

    render() {
        const { formBusy, formBlocked, fileTooBig } = this.state;

        return (
            <div className={cx('attach-doc')}>

                <input
                    className={cx('btn-file')}
                    name="file"
                    type="file"
                    id="file-chooser"
                    onChange={this.filePicked}
                />
                <button className="button" onClick={this.eventAttachDoc} disabled={formBlocked}>Upload</button>
                {formBusy && <Spinner />}
                {fileTooBig && <div className={cx('big-file')}>File exceeds the 25MB size limit</div>}
            </div>
        );
    }
}

AddAttachment.propTypes = {
    theCase: PropTypes.object.isRequired,
    onSave: PropTypes.func,
};

export default AddAttachment;
