import { actionsFor } from '@infosight/elmer/dist/utils/redux';
import moment from 'moment-timezone';
import { ArrayHardwareStatus } from '../arrays/collection/components/list/ArrayHealth';
import { FETCH_ALL_ARRAYS, RESET } from './constants';

const Request = require('../../../services/Request').default;

function fetchArrays() {
    return new Request().central().slash('reseller/tenant/arrays').get();
}

function fetchHeartbeats() {
    return new Request().central().slash('reseller/tenant/heartbeats').get();
}

export const getArraysFromAllAccounts = () => (dispatch) => {
    const { dispatchStart, dispatchSuccess, dispatchError } = actionsFor(FETCH_ALL_ARRAYS, dispatch);
    dispatchStart();
    return new Request().all([
        fetchArrays(),
        fetchHeartbeats(),
    ])
        .then(([arrays, heartbeats]) => {
            const arraysData = (arrays && arrays.data) || [];
            const heartbeatsData = (heartbeats && heartbeats.data) || {};
            const arraysWithHeartbeats = arraysData.map((array) => {
                const latestHeartbeat = heartbeatsData[array.arraySN];
                return ({
                    ...array,
                    arrayHostName: `${array.arraySN}${array.hostName ? array.hostName : ''}`,
                    hardwareStatusComparator: ArrayHardwareStatus.comparator(latestHeartbeat),
                    supportEndDate: moment(array.supportEndDate).isAfter(moment().add(10, 'years')) ? null : array.supportEndDate,
                    latestHeartbeat,
                });
            });
            return arraysWithHeartbeats;
        })
        .then(dispatchSuccess)
        .catch(dispatchError);
};

export const reset = () => ({ type: RESET });
