const { buildUrlFromBase } = require('progeny/util/url');
const { Highstock: Highcharts } = require('progeny/modules/nimble/highchartsProxy');
var numeral = require('numeral');
var _ = require('underscore');

module.exports = function (opts) {
    var dataValues = opts.dataValues;
    var categories = opts.categories;
    var element = (opts.element.get ? opts.element.get(0) : opts.element);
    var yAxisLabelFormatter = opts.yAxisLabelFormatter;
    var tooltipFormatter = opts.tooltipFormatter;

    var maxAxisValue = Math.ceil(_.max(dataValues));

    // Workaround Highchart's expansion of individual bars to fill the chart height rather than shrink the overall chart height.
    var LABEL_HEIGHT = 25;
    var PADDED_BAR_HEIGHT = 31;
    var height = LABEL_HEIGHT + dataValues.length * PADDED_BAR_HEIGHT;

    return new Highcharts.Chart(element, {
        chart: {
            type: 'bar',
            alignTicks: false,
            marginTop: 10,
            height: height,
        },
        title: {
            text: null,
        },
        plotOptions: {
            bar: {
                minPointLength: 3,
                pointWidth: 10,
            },
        },
        xAxis: {
            categories: categories,
            title: {
                text: null,
            },
            labels: {
                formatter: function () {
                    if (this.value.id) {
                        return `<a title="${this.value.name}" class="history-aware" href="${buildUrlFromBase('/infrastructure/virtualization/vmware/vms', this.value.id)}">${this.value.name}</a>`;
                    }
                },
                useHTML: true,
            },
            lineWidth: 0,
            tickWidth: 0,
        },
        yAxis: [
            {
                opposite: true,
                offset: -25,
                title: {
                    text: null,
                },
                labels: {
                    overflow: 'justify',
                    formatter: yAxisLabelFormatter,
                    y: -17,
                },
                gridLineWidth: 0,
                lineWidth: 0,
                showFirstLabel: false,
                tickWidth: 0,
                tickPositions: [0, maxAxisValue],
            },
        ],
        legend: {
            enabled: false,
        },
        tooltip: {
            formatter: tooltipFormatter,
            useHTML: true,
        },
        credits: {
            enabled: false,
        },
        series: [
            {
                name: 'TopVMs',
                data: dataValues,
                color: '#95cfdb',
                tooltip: {
                    valueSuffix: '',
                },
            },
        ],
    });
};
