import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames/bind';

import style from './style.scss';

const cx = classnames.bind(style);

let idCounter = 0;

const Switch = ({ on, handleChange, onLabel, offLabel, className }) => {
    const switchId = `switch${idCounter++}`; // eslint-disable-line no-plusplus
    return (
        <div className={classnames(className, cx('switch', 'small'))}>
            <input
                className={cx('switch-input')}
                id={switchId}
                type="checkbox"
                onChange={handleChange}
                checked={on}
            />
            <label className={cx('switch-paddle')} htmlFor={switchId}>
                <span className={cx('switch-active')}>{onLabel}</span>
                <span className={cx('switch-inactive')}>{offLabel}</span>
            </label>
        </div>
    );
};

Switch.propTypes = {
    on: PropTypes.bool,
    handleChange: PropTypes.func,
    onLabel: PropTypes.string,
    offLabel: PropTypes.string,
};

Switch.defaultProps = {
    onLabel: 'On',
    offLabel: 'Off',
};

export default Switch;
